import { useEffect, useState, VFC } from 'react'
import { FieldRenderProps, Field } from 'react-final-form'
import { components } from 'types/openapi'
import { OptgroupsT, Select, Value } from '@jifflenow/brightui/select'
import { defineMessages, useIntl } from 'react-intl'
import { DurationError } from '../common'
import { SelectContainer } from './styled-components'
import { CopyNotificationsProps, CopyNotificationsSelectProps } from './types'

type Role = components['schemas']['Role']
type EventUser = components['schemas']['EventUser']

const messages = defineMessages({
  allMms: {
    defaultMessage: 'All MMs',
    description: 'Option label',
  },
  allJmms: {
    defaultMessage: 'All JMMs',
    description: 'Option label',
  },
  meetingManager: {
    defaultMessage: 'Meeting Manager',
    description: 'Option label',
  },
  juniorMM: {
    defaultMessage: 'Junior MM',
    description: 'Option label',
  },
  jmmMappedWithTopic: {
    defaultMessage: 'JMMs mapped to Topic',
    description: 'Option label',
  },
  jmmMappedToMeetingTypes: {
    defaultMessage: 'JMMs mapped to meeting type',
    description: 'Option label',
  },
  executiveAdmin: {
    defaultMessage: 'Executive Admin',
    description: 'Option label',
  },
})

const filterRole = (userRole: Role) => ({ attributes: { role } }: EventUser) =>
  role === userRole

const optionMapper = ({
  id,
  attributes: { role, firstName, lastName },
}: EventUser) => ({
  label: `${firstName} ${lastName}`,
  id,
  role,
})

const CopyNotificationsSelect: VFC<
  FieldRenderProps<string[]> & CopyNotificationsSelectProps
> = ({
  input: { value: fieldValue, onChange },
  meta: { error, submitError },
  label,
  eventUsers,
}) => {
  const [dropdownOptions, setDropdownOptions] = useState<OptgroupsT>()
  const { formatMessage } = useIntl()
  useEffect(() => {
    if (eventUsers?.length) {
      setDropdownOptions({
        __ungrouped: [
          {
            label: formatMessage(messages.allMms),
            id: 'mms',
          },
          ...(eventUsers.some(({ attributes: { role } }) => role === 'JMM')
            ? [
                {
                  label: formatMessage(messages.jmmMappedWithTopic),
                  id: 'jmm_mapped_with_topic',
                },
                {
                  label: formatMessage(messages.allJmms),
                  id: 'jmms',
                },
                {
                  label: formatMessage(messages.jmmMappedToMeetingTypes),
                  id: 'jmm_mapped_to_meeting_types',
                },
              ]
            : []),
          {
            label: formatMessage(messages.executiveAdmin),
            id: 'eas',
          },
        ],
        [formatMessage(messages.meetingManager)]: eventUsers
          .filter(filterRole('MM'))
          .map(optionMapper),
        [formatMessage(messages.juniorMM)]: eventUsers
          .filter(filterRole('JMM'))
          .map(optionMapper),
      })
    }
  }, [eventUsers, formatMessage])

  const selectedValues = [
    ...(dropdownOptions?.__ungrouped ?? []),
    ...(dropdownOptions?.[formatMessage(messages.meetingManager)] ?? []),
    ...(dropdownOptions?.[formatMessage(messages.juniorMM)] ?? []),
  ].filter(({ id }) => fieldValue.some(value => id === value))

  return (
    <>
      {(error || submitError) && (
        <DurationError>{error || submitError}</DurationError>
      )}

      <SelectContainer label={label}>
        <Select
          options={dropdownOptions}
          clearable={false}
          multi
          value={selectedValues}
          onChange={({ value }: { value: Value }) => {
            let options = value.some(({ id }) => id === 'mms')
              ? value.filter(({ role }) => role !== 'MM')
              : value
            options = options.some(({ id }) => id === 'jmms')
              ? options.filter(({ role }) => role !== 'JMM')
              : options
            onChange(options.map(({ id }) => id))
          }}
          dataPendo="cc_notification_reply_to"
        />
      </SelectContainer>
    </>
  )
}

export const CopyNotifications: VFC<CopyNotificationsProps> = ({
  name,
  ...rest
}) => (
  <Field
    name={name}
    format={value => (value ? JSON.parse(value) : [])}
    parse={value => JSON.stringify(value ?? [])}
    render={props => <CopyNotificationsSelect {...props} {...rest} />}
  />
)
