import { ComponentType } from 'react'
import { useEventLocation } from 'providers/LocationProvider'
import { useMe } from 'providers/MeProvider'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { EngagementBasics } from './EngagementBasics'
import { TopicOptions } from './TopicOptions'
import { ApprovalProcess } from './ApprovalProcess'
import { Irp } from './Irp'
import { LocationTypes } from './LocationTypes'
import { SetAvailability } from './SetAvailability'
import { Paths } from './Paths'
import { Participants } from './Participants'
import { AdvancedSettings } from './AdvancedSettings'
import { CsmOnly } from './CsmOnly'
import { Notifications } from './Notifications'
import { NotificationsDetails } from './NotificationsDetails'
import { LinkSettings } from './LinkSettings'
import { Surveys } from './Surveys'
import { OutboundMeeting } from './Forms/OutboundMeeting'
import { InboundMeeting } from './Forms/InboundMeeting'
import { Survey } from './Forms/Survey'
import { useEngagementType } from './EngagementTypeProvider'

type Layout = { path: Paths; component: ComponentType }

// eslint-disable-next-line complexity
export const LayoutsV2 = () => {
  const {
    engagementTypeSurveys,
    engagementTypeSettings: {
      enable_external_widget: enable_external_widget_setting,
    },
    engagementType: {
      templateEngagementType: { masterEngagementType },
      status: engagementTypeEnabled,
    },
  } = useEngagementType()
  const { privileges, configs } = useEventLocation()
  const {
    enable_external_widget,
    enable_topic,
    enable_topic_to_activity,
    enable_room_to_topic,
    enable_user_to_topic,
  } = configs!
  const { role } = useMe().data.attributes

  const activeSurveys = engagementTypeSurveys.filter(({ status }) => status)

  const showSettings =
    masterEngagementType !== MasterEngagementTypes.ConsecutiveMeeting

  const layouts: Layout[] = [
    { path: Paths.BASICS, component: EngagementBasics },
    ...(showSettings
      ? [
          { path: Paths.SETTING, component: LocationTypes },
          { path: Paths.SET_AVAILABILITY, component: SetAvailability },
          { path: Paths.PARTICIPANTS, component: Participants },
          ...(enable_topic === 'true' &&
          enable_topic_to_activity === 'true' &&
          (enable_room_to_topic === 'true' || enable_user_to_topic === 'true')
            ? [{ path: Paths.TOPIC_OPTIONS, component: TopicOptions }]
            : []),
          { path: Paths.APPROVAL_PROCESS, component: ApprovalProcess },
          { path: Paths.LINK_SETTINGS, component: LinkSettings },
          ...(enable_external_widget === 'true'
            ? [{ path: Paths.INBOUND_REQUEST_FORM, component: Irp }]
            : []),
          ...(privileges?.canManageSurvey &&
          privileges.canManageSelfServeSettings &&
          !!engagementTypeSurveys.length
            ? [{ path: Paths.SURVEY, component: Surveys }]
            : []),
          { path: Paths.ADVANCED_SETTINGS, component: AdvancedSettings },
          ...(engagementTypeEnabled &&
          privileges?.canManageForms &&
          privileges.canManageSelfServeSettings
            ? [
                {
                  path: Paths.OUTBOUND_MEETING_FORM,
                  component: OutboundMeeting,
                },
                ...(enable_external_widget === 'true' &&
                enable_external_widget_setting === 'true'
                  ? [
                      {
                        path: Paths.INBOUND_MEETING_FORM,
                        component: InboundMeeting,
                      },
                    ]
                  : []),
                ...(activeSurveys.length
                  ? [{ path: Paths.SURVEY_FORM, component: Survey }]
                  : []),
              ]
            : []),
          ...(role === 'CSM'
            ? [{ path: Paths.CSM_ONLY, component: CsmOnly }]
            : []),
          ...(engagementTypeEnabled &&
          privileges?.canManageNotifications &&
          privileges.canManageSelfServeSettings
            ? [{ path: Paths.NOTIFICATIONS, component: Notifications }]
            : []),
          {
            path: Paths.NOTIFICATIONS_DETAILS,
            component: NotificationsDetails,
          },
        ]
      : []),
  ]

  return layouts
}
