import { useEffect, useState, useMemo, VFC } from 'react'
import { Form as FinalForm } from 'react-final-form'
import { defineMessages, useIntl } from 'react-intl'
import { Prompt } from 'react-router-dom'
import { Config, FORM_ERROR, ValidationErrors } from 'final-form'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import {
  BulkEngagementSettingUpdateInput,
  DemoSubTopicDataInput,
} from 'gql/types/globalTypes'
import {
  mutateBulkUpsertEngagementSettings,
  useApolloClient,
  useGetEngagementTypeSettings,
} from 'gql'
import {
  getEngagementTypeSettings_getEngagementTypeSettings_value_CustomNameValue as CustomNameValue,
  getEngagementTypeSettings_getEngagementTypeSettings_value_MeetingHookValue as MeetingHookValue,
  getEngagementTypeSettings_getEngagementTypeSettings_value_MeetingHookValue_MeetingHookValue as MeetingHookValue_MeetingHookValue,
  getEngagementTypeSettings_getEngagementTypeSettings_value_DemoSubTopic as DemoSubTopics,
  getEngagementTypeSettings_getEngagementTypeSettings_value_DemoSubTopic_DemoSubTopicData as DemoSubTopicsData,
} from 'gql/types/getEngagementTypeSettings'
import {
  bulkUpsertEngagementSettingsVariables,
  bulkUpsertEngagementSettings_bulkUpsertEngagementSettings_engagementSettings_value_CustomNameValue as UpsertCustomNameValue,
} from 'gql/types/bulkUpsertEngagementSettings'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { useEventLocation } from 'providers/LocationProvider'
import { useEngagementMeetingData } from 'rest'
import {
  Body,
  Footer,
  StyledForm,
  Container,
  usePageTitle,
  Header,
} from '../common'
import { useSettingsLoader } from '../common/useSettingsLoader'
import { filterEngagementSettings } from '../common/filterEngagementSettings'
import { updateQueryResults } from '../common/utils'
import { useEngagementType } from '../EngagementTypeProvider'
import { Loader } from './Loader'
import { CsmOnlyTitle } from './styled-components'
import { FormField } from './FormField'
import {
  DependentFields,
  FormProps,
  FormValues,
  Fields,
  MeetingSubmitHookField,
} from './types'
import { mappingDecorator } from './Mappings'
import { manualSchedulingDecorator } from './ManualScheduling'

const shallowEqual = (
  object1: { [key: string]: string },
  object2: { [key: string]: string }
): boolean => {
  const keys1 = Object.keys(object1)
  const keys2 = Object.keys(object2)
  if (keys1.length !== keys2.length) {
    return false
  }

  for (const key of keys1) {
    if (object1[key] !== object2[key]) {
      return false
    }
  }

  return true
}

const initialSubTopics = (demoSubTopicData: DemoSubTopicsData[] | null = []) =>
  demoSubTopicData!.reduce(
    (acc, value) => ({
      ...acc,
      ...(value.value ? { [value.key]: value.value } : null),
    }),
    {}
  )

const changeSubTopicFormValues = (demoSubTopicData: {
  [key: string]: string
}): DemoSubTopicDataInput[] =>
  Object.keys(demoSubTopicData).map(key => ({
    key,
    value: demoSubTopicData[key],
  }))

const LoadingShimmer = () => (
  <Body>
    <Container>
      <Loader />
    </Container>
  </Body>
)

const messages = defineMessages({
  pageTitle: {
    defaultMessage: 'CSM Only',
    description: 'Page title',
  },
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
  updateError: {
    defaultMessage:
      '{failureCount, number} out of {totalCount, number} {totalCount, plural, one {option} other {options}} failed to save',
    description: 'Error message',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  sectionTitle: {
    defaultMessage: 'CSM Only',
    description: 'Section title',
  },
  successMessage: {
    defaultMessage: 'Your changes have been saved successfully',
    description: 'Success message',
  },
  fieldErrorMessage: {
    defaultMessage: 'Error saving changes',
    description: 'field error message',
  },
  meetingDurationError: {
    defaultMessage:
      'Meeting break duration should be lesser than meeting duration.',
    description: 'Field error message',
  },
})

const settings = [
  'meeting_break_enabled',
  'meeting_break_meeting_duration',
  'meeting_break_duration',
  'show_meeting_break_notification',
  'default_mapping',
  'assign_topic',
  'assign_topic_mandatory',
  'assign_attendees',
  'assign_internal_attendees',
  'assign_external_attendees',
  'block_attendee_calendar',
  'assign_room',
  'block_room_calendar',
  'guided_tour_survey',
  'quick_demo',
  'demo_topic',
  'enable_manual_scheduling',
  'show_onsite_room_jmm_above',
  'block_user_calendar_on_acceptance',
  'send_mst_mails_to_ea',
  'send_mst_mail_to_attendee_managed_by_ea',
  'make_participants_optional_to_the_meeting',
]

const meetingHookSetting = ['meeting_submit_hook']
const demoSubTopics = ['demo_sub_topics']

const dependentSettings: Array<keyof DependentFields> = [
  'booth_tour_enabled',
  'enable_external_widget_auto_process',
]

const Form: VFC<FormProps> = ({
  formRenderProps: { handleSubmit, dirty },
  ...restProps
}) => {
  const { formatMessage } = useIntl()
  const unload = useBeforeUnload()
  const showLoading = useSettingsLoader(restProps.loading)

  useEffect(() => {
    unload(dirty)
  }, [dirty, unload])

  return (
    <StyledForm onSubmit={handleSubmit} noValidate>
      <Prompt when={dirty} message={formatMessage(messages.promptMessage)} />
      <Header />
      {showLoading ? (
        <LoadingShimmer />
      ) : (
        <Body>
          <Container>
            <CsmOnlyTitle>{formatMessage(messages.sectionTitle)}</CsmOnlyTitle>
            <FormField {...restProps} />
          </Container>
          <Footer />
        </Body>
      )}
    </StyledForm>
  )
}

const cleanMeetingHookData = (value: MeetingHookValue_MeetingHookValue) => {
  const { __typename, url_parameters, ...rest } = value

  return {
    ...rest,
    url_parameters: url_parameters?.map(({ __typename, ...rest }) => rest),
  }
}

export const CsmOnly: VFC = () => {
  const { formatMessage } = useIntl()
  const client = useApolloClient()
  usePageTitle(formatMessage(messages.pageTitle))

  const [showLoader, setShowLoader] = useState(false)

  const {
    id: engagementTypeId,
    uuid: activity_uuid,
  } = useEngagementType().engagementType
  const {
    id: current_location_uuid,
    attributes: { systemName: eventName },
  } = useEventLocation().location!

  const {
    getEngagementTypeSettings = [],
    refetch: refetchEngagementTypeSettings,
    loading,
    updateQuery,
  } = useGetEngagementTypeSettings({
    variables: {
      engagementTypeId,
      names: [
        ...settings,
        ...demoSubTopics,
        ...meetingHookSetting,
        ...dependentSettings,
      ],
    },
    fetchPolicy: 'cache-and-network',
    onError: () => showErrorToast(formatMessage(messages.genericError)),
  })

  const fields = useMemo(
    () => filterEngagementSettings(getEngagementTypeSettings, settings),
    [getEngagementTypeSettings]
  )

  const meetingHookField = useMemo(
    () =>
      filterEngagementSettings(getEngagementTypeSettings, meetingHookSetting),
    [getEngagementTypeSettings]
  )

  const demoSubTopicsField = useMemo(
    () => filterEngagementSettings(getEngagementTypeSettings, demoSubTopics),
    [getEngagementTypeSettings]
  )

  const dependentFields = useMemo(
    () =>
      filterEngagementSettings(getEngagementTypeSettings, dependentSettings),
    [getEngagementTypeSettings]
  )

  const {
    data: { data: engagementMeetingData } = {},
  } = useEngagementMeetingData({
    eventName,
    activity_uuid,
    current_location_uuid,
  })

  const initialValues = useMemo(
    () => ({
      ...fields.reduce(
        (acc, { name, value }) => ({
          ...acc,
          [name]: (value as CustomNameValue).value,
        }),
        {}
      ),
      ...demoSubTopicsField.reduce(
        (acc, { name, value }) => ({
          ...acc,
          [name]: initialSubTopics((value as DemoSubTopics).DemoSubTopicData),
        }),
        {}
      ),
      ...meetingHookField.reduce(
        (acc, { name, value }) => ({
          ...acc,
          [name]: JSON.stringify(
            cleanMeetingHookData((value as MeetingHookValue).MeetingHookValue)
          ),
        }),
        {}
      ),
    }),
    [fields, meetingHookField, demoSubTopicsField]
  )

  const dependentFieldValues = useMemo(
    () => ({
      ...dependentFields.reduce(
        (acc, { name, value }) => ({
          ...acc,
          [name]: (value as CustomNameValue).value,
        }),
        {}
      ),
    }),
    [dependentFields]
  ) as DependentFields

  const validate: Config<FormValues>['validate'] = ({
    meeting_break_meeting_duration,
    meeting_break_duration,
  }) => {
    const errors: ValidationErrors = {}
    if (+meeting_break_meeting_duration < +meeting_break_duration) {
      errors.meeting_break_meeting_duration = formatMessage(
        messages.meetingDurationError
      )
      errors.meeting_break_duration = formatMessage(
        messages.meetingDurationError
      )
    }

    return errors
  }

  const handleSubmit: Config<FormValues>['onSubmit'] = async (
    values,
    { change }
  ) => {
    try {
      setShowLoader(true)
      let configsToUpdate = fields.reduce<BulkEngagementSettingUpdateInput[]>(
        (acc, { name, value: previousValue }) => {
          const { display_name, value } = previousValue as CustomNameValue

          return value !== values[name as keyof FormValues]
            ? [
                ...acc,
                {
                  name,
                  customValue: {
                    display_name,
                    value: values[name as keyof Fields],
                  },
                },
              ]
            : acc
        },
        []
      )

      configsToUpdate = demoSubTopicsField.reduce<
        BulkEngagementSettingUpdateInput[]
      >((acc, { name, value: previousValue }) => {
        const {
          display_name,
          DemoSubTopicData,
        } = previousValue as DemoSubTopics

        return shallowEqual(
          initialSubTopics(DemoSubTopicData),
          values.demo_sub_topics
        )
          ? acc
          : [
              ...acc,
              {
                name,
                demoSubTopics: {
                  display_name,
                  subTopicConfigName: '',
                  value: changeSubTopicFormValues(values.demo_sub_topics),
                },
              },
            ]
      }, configsToUpdate)

      configsToUpdate = meetingHookField.reduce<
        BulkEngagementSettingUpdateInput[]
      >((acc, { name, value: previousValue }) => {
        const {
          display_name,
          MeetingHookValue,
        } = previousValue as MeetingHookValue

        return JSON.stringify(MeetingHookValue) !==
          values[name as keyof FormValues]
          ? [
              ...acc,
              {
                name,
                hookValue: {
                  display_name,
                  value: JSON.parse(
                    values[name as keyof MeetingSubmitHookField]
                  ),
                },
              },
            ]
          : acc
      }, configsToUpdate)

      if (configsToUpdate.length) {
        const variables: bulkUpsertEngagementSettingsVariables = {
          engagementTypeId,
          configsToUpdate,
        }
        const { failureCount, totalCount, engagementSettings, successCount } = (
          await mutateBulkUpsertEngagementSettings(client, {
            variables,
          })
        ).data!.bulkUpsertEngagementSettings
        setShowLoader(false)
        if (failureCount) {
          showErrorToast(
            formatMessage(messages.updateError, { totalCount, failureCount })
          )
          engagementSettings.forEach(
            ({ success, name, value }) =>
              !success &&
              change(
                name as keyof FormValues,
                (value as UpsertCustomNameValue).value ?? undefined
              )
          )
          const errors = engagementSettings.reduce(
            (acc, { success, name }) =>
              success
                ? acc
                : {
                    ...acc,
                    [name]: formatMessage(messages.fieldErrorMessage),
                  },
            {}
          )
          if (successCount) {
            updateQueryResults(updateQuery, engagementSettings)
          }

          return errors
        }
        showSuccessToast(formatMessage(messages.successMessage))
        updateQueryResults(updateQuery, engagementSettings)
      }

      return undefined
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
      return { [FORM_ERROR]: { code: 'GenericError' } }
    }
  }

  return (
    <>
      {showLoader && <ModalSpinner />}
      <FinalForm<FormValues>
        initialValues={initialValues}
        // TODO: Awaiting fix for this [bug](https://github.com/final-form/final-form-calculate/issues/38)
        // to remove `ts-expect-error`.
        // @ts-expect-error
        decorators={[mappingDecorator, manualSchedulingDecorator]}
        onSubmit={handleSubmit}
        render={props => (
          <Form
            formRenderProps={props}
            hasActiveMeetings={
              engagementMeetingData?.hasActiveMeetings ?? false
            }
            dependentFieldValues={dependentFieldValues}
            loading={loading}
            refetch={refetchEngagementTypeSettings}
          />
        )}
        validate={validate}
      />
    </>
  )
}
