import { useEffect, useMemo, useState, VFC } from 'react'
import { FieldValidator } from 'final-form'
import { FieldRenderProps } from 'react-final-form'
import { defineMessages, useIntl } from 'react-intl'
import { Select, Option } from '@jifflenow/brightui/select'
import { FormControl } from '@jifflenow/brightui/form-control'
import { Field } from 'ui-components/Field'
import { TimeIncrementProps } from './types'
import { InputFieldContainer } from './styled-components'

const messages = defineMessages({
  timeIncrementTitle: {
    defaultMessage: 'Time increment',
    description: 'Field label',
  },
  timeCanOnlyDecreaseError: {
    defaultMessage: 'Time increment can only be decreased',
    description: 'Error message',
  },
  timeCannotBeMoreThanMinMaxError: {
    defaultMessage: 'Time increment cannot be more than minimum duration',
    description: 'Error message',
  },
})

const MinuteField: VFC<FieldRenderProps<string>> = ({
  input: { value: mins, onChange },
  meta: { submitError, error },
}) => {
  const { formatMessage, formatNumber } = useIntl()
  const [value, setValue] = useState<Option[]>()

  const hoursDropdownOptions = useMemo(
    () =>
      [...Array(60).keys()].map((_, i) => ({
        label: formatNumber(i + 1, {
          style: 'unit',
          unit: 'minute',
          unitDisplay: 'short',
        }),
        id: i + 1,
      })),
    [formatNumber]
  )

  useEffect(() => {
    setValue(hoursDropdownOptions.filter(({ id }) => +mins === id))
  }, [hoursDropdownOptions, mins])

  const handleOnChange = (value: Option[]) => {
    setValue(value)
    onChange(value[0].id)
  }

  return (
    <InputFieldContainer>
      <FormControl
        label={formatMessage(messages.timeIncrementTitle)}
        error={submitError || error}
      >
        <Select
          options={hoursDropdownOptions}
          clearable={false}
          value={value}
          searchInput
          onChange={({ value }: { value: Option[] }) => handleOnChange(value)}
          dataPendo="time-increment"
        />
      </FormControl>
    </InputFieldContainer>
  )
}

export const TimeIncrement: VFC<TimeIncrementProps> = ({
  dependentFieldValues,
  hasActiveMeetings,
}) => {
  const { formatMessage } = useIntl()
  const {
    min_duration,
    offsite_enabled,
    offsite_min_duration,
    booth_tour_enabled,
    booth_tour_min_duration,
  } = dependentFieldValues

  const validateMeetingsPresent: FieldValidator<string> = (value, _, meta) =>
    hasActiveMeetings && +value > +meta?.initial!
      ? formatMessage(messages.timeCanOnlyDecreaseError)
      : undefined

  const validateLocationDurations: FieldValidator<string> = value =>
    +value > +min_duration ||
    (offsite_enabled === 'true' && +value > +offsite_min_duration) ||
    (booth_tour_enabled === 'true' && +value > +booth_tour_min_duration)
      ? formatMessage(messages.timeCannotBeMoreThanMinMaxError)
      : undefined

  const validate: FieldValidator<string> = (...args) =>
    [validateMeetingsPresent, validateLocationDurations].reduce(
      (acc, validation) => acc ?? validation(...args),
      undefined
    )

  return (
    <Field<string>
      name="minimum_timeslot"
      format={value => value ?? +value}
      parse={(value: number) => value.toString()}
      component={MinuteField}
      validate={validate}
    />
  )
}
