import { VFC } from 'react'
import { useStyletron } from '@jifflenow/brightui'
import { useSlides } from 'utils/useSlides'
import { SliderContainer, StyledContentLoader } from './styled-components'

const cardWidth = 136
const cardHeight = 68
const cardSpacing = 8

const LoadingCard: VFC = () => {
  const [, theme] = useStyletron()
  const contentLoaderOptions = {
    width: cardWidth,
    height: cardHeight,
    speed: 2,
    primaryColor: theme.colors.mono200,
    secondaryColor: theme.colors.mono300,
  }

  return (
    <StyledContentLoader
      $width={cardWidth}
      $height={cardHeight}
      $marginRight={cardSpacing}
      $marginBottom={cardSpacing}
      {...contentLoaderOptions}
    >
      <rect y="0" rx="4" ry="4" width={cardWidth} height={cardHeight} />
    </StyledContentLoader>
  )
}

export const LoadingShimmer: VFC = () => {
  const [ref, { slidesPerView }] = useSlides(0, cardWidth, cardSpacing)

  return (
    <SliderContainer ref={ref}>
      {Array.from({ length: Math.floor(slidesPerView) * 2 }).map((_, i) => (
        <LoadingCard key={i} />
      ))}
    </SliderContainer>
  )
}
