import { VFC, useState, ChangeEvent } from 'react'
import { QueryFunction, useQuery } from 'react-query'
import { GridApi } from '@ag-grid-community/core'
import { queryEngagementTypes, useApolloClient } from 'gql'
import { Pagination, KIND } from '@jifflenow/brightui/pagination'
import { useRouteMatch } from 'react-router-dom'
import { useDebounce } from 'utils/debounce'
import { usePageSizes } from 'utils/usePageSizes'
import {
  EngagementTypeAllowedStatusKeys,
  AllowedSortFormat,
  EngagementTypeAllowedSortKeys,
} from 'gql/types/globalTypes'
import { Paths } from 'components/Routes/Paths'
import { PAGE_SIZE, useSelection } from 'components/ag-grid'
import { EventMainHeader } from 'components/EventMainHeader'
import { useEventLocation } from 'providers/LocationProvider'
import {
  getEngagementTypesVariables,
  getEngagementTypes_getEngagementTypes_data,
} from 'gql/types/getEngagementTypes'
import { getEngagementTypesList } from 'rest'
import { useEngagementTypePreferences } from '../EngagementTypePreferencesProvider'
import { EngagementTypeEdit } from '../EngagementTypeEdit'
import { List } from './List'
import { Header } from './Header'
import { Filters } from './Filters'
import { BulkActions } from './BulkActions'
import { Footer, RootContainer, SubHeaderRoot } from './styled-components'
import { EngagementListTitle } from './EngagementListTitle'
import {
  ActivitySortBy,
  EngagementTypesSearchResponse,
  GetEngagementTypesQueryParams,
  GetEngagementTypesQueryParamsType,
  GetEngagementTypesQueryResponseType,
} from './types'

const getEngagementTypesQueryFunc = (
  client: any
): QueryFunction<
  GetEngagementTypesQueryResponseType,
  ['getEngagementTypes', string, GetEngagementTypesQueryParamsType]
> => async ({ queryKey: [, version, { ...queryParams }] }) => {
  if (version === '2') {
    const data = (
      await queryEngagementTypes(client, {
        variables: queryParams as getEngagementTypesVariables,
        fetchPolicy: 'network-only',
      })
    ).data!.getEngagementTypes

    return data
  } else {
    const { data, meta } = await getEngagementTypesList(
      queryParams as GetEngagementTypesQueryParams
    )

    return { data, page: meta.page }
  }
}

export const EngagementTypesList: VFC = () => {
  const client = useApolloClient()
  const [gridApi, setGridApi] = useState<GridApi | null>(null)
  const [search, setSearch] = useState<string>('')
  const searchTerm = useDebounce(search.trim(), 500)
  const { deselectAll, getSelectedItems, isSelectAll } = useSelection()
  const [pageNumber, setPageNumber] = useState<number>(1)

  const {
    version,
    systemName: eventName,
  } = useEventLocation().location!.attributes

  const matchEngagementTypesPath = useRouteMatch({
    exact: true,
    path: Paths.ENGAGEMENT_TYPES.path,
  })

  const resetToFirstPage = () => {
    setPageNumber(1)
    version === '2' && deselectAll()
  }

  const listPreferences = useEngagementTypePreferences()

  const {
    sortBy,
    sortOrder,
    statuses,
    createdBy,
    templateEngagementTypes,
    pageSize,
  } = listPreferences.preferences

  const [currentPageSize, setCurrentPageSize] = useState(pageSize ?? PAGE_SIZE)

  const {
    isLoading,
    isFetching,
    refetch,
    data: { page, data, grandTotal = 0 } = {},
  } = useQuery(
    [
      'getEngagementTypes',
      version,
      version === '2'
        ? {
            searchTerm,
            page: { size: currentPageSize, number: pageNumber },
            sortOrder: sortOrder as AllowedSortFormat,
            sortBy: sortBy as EngagementTypeAllowedSortKeys,
            filters: {
              templateEngagementTypes,
              createdBy: createdBy?.map(({ uuid }) => uuid),
              status: statuses as EngagementTypeAllowedStatusKeys[],
            },
          }
        : {
            path: {
              eventName,
            },
            query: {
              search_term: searchTerm,
              pageParams: pageNumber.toString(),
              perPageParams: currentPageSize.toString(),
              sortBy: sortBy as ActivitySortBy,
              ...(statuses?.length === 1 && {
                active: statuses[0] === 'active',
              }),
              'meeting_type_master_uuids[]': templateEngagementTypes,
            },
          },
    ],
    getEngagementTypesQueryFunc(client),
    {
      onSuccess: () => gridApi?.hideOverlay(),
      onError: () => gridApi?.showNoRowsOverlay(),
    }
  )

  const { total = 0 } = page ?? {}
  const pageSizes = usePageSizes(total)

  const refetchAndResetToFirstPage = () => {
    pageNumber === 1 && refetch()
    resetToFirstPage()
  }

  if (isLoading || isFetching) {
    gridApi?.showLoadingOverlay()
  }

  const rowDeleteHandler = () => {
    if (data!.length !== 1 || pageNumber === 1) {
      refetch()
    } else {
      setPageNumber(prevPageNumber => prevPageNumber - 1)
    }
  }

  return (
    <RootContainer>
      {matchEngagementTypesPath && <EngagementListTitle />}
      <EventMainHeader />
      <Header
        search={search}
        itemCount={total}
        onSearch={(e: ChangeEvent<HTMLInputElement>) => {
          resetToFirstPage()
          setSearch(e.target.value)
        }}
        onCreate={() => refetch()}
      />
      <SubHeaderRoot>
        <Filters onChange={resetToFirstPage} />
        {version === '2' && (isSelectAll() || getSelectedItems().length) ? (
          <BulkActions
            gridApi={gridApi!}
            totalItems={total}
            appliedSearchTerm={searchTerm}
            resetToFirstPage={refetchAndResetToFirstPage}
          />
        ) : null}
      </SubHeaderRoot>
      <List
        gridApi={gridApi}
        setGridApi={setGridApi}
        onSort={resetToFirstPage}
        paginationPageSize={currentPageSize}
        onRowDelete={rowDeleteHandler}
        {...listPreferences}
        {...{ refetch, grandTotal }}
        data={
          version === '2'
            ? (data as getEngagementTypes_getEngagementTypes_data[])
            : (data as EngagementTypesSearchResponse['data'])
        }
      />
      <Footer>
        {total > PAGE_SIZE && (
          <Pagination
            kind={KIND.light}
            totalItems={total}
            pageSizes={pageSizes}
            currentPageSize={currentPageSize}
            currentPageNumber={pageNumber}
            onPageNumberChange={setPageNumber}
            onPageSizeChange={pageSize => {
              setCurrentPageSize(pageSize)
              setPageNumber(1)
              listPreferences.patchPreferences({ pageSize })
            }}
          />
        )}
      </Footer>
      {useRouteMatch(Paths.ENGAGEMENT_TYPE.path) && <EngagementTypeEdit />}
    </RootContainer>
  )
}
