import ContentLoader from 'react-content-loader'
import { useStyletron } from '@jifflenow/brightui'
import { Fragment } from 'react'

export const Loader = () => {
  const [, theme] = useStyletron()
  const loaderOptions = {
    speed: 2,
    primaryColor: theme.colors.mono200,
    secondaryColor: theme.colors.mono300,
  }

  const accordionCount = 4
  const accordionHeaderStartIndex = 72
  const accordionArrowStartIndex = 80
  const HRStartIndex = 112

  const Distance = 56

  return (
    <ContentLoader
      width="1000"
      height="600"
      viewBox="0 0 1000 600"
      {...loaderOptions}
    >
      <rect y="20" x="0" width="145" height="24" />
      <rect y="24" x="161" width="364" height="16" />

      {[...Array(accordionCount)].map((_, i) => (
        <Fragment key={i}>
          <rect
            y={accordionHeaderStartIndex + i * Distance}
            x="0"
            width="98"
            height="24"
          />
          <rect
            y={accordionArrowStartIndex + i * Distance}
            x="106"
            width="14"
            height="7"
          />
          <rect y={HRStartIndex + i * Distance} x="0" width="1000" height="1" />
        </Fragment>
      ))}
    </ContentLoader>
  )
}
