import { VFC } from 'react'
import { Field, Validation } from 'ui-components/Field'
import { Input } from '@jifflenow/brightui/input'
import { FormControl } from '@jifflenow/brightui/form-control'

type InputProps = {
  label: string
  name: string
  placeholder: string
  min?: number
  type?: 'number' | 'text'
  required?: boolean
}

export const EngagementTypeInput: VFC<InputProps> = ({
  label,
  name,
  placeholder,
  type,
  min,
  required = false,
}) => (
  <Field<string>
    name={name}
    parse={value => value}
    validations={required ? [Validation.REQUIRED] : undefined}
    render={({ input: { value, onChange }, meta: { error, submitError } }) => (
      <FormControl label={label} error={submitError || error}>
        <Input
          name={name}
          type={type ? type : 'text'}
          value={value}
          min={min}
          placeholder={placeholder}
          onChange={(e: any) => onChange(e.target.value)}
        />
      </FormControl>
    )}
  />
)
