import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Select, Value, Option } from '@jifflenow/brightui/select'
import { availabilitySelectOverride } from './overrides'
import {
  AvailabilityOption,
  AvailabilitySelectProps,
  SelectOptions,
} from './types'

const { AVAILABILITY, EXCEPTION } = SelectOptions
const availabilitySelectOptions = [AVAILABILITY, EXCEPTION]

const availabilityOptions = availabilitySelectOptions.map((label, id) => ({
  label,
  id,
}))

export const AvailabilitySelect: VFC<AvailabilitySelectProps> = ({
  type,
  onChange,
}) => {
  const { formatMessage } = useIntl()
  const availabilitySelectLabel = defineMessages<SelectOptions>({
    AVAILABILITY: {
      defaultMessage: 'Availability Dates',
      description: 'Select Label',
    },
    EXCEPTION: {
      defaultMessage: 'Exception Dates',
      description: 'Select Label',
    },
  })
  const value: AvailabilityOption[] = [
    {
      label: type,
      id: availabilitySelectOptions.indexOf(type),
    },
  ]

  const handleStatusChange = (val: [AvailabilityOption]) => {
    onChange(availabilitySelectOptions[val[0].id])
  }

  return (
    <Select
      overrides={availabilitySelectOverride}
      options={availabilityOptions}
      filterOptions={(options: AvailabilityOption[]): Value =>
        options.map(
          ({ label, id }): Option => ({
            label: formatMessage(availabilitySelectLabel[label]),
            id,
          })
        )
      }
      value={value}
      getValueLabel={({ option }: { option: AvailabilityOption }) =>
        formatMessage(availabilitySelectLabel[option.label])
      }
      onChange={({ value }: { value: [AvailabilityOption] }) =>
        handleStatusChange(value)
      }
      clearable={false}
    />
  )
}
