import { VFC, useState, useEffect } from 'react'
import { DateTime } from 'luxon'
import { useIntl, defineMessages } from 'react-intl'
import {
  getISODateFromTime,
  getISOTimeFromJSDate,
  getFullMinutes,
  getEndDateOfDay,
  getISOTimeFromDate,
} from '../common'
import { CopyAvailability } from './CopyAvailability'
import { AvailabilityDay } from './AvailabilityDay'
import {
  Availabilities,
  AvailabilityProps,
  AvailabilityDayProps,
  TimeRangeType,
} from './types'

const messages = defineMessages({
  overlapError: {
    defaultMessage: 'Time overlap',
    description: 'Error message',
  },
  endTimeLessThanStartTime: {
    defaultMessage: 'Choose an end time later than start time.',
    description: 'Error message',
  },
})

export const Availability: VFC<AvailabilityProps> = ({
  weekwise,
  availabilities,
  setAvailabilities,
  setHandleDiscard,
}) => {
  const { formatMessage } = useIntl()
  const [isOpen, setIsOpen] = useState(false)
  const [copySource, setCopySource] = useState<string>('')
  const [newAvailabilities, setNewAvailabilities] = useState<Availabilities[]>(
    availabilities
  )
  const [oldAvailabilities, setOldAvailabilities] = useState<Availabilities[]>(
    availabilities
  )

  useEffect(() => {
    setHandleDiscard?.(() => () => setNewAvailabilities(availabilities))
  }, [availabilities, setHandleDiscard])

  useEffect(() => {
    setNewAvailabilities(availabilities)
    setOldAvailabilities(availabilities)
  }, [availabilities])

  useEffect(() => {
    setAvailabilities(
      JSON.stringify(newAvailabilities) === JSON.stringify(oldAvailabilities)
        ? []
        : newAvailabilities
    )
  }, [newAvailabilities, oldAvailabilities, setAvailabilities])

  const addSlotsToAvailability = (index: number, newSlot: TimeRangeType) => {
    setNewAvailabilities(
      newAvailabilities.map((available, i: number) => {
        const { availableSlots } = available

        return i === index
          ? {
              ...available,
              availableSlots: [...availableSlots, newSlot],
            }
          : available
      })
    )
  }

  const handleCopySlotsToOtherDays = (availabilityDate: string) => {
    setIsOpen(true)
    setCopySource(availabilityDate)
  }

  const deleteSingleSlot: AvailabilityDayProps['deleteSingleSlot'] = (
    availabilityDay,
    indexesToDelete
  ) => {
    setNewAvailabilities(
      (newAvailabilities ?? []).map(availability => {
        const { availableSlots } = availability

        return (
          weekwise
            ? availabilityDay === availability.availabilityDay
            : availabilityDay === availability.availabilityDate
        )
          ? {
              ...availability,
              availableSlots: availableSlots.filter(
                (_, index) => !indexesToDelete.includes(index)
              ),
            }
          : availability
      })
    )

    checkForOverlaps(availabilityDay)
  }

  const deleteAllSlots: AvailabilityDayProps['deleteAllSlots'] = index => {
    setNewAvailabilities(
      (newAvailabilities ?? []).map((availability, i) =>
        i === index ? { ...availability, availableSlots: [] } : availability
      )
    )
  }

  const checkForOverlaps = (availabilityDate: string) => {
    setNewAvailabilities(newAvailabilities =>
      newAvailabilities.map(availability =>
        (
          weekwise
            ? availabilityDate === availability.availabilityDay
            : availabilityDate === availability.availabilityDate
        )
          ? {
              ...availability,
              availableSlots: availability.availableSlots.some(
                ({ start, end }) => {
                  const endTimeToCompare =
                    getFullMinutes(end) === 0
                      ? DateTime.fromISO(end)
                          .endOf('day')
                          .toISOTime({ includeOffset: false })
                      : end

                  return endTimeToCompare <= start
                }
              )
                ? availability.availableSlots
                : availability.availableSlots.map(
                    (range, currentRangeIndex) => {
                      const { start, end } = range

                      const endTimeToCompare =
                        getFullMinutes(end) === 0
                          ? DateTime.fromISO(end)
                              .endOf('day')
                              .toISOTime({ includeOffset: false })
                          : end

                      const otherRanges = availability.availableSlots.filter(
                        (_, index) => index !== currentRangeIndex
                      )

                      const hasOverlap = otherRanges.some(
                        ({ start: rangeStart, end: rangeEnd }) => {
                          const rangeEndToCompare =
                            getFullMinutes(rangeEnd) === 0
                              ? DateTime.fromISO(rangeEnd)
                                  .endOf('day')
                                  .toISOTime({ includeOffset: false })
                              : rangeEnd

                          return (
                            rangeStart < endTimeToCompare &&
                            rangeEndToCompare > start
                          )
                        }
                      )

                      return {
                        start,
                        end,
                        ...(hasOverlap
                          ? { error: formatMessage(messages.overlapError) }
                          : null),
                      }
                    }
                  ),
            }
          : availability
      )
    )
  }

  const editSlotStartTime: AvailabilityDayProps['editSlotStartTime'] = (
    availabilityDate,
    indexToUpdate,
    startTime
  ) => {
    const startDateTime = DateTime.fromJSDate(startTime)
    setNewAvailabilities(
      (newAvailabilities ?? []).map(availability => {
        const { availableSlots } = availability

        return (
          weekwise
            ? availabilityDate === availability.availabilityDay
            : availabilityDate === availability.availabilityDate
        )
          ? {
              ...availability,
              availableSlots: availableSlots.map((range, index) =>
                index === indexToUpdate
                  ? {
                      start: getISOTimeFromJSDate(startTime),
                      end:
                        startDateTime.toMillis() >=
                        DateTime.fromJSDate(
                          new Date(
                            getISODateFromTime(
                              availableSlots.find((_, idx) => idx === index)
                                ?.end!
                            )
                          )
                        ).toMillis()
                          ? getISOTimeFromJSDate(
                              startDateTime.hour === 23
                                ? startDateTime.endOf('day').toJSDate()
                                : startDateTime.plus({ hour: 1 }).toJSDate()
                            )
                          : range.end,
                    }
                  : range
              ),
            }
          : availability
      })
    )

    checkForOverlaps(availabilityDate)
  }

  const editSlotEndTime: AvailabilityDayProps['editSlotEndTime'] = (
    availabilityDate,
    indexToUpdate,
    endTime
  ) => {
    setNewAvailabilities(
      (newAvailabilities ?? []).map(availability => {
        const { availableSlots } = availability

        return (
          weekwise
            ? availabilityDate === availability.availabilityDay
            : availabilityDate === availability.availabilityDate
        )
          ? {
              ...availability,
              availableSlots: availableSlots.map(
                (range: { start: string; end: string }, index: number) => {
                  if (index !== indexToUpdate) {
                    return range
                  }

                  const is12AM = getFullMinutes(endTime.toISOString()) === 0

                  const end = is12AM
                    ? getISOTimeFromDate(getEndDateOfDay(endTime.toISOString()))
                    : getISOTimeFromJSDate(endTime)

                  return {
                    start: range.start,
                    ...(!is12AM && end <= range.start
                      ? {
                          error: formatMessage(
                            messages.endTimeLessThanStartTime
                          ),
                        }
                      : null),
                    end,
                  }
                }
              ),
            }
          : availability
      })
    )

    checkForOverlaps(availabilityDate)
  }

  return (
    <>
      {newAvailabilities?.map((availability, key) => (
        <AvailabilityDay
          key={key}
          index={key}
          availability={availability}
          weekwise={weekwise}
          handleCopySlotsToOtherDays={handleCopySlotsToOtherDays}
          addSlotsToAvailability={addSlotsToAvailability}
          deleteAllSlots={deleteAllSlots}
          deleteSingleSlot={deleteSingleSlot}
          editSlotStartTime={editSlotStartTime}
          editSlotEndTime={editSlotEndTime}
        />
      ))}
      {isOpen && (
        <CopyAvailability
          isOpen
          weekwise={weekwise}
          sourceWeekDay={copySource}
          onClose={() => setIsOpen(false)}
          availabilities={newAvailabilities}
          setAvailabilities={setNewAvailabilities}
        />
      )}
    </>
  )
}
