import { FC } from 'react'
import { Input } from '@jifflenow/brightui/input'
import { FieldMetaState, FieldRenderProps } from 'react-final-form'
import { EngagementContainer, FormControlStyled } from './styled-components'

const fieldError: (meta: FieldMetaState<any>) => boolean = ({
  error,
  touched,
}) => touched && error

export const EngagementInputField: FC<FieldRenderProps<string>> = ({
  input,
  meta,
  label,
  placeholder,
}) => (
  <EngagementContainer>
    <FormControlStyled label={label} error={fieldError(meta)}>
      <Input
        {...input}
        autoComplete="off"
        type="text"
        placeholder={placeholder}
        overrides={{
          Root: {
            style: () => ({
              width: '282px',
            }),
          },
        }}
      />
    </FormControlStyled>
  </EngagementContainer>
)
