import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { TimePicker } from '@jifflenow/brightui/timepicker'
import { Trash } from '@jifflenow/brightui/icon'
import { useMediaQuery } from 'utils/useMediaQuery'
import { RangeTimeSlotProps } from './types'
import { timePickerOverrides } from './timePickerOverrides'
import {
  Root,
  Error,
  StyledIconButton,
  ToText,
  MobileToText,
} from './styled-components'
import { getButtonOverrides } from './buttonOverrides'

const getJSDate = (date: string) => new Date(date)

const messages = defineMessages({
  to: {
    defaultMessage: 'to',
    description: 'Separator text for start and end times',
  },
  noResultsMsg: {
    defaultMessage: 'Invalid time',
    description: 'Label text',
  },
})

export const RangeTimeSlot: VFC<RangeTimeSlotProps> = props => {
  const { formatMessage } = useIntl()
  const { start, end, error, onStartChange, onEndChange, onDelete } = props

  const noResultsMsg = formatMessage(messages.noResultsMsg)

  return (
    <>
      <Root>
        <TimePicker
          value={getJSDate(start)}
          onChange={date => onStartChange(date)}
          creatable
          overrides={timePickerOverrides({ noResultsMsg })}
          error={!!error}
        />
        {useMediaQuery('medium') ? (
          <ToText $hasError={!!error}>{formatMessage(messages.to)}</ToText>
        ) : (
          <MobileToText></MobileToText>
        )}

        <TimePicker
          value={getJSDate(end)}
          onChange={date => onEndChange(date)}
          creatable
          overrides={timePickerOverrides({ noResultsMsg })}
          error={!!error}
        />
        <StyledIconButton
          size={18}
          overrides={getButtonOverrides(error)}
          onClick={() => onDelete()}
        >
          <Trash />
        </StyledIconButton>
      </Root>
      {error ? <Error>{error}</Error> : null}
    </>
  )
}
