import {
  useContext,
  FC,
  useMemo,
  useState,
  useEffect,
  createContext,
} from 'react'
import { useQuery } from 'react-query'
import { useParams } from 'react-router-dom'
import merge from 'deepmerge'
import {
  patchEngagementTypesPreferences as patch,
  engagementTypesPreferencesQueryFn,
} from 'rest'
import { useEventLocation } from 'providers/LocationProvider'
import { ErrorPage } from 'ui-components/ErrorPage'
import { Spinner } from 'ui-components/Spinner'
import { components } from 'types/openapi'

type EngagementPreferences = components['schemas']['EngagementPreferences']
type preferencesT = EngagementPreferences['data']
type ContextT = {
  preferences: preferencesT
  patchPreferences: (preferences: preferencesT) => void
}

const INITIAL_STATE: {
  sortBy: preferencesT['sortBy']
  sortOrder: preferencesT['sortOrder']
} = {
  sortBy: 'displayOrder',
  sortOrder: 'ASC',
}
const Context = createContext<ContextT | null>(null)
const ENGAGEMENT_TYPES_PREFERENCES = 'engagementTypesPreferences'

const Provider: FC<ContextT> = ({
  children,
  preferences,
  patchPreferences,
}) => {
  const value = useMemo<ContextT>(() => ({ preferences, patchPreferences }), [
    preferences,
    patchPreferences,
  ])

  return <Context.Provider value={value}>{children}</Context.Provider>
}

const usePreferences = () => {
  const { eventName } = useParams<{ eventName: string }>()
  const [preferences, setPreferences] = useState<preferencesT>({})

  useEffect(() => {
    const handleLocalStorageChange = (e: StorageEvent) => {
      if (e.key === ENGAGEMENT_TYPES_PREFERENCES) {
        setPreferences(JSON.parse(e.newValue || '{}'))
      }
    }
    window.addEventListener('storage', handleLocalStorageChange)
    return () => {
      window.removeEventListener('storage', handleLocalStorageChange)
    }
  }, [setPreferences])

  const initPreferences = (data: EngagementPreferences) => {
    const initialPreferences = { ...INITIAL_STATE, ...data.data }
    setPreferences(initialPreferences)
    window.localStorage.setItem(
      ENGAGEMENT_TYPES_PREFERENCES,
      JSON.stringify(initialPreferences)
    )
  }

  const patchPreferences = (payload: preferencesT) => {
    const pref = merge(preferences, payload, { arrayMerge: (_, s) => s })
    setPreferences(pref)
    patch({ payload: { data: payload }, subSystem: eventName }).then(() =>
      window.localStorage.setItem(
        ENGAGEMENT_TYPES_PREFERENCES,
        JSON.stringify(pref)
      )
    )
  }

  return { preferences, initPreferences, patchPreferences }
}

const EngagementTypesPreferencesFetcher: FC<{
  onResolve: (data: EngagementPreferences) => void
}> = ({ onResolve }) => {
  const { eventName } = useParams<{ eventName: string }>()
  const location_uuid = useEventLocation().location?.id!

  const { error, refetch } = useQuery(
    [
      'engagementTypesPreferences',
      { path: { eventName }, query: { location_uuid } },
    ],
    engagementTypesPreferencesQueryFn,
    {
      onSuccess: res => onResolve(res),
    }
  )

  if (error) {
    return <ErrorPage onButtonClick={() => refetch()} />
  }

  return <Spinner />
}

export const EngagementTypePreferencesProvider: FC = ({ children }) => {
  const { preferences, initPreferences, patchPreferences } = usePreferences()

  return Object.keys(preferences).length === 0 ? (
    <EngagementTypesPreferencesFetcher onResolve={initPreferences} />
  ) : (
    <Provider preferences={preferences} patchPreferences={patchPreferences}>
      {children}
    </Provider>
  )
}

export const useEngagementTypePreferences = () => {
  const context = useContext(Context)!
  if (context === undefined && process.env.NODE_ENV !== 'production') {
    throw new Error(
      'useEngagementTypePreferences must be used within a EngagementTypePreferencesProvider'
    )
  }

  return context
}
