import { ChangeEvent, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { Field } from 'ui-components/Field'
import { showErrorToast } from 'utils/toast'
import { useFetchAllCompanyRoles } from 'gql'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { roles as roleLabel } from 'utils/messages'
import { Option } from '@jifflenow/brightui/select'
import { SelectField } from './SelectField'
import {
  CheckBoxWrapper,
  Divider,
  Header,
  HeaderDesc,
  RolesFieldWrapper,
  RolesSurveyWrapper,
  RolesTitle,
} from './styled-components'

const messages = defineMessages({
  postMeetingInternalLabel: {
    defaultMessage: 'Post Meeting Internal Survey',
    description: 'Field label',
  },
  enableSurveyLabel: {
    defaultMessage: 'Enable this survey',
    description: 'Description label',
  },
  whoTakeSurveyLabel: {
    defaultMessage: 'Who can take the survey',
    description: 'Field label',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  meetingHostLabel: {
    defaultMessage: 'Meeting Host',
    description: 'Field label',
  },
  meetingRequestorLabel: {
    defaultMessage: 'Meeting Requestor',
    description: 'Field label',
  },
  requesterRoleLabel: {
    defaultMessage: 'Requestors of these roles can fill survey',
    description: 'Field label',
  },
  participantsRoleLabel: {
    defaultMessage: 'Participants of these roles can fill the survey',
    description: 'Field label',
  },
  roleLabel: {
    defaultMessage:
      'Meetings with the following status will receive the survey',
    description: 'Field label',
  },
  allInternalRoles: {
    defaultMessage: 'All Internal Roles',
    description: 'Option label',
  },
})

export const RolesAndPermissionSettings: VFC = () => {
  const { formatMessage } = useIntl()
  const { loading, roles = [] } = useFetchAllCompanyRoles({
    notifyOnNetworkStatusChange: true,
    fetchPolicy: 'cache-and-network',
    onError: () => {
      showErrorToast(formatMessage(messages.genericError))
    },
  })

  const dropdownOptions = [
    {
      label: formatMessage(messages.allInternalRoles),
      id: 'INTERNAL',
    },
    ...roles.map(
      ({ roleCompanyid, roleDisplayName }): Option => ({
        label: formatMessage(roleLabel[roleDisplayName]),
        id: roleDisplayName === 'ET' ? 'EXTERNAL' : roleCompanyid,
      })
    ),
  ]

  return (
    <>
      {loading && <ModalSpinner />}
      <Header>{formatMessage(messages.postMeetingInternalLabel)}</Header>
      <HeaderDesc>{formatMessage(messages.enableSurveyLabel)}</HeaderDesc>
      <RolesSurveyWrapper>
        <RolesTitle>{formatMessage(messages.whoTakeSurveyLabel)}</RolesTitle>
        <CheckBoxWrapper>
          <Field<boolean>
            name="meetingHostCanTakeSurvey"
            render={({ input: { value, onChange, name } }) => (
              <Checkbox
                checked={Boolean(value)}
                onChange={(e: ChangeEvent<HTMLInputElement>) =>
                  onChange(e.target.checked)
                }
                name={name}
              >
                {formatMessage(messages.meetingHostLabel)}
              </Checkbox>
            )}
          />
        </CheckBoxWrapper>

        <CheckBoxWrapper>
          <Field<boolean>
            name="requestorShouldFillSurvey"
            render={({ input: { value, onChange, name } }) => (
              <Checkbox
                checked={Boolean(value)}
                onChange={(e: ChangeEvent<HTMLInputElement>) =>
                  onChange(e.target.checked)
                }
                name={name}
              >
                {formatMessage(messages.meetingRequestorLabel)}
              </Checkbox>
            )}
          />
        </CheckBoxWrapper>
        <RolesFieldWrapper>
          <SelectField
            options={dropdownOptions}
            label={formatMessage(messages.requesterRoleLabel)}
            name="requestorAccessLevel"
          />
        </RolesFieldWrapper>
        <Divider />
        <RolesFieldWrapper>
          <SelectField
            options={dropdownOptions}
            label={formatMessage(messages.participantsRoleLabel)}
            name="accessLevel"
          />
        </RolesFieldWrapper>
        <RolesFieldWrapper>
          <SelectField
            options={[
              { label: 'approved', id: 'approved' },
              { label: 'pending', id: 'pending' },
            ]}
            label={formatMessage(messages.roleLabel)}
            name="entityStatus"
          />
        </RolesFieldWrapper>
      </RolesSurveyWrapper>
    </>
  )
}
