import { VFC } from 'react'
import { FormattedMessage } from 'react-intl'
import { ErrorIcon } from 'ui-components/icons/ErrorIcon'
import { KIND } from '@jifflenow/brightui/button'
import {
  ErrorContainer,
  ErrorInfo,
  ButtonRetry,
  ErrorMessage,
} from './styled-components'
import { ErrorProps } from './types'

export const Error: VFC<ErrorProps> = ({ onClick }) => (
  <ErrorContainer>
    <ErrorIcon size={128} />
    <ErrorInfo>
      <ErrorMessage>
        <FormattedMessage
          defaultMessage="Something went wrong. Please try again."
          description="Error message"
        />
      </ErrorMessage>
      <ButtonRetry type="button" kind={KIND.secondary} onClick={onClick}>
        <FormattedMessage defaultMessage="Retry" description="Button label" />
      </ButtonRetry>
    </ErrorInfo>
  </ErrorContainer>
)
