import { VFC, ChangeEvent } from 'react'
import { Toggle } from '@jifflenow/brightui/toggle'
import { Field } from 'react-final-form'
import { defineMessages, useIntl } from 'react-intl'
import { StatefulTooltip } from '@jifflenow/brightui/tooltip'

const messages = defineMessages({
  helpText: {
    defaultMessage:
      'Survey type is inactive at survey list. Activate the survey and try again.',
    description: 'Help text',
  },
})

export const SurveyStatusField: VFC<{ surveyMasterstatus: boolean }> = ({
  surveyMasterstatus,
}) => {
  const { formatMessage } = useIntl()

  return (
    <Field<boolean>
      name="status"
      render={({ input: { value, onChange } }) =>
        surveyMasterstatus ? (
          <Toggle
            checked={Boolean(value)}
            onChange={(e: ChangeEvent<HTMLInputElement>) => {
              onChange(e.target.checked)
            }}
          />
        ) : (
          <StatefulTooltip
            content={() => formatMessage(messages.helpText)}
            showArrow
          >
            <span>
              <Toggle disabled />
            </span>
          </StatefulTooltip>
        )
      }
    />
  )
}
