export const getCategories = (
  actionSet: Set<ActionName>,
  isSession: boolean,
  mailActions: EngagementMailAction[]
): ReadonlyArray<CategoryResponse> =>
  (isSession ? session_categories : general_categories).flatMap(category => {
    const newActionTypes = category.actionTypes.flatMap(actionType => {
      let columnsPresent = false

      const newActionColumns = actionType.actionColumns.map(actions =>
        actions.filter(action => {
          const present = actionSet.has(action)
          columnsPresent = columnsPresent || present
          return present
        })
      ) as ActionTypeForCategory['actionColumns']

      const modifiedActionColumns = newActionColumns.map(actionName =>
        actionName.map(name =>
          mailActions.find(mailAction => mailAction.name === name)
        )
      )

      return columnsPresent
        ? [
            {
              ...actionType,
              actionColumns: modifiedActionColumns as ActionType['actionColumns'],
            },
          ]
        : []
    })

    return newActionTypes.length
      ? [{ ...category, actionTypes: newActionTypes }]
      : []
  })
