import { FC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { StatefulTooltip } from '@jifflenow/brightui/tooltip'
import { InfoOutlined } from '@jifflenow/brightui/icon'
import { InfoTooltipContainer } from '../common'
import { SectionWrapper, Section, SectionHeader } from './styled-components'
import { SectionsProps } from './types'

const messages = defineMessages({
  meetingManagerTooltip: {
    defaultMessage:
      'Click on the respective template name to choose a specific meeting manager to be notified.',
    description: 'Meeting Manager Header tooltip message',
  },
})

export const Sections: FC<SectionsProps> = ({ headers }) => {
  const { formatMessage } = useIntl()

  return (
    <SectionWrapper>
      {headers?.map((header, i) => (
        <Section key={i}>
          <SectionHeader>
            {header &&
              formatMessage(header, {
                meetingManagerTooltip: () => (
                  <StatefulTooltip
                    content={formatMessage(messages.meetingManagerTooltip)}
                    showArrow
                    popoverMargin={8}
                  >
                    <InfoTooltipContainer>
                      <InfoOutlined />
                    </InfoTooltipContainer>
                  </StatefulTooltip>
                ),
              })}
          </SectionHeader>
        </Section>
      ))}
    </SectionWrapper>
  )
}
