import { VFC } from 'react'
import { Radio } from '@jifflenow/brightui/radio'
import { defineMessages, useIntl } from 'react-intl'
import { useField } from 'react-final-form'
import { instructionRadioOverride } from '../overrides'

const messages = defineMessages({
  manualSchedulingLabel: {
    defaultMessage:
      'Manual scheduling - A Meeting Manager will need to process the requests upon submission',
    description: 'Field label',
  },
})

export const MethodRequestManualField: VFC = () => {
  const { formatMessage } = useIntl()
  const {
    value: enable_external_widget_auto_process,
    onChange: onChangeEnableExternalWidgetAutoProcess,
  } = useField('enable_external_widget_auto_process').input

  const {
    value: enable_external_widget_auto_process_without_mapping,
    onChange: onChangeEnableExternalWidgetAutoProcessWithoutMapping,
  } = useField('enable_external_widget_auto_process_without_mapping').input

  return (
    <Radio
      checked={
        enable_external_widget_auto_process === 'false' &&
        enable_external_widget_auto_process_without_mapping === 'false'
      }
      onChange={() => {
        onChangeEnableExternalWidgetAutoProcess('false')
        onChangeEnableExternalWidgetAutoProcessWithoutMapping('false')
      }}
      value="true"
      overrides={instructionRadioOverride}
      labelPlacement="right"
      name="manualing_scheduling"
    >
      {formatMessage(messages.manualSchedulingLabel)}
    </Radio>
  )
}
