import { useCallback, useEffect, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useField, useFormState } from 'react-final-form'
import createDecorator from 'final-form-calculate'
import { useEventLocation } from 'providers/LocationProvider'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { DependentLinks, EngagementTypeToggle } from '../common'
import { Paths } from '../Paths'
import { EngagementModal } from '../common/EngagementModal'
import { BorderStyled, Section } from './styled-components'
import { Fields, FormValues, ManualSchedulingProps } from './types'

const messages = defineMessages({
  enableManualScheduling: {
    defaultMessage: 'Enable manual scheduling',
    description: 'Section title',
  },
  showOnsiteRoomJmmAbove: {
    defaultMessage: 'Show onsite room listing only for JMM and above roles',
    description: 'Field label',
  },
  blockUserCalendarOnAcceptance: {
    defaultMessage: 'Block user(s) calendar upon meeting acceptance',
    description: 'Field label',
  },
  sendMstEmailsToEa: {
    defaultMessage:
      'Send a copy of the "Meeting Request/Update/Cancel EMAIL to Internal Attendee" to all the EA(s) of the attendee',
    description: 'Field label',
  },
  sendMstMailToAttendee: {
    defaultMessage: 'Send email notification to attendees managed by EA',
    description: 'Field label',
  },
  sendMstMailToAttendeeTooltip: {
    defaultMessage:
      'This will not affect the delivery of notification to attendees not managed by EA.',
    description: 'Tooltip label',
  },
  warningDisableLabel: {
    defaultMessage: 'Disable the following',
    description: 'Dependent settings group header',
  },
  enableMappingsLabel: {
    defaultMessage: 'Enable Mappings',
    description: 'Dependent settings group header',
  },
  warningModalTitle: {
    defaultMessage:
      'Please ensure following settings are done before enabling manual scheduling',
    description: 'Dependent settings modal title',
  },
})

export const manualSchedulingDecorator = createDecorator({
  field: 'enable_manual_scheduling',
  updates: {
    show_onsite_room_jmm_above: (value, allValues) =>
      value === 'false'
        ? 'false'
        : (allValues as Fields)?.show_onsite_room_jmm_above,
    block_user_calendar_on_acceptance: (value, allValues) =>
      value === 'false'
        ? 'false'
        : (allValues as Fields)?.block_user_calendar_on_acceptance,
    send_mst_mails_to_ea: (value, allValues) =>
      value === 'false' ? 'false' : (allValues as Fields)?.send_mst_mails_to_ea,
    send_mst_mail_to_attendee_managed_by_ea: (value, allValues) =>
      value === 'false'
        ? 'false'
        : (allValues as Fields)?.send_mst_mail_to_attendee_managed_by_ea,
  },
})

export const ManualScheduling: VFC<ManualSchedulingProps> = ({
  loading,
  refetch,
}) => {
  const { formatMessage } = useIntl()
  const [isOpen, setIsOpen] = useState(false)

  const { enable_manual_scheduling } = useEventLocation().configs!

  const { default_mapping } = useFormState<FormValues>().values

  const {
    input: { value, onChange },
  } = useField('enable_manual_scheduling')

  const canEnableManualScheduling = useCallback(
    () => default_mapping === 'false',
    [default_mapping]
  )

  useEffect(() => {
    value === 'true' && setIsOpen(!canEnableManualScheduling())
  }, [canEnableManualScheduling, value])

  const dependentLinks = [
    default_mapping === 'true' && {
      label: formatMessage(messages.enableMappingsLabel),
      path: Paths.CSM_ONLY,
      group: formatMessage(messages.warningDisableLabel),
    },
  ].filter(Boolean) as DependentLinks[]

  return enable_manual_scheduling === 'true' ? (
    <>
      {isOpen && (
        <>
          {loading && <ModalSpinner />}
          <EngagementModal
            title={formatMessage(messages.warningModalTitle)}
            dependentLinks={dependentLinks}
            onClose={() => {
              setIsOpen(false)
              !canEnableManualScheduling() && onChange('false')
            }}
            onConfirm={() => refetch()}
          />
        </>
      )}
      <Section>
        <EngagementTypeToggle
          label={formatMessage(messages.enableManualScheduling)}
          name="enable_manual_scheduling"
          bold
        >
          <EngagementTypeToggle
            name="show_onsite_room_jmm_above"
            label={formatMessage(messages.showOnsiteRoomJmmAbove)}
          />
          <BorderStyled />
          <EngagementTypeToggle
            name="block_user_calendar_on_acceptance"
            label={formatMessage(messages.blockUserCalendarOnAcceptance)}
          />
          <BorderStyled />
          <EngagementTypeToggle
            name="send_mst_mails_to_ea"
            label={formatMessage(messages.sendMstEmailsToEa)}
          />
          <BorderStyled />
          <EngagementTypeToggle
            name="send_mst_mail_to_attendee_managed_by_ea"
            label={formatMessage(messages.sendMstMailToAttendee)}
            infoTooltip={formatMessage(messages.sendMstMailToAttendeeTooltip)}
          />
          <BorderStyled />
        </EngagementTypeToggle>
      </Section>
    </>
  ) : null
}
