import { useMemo, VFC } from 'react'
import { Form as FinalForm, FormProps } from 'react-final-form'
import { defineMessages, useIntl } from 'react-intl'
import { Accordion, Panel } from '@jifflenow/brightui/accordion'
import { Prompt } from 'react-router-dom'
import { Footer } from '../common'
import { ActionsBlocks } from './ActionsBlocks'
import { accordionOverrides, panelOverrides } from './overrides'
import { Sections } from './Sections'
import { PanelBody } from './styled-components'
import { ActionsActiveMap, EngagementMailAction, ActionName } from './types'
import { getCategories } from './utils'
import { Survey } from './Survey'

type Props = {
  mailActions: EngagementMailAction[]
  onSubmit: FormProps<ActionsActiveMap>['onSubmit']
  isSession: boolean
}

const messages = defineMessages({
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
  surveyHeader: {
    defaultMessage: 'Survey Template',
    description: 'Header text',
  },
})

export const NotificationsForm: VFC<Props> = ({
  mailActions,
  onSubmit,
  isSession,
}) => {
  const nonSession = mailActions.filter(
    item =>
      item.templateType !== 'Session' &&
      item.templateType !== 'SurveyMailAction'
  )

  const categories = useMemo(
    () =>
      getCategories(
        new Set(nonSession.map(({ name }) => name) as ActionName[]),
        isSession,
        mailActions
      ),
    [isSession, nonSession, mailActions]
  )
  const { formatMessage } = useIntl()

  const actionsActiveMap = {} as ActionsActiveMap

  mailActions.forEach(({ active, id }) => {
    actionsActiveMap[`mail_actions_${id}`] = active
  })

  const surveyTemplate = mailActions.filter(
    item =>
      item.templateType === 'Session' ||
      item.templateType === 'SurveyMailAction'
  )

  return (
    <FinalForm<ActionsActiveMap>
      initialValues={actionsActiveMap}
      onSubmit={onSubmit}
      render={({ handleSubmit, dirty }) => (
        <form onSubmit={handleSubmit} noValidate>
          <Prompt
            when={dirty}
            message={formatMessage(messages.promptMessage)}
          />
          <Accordion
            accordion
            initialState={{ expanded: [formatMessage(categories[0].name)] }}
            overrides={accordionOverrides}
            renderAll
          >
            {categories.map(({ name, headers, actionTypes }) => {
              const formattedName = formatMessage(name)

              return (
                <Panel
                  key={formattedName}
                  title={formattedName}
                  overrides={panelOverrides}
                >
                  <PanelBody>
                    <Sections headers={headers} />
                    <ActionsBlocks
                      actionTypes={actionTypes}
                      name={formattedName}
                      headers={headers}
                    />
                  </PanelBody>
                </Panel>
              )
            })}
            {surveyTemplate.length > 0 && (
              <Panel
                title={formatMessage(messages.surveyHeader)}
                overrides={panelOverrides}
              >
                <PanelBody>
                  <Survey surveyTemplate={surveyTemplate} />
                </PanelBody>
              </Panel>
            )}
          </Accordion>
          <Footer />
        </form>
      )}
    />
  )
}
