import { useEffect, useState, useMemo, VFC } from 'react'
import { FormattedMessage, defineMessages, useIntl } from 'react-intl'
import { Field } from 'ui-components/Field'
import { Select, Value } from '@jifflenow/brightui/select'
import {
  DurationMinsContainer,
  DurationLabel,
  DurationFieldRoot,
  DurationError,
  SelectProps,
  MinsLabel,
} from '../common'
import { FieldProps } from './types'

const messages = defineMessages({
  durationMinsPlaceholder: {
    defaultMessage: 'Select Mins',
    description: 'Placeholder text',
  },
})

const MinsField: VFC<FieldProps> = ({
  input: { value: mins, onChange, name },
  disabled,
}) => {
  const { formatMessage, formatNumber } = useIntl()
  const [value, setValue] = useState<Value>([])
  const minsDropdownOptions = useMemo(
    () =>
      [...Array(24).keys()].map(i => ({
        label: formatNumber((i + 1) * 5),
        id: (i + 1) * 5,
      })),
    [formatNumber]
  )
  useEffect(() => {
    setValue(minsDropdownOptions.filter(({ id }) => +mins === id))
  }, [mins, minsDropdownOptions])

  const handleOnChange = (value: Value) => {
    setValue(value)
    onChange(value[0].id)
  }

  return (
    <Select
      options={minsDropdownOptions}
      clearable={false}
      placeholder={formatMessage(messages.durationMinsPlaceholder)}
      value={value}
      onChange={({ value }: { value: Value }) => handleOnChange(value)}
      disabled={disabled}
      dataPendo={name}
    />
  )
}

export const MeetingBreakDurationSelect: VFC<SelectProps> = ({
  name,
  label,
  disabled,
}) => (
  <Field<string>
    name={name}
    parse={value => value.toString()}
    format={value => +value}
    render={({ input, meta: { error } }) => (
      <DurationFieldRoot>
        <DurationLabel>{label}</DurationLabel>
        {error && <DurationError>{error}</DurationError>}
        <DurationMinsContainer>
          <MinsField input={input} disabled={disabled} />
          <MinsLabel>
            <FormattedMessage defaultMessage="mins" description="Mins label" />
          </MinsLabel>
        </DurationMinsContainer>
      </DurationFieldRoot>
    )}
  />
)
