import { useEffect, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Field } from 'ui-components/Field'
import { useField } from 'react-final-form'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { EngagementTypeToggle } from '../common/EngagementTypeToggle'
import { TimeDurationSelect } from '../common/TimeDurationSelect'
import { DependentLinks } from '../Irp/types'
import { EngagementModal } from '../common/EngagementModal'
import { RefetchProps } from '../common/types'
import { Paths } from '../Paths'
import { DurationWrapper, ToggleWrapper } from './styled-components'
import { EngagementInputField } from './EngagementInputField'
import { DependentFieldsProps } from './types'

const messages = defineMessages({
  durationRestrictionLabel: {
    defaultMessage:
      'Apply these restrictions to Meeting Managers and Junior MM s',
    description: 'Field label',
  },
  offSitesLabel: {
    defaultMessage: 'Offsite Label',
    description: 'Field label',
  },
  offSitesLabelPlaceholder: {
    defaultMessage: 'Offsite Label',
    description: 'Placeholder text',
  },
  minDurationLabel: {
    defaultMessage: 'Minimum Duration',
    description: 'Field label',
  },
  maxDurationLabel: {
    defaultMessage: 'Maximum Duration',
    description: 'Field label',
  },
  warningDisableLabel: {
    defaultMessage: 'Disable the following',
    description: 'Dependent settings group header',
  },
  autoUnMappedLabel: {
    defaultMessage:
      'Please disable the auto unmapped from IRP before proceeding',
    description: 'Dependent settings modal title',
  },
  warningModalTitle: {
    defaultMessage:
      'Please ensure following settings are done before enabling the instant',
    description: 'Dependent settings modal title',
  },
})

export const OffSitesFormField: VFC<DependentFieldsProps & RefetchProps> = ({
  refetch,
  dependentFieldValues,
  loading,
}) => {
  const { formatMessage } = useIntl()
  const [isOpen, setIsOpen] = useState(false)

  const { onChange, value } = useField('offsite_enabled').input

  const {
    enable_external_widget_auto_process_without_mapping,
    enable_external_widget,
  } = dependentFieldValues

  useEffect(() => {
    value === 'true' &&
      setIsOpen(
        enable_external_widget === 'true' &&
          enable_external_widget_auto_process_without_mapping !== 'false'
      )
  }, [
    enable_external_widget,
    enable_external_widget_auto_process_without_mapping,
    value,
  ])

  const dependentLinks = [
    enable_external_widget === 'true' &&
      enable_external_widget_auto_process_without_mapping !== 'false' && {
        label: formatMessage(messages.autoUnMappedLabel),
        path: Paths.INBOUND_REQUEST_FORM,
        group: formatMessage(messages.warningDisableLabel),
      },
  ].filter(Boolean) as DependentLinks[]

  return (
    <>
      {isOpen && (
        <>
          {loading && <ModalSpinner />}
          <EngagementModal
            title={formatMessage(messages.warningModalTitle)}
            dependentLinks={dependentLinks}
            onClose={() => {
              setIsOpen(false)
              enable_external_widget_auto_process_without_mapping !== 'false' &&
                onChange('false')
            }}
            onConfirm={() => refetch()}
          />
        </>
      )}
      <ToggleWrapper>
        <Field
          name="offsite_label"
          render={EngagementInputField}
          label={formatMessage(messages.offSitesLabel)}
          placeholder={formatMessage(messages.offSitesLabelPlaceholder)}
        />
        <DurationWrapper>
          <TimeDurationSelect
            label={formatMessage(messages.minDurationLabel)}
            name="offsite_min_duration"
          />
          <TimeDurationSelect
            label={formatMessage(messages.maxDurationLabel)}
            name="offsite_max_duration"
          />
        </DurationWrapper>
        <EngagementTypeToggle
          label={formatMessage(messages.durationRestrictionLabel)}
          name="offsite_duration_restrictions_for_mm"
        />
      </ToggleWrapper>
    </>
  )
}
