import { VFC } from 'react'
import { useFormState } from 'react-final-form'
import { defineMessages, useIntl } from 'react-intl'
import { CopyEmbedSourceCode } from 'ui-components/CopyEmbedSourceCode'
import { Field } from 'ui-components/Field'
import { EngagementTypeToggleWithDesc } from '../common'
import { EngagementLinkField } from './EngagementLinkField'
import { getCallToActionButtonCode, getFrameCode } from './GetCopiedContent'
import { FormControlStyledBody, FormFieldWrapper } from './styled-components'
import { DependentFieldValues, FormValues } from './types'

const messages = defineMessages({
  urlRequestLabel: {
    defaultMessage: 'URL for inbound request page',
    description: 'Field label',
  },
  sourceCodeIrpLabel: {
    defaultMessage:
      'Source code to embed the inbound request page of this engagement type',
    description: 'Field label',
  },
  sourceCodeIrpActionLabel: {
    defaultMessage:
      'Source code to embed Call to Action button for IRP of this engagement type',
    description: 'Field label',
  },
  embedIrpLabel: {
    defaultMessage: 'Copy code to embed IRP for the engagement type',
    description: 'Button label',
  },
  embedIrpActionLabel: {
    defaultMessage:
      'Copy source code to call to action button of IRP page for this engagement type',
    description: 'Button label',
  },
  requestMeetingLabel: {
    defaultMessage: 'Request Meeting',
    description: 'Field Label',
  },
  irpUrlLabel: {
    defaultMessage: 'Create separate URL for this engagement type',
    description: 'Field label',
  },
  buttonDisabledInformation: {
    defaultMessage: 'Save changes to access URL and source code',
    description: 'Help text',
  },
})

export const IrpUrlField: VFC<DependentFieldValues> = ({
  dependentFieldValues,
}) => {
  const { formatMessage } = useIntl()
  const { theme, enable_external_widget_url } = dependentFieldValues
  const {
    activity_external_widget_url,
    enable_external_widget_url: enable_external_widget_url_form,
    disable_url,
    warning,
  } = useFormState<FormValues>().values
  const disableLink =
    enable_external_widget_url !== 'true' || disable_url === 'true'
  const showHelpText = enable_external_widget_url_form === 'true' && disableLink

  return (
    <EngagementTypeToggleWithDesc
      label={formatMessage(messages.irpUrlLabel)}
      name="enable_external_widget_url"
      desc={
        showHelpText ? formatMessage(messages.buttonDisabledInformation) : ''
      }
      hint
    >
      <FormControlStyledBody>
        <FormFieldWrapper>
          <Field
            name="activity_external_widget_url"
            render={EngagementLinkField}
            label={formatMessage(messages.urlRequestLabel)}
            disabled={disableLink}
            warning={warning}
          />
        </FormFieldWrapper>
        <FormFieldWrapper>
          <CopyEmbedSourceCode
            buttonLabel={formatMessage(messages.embedIrpLabel)}
            link={getFrameCode(activity_external_widget_url)}
            title={formatMessage(messages.sourceCodeIrpLabel)}
            disabled={disableLink}
          />
        </FormFieldWrapper>
        <FormFieldWrapper>
          <CopyEmbedSourceCode
            buttonLabel={formatMessage(messages.embedIrpActionLabel)}
            link={getCallToActionButtonCode(
              activity_external_widget_url,
              theme ? theme.colors.primary : '#0096FF',
              formatMessage(messages.requestMeetingLabel)
            )}
            title={formatMessage(messages.sourceCodeIrpActionLabel)}
            disabled={disableLink}
          />
        </FormFieldWrapper>
      </FormControlStyledBody>
    </EngagementTypeToggleWithDesc>
  )
}
