import { defineMessages, FormattedMessage, useIntl } from 'react-intl'
import { useEffect, useState, VFC } from 'react'
import { useEventLocation } from 'providers/LocationProvider'
import { NavLink, Prompt } from 'react-router-dom'
import { JiffleClock } from '@jifflenow/brightui/jiffle-logo'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { Body, Container, Header } from '../common'
import { useEngagementType } from '../EngagementTypeProvider'
import { BackButtonText, Root, Wrapper } from './styled-components'

const messages = defineMessages({
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
})

export const NotificationsDetails: VFC = () => {
  const {
    attributes: { systemName: eventName },
  } = useEventLocation().location!
  const { formatMessage } = useIntl()
  const setBeforeUnload = useBeforeUnload()
  const [dirty, setDirty] = useState(false)
  const handleMessage = (event: MessageEvent) => {
    setDirty(event.data)
  }

  useEffect(() => {
    setBeforeUnload?.(dirty)
  }, [setBeforeUnload, dirty])

  useEffect(() => {
    window.addEventListener('message', handleMessage, false)

    return () => {
      window.removeEventListener('message', handleMessage, false)
    }
  }, [])

  const { uuid: activity_uuid } = useEngagementType().engagementType
  const searchParams = new URLSearchParams(window.location.search)
  const location = searchParams.get('location')
  const mail_actions = searchParams.get('mail_actions')
  const notificationId = searchParams.get('notificationId')
  const [showLoader, setShowLoader] = useState(true)

  return (
    <>
      <Prompt when={dirty} message={formatMessage(messages.promptMessage)} />
      <Header />
      <Body>
        <Container>
          <Root>
            <div>
              <NavLink
                to={`/events/${eventName}/engagement-types/${activity_uuid}/meeting-type-notifications?location=${location}`}
              >
                <BackButtonText>
                  <FormattedMessage
                    defaultMessage="< Notification Templates"
                    description="Anchor text"
                  />
                </BackButtonText>
              </NavLink>
            </div>
            <div>
              <iframe
                src={`${origin}/${eventName}/event/settings/notifications?current_location_uuid=${location}&engagement_type_uuid=${activity_uuid}&mail_action=${mail_actions}&notificationId=${notificationId}`}
                width="100%"
                style={{ border: 0, height: 'calc(100vh - 124px)' }}
                frameBorder="0"
                onLoad={() => setShowLoader(false)}
                title="Notification Details Page"
              >
                {showLoader && (
                  <Wrapper>
                    <JiffleClock animated />
                  </Wrapper>
                )}
              </iframe>
            </div>
          </Root>
        </Container>
      </Body>
    </>
  )
}
