import { useEffect, useMemo, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import {
  generatePath,
  useHistory,
  useLocation,
  useRouteMatch,
} from 'react-router-dom'
import { DrawerCloseButton, DrawerTitle, useDrawer } from 'ui-components/Drawer'
import { KIND } from '@jifflenow/brightui/button'
import { Paths } from 'components/Routes'
import { useEventLocation } from 'providers/LocationProvider'
import { useEngagementTypesQuery } from 'queries'
import { Select, Value, filterOptions } from '@jifflenow/brightui/select'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { useEngagementType } from '../EngagementTypeProvider'
import { CopySettings } from '../CopySettings'
import { CopyBtn, CopyBtnWrapper, HeaderContainer } from './styled-components'
import { selectOverrides } from './overrides'

const messages = defineMessages({
  settingsLabel: {
    defaultMessage: 'Settings for',
    description: 'Label',
  },
  searchPlaceholder: {
    defaultMessage: 'Search by engagement type',
    description: 'Search placeholder',
  },
  buttonLabel: {
    defaultMessage: 'Copy To',
    description: 'Button label',
  },
})

export const Header: VFC = () => {
  const {
    name,
    templateEngagementType: { masterEngagementType },
  } = useEngagementType().engagementType
  const { closeDrawer } = useDrawer()
  const { formatMessage } = useIntl()
  const [value, setValue] = useState<Value>([])
  const [isOpen, setIsOpen] = useState<boolean>(false)

  const history = useHistory()
  const { search } = useLocation()
  const {
    params: { engagementId, route },
  } = useRouteMatch<{
    eventName: string
    route: string
    engagementId: string
  }>({
    exact: true,
    path: Paths.ENGAGEMENT_TYPE_SUB_ROUTES.path,
  })!

  useEffect(() => {
    setValue([{ id: engagementId, label: name }])
  }, [engagementId, name])

  const {
    systemName: eventName,
    version,
  } = useEventLocation().location!.attributes

  const { isLoading, data: fetchEngagementTypes } = useEngagementTypesQuery({
    version,
    eventName,
    showIrpEnabled: true,
    ...(version === '1' && { showOnlyNonConsecutiveEngagementTypes: true }),
  })

  const options = useMemo(
    () =>
      fetchEngagementTypes
        ?.slice()
        .sort((a, b) => a.displayOrder - b.displayOrder)
        .map(({ uuid, id, name }) => ({
          label: name,
          id: uuid ?? id,
        })) ?? [],
    [fetchEngagementTypes]
  )

  const handleOnChange = (value: Value) => {
    setValue(value)
    value.length &&
      history.push(
        `${generatePath(Paths.ENGAGEMENT_TYPE_SUB_ROUTES.path, {
          eventName,
          engagementId: value[0].id as string,
          route,
        })}${search}`
      )
  }

  const showCopyTo =
    version === '2' &&
    masterEngagementType !== MasterEngagementTypes.ConsecutiveMeeting &&
    (fetchEngagementTypes?.filter(
      ({ templateEngagementType }) =>
        templateEngagementType.masterEngagementType === masterEngagementType
    ).length ?? 0) > 1

  return (
    <>
      {isOpen && <CopySettings onClose={() => setIsOpen(false)} />}
      <HeaderContainer $hasMenu>
        <DrawerCloseButton onClick={() => closeDrawer()} />
        <DrawerTitle>{formatMessage(messages.settingsLabel)}</DrawerTitle>
        <Select
          isLoading={isLoading}
          options={options}
          // TODO: Move filterOptions to brightui
          filterOptions={(
            options: Value,
            filterValue: string,
            excludeOptions?: Value
          ) =>
            // @ts-expect-error: filterOptions is also exported as function
            filterOptions(options, filterValue, excludeOptions, {
              matchProp: 'label',
            })
          }
          value={value}
          onChange={({ value }: { value: Value }) => handleOnChange(value)}
          searchable
          placeholder={formatMessage(messages.searchPlaceholder)}
          clearable={false}
          overrides={selectOverrides}
          onBlur={() => {
            !value.length && setValue([{ id: engagementId, label: name }])
          }}
        />
        {showCopyTo && (
          <CopyBtnWrapper>
            <CopyBtn
              type="button"
              kind={KIND.tertiary}
              onClick={() => setIsOpen(true)}
              data-pendo="engagement-copyto-button"
            >
              {formatMessage(messages.buttonLabel)}
            </CopyBtn>
          </CopyBtnWrapper>
        )}
      </HeaderContainer>
    </>
  )
}
