import { FC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { TextFilter, Option } from '@jifflenow/brightui/filter'
import {
  filterOverrides,
  FiltersContainer,
  FilterItem,
  DataOptions,
  Options,
  FilterProps,
} from 'components/ag-grid'
import { useEventLocation } from 'providers/LocationProvider'
import { useEngagementTypePreferences } from '../../EngagementTypePreferencesProvider'
import { UsersFilter } from './UsersFilter'
import { TemplatesFilter } from './TemplatesFilter'

const messages = defineMessages({
  status: {
    defaultMessage: 'Status',
    description: 'Filter label',
  },
  engagementTemplate: {
    defaultMessage: 'Engagement Template',
    description: 'Filter label',
  },
  createdBy: {
    defaultMessage: 'Created By',
    description: 'Filter label',
  },
  active: {
    defaultMessage: 'Active',
    description: 'Filter option label',
  },
  inactive: {
    defaultMessage: 'Inactive',
    description: 'Filter option label',
  },
})

const idMapper = (option: Option) => option.id
const statusOptions: string[] = ['active', 'inactive']

export const Filters: FC<FilterProps> = ({ onChange }) => {
  const { formatMessage } = useIntl()
  const { patchPreferences, preferences } = useEngagementTypePreferences()
  const { version } = useEventLocation().location!.attributes

  const {
    statuses = [],
    createdBy = [],
    templateEngagementTypes = [],
  } = preferences
  const optionMapper = (o: DataOptions) => ({ id: o.uuid, label: o.name })
  const strOptionMapper = (text: string) => ({
    id: text,
    label: formatMessage(messages[text as keyof typeof messages]),
  })

  return (
    <FiltersContainer>
      <FilterItem>
        <TextFilter
          value={statuses.map(strOptionMapper)}
          overrides={filterOverrides}
          label={formatMessage(messages.status)}
          onChange={(statuses: Option) => {
            onChange()
            patchPreferences({ statuses: statuses.map(idMapper) })
          }}
          options={statusOptions.map(strOptionMapper)}
          dataPendo="engagement-list/status-filter"
        />
      </FilterItem>
      <FilterItem>
        <TemplatesFilter
          label={formatMessage(messages.engagementTemplate)}
          value={templateEngagementTypes}
          onChange={(templates: Option) => {
            onChange()
            patchPreferences({
              templateEngagementTypes: templates.map(idMapper),
            })
          }}
          dataPendo="engagement-list/template-filter"
        />
      </FilterItem>
      {version === '2' && (
        <FilterItem>
          <UsersFilter
            label={formatMessage(messages.createdBy)}
            value={createdBy.map(optionMapper)}
            onChange={(users: Option) => {
              onChange()
              patchPreferences({
                createdBy: users.map(({ id, label }: Options) => ({
                  uuid: id,
                  name: label,
                })),
              })
            }}
            dataPendo="engagement-list/createdby-filter"
          />
        </FilterItem>
      )}
    </FiltersContainer>
  )
}
