import { useState } from 'react'
import { Prompt } from 'react-router-dom'
import { defineMessages, useIntl } from 'react-intl'
import { Select } from '@jifflenow/brightui/select'
import { ConfirmationDialog } from 'ui-components/ConfirmationDialog'
import { getEngagementTypeSurveys_getEngagementTypeSurveys as EngagementTypeSurveys } from 'gql/types/getEngagementTypeSurveys'
import { useEngagementType } from '../../EngagementTypeProvider'
import { Body, Header, usePageTitle, Root } from '../../common'
import { useFormsDirty } from '../common/useFormsDirty'
import { Container } from '../common/styled-components'
import { FormBuilder } from '../common/FormBuilder'
import { FilterLabel, SurveyDropDownRoot } from './styled-components'
import { surveyDropdownOverride } from './overrides'

const messages = defineMessages({
  surveyForm: {
    defaultMessage: 'Survey form',
    description: 'Page title',
  },
  surveyFilterLabel: {
    defaultMessage: 'Survey form for',
    description: 'Page title',
  },
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
})

const confirmationMessages = defineMessages({
  modalHeader: {
    defaultMessage: 'Your changes will be lost.',
    description: 'Modal heading',
  },
  modalMessage: {
    defaultMessage: 'Keep changes?',
    description: 'Confirmation message',
  },
  discardLabel: {
    defaultMessage: 'Discard',
    description: 'Button label',
  },
  keepLabel: {
    defaultMessage: 'Keep',
    description: 'Button label',
  },
})

export const Survey = () => {
  const { formatMessage } = useIntl()
  usePageTitle(formatMessage(messages.surveyForm))

  const options = useEngagementType().engagementTypeSurveys.filter(
    ({ status }) => status
  )

  const [dirty, setDirty] = useFormsDirty()
  const [survey, setSurvey] = useState<[EngagementTypeSurveys]>([options[0]])
  const [tempSelection, setTempSelection] = useState<
    [EngagementTypeSurveys] | undefined
  >()
  const [showConfirmation, setShowConfirmation] = useState(false)

  const handleDiscard = () => {
    setDirty(false)
    setShowConfirmation(false)
    tempSelection && setSurvey(tempSelection)
    setTempSelection(undefined)
  }

  const handleKeep = () => {
    setShowConfirmation(false)
    setTempSelection(undefined)
  }

  const handleOnChange = ({ value }: { value: [EngagementTypeSurveys] }) => {
    if (value[0].surveyMasterUuid === survey?.[0].surveyMasterUuid) {
      return
    }

    if (dirty) {
      setTempSelection(value)
      setShowConfirmation(true)
    } else {
      setSurvey(value)
    }
  }

  return (
    <Root>
      {showConfirmation && (
        <ConfirmationDialog
          heading={formatMessage(confirmationMessages.modalHeader)}
          message={formatMessage(confirmationMessages.modalMessage)}
          cancelLabel={formatMessage(confirmationMessages.discardLabel)}
          successLabel={formatMessage(confirmationMessages.keepLabel)}
          onCancel={handleDiscard}
          onSuccess={handleKeep}
        />
      )}
      <Prompt when={dirty} message={formatMessage(messages.promptMessage)} />
      <Header />
      <Body>
        <Container>
          <SurveyDropDownRoot>
            <FilterLabel>
              {formatMessage(messages.surveyFilterLabel)}
            </FilterLabel>
            <Select
              options={options}
              labelKey="surveyMasterName"
              valueKey="surveyMasterUuid"
              clearable={false}
              overrides={surveyDropdownOverride}
              value={survey}
              onChange={handleOnChange}
            />
          </SurveyDropDownRoot>
          {survey && (
            <FormBuilder
              name="survey"
              surveyId={survey[0].surveyMasterUuid}
              surveyMasterName={survey[0].surveyMasterName}
              title={formatMessage(messages.surveyForm)}
            />
          )}
        </Container>
      </Body>
    </Root>
  )
}
