import { ChangeEvent, VFC } from 'react'
import { FormattedMessage } from 'react-intl'
import { KIND, IconButton, Button } from '@jifflenow/brightui/button'
import { Close } from '@jifflenow/brightui/icon'
import { Field, Validation } from 'ui-components/Field'
import { Input } from '@jifflenow/brightui/input'
import {
  CommentsListHelpText,
  CommentsListFieldWrap,
  InputWrapper,
  StyledFormControl,
  Note,
} from './styled-components'
import { CommentsListFieldProps } from './types'
import { closeButtonOverride } from './overrides'

export const CommentsListField: VFC<CommentsListFieldProps> = ({
  helpText,
  name,
  placeholder,
  note,
}) => (
  <Field<string>
    name={name}
    validations={[Validation.REQUIRED]}
    format={value => (value ? JSON.parse(value) : [])}
    parse={value => JSON.stringify(value ? value : [])}
    render={({ input: { value, onChange }, meta: { error, submitError } }) => {
      const comments = (value as unknown) as string[]

      const handleInputChange = (newComment: string, index: number) =>
        onChange(
          comments.map((comment, i) => (i === index ? newComment : comment))
        )

      const handleAddOption = () => onChange([...comments, ''])

      const handleRemoveOption = (index: number) =>
        onChange(comments.filter((_, i) => i !== index))

      return (
        <>
          <CommentsListHelpText>{helpText}</CommentsListHelpText>
          <CommentsListFieldWrap>
            {comments.map((option, index) => (
              <InputWrapper key={index}>
                <StyledFormControl error={submitError || error}>
                  <Input
                    name={name}
                    value={option}
                    placeholder={placeholder}
                    onChange={(e: ChangeEvent<HTMLInputElement>) =>
                      handleInputChange(e.target.value, index)
                    }
                  />
                </StyledFormControl>
                <IconButton
                  type="button"
                  size={20}
                  overrides={closeButtonOverride}
                  onClick={() => handleRemoveOption(index)}
                  data-pendo="remove-comments"
                >
                  <Close />
                </IconButton>
              </InputWrapper>
            ))}
            <Button
              type="button"
              kind={KIND.minimal}
              onClick={handleAddOption}
              data-pendo="add-comments"
            >
              <FormattedMessage
                defaultMessage="+ Add"
                description="Button label"
              />
            </Button>
          </CommentsListFieldWrap>
          <CommentsListFieldWrap>
            <Note>{note}</Note>
          </CommentsListFieldWrap>
        </>
      )
    }}
  />
)
