import { FC, VFC, useState, KeyboardEvent, RefCallback } from 'react'
import { defineMessages, FormattedMessage, useIntl } from 'react-intl'
import { StatefulTooltip } from '@jifflenow/brightui/tooltip'
import { Warning } from '@jifflenow/brightui/icon'
import { Button, KIND } from '@jifflenow/brightui/button'
import { Tag, VARIANT, KIND as TagKIND } from '@jifflenow/brightui/tag'
import {
  Modal,
  ModalFooter,
  ModalHeader,
  SIZE,
} from '@jifflenow/brightui/modal'
import { useEngagementTemplatesQuery } from 'gql'
import { Field, Validation } from 'ui-components/Field'
import { formatDate } from 'utils/dates'
import { CheckboxSelect } from '@jifflenow/brightui/icon'
import { columnHeaderNames } from '../../../EngagementTypesList/List/constants'
import {
  Container,
  TemplateCard,
  TemplateName,
  PreviewModalBody,
  PreviewModalTemplateHeader,
  SelectTemplateButton,
  TemplateGroup,
  NoTemplatesAvailableMessage,
  TemplateListErrorMessage,
  EngagementDetailContainer,
  EngagementDetailRow,
  ColumnLeft,
  ColumnRight,
  StyledDescription,
  CheckedIndicator,
} from './styled-components'
import {
  CopyTemplatePreviewProps,
  EngagementTemplatesProps,
  TemplatePreviewProps,
  TemplateProps,
  TemplatesListProps,
} from './types'
import {
  templateErrorIconOverride,
  statusTagOverrides,
  moreBtnOverrides,
  checkedIconOverride,
} from './overrides'
import { Error } from './Error'
import { LoadingShimmer } from './LoadingShimmer'

const messages = defineMessages({
  select: {
    defaultMessage: 'Select',
    description: 'Button label',
  },
  selected: {
    defaultMessage: 'Selected',
    description: 'Button label',
  },
  active: {
    defaultMessage: 'Active',
    description: 'Grid cell value',
  },
  inactive: {
    defaultMessage: 'Inactive',
    description: 'Grid cell value',
  },
})

const CopyTemplatePreview: VFC<CopyTemplatePreviewProps> = ({
  copy,
  template,
  onClose,
}) => {
  const { formatMessage } = useIntl()

  return (
    <Modal size={SIZE.medium} onClose={onClose} isOpen>
      <ModalHeader>{copy?.name}</ModalHeader>
      <PreviewModalBody>
        <EngagementDetailContainer>
          <EngagementDetailRow>
            <ColumnLeft>{formatMessage(columnHeaderNames.name)}</ColumnLeft>
            <ColumnRight>{copy?.name}</ColumnRight>
          </EngagementDetailRow>
          <EngagementDetailRow>
            <ColumnLeft>
              {formatMessage(columnHeaderNames.templateEngagementType)}
            </ColumnLeft>
            <ColumnRight>{copy?.templateEngagementType.name}</ColumnRight>
          </EngagementDetailRow>
          <EngagementDetailRow>
            <ColumnLeft>
              {formatMessage(columnHeaderNames.createdBy)}
            </ColumnLeft>
            <ColumnRight>{copy?.createdBy}</ColumnRight>
          </EngagementDetailRow>
          <EngagementDetailRow>
            <ColumnLeft>
              {formatMessage(columnHeaderNames.createdAt)}
            </ColumnLeft>
            <ColumnRight>{formatDate(new Date(copy?.createdAt))}</ColumnRight>
          </EngagementDetailRow>
          <EngagementDetailRow>
            <ColumnLeft>{formatMessage(columnHeaderNames.status)}</ColumnLeft>
            <ColumnRight>
              <Tag
                closeable={false}
                variant={VARIANT.light}
                overrides={statusTagOverrides}
                kind={copy?.status ? TagKIND.positive : TagKIND.negative}
              >
                {formatMessage(messages[copy?.status ? 'active' : 'inactive'])}
              </Tag>
            </ColumnRight>
          </EngagementDetailRow>
        </EngagementDetailContainer>
        <PreviewModalTemplateHeader>{template.name}</PreviewModalTemplateHeader>
        {template.description}
      </PreviewModalBody>
    </Modal>
  )
}

const TemplatePreview: VFC<TemplatePreviewProps> = ({
  template,
  onClose,
  onTemplateSelect,
}) => (
  <Modal size={SIZE.medium} onClose={onClose} isOpen>
    <ModalHeader>{template.name}</ModalHeader>
    <PreviewModalBody>{template.description}</PreviewModalBody>
    <ModalFooter>
      <SelectTemplateButton
        type="button"
        kind={KIND.secondary}
        onClick={onTemplateSelect}
      >
        <FormattedMessage
          defaultMessage="Select This Template"
          description="Button label"
        />
      </SelectTemplateButton>
    </ModalFooter>
  </Modal>
)

const Template: VFC<TemplateProps> = ({
  onSelect,
  selected,
  copy,
  template,
}) => {
  const [isOpen, setIsOpen] = useState(false)

  const handleTemplateSelect = () => {
    setIsOpen(false)
    onSelect(template.id)
  }

  const showPreview = () => {
    setIsOpen(true)
  }

  const handleEnter = (e: KeyboardEvent<HTMLDivElement>) => {
    e.code === 'Enter' && e.target instanceof HTMLDivElement && showPreview()
  }

  const SelectedIndicator = () => (
    <CheckedIndicator>
      <CheckboxSelect size={10} overrides={checkedIconOverride} />
    </CheckedIndicator>
  )

  const [showMore, setShowMore] = useState(false)
  const refCallback: RefCallback<HTMLElement> = el =>
    el && setShowMore(el.scrollHeight > el.offsetHeight)

  const Description = () => (
    <>
      <StyledDescription ref={refCallback}>
        {template.description}
      </StyledDescription>
      {showMore && (
        <Button
          kind={KIND.minimal}
          overrides={moreBtnOverrides}
          onClick={e => {
            e.stopPropagation()
            showPreview()
            e.preventDefault()
          }}
        >
          <FormattedMessage defaultMessage="more" description="Button label" />
        </Button>
      )}
    </>
  )

  return (
    <>
      {isOpen &&
        (copy ? (
          <CopyTemplatePreview
            onClose={() => setIsOpen(false)}
            template={template}
            copy={copy}
          />
        ) : (
          <TemplatePreview
            onClose={() => setIsOpen(false)}
            onTemplateSelect={handleTemplateSelect}
            template={template}
          />
        ))}

      <TemplateCard
        role="radio"
        aria-checked={selected}
        tabIndex={0}
        onClick={handleTemplateSelect}
        onKeyPress={handleEnter}
        $selected={selected}
        data-pendo={template.id}
      >
        {selected && <SelectedIndicator />}
        <StatefulTooltip content={copy ? copy.name : template.name} showArrow>
          <TemplateName>{copy ? copy.name : template.name}</TemplateName>
        </StatefulTooltip>
        <Description />
      </TemplateCard>
    </>
  )
}

const TemplatesListError = () => (
  <TemplateListErrorMessage>
    <Warning overrides={templateErrorIconOverride} />
    <FormattedMessage
      defaultMessage="Required: Please select a template"
      description="Error message"
    />
  </TemplateListErrorMessage>
)

const TemplatesList: FC<TemplatesListProps> = ({
  input: { value, onChange },
  meta: { error, touched },
  copy,
}) => {
  const {
    loading,
    templates,
    error: fetchError,
    refetch,
  } = useEngagementTemplatesQuery({
    notifyOnNetworkStatusChange: true,
  })

  if (loading) {
    return <LoadingShimmer />
  }

  if (fetchError) {
    return <Error onClick={() => refetch()} />
  }

  if (!templates?.length) {
    return (
      <NoTemplatesAvailableMessage>
        <FormattedMessage
          defaultMessage="No templates available"
          description="Empty info message"
        />
      </NoTemplatesAvailableMessage>
    )
  }

  return (
    <>
      {touched && error && <TemplatesListError />}
      <TemplateGroup role="radiogroup">
        {(copy
          ? templates.filter(({ id }) => id === copy.templateEngagementTypeId)
          : templates
        ).map(template => (
          <Template
            key={template.id}
            template={template}
            selected={template.id === value}
            onSelect={id => onChange(id)}
            copy={copy}
          />
        ))}
      </TemplateGroup>
    </>
  )
}

export const EngagementTemplates: FC<EngagementTemplatesProps> = ({ copy }) => (
  <Container>
    <Field<string>
      name="templateId"
      render={props => <TemplatesList {...props} copy={copy} />}
      validations={[Validation.REQUIRED]}
    />
  </Container>
)
