import { defineMessages, MessageDescriptor } from 'react-intl'
import {
  CheckboxSelectionCallbackParams,
  ColDef,
  ValueFormatterParams,
} from '@ag-grid-community/core'
import { formatDate } from 'utils/dates'
import { components } from 'types/openapi'
import { getEngagementTypes_getEngagementTypes_data } from 'gql/types/getEngagementTypes'
import { ColumnsDataType } from './types'

type EngagementTypesSearchResponse = components['schemas']['ActivityDetails']

export const columnHeaderNames: {
  [key: string]: MessageDescriptor
} = defineMessages<
  Extract<
    | keyof getEngagementTypes_getEngagementTypes_data
    | keyof EngagementTypesSearchResponse,
    | 'name'
    | 'displayOrder'
    | 'templateEngagementType'
    | 'meetingTypeMasterName'
    | 'createdAt'
    | 'createdBy'
    | 'status'
  >
>({
  name: {
    defaultMessage: 'Engagement Type',
    description: 'Column header name',
  },
  displayOrder: {
    defaultMessage: 'Display Order',
    description: 'Column header name',
  },
  templateEngagementType: {
    defaultMessage: 'Engagement Template',
    description: 'Column header name',
  },
  meetingTypeMasterName: {
    defaultMessage: 'Engagement Template',
    description: 'Column header name',
  },
  createdBy: {
    defaultMessage: 'Created By',
    description: 'Column header name',
  },
  createdAt: {
    defaultMessage: 'Date Created',
    description: 'Column header name',
  },
  status: {
    defaultMessage: 'Status',
    description: 'Column header name',
  },
})

export const getColumnDefs = (version: string): ColDef[] =>
  [
    version === '2' && {
      checkboxSelection: (args: CheckboxSelectionCallbackParams) =>
        args.data.sagaCompleted,
      lockPosition: true,
      maxWidth: 50,
      pinned: 'left',
      resizable: false,
      headerComponent: 'checkboxHeader',
      headerComponentParams: {},
    },
    {
      field: 'name',
      sortable: true,
    },
    version === '2' && {
      field: 'displayOrder',
      sortable: true,
      cellRenderer: 'displayOrderCell',
    },
    {
      field:
        version === '2' ? 'templateEngagementType' : 'meetingTypeMasterName',
      sortable: true,
      cellRenderer: 'engagementTemplateCell',
    },
    version === '2' && {
      field: 'createdBy',
      sortable: true,
    },
    {
      field: 'createdAt',
      sortable: true,
      valueGetter: ({ data: { createdAt } }: ColumnsDataType) =>
        new Date(createdAt),
      valueFormatter: ({ value }: ValueFormatterParams) => formatDate(value),
    },
    {
      field: 'status',
      sortable: true,
      cellRenderer: 'statusCell',
    },
    version === '2' && {
      field: 'actions',
      pinned: 'right',
      resizable: false,
      maxWidth: 50,
      headerComponent: 'actionHeader',
      headerComponentParams: { columnHeaderNames },
      cellRenderer: 'actionCell',
    },
  ].filter(Boolean) as ColDef[]
