import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useFormState } from 'react-final-form'
import { useEventLocation } from 'providers/LocationProvider'
import {
  EngagementTypeToggle,
  EngagementTypeToggleWithDesc,
} from '../../common'
import { BorderStyled, Section, SectionTitle } from '../styled-components'
import { FormValues } from '../types'
import { CustomFields } from './CustomFields'

const messages = defineMessages({
  guidedTour: {
    defaultMessage: 'Guided Tour',
    description: 'Section title',
  },
  enableGuidedTour: {
    defaultMessage: 'Enable Guided Tour',
    description: 'Field label',
  },
  quickDemo: {
    defaultMessage: 'Quick Demo',
    description: 'Field label',
  },
  note: {
    defaultMessage: 'Note: Make sure to enable designated location.',
    description: 'Quick Demo help text',
  },
})

export const GuidedTour: VFC = () => {
  const { formatMessage } = useIntl()
  const { guided_tour_survey: eventGuidedTourSurvey, quick_demo } =
    useEventLocation().configs || {}
  const { guided_tour_survey } = useFormState<FormValues>().values

  return eventGuidedTourSurvey === 'true' ? (
    <Section>
      <SectionTitle>{formatMessage(messages.guidedTour)}</SectionTitle>
      <EngagementTypeToggle
        name="guided_tour_survey"
        label={formatMessage(messages.enableGuidedTour)}
      />
      <BorderStyled />
      {guided_tour_survey === 'true' && (
        <>
          {quick_demo === 'true' && (
            <EngagementTypeToggleWithDesc
              name="quick_demo"
              label={formatMessage(messages.quickDemo)}
              desc={formatMessage(messages.note)}
            />
          )}
          <CustomFields />
          <BorderStyled />
        </>
      )}
    </Section>
  ) : null
}
