import { FC, useState } from 'react'
import { useLocation } from 'react-router-dom'
import { defineMessages, useIntl } from 'react-intl'
import { QueryObserverBaseResult } from 'react-query'
import { ICellRendererParams } from '@ag-grid-community/core'
import { PLACEMENT, StatefulPopover } from '@jifflenow/brightui/popover'
import { StatefulMenu, OnItemSelect } from '@jifflenow/brightui/menu'
import { IconButton } from '@jifflenow/brightui/button'
import { ThreeDots } from '@jifflenow/brightui/icon'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import {
  useEngagementTypeStatusMutation,
  useDeleteEngagementTypeMutation,
} from 'gql'
import {
  getEngagementTypes_getEngagementTypes_data,
  getEngagementTypes_getEngagementTypes_data_actions,
} from 'gql/types/getEngagementTypes'
import {
  ThreeDotButtonOverrides,
  InlineMenuOverrides,
  PopoverOverrides,
} from 'components/ag-grid'
import { EngagementActionErrorCodes } from 'gql/types/globalTypes'
import { CreateEngagementDrawer } from '../../CreateEngagementDrawer'
import { DeleteConfirmationDialog } from '../DeleteConfirmationDialog'
import { ActionItem } from './types'

const messages = defineMessages({
  viewEditDetails: {
    defaultMessage: 'View/Edit Details',
    description: 'Grid row action label',
  },
  makeActive: {
    defaultMessage: 'Make Active',
    description: 'Grid row action label',
  },
  makeInactive: {
    defaultMessage: 'Make Inactive',
    description: 'Grid row action label',
  },
  delete: {
    defaultMessage: 'Delete',
    description: 'Grid row action label',
  },
  duplicate: {
    defaultMessage: 'Duplicate',
    description: 'Grid row action label',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  inactiveSuccess: {
    defaultMessage: '{name} inactivated successfully',
    description: 'Grid row action label',
  },
  activeSuccess: {
    defaultMessage: '{name} activated successfully',
    description: 'Grid row action label',
  },
  deleteSuccess: {
    defaultMessage: '{name} deleted successfully',
    description: 'Grid row action label',
  },
})

const errorMessages = defineMessages<EngagementActionErrorCodes>({
  ENGAGEMENT_TYPE_DELETE_ASSOCIATED_MEETING_EXISTS: {
    defaultMessage:
      'Engagement type cannot be deleted as it has active meetings.',
    description: 'Error message',
  },
  ENGAGEMENT_TYPE_DELETE_ASSOCIATED_SESSION_EXISTS: {
    defaultMessage:
      'Engagement type cannot be deleted as it has active sessions.',
    description: 'Error message',
  },
  ENGAGEMENT_TYPE_DEACTIVATE_ASSOCIATED_MEETING_EXISTS: {
    defaultMessage:
      'Engagement type cannot be deactivated as it has active meetings.',
    description: 'Error message',
  },
  ENGAGEMENT_TYPE_DEACTIVATE_ASSOCIATED_SESSION_EXISTS: {
    defaultMessage:
      'Engagement type cannot be deactivated as it has active sessions.',
    description: 'Error message',
  },
  ENGAGEMENT_TYPE_DEACTIVATE_ASSOCIATED_TRACK_EXISTS: {
    defaultMessage:
      'Engagement type cannot be deactivated as it is mapped to tracks.',
    description: 'Error message',
  },
  ENGAGEMENT_TYPE_DELETE_ASSOCIATED_TRACK_EXISTS: {
    defaultMessage:
      'Engagement type cannot be deleted as it is mapped to tracks.',
    description: 'Error message',
  },
  ENGAGEMENT_TYPE_ALREADY_DELETED: {
    defaultMessage: 'Engagement type already deleted.',
    description: 'Error message',
  },
  ENGAGEMENT_TYPE_CREATION_FAILED: {
    defaultMessage: 'Engagement type creation failed.',
    description: 'Error message',
  },
  ENGAGEMENT_TYPE_DUPLICATION_FAILED: {
    defaultMessage: 'Engagement type duplication failed.',
    description: 'Error message',
  },
  ENGAGEMENT_TYPE_INVALID_EVENT_LOCATION: {
    defaultMessage: 'Invalid event location.',
    description: 'Error message',
  },
  ENGAGEMENT_TYPE_NOT_FOUND: {
    defaultMessage: 'Engagement type not found.',
    description: 'Error message',
  },
  ENGAGEMENT_TYPE_SAGA_NOT_COMPLETE: {
    defaultMessage: 'Engagement type creation in progress.',
    description: 'Error message',
  },
})

const aria = defineMessages({
  labelEngagementMore: {
    defaultMessage: 'Engagement more option',
    description: 'Actions on this engagement type',
  },
})

const onItemSelect: OnItemSelect = args => args.item.onItemSelect?.(args)

const Content: FC<
  ICellRendererParams & {
    value: getEngagementTypes_getEngagementTypes_data_actions
    data: getEngagementTypes_getEngagementTypes_data
    refetch?: QueryObserverBaseResult['refetch']
    onRowDelete?: () => void
  }
> = ({ value, data, node, api, refetch, onRowDelete }) => {
  const { formatMessage } = useIntl()
  const { search, pathname } = useLocation()
  const [isOpen, setIsOpen] = useState(false)
  const [showConfirmation, setShowConfirmation] = useState(false)

  const { mutateStatus } = useEngagementTypeStatusMutation({
    onError: () => {
      api.hideOverlay()
      showErrorToast(formatMessage(messages.genericError))
    },
    onCompleted: ({ updateEngagementType }) => {
      refetch?.()
      api.hideOverlay()
      if (updateEngagementType.__typename === 'EngagementType') {
        showSuccessToast(
          formatMessage(
            messages[
              updateEngagementType.status ? 'activeSuccess' : 'inactiveSuccess'
            ],
            { name: data.name }
          )
        )
      } else if (updateEngagementType.__typename === 'EngagementTypeErrors') {
        const errors = updateEngagementType.errors.map(({ code }) =>
          formatMessage(errorMessages[code])
        )
        showErrorToast(errors.join('\n'))
      }
    },
  })

  const { deleteItem } = useDeleteEngagementTypeMutation({
    onError: () => {
      api.hideOverlay()
      showErrorToast(formatMessage(messages.genericError))
    },
    onCompleted: ({ deleteEngagementType }) => {
      api.hideOverlay()
      if (deleteEngagementType.__typename === 'EngagementTypeErrors') {
        const errors = deleteEngagementType.errors.map(({ code }) =>
          formatMessage(errorMessages[code])
        )
        showErrorToast(errors.join('\n'))
      } else {
        onRowDelete?.()
        api.applyTransaction({ remove: [node.data] })
        showSuccessToast(
          formatMessage(messages.deleteSuccess, { name: data.name })
        )
      }
    },
  })

  const updateStatus = () => {
    api.showLoadingOverlay()
    mutateStatus({ variables: { enabled: !data.status, uuid: data.uuid } })
  }

  const deleteEngagementType = () => {
    setShowConfirmation(false)
    api.showLoadingOverlay()
    deleteItem({ variables: { uuid: data.uuid } })
  }

  const onClose = () => setShowConfirmation(false)

  const menu: ActionItem[] = [
    {
      label: formatMessage(messages.viewEditDetails),
      href: `${pathname}/${data.uuid}${search}`,
      dataPendo: 'engagement-type/view-edit',
    },
    value.canActivate && {
      label: formatMessage(messages.makeActive),
      onItemSelect: updateStatus,
      dataPendo: 'engagement-type/make-active',
    },
    value.canDeactivate && {
      label: formatMessage(messages.makeInactive),
      onItemSelect: updateStatus,
      dataPendo: 'engagement-type/make-inactive',
    },
    value.canDelete && {
      label: formatMessage(messages.delete),
      onItemSelect: () => setShowConfirmation(true),
      dataPendo: 'engagement-type/delete',
    },
    value.canDuplicate && {
      label: formatMessage(messages.duplicate),
      onItemSelect: () => setIsOpen(true),
      dataPendo: 'engagement-type/duplicate',
    },
  ].filter(Boolean) as ActionItem[]

  return (
    <>
      <StatefulMenu
        items={menu}
        onItemSelect={onItemSelect}
        overrides={InlineMenuOverrides}
      />
      {isOpen && (
        <CreateEngagementDrawer
          isOpen
          onClose={() => setIsOpen(false)}
          onCreate={() => refetch?.()}
          copy={data}
        />
      )}
      {showConfirmation && (
        <DeleteConfirmationDialog
          onClose={onClose}
          onConfirm={deleteEngagementType}
        />
      )}
    </>
  )
}

export const ActionCell: FC<ICellRendererParams> = props => {
  const { formatMessage } = useIntl()

  return (
    <StatefulPopover
      popoverMargin={8}
      content={<Content {...props} />}
      placement={PLACEMENT.bottomRight}
      overrides={PopoverOverrides}
    >
      <IconButton
        size={20}
        overrides={ThreeDotButtonOverrides}
        aria-label={formatMessage(aria.labelEngagementMore)}
        disabled={!props.data.sagaCompleted}
      >
        <ThreeDots />
      </IconButton>
    </StatefulPopover>
  )
}
