import { ReactNode, useEffect, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useField } from 'react-final-form'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { ConfirmationDialog } from 'ui-components/ConfirmationDialog'
import { EngagementTypeToggle } from '../common'
import { useEngagementType } from '../EngagementTypeProvider'
import {
  BorderStyled,
  ParticipantsBody,
  ParticipantsSectionDesc,
  ParticipantsSectionTitle,
} from './styled-components'

const messages = defineMessages({
  sectionTitle: {
    defaultMessage: 'External Attendee Options',
    description: 'Section title',
  },
  sectionTitleDesc: {
    defaultMessage: 'External attendee capabilities during requesting meeting',
    description: 'Help text',
  },
  hideExternalAttendeesInBookMeetingLabel: {
    defaultMessage: 'Hide External Attendee section',
    description: 'Field label',
  },
  externalAttendeePresenceLabel: {
    defaultMessage: 'At least one external attendee is required',
    description: 'Field label',
  },
  allExternalAttendeesVisibleLabel: {
    defaultMessage:
      'Requestors can see External Attendees added by anyone (other Requestors + MMs)',
    description: 'Field label',
  },
  relaxExternalAttendeePresenceForMmLabel: {
    defaultMessage:
      'Relax this restriction for MMs/JMMs (applicable if above is enabled).',
    description: 'Field label',
  },
  externalAttendeesVisibleAddedByMmLabel: {
    defaultMessage: 'Requestors can see External Attendees added by MMs',
    description: 'Field label',
  },
  allowUserToAddNewExternalAttendeeLabel: {
    defaultMessage: 'Allow user to add new External Attendees',
    description: 'Field label',
  },
  externalIcsOptOutLabel: {
    defaultMessage:
      'Requestors (non-MMs) can opt out External Attendees from ICS',
    description: 'Field label',
  },
  dialogTitle: {
    defaultMessage: 'Warning',
    description: 'Dialog title',
  },
  dialogMessage: {
    defaultMessage:
      'Enabling this setting will result in external attendee(s) <b>not receiving the personal calendar invitations automatically</b> upon approval of requests in this engagement type.',
    description: 'Dialog Body',
  },
  labelConfirm: {
    defaultMessage: 'Confirm',
    description: 'Button label',
  },
  labelCancel: {
    defaultMessage: 'Cancel',
    description: 'Button label',
  },
})

const ExternalIcs: VFC = () => {
  const { formatMessage } = useIntl()
  const [showWarning, setShowWarning] = useState(false)

  const {
    input: { value: externalIcsOptOut, onChange: changeExternalIcsOptOut },
    meta: { initial: initialExternalIcsOptOut },
  } = useField('external_ics_opt_out')

  useEffect(() => {
    initialExternalIcsOptOut === 'false' &&
      externalIcsOptOut === 'true' &&
      setShowWarning(true)
  }, [externalIcsOptOut, initialExternalIcsOptOut])

  return (
    <>
      {showWarning && (
        <ConfirmationDialog
          heading={formatMessage(messages.dialogTitle)}
          message={formatMessage(messages.dialogMessage, {
            b: (chunks: ReactNode) => <strong>{chunks}</strong>,
          })}
          successLabel={formatMessage(messages.labelConfirm)}
          cancelLabel={formatMessage(messages.labelCancel)}
          onCancel={() => {
            setShowWarning(false)
            changeExternalIcsOptOut('false')
          }}
          onSuccess={() => setShowWarning(false)}
        />
      )}
      <EngagementTypeToggle
        label={formatMessage(messages.externalIcsOptOutLabel)}
        name="external_ics_opt_out"
      />
    </>
  )
}

export const ExternalAttendee: VFC = () => {
  const { formatMessage } = useIntl()
  const {
    masterEngagementType,
  } = useEngagementType().engagementType.templateEngagementType
  const isSession =
    masterEngagementType === MasterEngagementTypes.TracksAndSession

  return (
    <ParticipantsBody>
      <ParticipantsSectionTitle>
        {formatMessage(messages.sectionTitle)}
      </ParticipantsSectionTitle>
      <ParticipantsSectionDesc>
        {formatMessage(messages.sectionTitleDesc)}
      </ParticipantsSectionDesc>
      {!isSession && (
        <>
          <BorderStyled />
          <EngagementTypeToggle
            label={formatMessage(
              messages.hideExternalAttendeesInBookMeetingLabel
            )}
            name="hide_external_attendees_in_book_meeting"
          />
          <BorderStyled />
          <EngagementTypeToggle
            label={formatMessage(messages.externalAttendeePresenceLabel)}
            name="external_attendee_presence"
          >
            <BorderStyled />
            <EngagementTypeToggle
              label={formatMessage(
                messages.relaxExternalAttendeePresenceForMmLabel
              )}
              name="relax_external_attendee_presence_for_mm"
            />
          </EngagementTypeToggle>
          <BorderStyled />
          <EngagementTypeToggle
            label={formatMessage(messages.allExternalAttendeesVisibleLabel)}
            name="all_external_attendees_visible"
          />
          <BorderStyled />
          <EngagementTypeToggle
            label={formatMessage(
              messages.externalAttendeesVisibleAddedByMmLabel
            )}
            name="external_attendees_visible_added_by_mm"
          />
        </>
      )}
      <BorderStyled />
      <EngagementTypeToggle
        label={formatMessage(messages.allowUserToAddNewExternalAttendeeLabel)}
        name="allow_user_to_add_new_external_attendee"
      />
      {!isSession && (
        <>
          <BorderStyled />
          <ExternalIcs />
        </>
      )}
      <BorderStyled />
    </ParticipantsBody>
  )
}
