import { useCallback, useEffect, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useField, useFormState } from 'react-final-form'
import { Subtitle3 } from '@jifflenow/brightui/typography'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { Field } from 'ui-components/Field'
import { CopyEmbedSourceCode } from 'ui-components/CopyEmbedSourceCode'
import { useEventLocation } from 'providers/LocationProvider'
import { TimeDurationSelect } from '../common/TimeDurationSelect'
import { Paths } from '../Paths'
import { EngagementModal } from '../common/EngagementModal'
import { RefetchProps } from '../common/types'
import {
  DurationContainer,
  FormControlStyledBody,
  FormControlStyledToggleContainer,
  FormFieldWrapper,
} from './styled-components'
import { EngagementInputField } from './EngagementInputField'
import { EngagementLinkField } from './EngagementLinkField'
import { getFrameCode, getCallToActionButtonCode } from './GetCopiedContent'
import { EngagementTypeToggle } from './EngagementTypeToggle'
import { DependentFieldValues, DependentLinks, FormValues } from './types'

const messages = defineMessages({
  urlMeetNowLabel: {
    defaultMessage: 'URL for meet now page',
    description: 'Field label',
  },
  MeetingBufferLabel: {
    defaultMessage: 'Buffer time for meet now slots',
    description: 'Field label',
  },
  sourceCodeIrpLabel: {
    defaultMessage:
      'Source code to embed the inbound request page of this engagement type',
    description: 'Field label',
  },
  sourceCodeIrpActionLabel: {
    defaultMessage:
      'Source code to embed Call to Action button for IRP of this engagement type',
    description: 'Field label',
  },
  embedIrpLabel: {
    defaultMessage:
      'Copy Iframe code to embed meet now page for the engagement type',
    description: 'Button label',
  },
  embedIrpActionLabel: {
    defaultMessage:
      'Copy source code to call to action button of meet now page for this engagement type',
    description: 'Button label',
  },
  irpInstantLabel: {
    defaultMessage: 'Meet now page URL for this engagement type',
    description: 'Field label',
  },
  enableMeetNowLabel: {
    defaultMessage: 'Enable meet now for this engagement type',
    description: 'Field label',
  },
  meetNowButtonLabel: {
    defaultMessage: 'Meet now call to action button text',
    description: 'Field label',
  },
  meetNowButtonPlaceholder: {
    defaultMessage: 'Meet now call to action button text',
    description: 'Placeholder text',
  },
  meetNowLabel: {
    defaultMessage: 'Meet Now',
    description: 'Field label',
  },
  approvalProcessLabel: {
    defaultMessage: 'Meeting APPROVAL PROCESS is set to Instant',
    description: 'Dependent setting info',
  },
  mappedOrUnMappedLabel: {
    defaultMessage:
      'Method to process the inbound request page is set to Auto schedule - mapped request page or Auto schedule - unmapped request page',
    description: 'Dependent setting info',
  },
  warningEnableLabel: {
    defaultMessage: 'Enable the following',
    description: 'Dependent settings group header',
  },
  warningModalTitle: {
    defaultMessage:
      'Please ensure following settings are done before enabling Meet Now',
    description: 'Dependent settings modal title',
  },
})

export const MeetNowField: VFC<RefetchProps & DependentFieldValues> = ({
  refetch,
  loading,
  dependentFieldValues,
}) => {
  const { formatMessage } = useIntl()
  const {
    irp_instant_meeting_url,
    enable_irp_instant_meeting_url,
    irp_instant_meeting_label,
    enable_external_widget_auto_process,
    enable_external_widget_auto_process_without_mapping,
    disable_url,
    warning,
  } = useFormState<FormValues>().values

  const { version } = useEventLocation().location!.attributes

  const {
    approval_process,
    theme,
    enable_irp_instant_meeting,
  } = dependentFieldValues
  const [isOpen, setIsOpen] = useState(false)

  const getMeetNowState = useCallback(
    () =>
      approval_process === 'instant' &&
      (enable_external_widget_auto_process === 'true' ||
        enable_external_widget_auto_process_without_mapping === 'true'),
    [
      approval_process,
      enable_external_widget_auto_process,
      enable_external_widget_auto_process_without_mapping,
    ]
  )

  const { value, onChange } = useField('enable_irp_instant_meeting').input

  useEffect(() => {
    value === 'true' && setIsOpen(!getMeetNowState())
  }, [getMeetNowState, value])

  const dependentLinks = [
    approval_process !== 'instant' && {
      label: formatMessage(messages.approvalProcessLabel),
      path: Paths.APPROVAL_PROCESS,
      group: formatMessage(messages.warningEnableLabel),
    },
    (enable_external_widget_auto_process !== 'true' ||
      enable_external_widget_auto_process_without_mapping !== 'true') && {
      label: formatMessage(messages.mappedOrUnMappedLabel),
      path: Paths.INBOUND_REQUEST_FORM,
      group: formatMessage(messages.warningEnableLabel),
    },
  ].filter(Boolean) as DependentLinks[]

  const disableLink =
    enable_irp_instant_meeting !== 'true' || disable_url === 'true'

  return (
    <FormControlStyledToggleContainer>
      <Subtitle3>{formatMessage(messages.meetNowLabel)} </Subtitle3>
      <Field
        label={formatMessage(messages.enableMeetNowLabel)}
        name="enable_irp_instant_meeting"
        render={EngagementTypeToggle}
      >
        {isOpen && (
          <>
            {loading && <ModalSpinner />}
            <EngagementModal
              title={formatMessage(messages.warningModalTitle)}
              dependentLinks={dependentLinks}
              onClose={() => {
                setIsOpen(false)
                getMeetNowState() ? onChange('true') : onChange('false')
              }}
              onConfirm={() => refetch()}
              version={version}
            />
          </>
        )}
        <FormControlStyledBody>
          <FormControlStyledToggleContainer>
            <Field
              label={formatMessage(messages.irpInstantLabel)}
              name="enable_irp_instant_meeting_url"
              render={EngagementTypeToggle}
            >
              <FormFieldWrapper>
                <Field
                  name="irp_instant_meeting_label"
                  render={EngagementInputField}
                  label={formatMessage(messages.meetNowButtonLabel)}
                  placeholder={formatMessage(messages.meetNowButtonPlaceholder)}
                />
              </FormFieldWrapper>
              <FormFieldWrapper>
                <DurationContainer>
                  <TimeDurationSelect
                    label={formatMessage(messages.MeetingBufferLabel)}
                    name="irp_instant_meeting_buffer"
                  />
                </DurationContainer>
              </FormFieldWrapper>
              <FormFieldWrapper>
                <Field
                  name="irp_instant_meeting_url"
                  render={EngagementLinkField}
                  label={formatMessage(messages.urlMeetNowLabel)}
                  disabled={disableLink}
                  warning={warning}
                />
              </FormFieldWrapper>
              <FormFieldWrapper>
                <CopyEmbedSourceCode
                  title={formatMessage(messages.sourceCodeIrpLabel)}
                  link={getFrameCode(irp_instant_meeting_url)}
                  buttonLabel={formatMessage(messages.embedIrpLabel)}
                  disabled={disableLink}
                />
              </FormFieldWrapper>
              <FormFieldWrapper>
                <CopyEmbedSourceCode
                  title={formatMessage(messages.sourceCodeIrpActionLabel)}
                  link={getCallToActionButtonCode(
                    irp_instant_meeting_url,
                    theme ? theme.colors.primary : '#0096FF',
                    irp_instant_meeting_label
                  )}
                  buttonLabel={formatMessage(messages.embedIrpActionLabel)}
                  disabled={disableLink}
                />
              </FormFieldWrapper>
            </Field>
          </FormControlStyledToggleContainer>
          {enable_irp_instant_meeting_url === 'false' && (
            <FormFieldWrapper>
              <DurationContainer>
                <TimeDurationSelect
                  label={formatMessage(messages.MeetingBufferLabel)}
                  name="irp_instant_meeting_buffer"
                />
              </DurationContainer>
            </FormFieldWrapper>
          )}
        </FormControlStyledBody>
      </Field>
    </FormControlStyledToggleContainer>
  )
}
