import { ComponentType } from 'react'
import { useEventLocation } from 'providers/LocationProvider'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { Paths } from './Paths'
import { Irp } from './Irp'
import { Notifications } from './Notifications'
import { NotificationsDetails } from './NotificationsDetails'
import { useEngagementType } from './EngagementTypeProvider'
import { OutboundMeeting } from './Forms/OutboundMeeting'
import { InboundMeeting } from './Forms/InboundMeeting'
import { Survey } from './Forms/Survey'

type Layout = { path: Paths; component: ComponentType }

export const LayoutsV1 = () => {
  const { privileges, configs } = useEventLocation()
  const {
    engagementTypeSurveys,
    engagementTypeSettings: {
      enable_external_widget: enable_external_widget_setting,
    },
    engagementType: {
      templateEngagementType: { masterEngagementType },
      status: engagementTypeEnabled,
    },
  } = useEngagementType()
  const { enable_external_widget } = configs!
  const activeSurveys = engagementTypeSurveys.filter(({ status }) => status)

  const layouts: Layout[] = [
    ...(masterEngagementType !== MasterEngagementTypes.ConsecutiveMeeting &&
    enable_external_widget === 'true'
      ? [{ path: Paths.INBOUND_REQUEST_FORM, component: Irp }]
      : []),

    ...(engagementTypeEnabled && privileges?.canManageNotifications
      ? [{ path: Paths.NOTIFICATIONS, component: Notifications }]
      : []),
    {
      path: Paths.NOTIFICATIONS_DETAILS,
      component: NotificationsDetails,
    },
    ...(engagementTypeEnabled &&
    privileges?.canManageForms &&
    privileges.canManageSelfServeSettings
      ? [
          { path: Paths.OUTBOUND_MEETING_FORM, component: OutboundMeeting },
          ...(enable_external_widget === 'true' &&
          enable_external_widget_setting === 'true'
            ? [{ path: Paths.INBOUND_MEETING_FORM, component: InboundMeeting }]
            : []),
          ...(activeSurveys.length
            ? [{ path: Paths.SURVEY_FORM, component: Survey }]
            : []),
        ]
      : []),
  ]

  return layouts
}
