import { FC, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { Subtitle3 } from '@jifflenow/brightui/typography'
import { FieldRenderProps, useFormState } from 'react-final-form'
import { Textarea } from '@jifflenow/brightui/textarea'
import { Field } from 'ui-components/Field'
import { useEventLocation } from 'providers/LocationProvider'
import { useEngagementType } from '../EngagementTypeProvider'
import { EngagementTypeToggle } from '../common'
import { RefetchProps } from '../common/types'
import { IrpUrlField } from './IrpUrlField'
import { SchedulingLogicField } from './SchedulingLogicField'
import { MethodRequestField } from './MethodRequest'
import { MeetNowField } from './MeetNowField'
import {
  EngagementContainer,
  FormControlStyled,
  FormControlStyledToggleContainer,
  TextAreaContainer,
} from './styled-components'
import { DependentFieldValues, FormValues } from './types'

const messages = defineMessages({
  topicOptionsLabel: {
    defaultMessage: 'Topic Options',
    description: 'Field label',
  },
  unmappedUsersLabel: {
    defaultMessage: 'Show unmapped users for topic',
    description: 'Field label',
  },
  unmappedRoomsLabel: {
    defaultMessage: 'Show unmapped rooms for topic',
    description: 'Field label',
  },
  irpSuccessLabel: {
    defaultMessage: 'IRP success message',
    description: 'Field label',
  },
  irpSuccessLabelPlaceholder: {
    defaultMessage: 'IRP Success message',
    description: 'Placeholder text',
  },
})

const SuccessMessageFields: FC<FieldRenderProps<string>> = ({
  input,
  label,
  placeholder,
}) => (
  <TextAreaContainer>
    <EngagementContainer>
      <FormControlStyled label={label}>
        <Textarea {...input} autoComplete="off" placeholder={placeholder} />
      </FormControlStyled>
    </EngagementContainer>
  </TextAreaContainer>
)

const ExternalWidgetAutoProcessWithoutMapping: FC = () => {
  const { formatMessage } = useIntl()
  const {
    enable_external_widget_auto_process_without_mapping,
  } = useFormState<FormValues>().values
  const {
    enable_topic,
    enable_topic_to_activity,
    enable_user_to_topic,
    enable_room_to_topic,
  } = useEventLocation().configs!

  return enable_external_widget_auto_process_without_mapping === 'true' &&
    enable_topic === 'true' &&
    enable_topic_to_activity === 'true' &&
    (enable_room_to_topic === 'true' || enable_user_to_topic === 'true') ? (
    <FormControlStyledToggleContainer>
      <Subtitle3>{formatMessage(messages.topicOptionsLabel)} </Subtitle3>
      {enable_user_to_topic === 'true' && (
        <EngagementTypeToggle
          label={formatMessage(messages.unmappedUsersLabel)}
          name="show_unmapped_users_for_topic_IRP"
        />
      )}
      {enable_room_to_topic === 'true' && (
        <EngagementTypeToggle
          label={formatMessage(messages.unmappedRoomsLabel)}
          name="show_unmapped_rooms_for_topic_IRP"
        />
      )}
    </FormControlStyledToggleContainer>
  ) : null
}

export const WidgetFormFields: VFC<RefetchProps & DependentFieldValues> = ({
  refetch,
  loading,
  dependentFieldValues,
}) => {
  const { formatMessage } = useIntl()
  const { enable_irp_instant_meeting } = useEventLocation().configs!
  const {
    masterEngagementType,
  } = useEngagementType().engagementType.templateEngagementType
  const isGeneralMeeting =
    masterEngagementType === MasterEngagementTypes.General

  return (
    <>
      <IrpUrlField dependentFieldValues={dependentFieldValues} />
      {isGeneralMeeting && (
        <>
          <SchedulingLogicField />
          <MethodRequestField
            refetch={refetch}
            loading={loading}
            dependentFieldValues={dependentFieldValues}
          />
        </>
      )}
      <Field
        name="irp_request_success_message_text"
        render={SuccessMessageFields}
        label={formatMessage(messages.irpSuccessLabel)}
        placeholder={formatMessage(messages.irpSuccessLabelPlaceholder)}
      />
      {isGeneralMeeting && (
        <>
          <ExternalWidgetAutoProcessWithoutMapping />
          {enable_irp_instant_meeting === 'true' && (
            <MeetNowField
              refetch={refetch}
              loading={loading}
              dependentFieldValues={dependentFieldValues}
            />
          )}
        </>
      )}
    </>
  )
}
