import { VFC, useState } from 'react'
import { Drawer } from 'ui-components/Drawer'
import { Wizard } from 'ui-components/Wizard'
import { CreateEngagementDrawerProps } from './types'
import { Form } from './Form'
import { CreateSuccess } from './CreateSuccess'
import { FormProps } from './Form/types'

export const CreateEngagementDrawer: VFC<CreateEngagementDrawerProps> = ({
  copy,
  onCreate,
  ...rest
}) => {
  const [engagementName, setEngagementName] = useState('')

  const handleCreateSuccess: FormProps['onCreateSuccess'] = name => {
    setEngagementName(name)
    onCreate?.()
  }

  return (
    <Drawer closeable={false} size="90%" {...rest}>
      <Wizard>
        <Form onCreateSuccess={handleCreateSuccess} copy={copy} />
        <CreateSuccess engagementName={engagementName} />
      </Wizard>
    </Drawer>
  )
}
