import { getRoomAvailabilities_getRoomAvailabilities } from 'gql/types/getRoomAvailabilities'
import {
  RoomAvailabilityDay,
  SingleAvailabilityInput,
} from 'gql/types/globalTypes'
import { getDateFromMinute, getMinutes, getTimeFromDate } from '../common'
import { Availabilities } from './types'

const daysMap: Record<RoomAvailabilityDay, number> = {
  Monday: 1,
  Tuesday: 2,
  Wednesday: 3,
  Thursday: 4,
  Friday: 5,
  Saturday: 6,
  Sunday: 7,
}

export const getNonRecurringAvailabilities = (
  availabilities: getRoomAvailabilities_getRoomAvailabilities[]
) =>
  availabilities
    .map(({ availableSlots, availabilityDate, ...rest }, dayIndex) => ({
      availableSlots: getSlots(availableSlots, dayIndex),
      availabilityDate: availabilityDate!,
      ...rest,
    }))
    .sort(
      (a, b) => +new Date(a.availabilityDate) - +new Date(b.availabilityDate)
    )

export const getRecurringAvailabilities = (
  availabilities: getRoomAvailabilities_getRoomAvailabilities[]
) =>
  availabilities
    .map(({ availableSlots, availabilityDay, ...rest }, dayIndex) => ({
      availableSlots: getSlots(availableSlots, dayIndex),
      availabilityDay: availabilityDay!,
      ...rest,
    }))
    .sort((a, b) => daysMap[a.availabilityDay] - daysMap[b.availabilityDay])

export const getSlots = (availableSlots: number[], dayIndex: number) =>
  availableSlots
    .slice()
    .sort((a, b) => a - b)
    .reduce<{ start: string; end: string }[]>((acc, _, index, array) => {
      if (index % 2 === 0) {
        acc.push({
          start: getTimeFromDate(
            getDateFromMinute({
              days: dayIndex,
              minutes: array[index],
            })
          ),
          end: getTimeFromDate(
            getDateFromMinute({
              days: dayIndex,
              minutes: array[index + 1],
            })
          ),
        })
      }

      return acc
    }, [])

const mergeContinuousTimeRanges = (timeRanges: number[]): number[] =>
  timeRanges
    .sort((a, b) => (a > b ? 1 : a < b ? -1 : 0))
    .filter((v, _, a) => a.indexOf(v) === a.lastIndexOf(v))

export const getAvailabilityInMinutes = (
  availability: Availabilities[]
): SingleAvailabilityInput[] =>
  availability.map(({ availableSlots, availabilityDate, availabilityDay }) => {
    const timeRange = availableSlots.reduce<number[]>(
      (acc, slot) => [
        ...acc,
        ...[getMinutes(slot.start), getMinutes(slot.end) || 1440],
      ],
      []
    )

    return {
      availableSlots: mergeContinuousTimeRanges(timeRange),
      availabilityDate,
      availabilityDay,
    }
  })
