import { useState, VFC, useEffect } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { showErrorToast } from 'utils/toast'
import { useGetEngagementTypeSurveys } from 'gql'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { Body, Header } from '../common'
import { useEngagementType } from '../EngagementTypeProvider'
import { SurveyOptions } from './types'
import { SurveyFormField } from './SurveyFormField'

const messages = defineMessages({
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
})

export const Surveys: VFC = () => {
  const { formatMessage } = useIntl()
  const { id: engagementId } = useEngagementType().engagementType
  const [options, setOptions] = useState<SurveyOptions>([])

  const {
    loading,
    getEngagementTypeSurveys = [],
  } = useGetEngagementTypeSurveys({
    notifyOnNetworkStatusChange: true,
    variables: {
      engagementId,
    },
    fetchPolicy: 'cache-and-network',
    onError: () => {
      showErrorToast(formatMessage(messages.genericError))
    },
  })

  useEffect(() => {
    setOptions(
      getEngagementTypeSurveys.map(
        ({ surveyMasterName, engagementTypeSurveyId }) => ({
          label: surveyMasterName,
          value: surveyMasterName,
          id: engagementTypeSurveyId,
        })
      )
    )
  }, [getEngagementTypeSurveys])

  const [surveyValue, setSurveyValue] = useState<SurveyOptions>([])
  useEffect(() => {
    options.length > 0 ? setSurveyValue([options[0]]) : setSurveyValue([])
  }, [options])

  return (
    <>
      {loading && <ModalSpinner />}
      <Header />
      <Body>
        {surveyValue.length > 0 && (
          <SurveyFormField
            surveyValue={surveyValue}
            options={options}
            setSurveyValue={setSurveyValue}
          />
        )}
      </Body>
    </>
  )
}
