import { SelectOverrides } from '@jifflenow/brightui/select'
import { TimePickerOverrides } from '@jifflenow/brightui/timepicker'

const selectOverrides: SelectOverrides = {
  Root: {
    style: ({ $theme: { typography } }) => ({
      ...typography.Paragraph2,
      minWidth: '98px',
    }),
  },
  ControlContainer: {
    style: ({ $theme: { colors }, $error }) => ({
      borderLeftStyle: 'none',
      borderRightStyle: 'none',
      borderTopStyle: 'none',
      borderBottomStyle: 'none',
      borderTopLeftRadius: 0,
      borderTopRightRadius: 0,
      borderBottomLeftRadius: 0,
      borderBottomRightRadius: 0,
      backgroundColor: $error ? colors.negative50 : colors.mono300,
    }),
  },
  ValueContainer: {
    style: ({ $theme: { sizing } }) => ({
      paddingTop: sizing.scale300,
      paddingBottom: sizing.scale300,
    }),
  },
  SingleValue: {
    style: ({
      $disabled,
      $error,
      $isFocused,
      $isPseudoFocused,
      $theme: { colors },
    }) => {
      const borderColor = $disabled
        ? colors.inputBorder
        : $error && $isFocused
        ? colors.borderFocus
        : $error
        ? colors.inputBorderError
        : $isFocused || $isPseudoFocused
        ? colors.borderFocus
        : ''

      return {
        display: 'flex',
        alignItems: 'center',
        ...(borderColor
          ? {
              backgroundColor: colors.primary50,
              borderBottomColor: borderColor,
              borderBottomStyle: 'solid',
              borderBottomWidth: '1px',
              display: 'flex',
              alignItems: 'center',
            }
          : null),
      }
    },
  },
  Dropdown: {
    style: ({ $theme: { colors } }) => ({
      outline: 'none',
      maxHeight: '144px',
      border: `1px solid ${colors.mono600}`,
    }),
  },

  DropdownContainer: {
    style: {
      outline: 'none',
      width: '76px',
    },
  },
  DropdownListItem: {
    style: ({ $theme: { sizing } }) => ({
      paddingLeft: sizing.scale400,
      paddingRight: sizing.scale300,
    }),
  },
  Popover: {
    props: {
      overrides: {
        Body: {
          style: { zIndex: 1 },
        },
      },
    },
  },
  InputContainer: {
    style: ({ $theme: { sizing } }) => ({
      display: 'flex',
      flexBasis: sizing.scale0,
      flexGrow: 1,
    }),
  },
}

export const timePickerOverrides = ({
  noResultsMsg,
}: {
  noResultsMsg: string
}): TimePickerOverrides => ({
  Select: {
    props: {
      overrides: selectOverrides,
      noResultsMsg,
    },
  },
})
