import { VFC, ComponentType } from 'react'
import {
  generatePath,
  Redirect,
  Route,
  Switch,
  useLocation,
  useParams,
} from 'react-router-dom'
import { Paths as BasePaths } from 'components/Routes'
import { useEventLocation } from 'providers/LocationProvider'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { useEngagementType } from './EngagementTypeProvider'
import { Paths } from './Paths'
import { LayoutsV1 } from './LayoutsV1'
import { LayoutsV2 } from './LayoutsV2'

const DefaultRedirect: VFC<{ path: Paths }> = ({ path }) => {
  const { search } = useLocation()
  const { eventName, engagementId } = useParams<{
    engagementId: string
    eventName: string
  }>()

  return (
    <Redirect
      to={`${generatePath(BasePaths.ENGAGEMENT_TYPE.path, {
        eventName,
        engagementId,
      })}${path}${search}`}
    />
  )
}

type Layout = { path: Paths; component: ComponentType }

export const Routes = () => {
  const { configs, location, privileges } = useEventLocation()

  const {
    attributes: { version },
  } = location!
  const { enable_external_widget } = configs!

  const {
    masterEngagementType,
  } = useEngagementType().engagementType.templateEngagementType

  const getPath = (path: Paths | string) =>
    `${BasePaths.ENGAGEMENT_TYPE.path}${path}`

  const layouts: Layout[] = version === '1' ? LayoutsV1() : LayoutsV2()

  // TODO: check for IRP enabled and privileges
  const defaultRoutes = () => {
    let route
    if (
      version === '1' &&
      masterEngagementType !== MasterEngagementTypes.ConsecutiveMeeting &&
      enable_external_widget === 'true'
    ) {
      route = Paths.INBOUND_REQUEST_FORM
    } else if (version === '1' && privileges?.canManageNotifications) {
      route = Paths.NOTIFICATIONS
    } else {
      route = Paths.BASICS
    }

    return route
  }

  return (
    <Switch>
      <Route
        exact
        path={getPath('')}
        render={() => <DefaultRedirect path={defaultRoutes()} />}
      />
      {layouts.map(({ path, component }, index) => (
        <Route key={index} exact path={getPath(path)} component={component} />
      ))}
      <Route render={() => <DefaultRedirect path={defaultRoutes()} />} />
    </Switch>
  )
}
