import { VFC, useMemo } from 'react'
import { DateTime } from 'luxon'
import { FormattedMessage } from 'react-intl'
import { useEventLocation } from 'providers/LocationProvider'
import { EventStatus } from 'components/Events'
import { formatDateRange, formatToStartOfTheDay } from 'utils/dates'
import { TimeZoneOutlined } from '@jifflenow/brightui/icon'
import { Logo } from 'components/Common/Logo'
import {
  Root,
  EventDetails,
  LocationDetails,
  EventName,
  StatusWrapper,
  TimeZone,
  EventDates,
  TimeIconWrapper,
  EventInfo,
  Wrapper,
  LogoWrapper,
  SandboxText,
  SandboxWrapper,
} from './styled-components'

export const EventMainHeader: VFC = () => {
  const {
    attributes: {
      displayName,
      filterStatus,
      mode,
      timeZone,
      startDate,
      endDate,
      address,
    },
  } = useEventLocation().location!

  const eventTimeZone = useMemo(
    () => (timeZone ? DateTime.local().setZone(timeZone).offsetNameLong : ''),
    [timeZone]
  )

  const urlPath = window.location.pathname
  const sandboxInstance =
    urlPath.includes('sandbox-') || urlPath.includes('staging-')

  return (
    <Wrapper>
      <Root>
        <EventDetails>
          <StatusWrapper>
            <EventStatus status={filterStatus} mode={mode} />
          </StatusWrapper>
          <EventName>{displayName}</EventName>
        </EventDetails>
        <LocationDetails>
          <EventInfo>
            <EventDates>
              {`${formatDateRange(
                formatToStartOfTheDay(startDate),
                formatToStartOfTheDay(endDate)
              )} | ${address}`}
            </EventDates>
            <TimeIconWrapper>
              <TimeZoneOutlined />
            </TimeIconWrapper>
            <TimeZone>{eventTimeZone}</TimeZone>
          </EventInfo>
        </LocationDetails>
      </Root>
      <LogoWrapper>
        {sandboxInstance && (
          <SandboxWrapper>
            <SandboxText>
              <FormattedMessage
                defaultMessage="You are currently using the sandbox instance.{br}Intended for use with Test Data only."
                description="Warning text"
                values={{
                  br: <br />,
                }}
              />
            </SandboxText>
          </SandboxWrapper>
        )}
        <Logo />
      </LogoWrapper>
    </Wrapper>
  )
}
