import { Prompt } from 'react-router-dom'
import { defineMessages, useIntl } from 'react-intl'
import { Body, Header, usePageTitle, Root } from '../../common'
import { useFormsDirty } from '../common/useFormsDirty'
import { Container, Title } from '../common/styled-components'
import { FormBuilder } from '../common/FormBuilder'

const messages = defineMessages({
  outboundMeetingForm: {
    defaultMessage: 'Outbound Meeting Form',
    description: 'Page title',
  },
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
})

export const OutboundMeeting = () => {
  const { formatMessage } = useIntl()
  usePageTitle(formatMessage(messages.outboundMeetingForm))

  const [dirty] = useFormsDirty()

  return (
    <Root>
      <Prompt when={dirty} message={formatMessage(messages.promptMessage)} />
      <Header />
      <Body>
        <Container>
          <Title>{formatMessage(messages.outboundMeetingForm)}</Title>
          <FormBuilder
            name="outbound-request"
            title={formatMessage(messages.outboundMeetingForm)}
          />
        </Container>
      </Body>
    </Root>
  )
}
