import { ReactNode, VFC } from 'react'
import { Field } from 'react-final-form'
import { Toggle } from '@jifflenow/brightui/toggle'
import {
  FieldToggleLabel,
  FieldToggleWithDescWrapper,
  GenericError,
  ToggleContainerWithDesc,
  EngagementToggleDesc,
  FieldToggleDescWrapper,
  EngagementToggleHint,
} from './styled-components'

type ToggleProps = {
  label: string
  name: string
  desc: string
  hint?: boolean
  children?: ReactNode
  disabled?: boolean
}

export const EngagementTypeToggleWithDesc: VFC<ToggleProps> = ({
  label,
  name,
  desc,
  hint,
  children,
  disabled,
}) => (
  <Field<string>
    name={name}
    render={({ input: { value, onChange }, meta: { error, submitError } }) => (
      <>
        {(submitError || error) && (
          <GenericError>{submitError || error}</GenericError>
        )}
        <FieldToggleWithDescWrapper $error={submitError || error}>
          <FieldToggleDescWrapper>
            <FieldToggleLabel>{label}</FieldToggleLabel>
            {desc && hint ? (
              <EngagementToggleHint>{desc}</EngagementToggleHint>
            ) : desc ? (
              <EngagementToggleDesc>{desc}</EngagementToggleDesc>
            ) : null}
          </FieldToggleDescWrapper>

          <ToggleContainerWithDesc>
            <Toggle
              name={name}
              ariaLabel={label}
              checked={value === 'true'}
              onChange={(e: any) =>
                onChange(e.target.checked ? 'true' : 'false')
              }
              disabled={!!disabled}
            />
          </ToggleContainerWithDesc>
        </FieldToggleWithDescWrapper>
        {value === 'true' && children}
      </>
    )}
  />
)
