import { ChangeEvent, useState, VFC } from 'react'
import { Field } from 'react-final-form'
import { Toggle } from '@jifflenow/brightui/toggle'
import { StatefulTooltip } from '@jifflenow/brightui/tooltip'
import { InfoOutlined } from '@jifflenow/brightui/icon'
import { ReasonForCancellationDialog } from 'components/EngagementTypes/EngagementTypeEdit/ReasonForCancellationDialog'
import { defineMessages, useIntl } from 'react-intl'
import {
  FieldLabel,
  FieldLabelContainer,
  FieldToggleWrapper,
  InfoTooltipContainer,
  GenericError,
  ToggleContainer,
} from './styled-components'
import { InfoIconOverride, overviewTooltipOverride } from './overrides'
import { EngagementTypeToggleProps, InfoTooltipProps } from './types'

const InfoTooltip: VFC<InfoTooltipProps> = ({ content }) => (
  <StatefulTooltip
    content={content}
    showArrow
    overrides={overviewTooltipOverride}
  >
    <InfoTooltipContainer>
      <InfoOutlined size={16} overrides={InfoIconOverride} />
    </InfoTooltipContainer>
  </StatefulTooltip>
)

const positive = {
  on: 'true',
  off: 'false',
}

const negative = {
  on: 'false',
  off: 'true',
}

export const EngagementTypeToggle: VFC<EngagementTypeToggleProps> = ({
  label,
  name,
  children,
  bold,
  infoTooltip,
  disabled,
  inverted,
}) => {
  const { on, off } = inverted ? negative : positive
  const [showConfirmation, setShowConfirmation] = useState(false)
  const messages = defineMessages({
    warningHeader: {
      defaultMessage: 'Warning',
      description: 'Modal heading',
    },
    warningMessage: {
      defaultMessage:
        'Please ensure following cancellation notifications are updated as per cancel comment changes.',
      description: 'Confirmation message',
    },
    buttonLabel: {
      defaultMessage: 'OK',
      description: 'Button label',
    },
  })
  const { formatMessage } = useIntl()

  return (
    <Field<string>
      name={name}
      render={({
        input: { value, onChange },
        meta: { error, submitError },
      }) => (
        <>
          {(submitError || error) && (
            <GenericError>{submitError || error}</GenericError>
          )}
          <FieldToggleWrapper $error={submitError || error}>
            <FieldLabelContainer>
              <FieldLabel $bold={bold}>{label}</FieldLabel>
              {infoTooltip ? <InfoTooltip content={infoTooltip} /> : null}
            </FieldLabelContainer>

            <ToggleContainer>
              <Toggle
                name={name}
                ariaLabel={label}
                checked={value === on}
                onChange={(e: ChangeEvent<HTMLInputElement>) => {
                  if (name === 'cancel_comments_enabled') {
                    setShowConfirmation(true)
                  }

                  return onChange(e.target.checked ? on : off)
                }}
                disabled={disabled}
              />
            </ToggleContainer>
          </FieldToggleWrapper>
          {value === on && children}
          {showConfirmation && (
            <ReasonForCancellationDialog
              onClose={() => setShowConfirmation(false)}
              heading={formatMessage(messages.warningHeader)}
              message={formatMessage(messages.warningMessage)}
              buttonLabel={formatMessage(messages.buttonLabel)}
            />
          )}
        </>
      )}
    />
  )
}
