import { VFC } from 'react'
import { FormattedMessage } from 'react-intl'
import {
  DrawerCloseButton,
  DrawerTitle,
  HeaderContainer,
} from 'ui-components/Drawer'
import { HeaderProps } from './types'

export const Header: VFC<HeaderProps> = ({ onClose, copy }) => (
  <HeaderContainer>
    <DrawerCloseButton onClick={onClose} />
    <DrawerTitle>
      {copy ? (
        <FormattedMessage
          defaultMessage="Copy an Engagement Type"
          description="Drawer title"
        />
      ) : (
        <FormattedMessage
          defaultMessage="Create New Engagement Type"
          description="Drawer title"
        />
      )}
    </DrawerTitle>
  </HeaderContainer>
)
