import { Config, FORM_ERROR, ValidationErrors } from 'final-form'
import {
  Form as FinalForm,
  FormRenderProps,
  useForm,
  useFormState,
} from 'react-final-form'
import { ConfirmationDialog } from 'ui-components/ConfirmationDialog'
import { defineMessages, useIntl } from 'react-intl'
import {
  mutateBulkUpsertEngagementSettings,
  useApolloClient,
  useGetEngagementTypeSettings,
} from 'gql'
