import {
  getEngagementTypeSettings_getEngagementTypeSettings_value_CustomNameValue as EngagementTypeSettings,
  getEngagementTypeSettings_getEngagementTypeSettings_value_DefaultLocationVconPreferenceValue as LocationVconPreference,
  getEngagementTypeSettings_getEngagementTypeSettings_value_DefaultLocationVconPreferenceValue_defaultLocationVconPreferenceValue as DefaultLocationVconPreferenceValue,
} from 'gql/types/getEngagementTypeSettings'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import {
  BulkEngagementSettingUpdateInput,
  MasterEngagementTypes,
} from 'gql/types/globalTypes'
import {
  bulkUpsertEngagementSettingsVariables,
  bulkUpsertEngagementSettings_bulkUpsertEngagementSettings_engagementSettings_value_CustomNameValue as BulkUpsertEngagementSettings,
} from 'gql/types/bulkUpsertEngagementSettings'
import { useEngagementMeetingData } from 'rest'
import { useEventLocation } from 'providers/LocationProvider'
import {
  Body,
  Footer,
  Header,
  StyledForm,
  Container,
  usePageTitle,
} from '../common'
import { useEngagementType } from '../EngagementTypeProvider'
import { RefetchProps } from '../common/types'
import { useSettingsLoader } from '../common/useSettingsLoader'
import { filterEngagementSettings } from '../common/filterEngagementSettings'
import { updateQueryResults } from '../common/utils'
import { TimeIncrement } from './TimeIncrement'
import { FieldSeparator, Section, SectionTitle } from './styled-components'
import {
  DefaultLocationOption,
  DefaultLocationPreferenceType,
  DefaultSettingsFormValues,
  DependentFields,
  DependentFieldsProps,
  FormValues,
  LocationTypeSettings,
} from './types'
import { FieldWrapper, Heading } from './styled-components'
import { FormFields } from './FormFields'
import { LocationTypesContentLoader } from './LocationTypesContentLoader'

const messages = defineMessages({
  calendarsTitle: {
    defaultMessage: 'Calendars',
    description: 'Section title',
  },
  locationTypes: {
    defaultMessage: 'Location Types',
    description: 'Page title',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
  sectionTitle: {
    defaultMessage: 'Location Types',
    description: 'Section title',
  },
  helpText: {
    defaultMessage: 'Help Text',
    description: 'Help Text',
  },
  successMessage: {
    defaultMessage: 'Your changes have been saved successfully',
    description: 'Success message',
  },
  updateError: {
    defaultMessage:
      '{failureCount, number} out of {totalCount, number} {totalCount, plural, one {option} other {options}} failed to save',
    description: 'Error message',
  },
  filedError: {
    defaultMessage: 'Error saving changes',
    description: 'Field error message',
  },
  minTimeIncrementError: {
    defaultMessage:
      'Minimum duration cannot be less than the time increment of {minTimeIncrement} min',
    description: 'Field error message',
  },
  maxDurationError: {
    defaultMessage: 'Maximum duration cannot be less than minimum duration.',
    description: 'Field error message',
  },
  roomsLabel: {
    defaultMessage: 'Onsite Locations',
    description: 'Options label',
  },
  boothTourLabel: {
    defaultMessage: 'Designated Location',
    description: 'Options label',
  },
  modalHeader: {
    defaultMessage:
      'Should meeting participants be sent an updated calendar invitation?',
    description: 'Header text',
  },
  modalMessage: {
    defaultMessage:
      '(Ignore this if there are no booth tour meetings requested)<br/>Note that participants may have the previous booth tour location in their calendars.<br/><br/>Select Yes if you want to send updated calendar invitations to participants who have previously received invitations for their booth tour meetings of this meeting type.',
    description: 'Message text',
  },
  noButtonLabel: {
    defaultMessage: 'No',
    description: 'Button text',
  },
  yesButtonLabel: {
    defaultMessage: 'Yes',
    description: 'Button text',
  },
})

const LoadingShimmer = () => (
  <Body>
    <Container>
      <LocationTypesContentLoader />
    </Container>
  </Body>
)

const Form: FC<
  FormRenderProps<FormValues> &
    RefetchProps &
    DependentFieldsProps & {
      hasActiveMeetings: boolean
      booth_tour_default_location?: string
      booth_tour_default_name?: string
    }
> = ({
  handleSubmit,
  dirty,
  refetch,
  loading,
  dependentFieldValues,
  hasActiveMeetings,
  booth_tour_default_location,
  booth_tour_default_name,
}) => {
  const { formatMessage } = useIntl()
  const showLoading = useSettingsLoader(loading)
  const [isOpen, setIsOpen] = useState(false)

  const { values } = useFormState()
  const { change } = useForm()
  const {
    masterEngagementType,
  } = useEngagementType().engagementType.templateEngagementType
  const isGeneralMeeting =
    masterEngagementType === MasterEngagementTypes.General

  const validateBootTour = (e: FormEvent) => {
    e.preventDefault()
    if (
      booth_tour_default_name !== values.booth_tour_default_name ||
      booth_tour_default_location !== values.booth_tour_default_location
    ) {
      setIsOpen(true)
    } else {
      handleSubmit()
    }
  }

  const handleResponse = (sendIcs: boolean) => {
    change('sendIcs', sendIcs)
    handleSubmit()
    setIsOpen(false)
  }

  return (
    <>
      {isOpen && (
        <ConfirmationDialog
          heading={formatMessage(messages.modalHeader)}
          message={
            <div
              dangerouslySetInnerHTML={{
                __html: formatMessage(messages.modalMessage),
              }}
            />
          }
          cancelLabel={formatMessage(messages.noButtonLabel)}
          successLabel={formatMessage(messages.yesButtonLabel)}
          onCancel={() => handleResponse(false)}
          onSuccess={() => handleResponse(true)}
        />
      )}
      <StyledForm onSubmit={validateBootTour} noValidate>
        <Prompt when={dirty} message={formatMessage(messages.promptMessage)} />
        <Header />
        {showLoading ? (
          <LoadingShimmer />
        ) : (
          <Body>
            <Container>
              <FieldWrapper>
                <Heading>{formatMessage(messages.sectionTitle)}</Heading>
                <Section>
                  <SectionTitle>
                    {formatMessage(messages.calendarsTitle)}
                  </SectionTitle>
                  <TimeIncrement
                    dependentFieldValues={dependentFieldValues}
                    hasActiveMeetings={hasActiveMeetings}
                  />
                  <FieldSeparator />
                </Section>
                {isGeneralMeeting && (
                  <FormFields
                    refetch={refetch}
                    loading={loading}
                    dependentFieldValues={dependentFieldValues}
                  />
                )}
              </FieldWrapper>
            </Container>
            <Footer />
          </Body>
        )}
      </StyledForm>
    </>
  )
}

const fieldNames = [
  'duration_restrictions_for_mm',
  'max_duration',
  'min_duration',
  'offsite_enabled',
  'offsite_duration_restrictions_for_mm',
  'offsite_label',
  'offsite_min_duration',
  'offsite_max_duration',
  'booth_tour_enabled',
  'booth_tour_label',
  'booth_tour_header',
  'booth_tour_default_name',
  'booth_tour_default_location',
  'booth_tour_min_duration',
  'booth_tour_max_duration',
  'default_location_preference',
  'default_location_vcon_preference',
  'enable_external_widget',
  'enable_external_widget_auto_process_without_mapping',
  'minimum_timeslot',
  'default_mapping',
]

const dependentFieldNames = [
  'enable_external_widget',
  'enable_external_widget_auto_process_without_mapping',
  'minimum_timeslot',
  'default_mapping',
  'min_duration',
  'offsite_enabled',
  'offsite_min_duration',
  'booth_tour_enabled',
  'booth_tour_min_duration',
]

const cleanLocationVconPreference = (
  value: DefaultLocationVconPreferenceValue
) => {
  const { __typename, ...rest } = value

  return rest
}

const { ROOM, BOOTH_TOUR, VCON } = DefaultLocationPreferenceType

export const LocationTypes: VFC = () => {
  const { formatMessage } = useIntl()
  const client = useApolloClient()
  usePageTitle(formatMessage(messages.locationTypes))
  const [showLoader, setShowLoader] = useState(false)
  const {
    id: engagementTypeId,
    uuid: activity_uuid,
  } = useEngagementType().engagementType
  const {
    id: current_location_uuid,
    attributes: { systemName: subSystem },
  } = useEventLocation().location!

  const {
    loading,
    getEngagementTypeSettings = [],
    refetch: refetchGetEngagementTypeSettings,
    updateQuery,
  } = useGetEngagementTypeSettings({
    notifyOnNetworkStatusChange: true,
    variables: {
      engagementTypeId,
      names: [...fieldNames, ...dependentFieldNames],
    },
    fetchPolicy: 'cache-and-network',
    onError: () => {
      showErrorToast(formatMessage(messages.genericError))
    },
  })

  const {
    data: { data: engagementMeetingData } = {},
  } = useEngagementMeetingData({
    eventName: subSystem,
    activity_uuid,
    current_location_uuid,
  })

  const fieldValues = useMemo(
    () => filterEngagementSettings(getEngagementTypeSettings, fieldNames),
    [getEngagementTypeSettings]
  )

  const dependentValues = useMemo(
    () =>
      filterEngagementSettings(getEngagementTypeSettings, dependentFieldNames),
    [getEngagementTypeSettings]
  )

  const dependentFieldValues = useMemo(
    () =>
      dependentValues.reduce(
        (acc, { name, value }) => ({
          ...acc,
          [name]: (value as EngagementTypeSettings).value,
        }),
        {}
      ) as DependentFields,
    [dependentValues]
  )

  const initialValues: FormValues | undefined = useMemo(() => {
    if (!fieldValues.length) {
      return undefined
    }

    const settings = fieldValues.reduce(
      (acc, { name, value }) => ({
        ...acc,
        [name]:
          name === 'default_location_vcon_preference'
            ? cleanLocationVconPreference(
                (value as LocationVconPreference)
                  .defaultLocationVconPreferenceValue
              )
            : (value as EngagementTypeSettings).value,
      }),
      {}
    ) as LocationTypeSettings

    const {
      default_location_preference: location,
      default_location_vcon_preference,
      ...rest
    } = settings

    const {
      integration_adapter_uuid,
      integration_name,
      integration_uuid,
    } = default_location_vcon_preference

    const locationPreference: DefaultLocationOption[] = [
      location === ROOM
        ? {
            label: formatMessage(messages.roomsLabel),
            id: ROOM,
            type: ROOM,
          }
        : location === BOOTH_TOUR
        ? {
            label: formatMessage(messages.boothTourLabel),
            id: BOOTH_TOUR,
            type: BOOTH_TOUR,
          }
        : {
            label: integration_name,
            id: integration_uuid,
            type: VCON,
          },
    ]

    return {
      ...rest,
      default_location_vcon_preference:
        location === VCON
          ? JSON.stringify({
              integration_adapter_uuid,
              integration_name,
              integration_uuid,
            })
          : undefined,
      default_location_preference: JSON.stringify(locationPreference),
    }
  }, [fieldValues, formatMessage])

  // eslint-disable-next-line complexity
  const validate: Config<FormValues>['validate'] = ({
    min_duration,
    max_duration,
    offsite_min_duration,
    offsite_max_duration,
    booth_tour_min_duration,
    booth_tour_max_duration,
    offsite_enabled,
    booth_tour_enabled,
  }) => {
    const errors: ValidationErrors = {}
    const { minimum_timeslot: minTimeIncrement } = dependentFieldValues

    const minError = formatMessage(messages.minTimeIncrementError, {
      minTimeIncrement,
    })
    const maxError = formatMessage(messages.maxDurationError)

    min_duration &&
      +min_duration < +minTimeIncrement &&
      (errors.min_duration = minError)

    min_duration &&
      max_duration &&
      +min_duration > +max_duration &&
      (errors.max_duration = maxError)

    offsite_enabled === 'true' &&
      offsite_min_duration &&
      +offsite_min_duration < +minTimeIncrement &&
      (errors.offsite_min_duration = minError)

    offsite_enabled === 'true' &&
      offsite_min_duration &&
      offsite_max_duration &&
      +offsite_min_duration > +offsite_max_duration &&
      (errors.offsite_max_duration = maxError)

    booth_tour_enabled === 'true' &&
      booth_tour_min_duration &&
      +booth_tour_min_duration < +minTimeIncrement &&
      (errors.booth_tour_min_duration = minError)

    booth_tour_enabled === 'true' &&
      booth_tour_min_duration &&
      booth_tour_max_duration &&
      +booth_tour_min_duration > +booth_tour_max_duration &&
      (errors.booth_tour_max_duration = maxError)

    return errors
  }

  const handleSubmit: Config<FormValues>['onSubmit'] = async (
    values,
    { change }
  ) => {
    try {
      const prevValues = fieldValues.filter(
        field =>
          field.name !== 'default_location_vcon_preference' &&
          field.name !== 'default_location_preference'
      )
      const prevLocationPreference = fieldValues.filter(
        field => field.name === 'default_location_preference'
      )
      const prevLocationVconPreference = fieldValues.filter(
        field => field.name === 'default_location_vcon_preference'
      )

      const {
        default_location_preference,
        default_location_vcon_preference,
        sendIcs,
        ...newValues
      } = values

      const configsToUpdate = prevValues.reduce<
        BulkEngagementSettingUpdateInput[]
      >((acc, { name, value: setting }) => {
        const {
          display_name,
          value: oldValue,
        } = setting as EngagementTypeSettings
        const value = newValues[name as keyof DefaultSettingsFormValues]

        return oldValue !== value
          ? [
              ...acc,
              {
                name,
                customValue: {
                  display_name,
                  value,
                },
              },
            ]
          : acc
      }, [])

      if (
        default_location_preference !==
        initialValues?.default_location_preference
      ) {
        const type = (JSON.parse(
          default_location_preference
        ) as DefaultLocationOption[])[0].type

        const defaultLocationPreference = prevLocationPreference.reduce<
          BulkEngagementSettingUpdateInput[]
        >((acc, { name, value: previousValue }) => {
          const display_name = (previousValue as EngagementTypeSettings)
            .display_name

          return [
            ...acc,
            {
              name,
              customValue: {
                display_name,
                value: type,
              },
            },
          ]
        }, [])

        const defaultLocationVconPreference =
          default_location_vcon_preference && type === VCON
            ? prevLocationVconPreference.reduce<
                BulkEngagementSettingUpdateInput[]
              >((acc, { name, value }) => {
                const display_name = (value as LocationVconPreference)
                  .display_name

                return [
                  ...acc,
                  {
                    name,
                    defaultLocationVconPreferenceValue: {
                      display_name,
                      value: JSON.parse(default_location_vcon_preference),
                    },
                  },
                ]
              }, [])
            : []

        configsToUpdate.push(
          ...defaultLocationPreference,
          ...defaultLocationVconPreference
        )
      }

      setShowLoader(true)
      const variables: bulkUpsertEngagementSettingsVariables = {
        engagementTypeId,
        configsToUpdate,
        sendIcs,
      }
      const { failureCount, totalCount, engagementSettings, successCount } = (
        await mutateBulkUpsertEngagementSettings(client, {
          variables,
        })
      ).data!.bulkUpsertEngagementSettings
      setShowLoader(false)
      if (failureCount) {
        showErrorToast(
          formatMessage(messages.updateError, { totalCount, failureCount })
        )
        engagementSettings.forEach(
          ({ success, name, value }) =>
            !success &&
            change(
              name as keyof FormValues,
              (value as BulkUpsertEngagementSettings).value ?? undefined
            )
        )
        const errors = engagementSettings.reduce(
          (acc, { success, name }) =>
            success
              ? acc
              : {
                  ...acc,
                  [name]: formatMessage(messages.filedError),
                },
          {}
        )
        if (successCount) {
          updateQueryResults(updateQuery, engagementSettings)
        }

        return errors
      }
      showSuccessToast(formatMessage(messages.successMessage))
      updateQueryResults(updateQuery, engagementSettings)
      return undefined
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
      return { [FORM_ERROR]: { code: 'GenericError' } }
    }
  }

  const { booth_tour_default_location, booth_tour_default_name } =
    initialValues || {}

  return (
    <>
      {showLoader && <ModalSpinner />}
      <FinalForm<FormValues>
        onSubmit={handleSubmit}
        render={props => (
          <Form
            {...props}
            booth_tour_default_location={booth_tour_default_location}
            booth_tour_default_name={booth_tour_default_name}
            refetch={() => refetchGetEngagementTypeSettings()}
            loading={loading}
            dependentFieldValues={dependentFieldValues}
            hasActiveMeetings={
              engagementMeetingData?.hasActiveMeetings ?? false
            }
          />
        )}
        initialValues={initialValues}
        validate={validate}
      />
    </>
  )
}
