import { ChangeEvent, VFC } from 'react'
import { Field } from 'react-final-form'
import { Toggle } from '@jifflenow/brightui/toggle'
import {
  EngagementTypeToggleProps,
  FieldLabel,
  FieldToggleWrapper,
  GenericError,
  ToggleContainer,
  FieldLabelContainer,
} from '../common'

export const AreYouAttendingToggle: VFC<EngagementTypeToggleProps> = ({
  label,
  name,
}) => (
  <Field<string>
    name={name}
    render={({ input: { value, onChange }, meta: { error, submitError } }) => (
      <>
        {(submitError || error) && (
          <GenericError>{submitError || error}</GenericError>
        )}
        <FieldToggleWrapper $error={submitError || error}>
          <FieldLabelContainer>
            <FieldLabel>{label}</FieldLabel>
          </FieldLabelContainer>

          <ToggleContainer>
            <Toggle
              name={name}
              ariaLabel={label + value}
              checked={value === '2'}
              onChange={(e: ChangeEvent<HTMLInputElement>) =>
                onChange(e.target.checked ? '2' : '1')
              }
            />
          </ToggleContainer>
        </FieldToggleWrapper>
      </>
    )}
  />
)
