import { styled } from '@jifflenow/brightui'
import { FormControl as Control } from '@jifflenow/brightui/form-control'

export const FormControl = styled(Control, ({ $theme: { sizing } }) => ({
  marginBottom: sizing.scale700,
}))

export const StyledHeader = styled<{ $isHidden?: boolean }, 'header'>(
  'header',
  ({ $theme: { typography, sizing, breakpoints }, $isHidden }) => ({
    marginTop: 0,
    display: $isHidden ? 'none' : 'block',
    marginBottom: sizing.scale800,
    maxWidth: '282px',
    ...typography.font350,
    fontWeight: 400,
    [breakpoints.medium]: {
      marginBottom: sizing.scale600,
      flexBasis: '100%',
    },
  })
)

export const Hr = styled<{ $isHidden?: boolean }, 'div'>(
  'div',
  ({ $theme: { colors, sizing }, $isHidden }) => ({
    flexBasis: '100%',
    borderBottomColor: colors.mono400,
    borderBottomStyle: 'solid',
    borderBottomWidth: '1px',
    marginTop: sizing.scale600,
    marginBottom: sizing.scale600,
    display: $isHidden ? 'none' : 'block',
  })
)

export const StyledPageBreak = styled<{ $isHidden?: boolean }, 'header'>(
  'header',
  ({ $theme: { typography, sizing, breakpoints }, $isHidden }) => ({
    marginTop: 0,
    marginBottom: sizing.scale800,
    ...typography.font650,
    display: $isHidden ? 'none' : 'block',
    flexBasis: '100%',
    maxWidth: '282px',
    [breakpoints.medium]: {
      ...typography.font550,
      marginBottom: sizing.scale600,
    },
  })
)
