import { ObservableQuery } from '@apollo/client'
import { bulkUpsertEngagementSettings_bulkUpsertEngagementSettings_engagementSettings as BulkUpsertEngagementSettings } from 'gql/types/bulkUpsertEngagementSettings'
import { getEngagementTypeSettings } from 'gql/types/getEngagementTypeSettings'

export const updateQueryResults = (
  updateQuery: ObservableQuery<getEngagementTypeSettings>['updateQuery'],
  settings: BulkUpsertEngagementSettings[]
) => {
  const settingsSuccess = settings.filter(({ success }) => success)
  updateQuery(existingSettings => ({
    getEngagementTypeSettings: existingSettings.getEngagementTypeSettings.map(
      setting => {
        const updatedSetting = settingsSuccess.find(
          ({ name }) => name === setting.name
        )

        return updatedSetting
          ? { ...setting, value: updatedSetting.value }
          : setting
      }
    ),
  }))
}
