import { defineMessages, useIntl } from 'react-intl'
import { RadioGroup, Radio, ALIGN } from '@jifflenow/brightui/radio'
import { useEventLocation } from 'providers/LocationProvider'
import { Field } from 'ui-components/Field'
import {
  EngagementTypeToggleWithDesc,
  FieldLabel,
  GenericError,
} from '../common'
import { BorderStyled, ParticipantsBody } from './styled-components'
import { instructionRadioOverride, radioGroupOverrides } from './overrides'

const messages = defineMessages({
  sectionTitle: {
    defaultMessage: 'Attendee Mode of attendance',
    description: 'Section title',
  },
  sectionTitleDesc: {
    defaultMessage:
      'In-person attendees will be considered for room capacity utilization',
    description: 'Help text',
  },
  defaultModeForAllAttendeesLabel: {
    defaultMessage: 'Enable Default Mode of attendance for all attendees',
    description: 'Field label',
  },
})

const AttendeeModeSelect = () => {
  const { formatMessage } = useIntl()

  const {
    in_person_attendance_custom_label,
    virtual_attendance_custom_label,
  } = useEventLocation().configs!

  return (
    <Field<string>
      name="mode_of_attendance"
      render={({
        input: { value, onChange },
        meta: { error, submitError },
      }) => (
        <>
          {(submitError || error) && (
            <GenericError>{submitError || error}</GenericError>
          )}
          <FieldLabel $bold={false}>
            {formatMessage(messages.defaultModeForAllAttendeesLabel)}
          </FieldLabel>
          <RadioGroup
            align={ALIGN.horizontal}
            value={value}
            onChange={e => onChange(e.target.value)}
            overrides={radioGroupOverrides}
          >
            <Radio value="in_person" overrides={instructionRadioOverride}>
              {in_person_attendance_custom_label}
            </Radio>
            <Radio value="virtual">{virtual_attendance_custom_label}</Radio>
          </RadioGroup>
        </>
      )}
    />
  )
}

export const AttendeeMode = () => {
  const { formatMessage } = useIntl()
  const { enable_mode_of_attendance } = useEventLocation().configs!

  return enable_mode_of_attendance === 'true' ? (
    <ParticipantsBody>
      <EngagementTypeToggleWithDesc
        label={formatMessage(messages.sectionTitle)}
        name="enable_mode_of_attendance"
        desc={formatMessage(messages.sectionTitleDesc)}
      >
        <AttendeeModeSelect />
      </EngagementTypeToggleWithDesc>
      <BorderStyled />
    </ParticipantsBody>
  ) : null
}
