import { VFC } from 'react'
import { stringifyUrl } from 'query-string'
import { JiffleClock } from '@jifflenow/brightui/jiffle-logo'
import { useEventLocation } from 'providers/LocationProvider'
import { useEngagementType } from '../../EngagementTypeProvider'
import { FormBuilderProps } from './types'
import { Iframe, Wrapper } from './styled-components'

export const FormBuilder: VFC<FormBuilderProps> = ({
  name,
  surveyId: survey_uuid,
  surveyMasterName: survey_master_name,
  title,
}) => {
  const {
    id: current_location_uuid,
    attributes: { systemName: eventName },
  } = useEventLocation().location!
  const { uuid: engagement_type_uuid } = useEngagementType().engagementType

  return (
    <Iframe
      src={stringifyUrl(
        {
          url: `${origin}/${eventName}/event/engagement_types_form_builder`,
          query: {
            current_location_uuid,
            engagement_type_uuid,
            survey_uuid,
            survey_master_name,
            form_name: name,
          },
        },
        { skipEmptyString: true }
      )}
      width="100%"
      frameBorder="0"
      title={title}
    >
      <Wrapper>
        <JiffleClock animated />
      </Wrapper>
    </Iframe>
  )
}
