import { VFC, useState, useEffect } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { useRoomExceptionsQuery } from 'gql'
import { useEngagementType } from '../../EngagementTypeProvider'
import { RoomExceptions } from '../types'
import { Root, NoException, Text } from './styled-components'
import { ExceptionsProps } from './types'
import { getExceptions } from './transformExceptionDates'
import { AddExceptionButton } from './AddExceptionButton'
import { ListExceptions } from './ListExceptions'

const messages = defineMessages({
  addException: {
    defaultMessage: 'Add Exception',
    description: 'Button text',
  },
  noException: {
    defaultMessage: 'Add dates that deviate from your standard working hours.',
    description: 'Button text',
  },
  success: {
    defaultMessage: 'Exceptions updated successfully',
    description: 'Button text',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
})

export const Exceptions: VFC<ExceptionsProps> = ({
  locationType,
  setExceptions,
  setHandleDiscard,
}) => {
  const { formatMessage } = useIntl()
  const [showLoader] = useState(false)
  const [newExceptions, setNewExceptions] = useState<RoomExceptions[]>([])
  const [oldExceptions, setOldExceptions] = useState<RoomExceptions[]>([])

  const { id: engagementTypeId } = useEngagementType().engagementType
  const { getRoomExceptions = [], loading } = useRoomExceptionsQuery({
    variables: {
      engagementTypeId,
      availabilityLocationType: locationType,
    },
    fetchPolicy: 'cache-and-network',
  })

  useEffect(() => {
    const exceptions = getExceptions(getRoomExceptions)
    setOldExceptions(exceptions)
    setNewExceptions(exceptions)
    setHandleDiscard?.(() => () => setNewExceptions(exceptions))
  }, [getRoomExceptions, setHandleDiscard])

  useEffect(() => {
    setExceptions(
      JSON.stringify(newExceptions) === JSON.stringify(oldExceptions)
        ? []
        : ([
            ...oldExceptions
              .filter(
                oldException =>
                  !newExceptions.some(
                    exception => oldException.date === exception.date
                  )
              )
              .map(exception => ({ ...exception, timeRanges: null })),
            ...newExceptions,
          ] as [])
    )
  }, [newExceptions, oldExceptions, setExceptions])

  return (
    <>
      {showLoader && <ModalSpinner />}
      <Root>
        {!loading &&
          newExceptions &&
          (newExceptions.length ? (
            <ListExceptions
              exceptions={newExceptions}
              setExceptions={setNewExceptions}
            />
          ) : (
            <NoException>
              <Text>{formatMessage(messages.noException)}</Text>
              <AddExceptionButton
                exceptions={newExceptions}
                setExceptions={setNewExceptions}
              />
            </NoException>
          ))}
      </Root>
    </>
  )
}
