import { FC, ReactNode, useEffect, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { FieldRenderProps } from 'react-final-form'
import { Select, Value } from '@jifflenow/brightui/select'
import { useEventLocation } from 'providers/LocationProvider'
import { Field } from 'ui-components/Field'
import { Button, KIND } from '@jifflenow/brightui/button'
import { DurationError, FieldLabelContainer } from '../common'
import { InstructionsModal } from './InstructionsModal'
import { ChangeMeetingTypeProps, ChangeMeetingTypeSelectProps } from './types'
import {
  BorderStyled,
  MeetingTypeHelpText,
  Section,
  SectionTitle,
  Info,
  ChangeMeetingTypeSelectContainer,
  FieldLabel,
} from './styled-components'

const messages = defineMessages({
  changeMeetingTypeTitle: {
    defaultMessage: 'Change Engagement Type',
    description: 'Section title',
  },
  changeMeetingType: {
    defaultMessage:
      'Meetings of this type can be changed to following engagement types',
    description: 'Field label',
  },
  actionsSelectPlaceholder: {
    defaultMessage: 'Select engagement type',
    description: 'Placeholder text',
  },
  meetingTypeHelpText: {
    defaultMessage:
      'It is recommended to set this after you complete setting up all engagement types',
    description: 'Field label',
  },
  changeMeetingTypeSubText: {
    defaultMessage:
      '<b>NOTE:</b> Some Engagement Types may not show as selectable due to inconsistent settings. Location, Participant, and Approval settings, etc. must match to change a meeting from one Engagement Type to the other. Please<button>click here</button>for further instructions.',
    description: 'Field label',
  },
  meetingTypeTooltip: {
    defaultMessage:
      'It is recommended to only add meeting types satisfying the following conditions',
    description: 'Tooltip label',
  },
  selectAllEngagamentTypes: {
    defaultMessage: 'All eligible engagement types',
    description: 'Option label',
  },
})

const ChangeMeetingTypeSelect: FC<
  FieldRenderProps<string> & ChangeMeetingTypeSelectProps
> = ({
  input: { value, onChange, name },
  meta: { error, submitError },
  meetingType,
}) => {
  const { formatMessage } = useIntl()
  const [options, setOptions] = useState<Value>([])

  const actionValue = (value as unknown) as string[]

  useEffect(() => {
    setOptions([
      { label: formatMessage(messages.selectAllEngagamentTypes), id: 'ALL' },
      ...meetingType.map(({ text: label, id }) => ({
        label,
        id,
      })),
    ])
  }, [formatMessage, meetingType])

  const selectedOptions = options.filter(({ id }) =>
    actionValue?.some(typeId => typeId === id)
  )

  return (
    <>
      {(error || submitError) && (
        <DurationError>{error || submitError}</DurationError>
      )}
      <ChangeMeetingTypeSelectContainer>
        <Select
          options={options}
          clearable={false}
          multi
          value={selectedOptions}
          placeholder={formatMessage(messages.actionsSelectPlaceholder)}
          onChange={({ value }: { value: Value }) => {
            onChange(value.map(({ id }) => id))
          }}
          dataPendo={name}
        />
      </ChangeMeetingTypeSelectContainer>
      <MeetingTypeHelpText>
        {formatMessage(messages.meetingTypeHelpText)}
      </MeetingTypeHelpText>
    </>
  )
}

export const ChangeMeetingType: VFC<ChangeMeetingTypeProps> = ({
  meetingType,
}) => {
  const { formatMessage } = useIntl()
  const { enable_change_meeting_type } = useEventLocation().configs!
  const [showInfo, setShowInfo] = useState(false)

  return enable_change_meeting_type === 'true' ? (
    <Section>
      <SectionTitle>
        {formatMessage(messages.changeMeetingTypeTitle)}
      </SectionTitle>
      <Info>
        <FieldLabel>
          {formatMessage(messages.changeMeetingTypeSubText, {
            b: (chunks: ReactNode) => <strong>{chunks}</strong>,
            button: (chunks: ReactNode) => (
              <Button
                kind={KIND.minimal}
                type="button"
                onClick={() => {
                  setShowInfo(true)
                }}
              >
                {chunks}
              </Button>
            ),
          })}
        </FieldLabel>
      </Info>
      <FieldLabelContainer>
        <FieldLabel>{formatMessage(messages.changeMeetingType)}</FieldLabel>
      </FieldLabelContainer>
      <Field<string>
        name="change_meeting_type_links"
        format={value => (value ? JSON.parse(value) : value)}
        parse={value => JSON.stringify(value)}
        render={props => (
          <ChangeMeetingTypeSelect {...props} meetingType={meetingType} />
        )}
      />
      <BorderStyled />
      {showInfo && <InstructionsModal onClose={() => setShowInfo(false)} />}
    </Section>
  ) : null
}
