import { useEffect, useMemo, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { FieldInputProps } from 'react-final-form'
import { Select } from '@jifflenow/brightui/select'
import { Field } from 'ui-components/Field'
import {
  DurationHoursContainer,
  Flex,
  DurationMinsContainer,
  DurationLabel,
  DurationFieldRoot,
  DurationError,
} from './styled-components'
import {
  DayOption,
  HourOption,
  TimeDurationSelectProps,
  MinuteOption,
  DurationProps,
} from './types'

const messages = defineMessages({
  durationDayPlaceholder: {
    defaultMessage: 'Select day',
    description: 'Placeholder text',
  },
  durationHourPlaceholder: {
    defaultMessage: 'Select hour',
    description: 'Placeholder text',
  },
  durationMinsPlaceholder: {
    defaultMessage: 'Select Mins',
    description: 'Placeholder text',
  },
})

type FieldProps = {
  input: FieldInputProps<string>
}

const DaysField: VFC<FieldProps> = ({
  input: { value: fieldValue, onChange },
}) => {
  const { formatMessage, formatNumber } = useIntl()
  const { days = 0, hours, mins } = (fieldValue as unknown) as DurationProps
  const [value, setValue] = useState<DayOption[]>()

  const daysDropdownOptions: DayOption[] = useMemo(
    () =>
      Array.from({ length: 31 }, (_, i) => ({
        label: formatNumber(i, {
          style: 'unit',
          unit: 'day',
          unitDisplay: 'narrow',
        }),
        value: i,
      })),
    [formatNumber]
  )

  useEffect(() => {
    setValue(daysDropdownOptions.filter(({ value }) => days === value))
  }, [days, daysDropdownOptions])

  const handleOnChange = (value: DayOption[]) => {
    setValue(value)
    onChange(
      value[0].value * 1440 + (value[0].value === 30 ? 0 : hours * 60 + mins)
    )
  }

  return (
    <Select
      options={daysDropdownOptions}
      clearable={false}
      placeholder={formatMessage(messages.durationDayPlaceholder)}
      value={value}
      searchInput
      valueKey="value"
      onChange={({ value }: { value: DayOption[] }) => handleOnChange(value)}
      dataPendo="days-field"
    />
  )
}

const HoursField: VFC<FieldProps & { maxHours: number }> = ({
  input: { value: fieldValue, onChange },
  maxHours,
}) => {
  const { formatMessage, formatNumber } = useIntl()
  const { days = 0, hours, mins } = (fieldValue as unknown) as DurationProps
  const [value, setValue] = useState<HourOption[]>()

  const hoursDropdownOptions: HourOption[] = useMemo(
    () =>
      Array.from({ length: maxHours + 1 }, (_, i) => ({
        label: formatNumber(i, {
          style: 'unit',
          unit: 'hour',
          unitDisplay: 'narrow',
        }),
        value: i,
      })),
    [maxHours, formatNumber]
  )

  useEffect(() => {
    setValue(hoursDropdownOptions.filter(({ value }) => hours === value))
  }, [hours, hoursDropdownOptions])

  useEffect(() => {
    if (days === 30) {
      setValue(hoursDropdownOptions.filter(({ value }) => value === 0))
    }
  }, [days, hoursDropdownOptions])

  const handleOnChange = (value: HourOption[]) => {
    setValue(value)
    onChange(
      days * 1440 + value[0].value * 60 + (value[0].value === 24 ? 0 : mins)
    )
  }

  const filteredHoursDropdownOptions =
    days === 30
      ? hoursDropdownOptions.filter(({ value }) => value === 0)
      : hoursDropdownOptions

  return (
    <Select
      options={filteredHoursDropdownOptions}
      clearable={false}
      placeholder={formatMessage(messages.durationHourPlaceholder)}
      value={value}
      searchInput
      valueKey="value"
      onChange={({ value }: { value: HourOption[] }) => handleOnChange(value)}
      dataPendo="hours-field"
    />
  )
}

const MinsField: VFC<FieldProps> = ({
  input: { value: fieldValue, onChange },
}) => {
  const { formatMessage, formatNumber } = useIntl()
  const { days = 0, hours, mins } = (fieldValue as unknown) as DurationProps
  const [value, setValue] = useState<MinuteOption[]>()

  const minsDropdownOptions: MinuteOption[] = useMemo(
    () =>
      Array.from({ length: 60 }, (_, i) => ({
        label: formatNumber(i, {
          style: 'unit',
          unit: 'minute',
          unitDisplay: 'narrow',
        }),
        value: i,
      })),
    [formatNumber]
  )

  useEffect(() => {
    setValue(minsDropdownOptions.filter(({ value }) => mins === value))
  }, [mins, minsDropdownOptions])

  useEffect(() => {
    if (hours === 24 || days === 30) {
      setValue(minsDropdownOptions.filter(({ value }) => value === 0))
    }
  }, [hours, days, minsDropdownOptions])

  const handleOnChange = (value: MinuteOption[]) => {
    setValue(value)
    onChange(days * 1440 + hours * 60 + value[0].value)
  }

  const filteredMinsDropdownOptions =
    hours === 24 || days === 30
      ? minsDropdownOptions.filter(({ value }) => value === 0)
      : minsDropdownOptions

  return (
    <Select
      options={filteredMinsDropdownOptions}
      clearable={false}
      placeholder={formatMessage(messages.durationMinsPlaceholder)}
      value={value}
      searchInput
      valueKey="value"
      onChange={({ value }: { value: MinuteOption[] }) => handleOnChange(value)}
      dataPendo="minutes-field"
    />
  )
}

export const TimeDurationSelect: VFC<TimeDurationSelectProps> = ({
  name,
  label,
  multiDay = false,
}) => (
  <Field<string>
    name={name}
    format={value =>
      multiDay
        ? {
            days: Math.floor(+(value ?? 0) / 1440),
            hours: Math.floor((+(value ?? 0) % 1440) / 60),
            mins: Math.floor((+(value ?? 0) % 1440) % 60),
          }
        : {
            hours: Math.floor(+(value ?? 0) / 60),
            mins: Math.floor(+(value ?? 0) % 60),
          }
    }
    parse={(value: number) => value.toString()}
    render={({ input, meta: { error } }) => (
      <DurationFieldRoot>
        <DurationLabel> {label} </DurationLabel>
        {error && <DurationError>{error}</DurationError>}
        <Flex>
          {multiDay && (
            <DurationHoursContainer>
              <DaysField input={input} />
            </DurationHoursContainer>
          )}
          <DurationHoursContainer>
            <HoursField input={input} maxHours={multiDay ? 23 : 24} />
          </DurationHoursContainer>
          <DurationMinsContainer>
            <MinsField input={input} />
          </DurationMinsContainer>
        </Flex>
      </DurationFieldRoot>
    )}
  />
)
