import { useMemo } from 'react'

export const usePageSizes = (
  totalItems: number,
  PAGE_SIZES = [10, 20, 50, 100]
) => {
  const pageSizes = useMemo(() => {
    const pageSizeIndex = PAGE_SIZES.findIndex(size => size >= totalItems)

    return PAGE_SIZES.slice(
      0,
      (pageSizeIndex === -1 ? PAGE_SIZES.length - 1 : pageSizeIndex) + 1
    )
  }, [PAGE_SIZES, totalItems])

  return pageSizes
}
