import { VFC } from 'react'
import {
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  SIZE,
} from '@jifflenow/brightui/modal'
import { defineMessages, useIntl } from 'react-intl'
import { basename } from 'components/Routes'
import { useEventLocation } from 'providers/LocationProvider'
import { useEngagementType } from 'components/EngagementTypes/EngagementTypeEdit/EngagementTypeProvider'
import { RightLongArrow } from '@jifflenow/brightui/icon'
import {
  WarningMessage,
  FooterButton,
  WarningList,
  ListItem,
  StyledListItemAnchor,
  StyledRightArrow,
} from './styled-components'
import { ReasonForCancellationProps } from './types'
import { okButtonOverrides } from './overrides'

const messages = defineMessages({
  cancelled_calendar_to_internal_attendee: {
    defaultMessage: 'Meeting Cancelled ICS to Internal Attendee',
    description: 'warning message',
  },
  cancelled_calendar_to_external_attendee: {
    defaultMessage: 'Meeting Cancelled ICS to External Attendee',
    description: 'warning message',
  },
  cancel_email_int_attendee_mst: {
    defaultMessage: 'Meeting Cancelled Email to Internal Attendee',
    description: 'warning message',
  },
  email_to_mm_on_meeting_cancellation: {
    defaultMessage: 'Meeting Cancelled Email to MM',
    description: 'warning message',
  },
  email_to_req_on_meeting_cancellation: {
    defaultMessage: 'Meeting Cancelled Email to Requestor',
    description: 'warning message',
  },
})

const mail_actions = [
  'cancelled_calendar_to_internal_attendee',
  'cancelled_calendar_to_external_attendee',
  'cancel_email_int_attendee_mst',
  'email_to_mm_on_meeting_cancellation',
  'email_to_req_on_meeting_cancellation',
]

export const ReasonForCancellationDialog: VFC<ReasonForCancellationProps> = ({
  pendoName = 'ReasonForCancellation-dialog',
  heading,
  message,
  buttonLabel,
  onClose,
}) => {
  const { formatMessage } = useIntl()

  const origin = window.location.origin
  const baseUrl = `${origin}${basename}`
  const {
    attributes: { systemName: eventName },
  } = useEventLocation().location!
  const { uuid: activity_uuid } = useEngagementType().engagementType
  const location = new URLSearchParams(window.location.search).get('location')

  return (
    <Modal size={SIZE.medium} closeable={false} onClose={onClose} isOpen>
      <ModalHeader>{heading}</ModalHeader>
      <ModalBody>
        <WarningMessage>{message}</WarningMessage>
        <WarningList>
          {mail_actions.map(item => (
            <ListItem key={item}>
              <StyledListItemAnchor
                href={`${baseUrl}/events/${eventName}/engagement-types/${activity_uuid}/meeting-type-notifications-details?location=${location}&mail_actions=${item}`}
                target="_blank"
              >
                {formatMessage(messages[item as keyof typeof messages])}
                <StyledRightArrow>
                  <RightLongArrow />
                </StyledRightArrow>
              </StyledListItemAnchor>
            </ListItem>
          ))}
        </WarningList>
      </ModalBody>
      <ModalFooter>
        <FooterButton
          type="button"
          overrides={okButtonOverrides}
          onClick={onClose}
          data-pendo={`${pendoName}/submit`}
        >
          {buttonLabel}
        </FooterButton>
      </ModalFooter>
    </Modal>
  )
}
