import { ReactElement, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import {
  MasterEngagementTypes,
  TemplateEngagementTypes,
} from 'gql/types/globalTypes'
import { useEventLocation } from 'providers/LocationProvider'
import { EngagementTemplateProps, EngagementTypeMasterProps } from './types'

const archiveTemplateNames = defineMessages<keyof typeof MasterEngagementTypes>(
  {
    ConsecutiveMeeting: {
      defaultMessage: 'Consecutive Meeting',
      description: 'Engagement template name',
    },
    General: {
      defaultMessage: 'General Meetings',
      description: 'Engagement template name',
    },
    TracksAndSession: {
      defaultMessage: 'Sessions and Briefings',
      description: 'Engagement template name',
    },
  }
)

export const EngagementTemplate: VFC<
  EngagementTemplateProps | EngagementTypeMasterProps
> = value => {
  const { formatMessage } = useIntl()
  const { version } = useEventLocation().location!.attributes

  if (version === '2') {
    const {
      value: { name, masterEngagementType, type },
    } = value as EngagementTemplateProps

    return ((type === TemplateEngagementTypes.ArchivedEvent
      ? formatMessage(archiveTemplateNames[masterEngagementType])
      : name) as unknown) as ReactElement
  } else {
    const { value: engagementTypeMaster } = value as EngagementTypeMasterProps

    return (engagementTypeMaster as unknown) as ReactElement
  }
}
