import { FC } from 'react'
import { DateTime } from 'luxon'
import { Plus, Trash, CopyOutlined } from '@jifflenow/brightui/icon'
import { IconButton } from '@jifflenow/brightui/button'
import { RangeTimeSlot } from 'ui-components/RangeTimeSlot'
import { buttonOverrides, iconButtonOverrides } from '../overrides'
import {
  getISODateFromTime,
  getPlusISOTime,
  getTimeFromDate,
  getEndTimeOfDay,
  getDate,
} from '../common'
import {
  WeekDayRoot,
  WeekDayTitle,
  SlotsRoot,
  ActionsContainer,
  TitleActionsContainer,
  TimeSlotWrapper,
} from './styled-components'
import { TimeRangeType, AvailabilityDayProps } from './types'

export const AvailabilityDay: FC<AvailabilityDayProps> = ({
  availability,
  index,
  weekwise,
  ...restProps
}) => {
  const { availabilityDate, availableSlots, availabilityDay } = availability

  const lastSlotEndsAt12AM = availableSlots.length
    ? DateTime.fromISO(availableSlots[availableSlots.length - 1].end).hour === 0
    : false

  const deleteAllSlots = () => {
    restProps.deleteAllSlots(index)
  }
  const addNewSlot = () => {
    const prevEndTime = availableSlots[availableSlots.length - 1]?.end
    const newStartDate = prevEndTime
      ? getISODateFromTime(prevEndTime)
      : getISODateFromTime('09:00:00')

    restProps.addSlotsToAvailability(index, {
      start: getTimeFromDate(newStartDate),
      end:
        DateTime.fromISO(newStartDate).hour === 23
          ? getEndTimeOfDay(newStartDate)
          : prevEndTime
          ? getPlusISOTime(newStartDate, { hour: 1 })
          : getTimeFromDate(getISODateFromTime('17:00:00')),
    })
  }

  const deleteSlot = (index: number) => {
    if (weekwise) {
      restProps.deleteSingleSlot(availabilityDay!, [index])
    } else {
      restProps.deleteSingleSlot(availabilityDate!, [index])
    }
  }

  const editSlotStartTime = (newDate: Date, index: number) => {
    if (weekwise) {
      restProps.editSlotStartTime(availabilityDay!, index, newDate)
    } else {
      restProps.editSlotStartTime(availabilityDate!, index, newDate)
    }
  }

  const editSlotEndTime = (newDate: Date, index: number) => {
    if (weekwise) {
      restProps.editSlotEndTime(availabilityDay!, index, newDate)
    } else {
      restProps.editSlotEndTime(availabilityDate!, index, newDate)
    }
  }

  return (
    <WeekDayRoot key={index}>
      <TitleActionsContainer>
        <WeekDayTitle>
          {weekwise
            ? availabilityDay
            : getDate(availabilityDate!).toLocaleString({
                ...DateTime.DATE_MED_WITH_WEEKDAY,
                weekday: 'long',
              })}
        </WeekDayTitle>
        <ActionsContainer>
          <IconButton
            size={20}
            overrides={buttonOverrides}
            disabled={lastSlotEndsAt12AM}
            onClick={addNewSlot}
            data-pendo="engangemnet-type/add-new-slot"
          >
            <Plus />
          </IconButton>
          <IconButton
            size={20}
            overrides={iconButtonOverrides}
            disabled={!availableSlots?.length}
            onClick={() =>
              restProps.handleCopySlotsToOtherDays(
                weekwise ? availabilityDay! : availabilityDate!
              )
            }
            data-pendo="engangemnet-type/copy-slot"
          >
            <CopyOutlined />
          </IconButton>
          <IconButton
            size={20}
            disabled={!availableSlots?.length}
            overrides={iconButtonOverrides}
            onClick={deleteAllSlots}
            data-pendo="engangemnet-type/delete-all-slot"
          >
            <Trash />
          </IconButton>
        </ActionsContainer>
      </TitleActionsContainer>
      <SlotsRoot>
        {availableSlots
          ? availableSlots.map(
              ({ start, end, error }: TimeRangeType, index: number) => (
                <TimeSlotWrapper key={index}>
                  <RangeTimeSlot
                    start={getISODateFromTime(start)}
                    end={getISODateFromTime(end)}
                    error={error}
                    onDelete={() => deleteSlot(index)}
                    onStartChange={date => editSlotStartTime(date, index)}
                    onEndChange={date => editSlotEndTime(date, index)}
                  />
                </TimeSlotWrapper>
              )
            )
          : null}
      </SlotsRoot>
    </WeekDayRoot>
  )
}
