import { useEffect, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Option, Select as BrightSelect } from '@jifflenow/brightui/select'
import { Field, Validation } from 'ui-components/Field'
import { SelectFieldProps } from './types'
import { FormControlStyled } from './styled-components'
import { FieldProps } from './types'

const messages = defineMessages({
  searchPlaceholder: {
    defaultMessage: 'Search by name',
    description: 'Field label',
  },
})

const Select: VFC<FieldProps> = ({
  input: { value: fieldValue, onChange, name },
  options,
}) => {
  const { formatMessage } = useIntl()
  const [value, setValue] = useState<Option[]>([])

  useEffect(() => {
    if (fieldValue) {
      setValue(fieldValue?.map((roleId): Option => ({ id: roleId })))
    }
  }, [fieldValue])

  return (
    <BrightSelect
      value={value}
      multi
      options={options}
      backspaceClearsInputValue
      searchable
      placeholder={formatMessage(messages.searchPlaceholder)}
      onChange={({ value }: { value: Option[] }) =>
        onChange(value?.map(option => option.id))
      }
      required
      dataPendo={name}
    />
  )
}
export const SelectField: VFC<SelectFieldProps> = ({
  options,
  name,
  label,
}) => (
  <Field<string[]>
    name={name}
    render={({ input, meta: { touched, error } }) => (
      <FormControlStyled label={label} error={touched && error}>
        <Select input={input} options={options} />
      </FormControlStyled>
    )}
    validations={[Validation.REQUIRED]}
  />
)
