import { VFC } from 'react'
import { useFormState } from 'react-final-form'
import { useEventLocation } from 'providers/LocationProvider'
import { useQuery } from 'react-query'
import { ErrorPage } from 'ui-components/ErrorPage'
import { getEngagementTypeCustomFormQueryFn, useSummitClient } from 'rest'
import { useEngagementType } from '../../EngagementTypeProvider'
import { FormValues } from '../types'
import { DemoSubTopicFields } from './DemoSubTopicsFields'
import { DemoTopicField } from './DemoTopicField'
import { FieldLoader } from './FieldLoader'

export const CustomFields: VFC = () => {
  const { demo_topic } = useFormState<FormValues>().values
  const { uuid } = useEngagementType().engagementType
  const {
    attributes: { systemName: eventName },
  } = useEventLocation().location!

  const client = useSummitClient()

  const { isLoading, data, error, refetch } = useQuery(
    [
      'engagementTypeCustomForm',
      {
        path: {
          eventName,
        },
        query: {
          uuid,
        },
      },
    ],
    getEngagementTypeCustomFormQueryFn(client)
  )

  if (isLoading) {
    return <FieldLoader />
  }
  if (error || !data) {
    return <ErrorPage onButtonClick={() => refetch()} />
  }

  const meetingTypeName = Object.keys(data.data)[0]
  const fields = data.data[meetingTypeName].forms?.meeting_request_form || []

  // @ts-expect-error: options is wrong in openapi spec
  const selectFields: SelectField[] = fields.filter(
    ({ type }) => type === 'dropdown'
  )
  const topicOptions = selectFields.map(({ label, field_name }) => ({
    label,
    id: field_name,
  }))

  return (
    <>
      <DemoTopicField topicOptions={topicOptions} />
      {demo_topic && <DemoSubTopicFields fields={selectFields} />}
    </>
  )
}
