import { useEffect, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useField } from 'react-final-form'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { DependentLinks, EngagementModal } from '../../common'
import { Paths } from '../../Paths'
import { BoothTourProps } from '../types'
import { BoothTourSettings } from './BoothTourSettings'

const messages = defineMessages({
  warningDisableLabel: {
    defaultMessage: 'Disable the following',
    description: 'Dependent settings group header',
  },
  disableMappingLabel: {
    defaultMessage: 'Please disable mappings to enable the designated location',
    description: 'Dependent settings modal title',
  },
  warningModalTitle: {
    defaultMessage:
      'Please ensure following settings are done before enabling designated location',
    description: 'Dependent settings modal title',
  },
})

export const BoothTour: VFC<BoothTourProps> = ({
  dependentFieldValues: { default_mapping },
  loading,
  refetch,
}) => {
  const { formatMessage } = useIntl()
  const { onChange, value } = useField('booth_tour_enabled').input
  const [isOpen, setIsOpen] = useState(false)

  useEffect(() => {
    value === 'true' && setIsOpen(default_mapping === 'true')
  }, [default_mapping, value])

  const dependentLinks = [
    default_mapping === 'true' && {
      label: formatMessage(messages.disableMappingLabel),
      path: Paths.CSM_ONLY,
      group: formatMessage(messages.warningDisableLabel),
    },
  ].filter(Boolean) as DependentLinks[]

  return (
    <>
      {isOpen && (
        <>
          {loading && <ModalSpinner />}
          <EngagementModal
            title={formatMessage(messages.warningModalTitle)}
            dependentLinks={dependentLinks}
            onClose={() => {
              setIsOpen(false)
              default_mapping === 'true' && onChange('false')
            }}
            onConfirm={() => refetch()}
          />
        </>
      )}
      <BoothTourSettings />
    </>
  )
}
