import { FC, VFC, useMemo, useState, useEffect } from 'react'
import { Prompt } from 'react-router-dom'
import { Config, FORM_ERROR } from 'final-form'
import { Form as FinalForm, FormRenderProps } from 'react-final-form'
import createFocusDecorator from 'final-form-focus'
import { defineMessages, useIntl } from 'react-intl'
import { useQueryClient } from 'react-query'
import { Option } from '@jifflenow/brightui/select'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import {
  mutateUpdateEngagementType,
  useApolloClient,
  useEngagementTypesGrandTotalQuery,
  useFetchRolesForRequestEngagement,
} from 'gql'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { commonMessages, roles as roleLabel } from 'utils/messages'
import { fetchRoles_fetchRoles } from 'gql/types/fetchRoles'
import { updateEngagementTypeVariables } from 'gql/types/updateEngagementType'
import { useEngagementType } from '../EngagementTypeProvider'
import {
  EngagementNameField,
  RolesField,
  EngagementDescriptionField,
  DisplayOrderField,
} from '../../common'
import {
  Body,
  Footer,
  Header,
  StyledForm,
  Container,
  usePageTitle,
} from '../common'
import { EngagementTemplateField } from './EngagementTemplateField'
import { FieldWrapper } from './styled-components'

interface FormValues {
  engagementTemplateName: string
  name: string
  description?: string
  roles: Option[]
  displayOrder: number
}

const messages = defineMessages({
  basics: {
    defaultMessage: 'Basics',
    description: 'Page title',
  },
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  engagementNameLabel: {
    defaultMessage: 'Name of Engagement Type',
    description: 'Field label',
  },
  engagementDescriptionLabel: {
    defaultMessage: 'Description',
    description: 'Field label',
  },
  engagementTypeUpdateSuccess: {
    defaultMessage: 'Engagement type updated successfully.',
    description: 'Success message',
  },
})

const focusDecorator = createFocusDecorator<FormValues>()

const Form: FC<FormRenderProps<FormValues>> = ({
  handleSubmit,
  dirty,
  children,
}) => {
  const { formatMessage } = useIntl()
  const unload = useBeforeUnload()

  useEffect(() => {
    unload(dirty)
  }, [dirty, unload])

  return (
    <StyledForm onSubmit={handleSubmit} noValidate>
      <Prompt when={dirty} message={formatMessage(messages.promptMessage)} />
      <Header />
      <Body>
        <Container>
          <FieldWrapper>
            <EngagementTemplateField />
            <EngagementNameField
              label={formatMessage(messages.engagementNameLabel)}
            />
            <EngagementDescriptionField
              label={formatMessage(messages.engagementDescriptionLabel)}
            />
            {children}
          </FieldWrapper>
        </Container>
        <Footer />
      </Body>
    </StyledForm>
  )
}

const RoleMapper = ({ id }: fetchRoles_fetchRoles | Option) => id as string

export const EngagementBasics: VFC = () => {
  const { formatMessage } = useIntl()
  const queryClient = useQueryClient()
  const client = useApolloClient()
  usePageTitle(formatMessage(messages.basics))

  const [showLoader, setShowLoader] = useState(false)

  const {
    uuid,
    name,
    defaultRoles,
    description,
    displayOrder,
    templateEngagementType: { name: engagementTemplateName },
  } = useEngagementType().engagementType

  const { roles = [] } = useFetchRolesForRequestEngagement({
    onError: () => showErrorToast(formatMessage(messages.genericError)),
  })

  const { getEngagementTypes } = useEngagementTypesGrandTotalQuery({
    notifyOnNetworkStatusChange: true,
    fetchPolicy: 'cache-and-network',
    variables: {
      page: {},
      filters: {},
    },
    onError: () => showErrorToast(formatMessage(messages.genericError)),
  })

  const { grandTotal = 0 } = getEngagementTypes ?? {}

  const handleSubmit: Config<FormValues>['onSubmit'] = async values => {
    try {
      const { roles: selectedRoles, description } = values

      const defaultRoles = selectedRoles.some(({ id }) => id === 'ALL')
        ? ['ALL']
        : selectedRoles.filter(({ id }) => id !== 'MM').map(RoleMapper)

      const variables: updateEngagementTypeVariables = {
        uuid,
        name: values.name,
        defaultRoles,
        description,
        displayOrder: values.displayOrder,
      }
      setShowLoader(true)
      await mutateUpdateEngagementType(client, {
        variables,
        onQueryUpdated: () => {
          queryClient.refetchQueries('getEngagementTypes')
          if (name !== values.name || displayOrder !== values.displayOrder) {
            queryClient.refetchQueries('getEngagementTypeSettings')
          }
        },
      })
      setShowLoader(false)
      showSuccessToast(formatMessage(messages.engagementTypeUpdateSuccess))
      return undefined
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
      return { [FORM_ERROR]: { code: 'GenericError' } }
    }
  }

  const initialValues: FormValues = useMemo(
    () => ({
      engagementTemplateName,
      name,
      description: description || '',
      displayOrder,
      roles: [
        {
          label: formatMessage(roleLabel.MM),
          id: 'MM',
          clearableValue: false,
        },
        ...(defaultRoles.some(id => id === 'ALL')
          ? [
              {
                label: formatMessage(commonMessages.allMyAttendees),
                id: 'ALL',
              },
            ]
          : [
              ...roles
                .filter(({ roleCompanyid }) =>
                  defaultRoles.some(roleId => roleId === roleCompanyid)
                )
                .map(
                  ({ roleCompanyid, roleDisplayName }): Option => ({
                    label: formatMessage(roleLabel[roleDisplayName]),
                    id: roleCompanyid,
                  })
                ),
            ]),
      ],
    }),
    [
      defaultRoles,
      description,
      displayOrder,
      engagementTemplateName,
      formatMessage,
      name,
      roles,
    ]
  )

  return (
    <>
      {showLoader && <ModalSpinner />}
      <FinalForm<FormValues>
        decorators={[focusDecorator]}
        onSubmit={handleSubmit}
        initialValues={initialValues}
        render={props => (
          <Form {...props}>
            <RolesField roles={roles} />
            <DisplayOrderField grandTotal={grandTotal} />
          </Form>
        )}
      />
    </>
  )
}
