import { VFC } from 'react'
import { defineMessages, useIntl, FormattedMessage } from 'react-intl'
import { Input } from '@jifflenow/brightui/input'
import { Search } from '@jifflenow/brightui/icon'
import { useEventLocation } from 'providers/LocationProvider'
import { NewEngagementButton } from '../NewEngagementButton'
import {
  Container,
  RightContainer,
  InputControl,
  Root,
  Title,
  PageHint,
} from './styled-components'
import { HeaderProps } from './types'

const messages = defineMessages({
  search: {
    defaultMessage: 'Search',
    description: 'Placeholder text',
  },
})

export const Header: VFC<HeaderProps> = ({
  itemCount,
  onSearch,
  search,
  onCreate,
}) => {
  const { formatMessage } = useIntl()

  const { version } = useEventLocation().location!.attributes

  return (
    <Root>
      <Container>
        <Title>
          <FormattedMessage
            defaultMessage="Engagement Types  {count, number}"
            description="Section title"
            values={{ count: itemCount }}
          />
        </Title>
        {version === '2' && (
          <PageHint>
            <FormattedMessage
              defaultMessage="Manage engagement type creation, modification and activation."
              description="Section sub title"
            />
          </PageHint>
        )}
      </Container>
      <RightContainer>
        <InputControl>
          <Input
            type="search"
            value={search}
            IconAfter={Search}
            onChange={onSearch}
            placeholder={formatMessage(messages.search)}
            name="engagement-list/search"
          />
        </InputControl>
        {version !== '1' && <NewEngagementButton onCreate={onCreate} />}
      </RightContainer>
    </Root>
  )
}
