import { useState, FC, useMemo } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { showErrorToast } from 'utils/toast'
import { useGetEngagementTypeSurveySettings } from 'gql'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { Config, FORM_ERROR } from 'final-form'
import { Form as FinalForm } from 'react-final-form'
import { showSuccessToast } from 'utils/toast'
import { mutateEngagementTypeSurveySettings, useApolloClient } from 'gql'
import { upsertEngagementTypeSurveySettingsVariables } from 'gql/types/upsertEngagementTypeSurveySettings'
import { FormProps } from './types'
import { RolesAndPermissionFormValues } from './types'
import { Form } from './Form'

const messages = defineMessages({
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  successMessage: {
    defaultMessage: 'Your changes have been saved successfully',
    description: 'Success message',
  },
})

const isSameAsPreviousValue = (prevValues: string[], newValues: string[]) =>
  prevValues.length === newValues.length &&
  newValues.every(newValueItem => prevValues.includes(newValueItem))

const getAccessLevels = (accessLevels?: string[]) =>
  accessLevels?.includes('INTERNAL')
    ? ['INTERNAL', ...accessLevels.filter(id => id === 'EXTERNAL')]
    : accessLevels

export const SurveyFormField: FC<FormProps> = ({
  surveyValue,
  options,
  setSurveyValue,
}) => {
  const [showLoader, setShowLoader] = useState(false)
  const { formatMessage } = useIntl()
  const client = useApolloClient()
  const {
    refetch: refetchGetEngagementTypeSurveySettings,
    getEngagementTypeSurveySettings,
  } = useGetEngagementTypeSurveySettings({
    notifyOnNetworkStatusChange: true,
    variables: {
      engagementSurveyId: surveyValue[0].id.toString(),
    },
    fetchPolicy: 'cache-and-network',
    onError: () => {
      showErrorToast(formatMessage(messages.genericError))
    },
  })

  const initialValues = useMemo(() => {
    const { requestorAccessLevel, accessLevel, ...restValues } =
      getEngagementTypeSurveySettings ?? {}

    return {
      ...restValues,
      requestorAccessLevel: getAccessLevels(requestorAccessLevel),
      accessLevel: getAccessLevels(accessLevel),
    }
  }, [getEngagementTypeSurveySettings])

  const handleSubmit: Config<RolesAndPermissionFormValues>['onSubmit'] = async values => {
    try {
      setShowLoader(true)
      const engagementTypeSurveyId = values.engagementTypeSurveyId

      if (!engagementTypeSurveyId) {
        return { [FORM_ERROR]: { code: 'GenericError' } }
      }

      const {
        meetingHostCanTakeSurvey,
        requestorShouldFillSurvey,
        requestorAccessLevel,
        accessLevel,
        entityStatus,
        status,
      } = getEngagementTypeSurveySettings || {}

      const variables: upsertEngagementTypeSurveySettingsVariables = {
        ...(meetingHostCanTakeSurvey !== values.meetingHostCanTakeSurvey && {
          meetingHostCanTakeSurvey: values.meetingHostCanTakeSurvey,
        }),
        ...(requestorShouldFillSurvey !== values.requestorShouldFillSurvey && {
          requestorShouldFillSurvey: values.requestorShouldFillSurvey,
        }),
        ...(accessLevel &&
          !isSameAsPreviousValue(values.accessLevel, accessLevel) && {
            accessLevel: getAccessLevels(values.accessLevel),
          }),
        ...(requestorAccessLevel &&
          !isSameAsPreviousValue(
            values.requestorAccessLevel,
            requestorAccessLevel
          ) && {
            requestorAccessLevel: getAccessLevels(values.requestorAccessLevel),
          }),
        ...(entityStatus &&
          !isSameAsPreviousValue(values.entityStatus, entityStatus) && {
            entityStatus: values.entityStatus,
          }),
        ...(status !== values.status && {
          status: values.status,
        }),
        engagementTypeSurveyId,
      }

      await mutateEngagementTypeSurveySettings(client, {
        variables,
        refetchQueries: ['getEngagementTypeDetails'],
      })
      setShowLoader(false)
      showSuccessToast(formatMessage(messages.successMessage))
      refetchGetEngagementTypeSurveySettings()
      return undefined
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
      return { [FORM_ERROR]: { code: 'GenericError' } }
    }
  }

  return getEngagementTypeSurveySettings ? (
    <>
      {showLoader && <ModalSpinner />}
      <FinalForm<RolesAndPermissionFormValues>
        onSubmit={handleSubmit}
        initialValues={initialValues}
        render={props => (
          <Form
            {...props}
            options={options}
            surveyValue={surveyValue}
            setSurveyValue={setSurveyValue}
            surveyMasterstatus={
              getEngagementTypeSurveySettings?.surveyMasterstatus
            }
          />
        )}
      />
    </>
  ) : null
}
