import { ChangeEvent, VFC } from 'react'
import { Field, Validation } from 'ui-components/Field'
import { Input } from '@jifflenow/brightui/input'
import { FormControl } from '@jifflenow/brightui/form-control'
import { FormattedMessage } from 'react-intl'
import { DurationError, MeetingDurationInput, MinsLabel } from '../common'

type InputProps = {
  name: string
  label?: string
  placeholder?: string
  min?: number
  type?: 'number' | 'text'
  disabled?: boolean
}

export const BreakDurationInput: VFC<InputProps> = ({
  label,
  name,
  placeholder,
  type,
  min,
  disabled,
}) => (
  <Field<string>
    name={name}
    validations={[Validation.REQUIRED]}
    render={({ input: { value, onChange }, meta: { error, submitError } }) => (
      <>
        {submitError ||
          (error && <DurationError>{submitError || error}</DurationError>)}
        <MeetingDurationInput>
          <>
            <FormControl label={label}>
              <Input
                name={name}
                type={type ? type : 'text'}
                value={value}
                min={min}
                placeholder={placeholder}
                onChange={(e: ChangeEvent<HTMLInputElement>) =>
                  onChange(e.target.value)
                }
                disabled={disabled}
              />
            </FormControl>
            <MinsLabel>
              <FormattedMessage
                defaultMessage="mins"
                description="Short form of Minutes"
              />
            </MinsLabel>
          </>
        </MeetingDurationInput>
      </>
    )}
  />
)
