import { VFC } from 'react'
import { useField } from 'react-final-form'
import { defineMessages, useIntl } from 'react-intl'
import { RefetchProps } from '../../common/types'
import { GenericError } from '../styled-components'
import {
  FormControlStyledToggleContainer,
  FormFieldWrapper,
  Subtitle,
} from '../styled-components'
import { DependentFieldValues } from '../types'
import { MethodRequestManualField } from './MethodRequestManualField'
import { MethodRequestMappedField } from './MethodRequestMappedField'
import { MethodRequestUnMappedField } from './MethodRequestUnMappedField'

const messages = defineMessages({
  irpMethodLabel: {
    defaultMessage: 'Method to process inbound request page',
    description: 'Field label',
  },
})

export const MethodRequestField: VFC<RefetchProps & DependentFieldValues> = ({
  refetch,
  loading,
  dependentFieldValues,
}) => {
  const { formatMessage } = useIntl()

  const { submitError: submitError1 } = useField(
    'enable_external_widget_auto_process'
  ).meta

  const { submitError: submitError2 } = useField(
    'enable_external_widget_auto_process_without_mapping'
  ).meta

  return (
    <FormFieldWrapper>
      <FormControlStyledToggleContainer>
        <Subtitle>{formatMessage(messages.irpMethodLabel)}</Subtitle>
        <GenericError>{submitError1 || submitError2}</GenericError>
        <MethodRequestManualField />
        <MethodRequestMappedField
          refetch={refetch}
          loading={loading}
          dependentFieldValues={dependentFieldValues}
        />
        <MethodRequestUnMappedField
          refetch={refetch}
          loading={loading}
          dependentFieldValues={dependentFieldValues}
        />
      </FormControlStyledToggleContainer>
    </FormFieldWrapper>
  )
}
