import { useState, VFC } from 'react'
import { FormattedMessage } from 'react-intl'
import { Button } from '@jifflenow/brightui/button'
import { CreateEngagementDrawer } from '../CreateEngagementDrawer'
import { NewEngagementButtonProps } from './types'

export const NewEngagementButton: VFC<NewEngagementButtonProps> = ({
  onCreate,
}) => {
  const [isOpen, setIsOpen] = useState(false)

  return (
    <>
      <CreateEngagementDrawer
        isOpen={isOpen}
        onClose={() => setIsOpen(false)}
        onCreate={onCreate}
      />
      <Button
        onClick={() => setIsOpen(true)}
        data-pendo="engagement-list/create-new"
      >
        <FormattedMessage
          defaultMessage="+ New Engagement Type"
          description="Button label"
        />
      </Button>
    </>
  )
}
