import { defineMessages, useIntl } from 'react-intl'
import { useEventLocation } from 'providers/LocationProvider'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { useEngagementType } from '../EngagementTypeProvider'
import { EngagementTypeToggle } from '../common'
import { FieldSeparator, Section, SectionTitle } from './styled-components'

const messages = defineMessages({
  attachDocumentOptions: {
    defaultMessage: 'Attach Document Options',
    description: 'Section title',
  },
  includeBriefingDocs: {
    defaultMessage: 'Attach Document',
    description: 'Section title',
  },
  briefingDocsInMobile: {
    defaultMessage: 'Allow viewing of attachments on mobile',
    description: 'Field label',
  },
  briefingDocsAsAttachment: {
    defaultMessage: 'Attach documents by uploading files',
    description: 'Field label',
  },
  briefingDocsAsLink: {
    defaultMessage: 'Attach documents by inserting links',
    description: 'Field label',
  },
  briefingDocsInNotificationTemplates: {
    defaultMessage:
      'Allow document link variable to be used in Notification templates',
    description: 'Field label',
  },
  docAccessOnlyForLoggedIn: {
    defaultMessage: 'Participants need to login to access uploaded files.',
    description: 'Field label',
  },
})

export const AttachDocument = () => {
  const { formatMessage } = useIntl()
  const {
    templateEngagementType: { masterEngagementType },
  } = useEngagementType().engagementType
  const { include_briefing_docs } = useEventLocation().configs!

  return include_briefing_docs === 'true' &&
    masterEngagementType === MasterEngagementTypes.General ? (
    <Section>
      <SectionTitle>
        {formatMessage(messages.attachDocumentOptions)}
      </SectionTitle>
      <EngagementTypeToggle
        name="include_briefing_docs"
        label={formatMessage(messages.includeBriefingDocs)}
      >
        <FieldSeparator />
        <EngagementTypeToggle
          name="briefing_docs_in_mobile"
          label={formatMessage(messages.briefingDocsInMobile)}
        />
        <FieldSeparator />
        <EngagementTypeToggle
          name="briefing_doc_as_attachment"
          label={formatMessage(messages.briefingDocsAsAttachment)}
        />
        <FieldSeparator />
        <EngagementTypeToggle
          name="briefing_doc_as_link"
          label={formatMessage(messages.briefingDocsAsLink)}
        />
        <FieldSeparator />
        <EngagementTypeToggle
          name="briefing_docs_in_notification_templates"
          label={formatMessage(messages.briefingDocsInNotificationTemplates)}
        />
        <FieldSeparator />
        <EngagementTypeToggle
          name="doc_access_only_for_logged_in"
          label={formatMessage(messages.docAccessOnlyForLoggedIn)}
        />
        <FieldSeparator />
      </EngagementTypeToggle>
    </Section>
  ) : null
}
