import { useCallback, useEffect, useState, FC } from 'react'
import { useField } from 'react-final-form'
import { useIntl, defineMessages } from 'react-intl'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { useEventLocation } from 'providers/LocationProvider'
import { EngagementTypeToggleWithDesc } from '../common/EngagementTypeToggleWithDesc'
import { Paths } from '../Paths'
import { DependentLinks } from '../common'
import { EngagementModal } from '../common/EngagementModal'
import { useEngagementType } from '../EngagementTypeProvider'
import { DependentFieldValues } from './types'

const messages = defineMessages({
  irpLabel: {
    defaultMessage: 'Enable Inbound Request Page Widget',
    description: 'Field label',
  },
  irpDesc: {
    defaultMessage: 'Enable external widget for this engagement type',
    description: 'Field desc',
  },
  irpSessionDesc: {
    defaultMessage:
      'Enable external widget for self-nomination sessions of this engagement type',
    description: 'Field desc for session type',
  },
  warningModalTitle: {
    defaultMessage:
      "For an engagement type, Inbound request page can't be enabled when the Approval process is manual.",
    description: 'Dependent settings modal title',
  },
  approvalProcessLabel: {
    defaultMessage: 'Approval Process',
    description: 'Dependent setting info',
  },
  warningEnableLabel: {
    defaultMessage: 'Change Approval Process',
    description: 'Dependent settings group header',
  },
})

export const EnableIRPField: FC<
  { refetch: () => void } & DependentFieldValues
> = ({ children, refetch, dependentFieldValues }) => {
  const { formatMessage } = useIntl()
  const [isOpen, setIsOpen] = useState(false)
  const { version } = useEventLocation().location!.attributes

  const {
    input: { value, onChange },
  } = useField('enable_external_widget')

  const {
    masterEngagementType,
  } = useEngagementType().engagementType.templateEngagementType
  const isSession =
    masterEngagementType === MasterEngagementTypes.TracksAndSession

  const { approval_process } = dependentFieldValues

  const canEnableIrp = useCallback(
    () => approval_process === 'instant' || !isSession,
    [approval_process, isSession]
  )

  useEffect(() => {
    value === 'true' && setIsOpen(!canEnableIrp())
  }, [canEnableIrp, value])

  const dependentLinks = [
    approval_process !== 'instant' && {
      label: formatMessage(messages.approvalProcessLabel),
      path: Paths.APPROVAL_PROCESS,
      group: formatMessage(messages.warningEnableLabel),
    },
  ].filter(Boolean) as DependentLinks[]

  return (
    <>
      {isOpen && (
        <EngagementModal
          title={formatMessage(messages.warningModalTitle)}
          dependentLinks={dependentLinks}
          onClose={() => {
            setIsOpen(false)
            !canEnableIrp() && onChange('false')
          }}
          onConfirm={() => refetch()}
          version={version}
        />
      )}
      <EngagementTypeToggleWithDesc
        label={formatMessage(messages.irpLabel)}
        name="enable_external_widget"
        desc={
          isSession
            ? formatMessage(messages.irpSessionDesc)
            : formatMessage(messages.irpDesc)
        }
      >
        {children}
      </EngagementTypeToggleWithDesc>
    </>
  )
}
