import { FC } from 'react'
import { useHistory, useLocation } from 'react-router-dom'
import { defineMessages, useIntl } from 'react-intl'
import { List as AgGridList } from 'components/ag-grid'
import { RowClickedEvent, RowDataChangedEvent } from '@ag-grid-community/core'
import { getEngagementTypes_getEngagementTypes_data } from 'gql/types/getEngagementTypes'
import { useEventLocation } from 'providers/LocationProvider'
import { NoRowsAndError } from './NoRowsAndError'
import { ActionCell } from './ActionCell'
import { StatusCell } from './StatusCell'
import { EngagementTypesSearchResponse, ListProps } from './types'
import { DisplayOrderCell } from './DisplayOrderCell'
import { EngagementTemplate } from './EngagementTemplate'
import { columnHeaderNames, getColumnDefs } from './constants'

const messages = defineMessages({
  sagaNotCompleted: {
    defaultMessage:
      'Engagement type creation in progress. Please refresh and retry in some time.',
    description: 'Hover message',
  },
})

export const List: FC<
  ListProps<
    getEngagementTypes_getEngagementTypes_data | EngagementTypesSearchResponse
  >
> = props => {
  const { formatMessage } = useIntl()
  const history = useHistory()
  const { search, pathname } = useLocation()

  const { location, configs, privileges } = useEventLocation()
  const { version } = location!.attributes
  const { enable_external_widget } = configs!

  const onRowClicked = ({ data, event }: RowClickedEvent) => {
    const rowElement = event?.target as Element
    if (
      version === '2' &&
      data.sagaCompleted &&
      !rowElement.closest('[col-id=actions]') &&
      !rowElement.closest('[col-id=displayOrder]')
    ) {
      history.push(`${pathname}/${data.uuid}${search}`)
    } else if (
      version === '1' &&
      data.meetingTypeMasterName !== 'Consecutive Meetings' &&
      (enable_external_widget === 'true' || privileges?.canManageNotifications)
    ) {
      history.push(`${pathname}/${data.id}${search}`)
    }
  }

  const constants = {
    columnHeaderNames,
    columnDefs: getColumnDefs(version),
  }

  const onRowDataChanged = ({ api }: RowDataChangedEvent) => {
    api.forEachNode(node => {
      if (!node.data.sagaCompleted) {
        const rows = document.querySelectorAll(`[row-id="${node.data.uuid}"]`)
        rows.forEach(row => {
          row.setAttribute('title', formatMessage(messages.sagaNotCompleted))
        })
      }
    })
  }

  return (
    <AgGridList
      {...props}
      data={
        version === '2'
          ? (props.data as getEngagementTypes_getEngagementTypes_data[])
          : (props.data as EngagementTypesSearchResponse[])
      }
      constants={constants}
      frameworkComponents={{
        ...(version === '2' && { actionCell: ActionCell }),
        statusCell: StatusCell,
        ...(version === '2' && { displayOrderCell: DisplayOrderCell }),
        noRowsAndError: NoRowsAndError,
        engagementTemplateCell: EngagementTemplate,
      }}
      onRowClicked={onRowClicked}
      onRowDataChanged={onRowDataChanged}
    />
  )
}
