import { useState } from 'react'
import { TextFilter } from '@jifflenow/brightui/filter'
import { getMeetingTypeMastersQueryFn, useQuery } from 'rest'
import { useEngagementTemplatesForLocationQuery } from 'gql'
import { fetchTemplatesForLocation_fetchTemplatesForLocation } from 'gql/types/fetchTemplatesForLocation'
import { TemplateOptions, TemplatesFilterProps } from 'components/ag-grid'
import { useEventLocation } from 'providers/LocationProvider'

// TODO: Handle error and loading case once design direction is finalized
export const TemplatesFilter = ({
  label,
  value,
  onChange,
  dataPendo,
}: TemplatesFilterProps) => {
  const {
    systemName: eventName,
    version,
  } = useEventLocation().location!.attributes

  const { data: { data: v1Templates = [] } = {} } = useQuery(
    ['getMeetingTypeMasters', { eventName }],
    getMeetingTypeMastersQueryFn,
    { enabled: version === '1' }
  )

  const {
    templates: v2Templates = [],
  } = useEngagementTemplatesForLocationQuery({
    notifyOnNetworkStatusChange: true,
    skip: version === '1',
  })

  const templates = version === '1' ? v1Templates : v2Templates

  const [searchText, setSearchText] = useState('')

  return (
    <TextFilter
      searchable
      label={label}
      onChange={onChange}
      asyncOptions={false}
      searchText={searchText}
      onSearch={setSearchText}
      options={templates.map(({ id, name }: TemplateOptions) => ({
        id,
        label: name,
      }))}
      value={(templates as fetchTemplatesForLocation_fetchTemplatesForLocation[])
        .filter(({ id }) => value.indexOf(id) > -1)
        .map(({ id, name }) => ({ id, label: name }))}
      dataPendo={dataPendo}
    />
  )
}
