interface EngagementTypeSettingsItems {
  labelKey: keyof typeof engagementTypeSettingsLabels
  uuid: EngagementSettingsGroupNames
}

interface EngagementTypeSettingsMenuItems {
  labelKey: string
  uuid: EngagementSettingsGroupNames
}

const useEngagementTypeSettingsItems: () => EngagementTypeSettingsItems[] = () => {
  const { eventRole } = useMe().data.attributes
  const { engagementTypeSurveys } = useEngagementType()
  const { configs, privileges } = useEventLocation()
  const { enable_external_widget } = configs!

  const engagementTypeSettingsItems: EngagementTypeSettingsItems[] = [
    enable_external_widget === 'true' && {
      labelKey: 'inboundRequestForm',
      uuid: EngagementSettingsGroupNames.INBOUND_REQUEST_FORM,
    },
    privileges?.canManageSurvey &&
      !!engagementTypeSurveys.length && {
        labelKey: 'survey',
        uuid: EngagementSettingsGroupNames.SURVEYS,
      },
    {
      labelKey: 'advancedSettings',
      uuid: EngagementSettingsGroupNames.ADVANCED_SETTINGS,
    },
    eventRole === 'CSM' && {
      labelKey: 'csmOnly',
      uuid: EngagementSettingsGroupNames.CSM_ONLY,
    },
  ].filter(Boolean) as EngagementTypeSettingsItems[]

  return engagementTypeSettingsItems
}

export const CopyEngagementTypeSettings: VFC<{
  setSettingsToCopy: (settings: string[]) => void
}> = ({ setSettingsToCopy }) => {
  const { formatMessage } = useIntl()
  const {
    engagementTypeSurveys,
    engagementTypeSettings: {
      enable_external_widget: enable_external_widget_setting,
    },
    engagementType: {
      templateEngagementType: { masterEngagementType },
    },
  } = useEngagementType()

  const isGeneralMeeting =
    masterEngagementType === MasterEngagementTypes.General

  const { location, configs, privileges } = useEventLocation()
  const {
    attributes: { subType },
  } = location!

  const {
    enable_topic,
    enable_topic_to_activity,
    enable_room_to_topic,
    enable_user_to_topic,
    enable_external_widget,
  } = configs!

  const engagementTypeSettingsOptions = useEngagementTypeSettingsItems()

  const engagementTypeSettingsItems: EngagementTypeSettingsItems[] = [
    {
      labelKey: 'approvalProcess',
      uuid: EngagementSettingsGroupNames.APPROVAL_PROCESS,
    },
    {
      labelKey: 'locationTypes',
      uuid: EngagementSettingsGroupNames.LOCATION_TYPES,
    },
    subType === 'Emeet' && {
      labelKey: 'linkSettings',
      uuid: EngagementSettingsGroupNames.LINK_SETTINGS,
    },
    privileges?.canManageForms &&
      privileges.canManageSelfServeSettings && {
        labelKey: 'outboundMeetingRequestForm',
        uuid: EngagementSettingsGroupNames.OUTBOUND_FORM,
      },
    enable_external_widget === 'true' &&
      enable_external_widget_setting === 'true' && {
        labelKey: 'inboundMeetingRequestForm',
        uuid: EngagementSettingsGroupNames.INBOUND_FORM,
      },
    privileges?.canManageForms &&
      privileges.canManageSelfServeSettings &&
      !!engagementTypeSurveys.length && {
        labelKey: 'surveyForms',
        uuid: EngagementSettingsGroupNames.SURVEY_FORM,
      },
    privileges?.canManageNotifications && {
      labelKey: 'notifications',
      uuid: EngagementSettingsGroupNames.NOTIFICATIONS,
    },
    {
      labelKey: 'participants',
      uuid: EngagementSettingsGroupNames.PARTICIPANTS,
    },
    isGeneralMeeting
      ? enable_topic === 'true' &&
        enable_topic_to_activity === 'true' &&
        (enable_room_to_topic === 'true' ||
          enable_user_to_topic === 'true') && {
          labelKey: 'topicOptions',
          uuid: EngagementSettingsGroupNames.TOPIC_OPTIONS,
        }
      : {
          labelKey: 'topicTrackOptions',
          uuid: EngagementSettingsGroupNames.TOPIC_OPTIONS,
        },
    // TODO: Un-comment when feature is developed
    // { labelKey: 'forms', uuid: EngagementSettingsGroupNames.FORMS },
    ...engagementTypeSettingsOptions,
  ].filter(Boolean) as EngagementTypeSettingsItems[]

  const [engagementTypeSettings, setEngagementTypeSettings] = useState<
    EngagementTypeOptions[]
  >([])

  const itemMapper = () => (
    item: EngagementTypeSettingsItems
  ): EngagementTypeSettingsMenuItems => {
    const { labelKey, uuid } = item

    return {
      labelKey: formatMessage(engagementTypeSettingsLabels[labelKey]),
      uuid,
    }
  }
  const engagementMenuSettings = engagementTypeSettingsItems.map(itemMapper())

  useEffect(() => {
    setEngagementTypeSettings([
      {
        __typename: 'EngagementType',
        id: 'all',
        isActive: false,
        name: formatMessage(messages.all),
        uuid: 'all',
      },
      ...engagementMenuSettings.map(engagementSettings => ({
        __typename: 'EngagementType',
        name: engagementSettings.labelKey,
        uuid: engagementSettings.uuid,
        isActive: false,
      })),
    ])
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [engagementMenuSettings.length])

  const handleChange = (checked: boolean, uuid: string) => {
    if (uuid === 'all') {
      setEngagementTypeSettings([
        ...engagementTypeSettings.map(engagement => ({
          ...engagement,
          isActive: checked,
        })),
      ])
    } else {
      const engagementSettingsOptions = [
        {
          __typename: 'EngagementType',
          id: 'all',
          isActive: false,
          name: formatMessage(messages.all),
          uuid: 'all',
        },
        ...engagementTypeSettings.filter(item => item.uuid !== 'all'),
      ]
      const index = engagementTypeSettings.findIndex(
        element => element.uuid === uuid
      )
      const activeEngagement = engagementTypeSettings
        .filter(item => item.uuid === uuid)
        .map(item =>
          item.uuid === uuid ? { ...item, isActive: checked } : item
        )
      engagementSettingsOptions.splice(index, 1, ...activeEngagement)
      setEngagementTypeSettings(engagementSettingsOptions)
    }
  }

  useEffect(() => {
    setSettingsToCopy(
      engagementTypeSettings
        .filter(item => item.uuid !== 'all' && item.isActive === true)
        .map(item => item.uuid)
    )
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [engagementTypeSettings])

  return (
    <>
      {engagementTypeSettings.map((engagementTypeSettings, index) => (
        <EngagementCheckBoxField
          key={index}
          label={engagementTypeSettings.name}
          isActive={engagementTypeSettings.isActive}
          uuid={engagementTypeSettings.uuid}
          handleChange={handleChange}
        />
      ))}
    </>
  )
}
