import { AccordionOverrides, SharedProps } from '@jifflenow/brightui/accordion'
import { IconOverrides } from '@jifflenow/brightui/icon'
import {
  getBorderColor,
  getBorderStyle,
  getBorderWidth,
  getPadding,
} from 'utils/style-helpers'

export const accordionOverrides: AccordionOverrides<SharedProps> = {
  Root: {
    style: ({ $theme: { colors, mediaQuery, sizing } }) => ({
      ...getBorderColor(colors.mono400),
      ...getBorderStyle('solid'),
      ...getBorderWidth(0),
      paddingLeft: sizing.scale600,
      paddingRight: sizing.scale600,
      [mediaQuery.large]: {
        paddingLeft: '112px',
        paddingRight: '112px',
      },
    }),
  },
}

export const panelOverrides: AccordionOverrides<SharedProps> = {
  Header: {
    style: ({ $expanded, $theme: { colors, sizing, typography } }) => ({
      ...typography.font300,
      fontWeight: 600,
      justifyContent: 'flex-start',
      paddingLeft: 0,
      paddingTop: sizing.scale600,
      paddingBottom: sizing.scale600,
      backgroundColor: colors.white,
      color: $expanded ? colors.primary : colors.mono700,
      borderBottomWidth: '1px',
      borderBottomStyle: 'solid',
      borderBottomColor: colors.mono400,
      ':hover': { color: colors.mono900 },
    }),
  },
  PanelContainer: {
    style: {
      borderBottomStyle: 'none',
    },
  },
  Content: {
    style: ({ $theme: { colors } }) => ({
      ...getPadding(0),
      backgroundColor: colors.white,
    }),
  },
}

export const warningIconOverride: IconOverrides = {
  Svg: {
    style: ({ $theme: { sizing } }) => ({
      strokeWidth: '1',
      marginRight: sizing.scale100,
    }),
  },
}
