import { useEffect, useState, VFC } from 'react'
import { useField } from 'react-final-form'
import { defineMessages, useIntl } from 'react-intl'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { useEventLocation } from 'providers/LocationProvider'
import { DependentLinks, RefetchProps } from '../common/types'
import { EngagementModal } from '../common/EngagementModal'
import { Paths } from '../Paths'
import { useEngagementType } from '../EngagementTypeProvider'
import { InternalAttendee } from './InternalAttendee'
import { ExternalAttendee } from './ExternalAttendee'
import { MeetingHost } from './MeetingHost'
import { AttendeeMode } from './AttendeeMode'
import { DependentFields } from './types'
import { OptionalParticipant } from './OptionalParticipant'

const messages = defineMessages({
  unMappedSchedulingLabel: {
    defaultMessage: 'Auto process without mapping request page',
    description: 'Field label',
  },
  warningDisableLabel: {
    defaultMessage: 'Disable the following',
    description: 'Dependent settings group header',
  },
  warningModalTitle: {
    defaultMessage:
      'Please ensure following settings are done before enabling the Hide Internal Attendee section.',
    description: 'Dependent settings modal title',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
})

export const FormField: VFC<RefetchProps & DependentFields> = ({
  refetch,
  loading,
  dependentFields,
}) => {
  const { formatMessage } = useIntl()
  const [isOpen, setIsOpen] = useState(false)
  const { subType } = useEventLocation().location?.attributes ?? {}

  const {
    masterEngagementType,
  } = useEngagementType().engagementType.templateEngagementType
  const isGeneralMeetingType =
    masterEngagementType === MasterEngagementTypes.General
  const {
    onChange: hideInternalAttendeesOnChange,
    value: hideInternalValue,
  } = useField('hide_internal_attendees_in_book_meeting').input

  const {
    onChange: InternalAttendeeOnChange,
    value: InternalAttendeeValue,
  } = useField('internal_attendee_presence').input

  const {
    onChange: relaxInternalAttendeeOnChange,
    value: relaxInternalAttendeeValue,
  } = useField('relax_internal_attendee_presence_for_mm').input

  const {
    enable_external_widget_auto_process_without_mapping,
  } = dependentFields

  useEffect(() => {
    if (
      hideInternalValue === 'true' ||
      relaxInternalAttendeeValue === 'true' ||
      InternalAttendeeValue === 'false'
    ) {
      setIsOpen(enable_external_widget_auto_process_without_mapping !== 'false')
    }
  }, [
    hideInternalValue,
    enable_external_widget_auto_process_without_mapping,
    relaxInternalAttendeeValue,
    InternalAttendeeValue,
  ])

  const dependentLinks = [
    enable_external_widget_auto_process_without_mapping !== 'false' && {
      label: formatMessage(messages.unMappedSchedulingLabel),
      path: Paths.INBOUND_REQUEST_FORM,
      group: formatMessage(messages.warningDisableLabel),
    },
  ].filter(Boolean) as DependentLinks[]

  const dependentValueChange = () => {
    if (enable_external_widget_auto_process_without_mapping !== 'false') {
      hideInternalAttendeesOnChange('false')
      relaxInternalAttendeeOnChange('false')
      InternalAttendeeOnChange('true')
    }
  }

  return (
    <>
      {isOpen && (
        <>
          {loading && <ModalSpinner />}
          <EngagementModal
            title={formatMessage(messages.warningModalTitle)}
            dependentLinks={dependentLinks}
            onClose={() => {
              setIsOpen(false)
              dependentValueChange()
            }}
            onConfirm={() => refetch()}
          />
        </>
      )}
      <InternalAttendee />
      <ExternalAttendee />
      {isGeneralMeetingType && (
        <>
          <AttendeeMode />
          {subType === 'Emeet' && <OptionalParticipant />}
          <MeetingHost />
        </>
      )}
    </>
  )
}
