import { getRoomExceptions_getRoomExceptions } from 'gql/types/getRoomExceptions'
import { RoomExceptions } from '../types'
import {
  getISODateFromDateAndTime,
  getISODateString,
  getTimeFromDate,
  getDateFromMinute,
  getDate,
  getMinutes,
} from '../common'
import { TimeSlots } from './types'

export const getExceptions = (
  exceptions: getRoomExceptions_getRoomExceptions[]
) => {
  const flatted = exceptions.flatMap((exception, dayIndex) => {
    const { exceptionDate, availableSlots } = exception

    const timeRange = availableSlots.reduce(
      (acc: [string, string][], range, index, array) => {
        if (index % 2 === 0) {
          acc.push([
            getISODateFromDateAndTime(
              exceptionDate,
              getTimeFromDate(
                getDateFromMinute({ days: dayIndex, minutes: array[index] })
              )
            ),
            getISODateFromDateAndTime(
              exceptionDate,
              array[index + 1] === 1440
                ? '24:00:00.000'
                : getTimeFromDate(
                    getDateFromMinute({
                      days: dayIndex,
                      minutes: array[index + 1],
                    })
                  )
            ),
          ])
        }

        return acc
      },
      []
    )

    return timeRange.length === 0
      ? [[exceptionDate, exceptionDate]]
      : timeRange
          .sort(([a], [b]) => (a < b ? -1 : a > b ? 1 : 0))
          .flatMap(([start, end]: string[]) => {
            const startDateTime = getDate(start)
            const endDateTime = getDate(end)

            return startDateTime.toISODate() === endDateTime.toISODate() ||
              (endDateTime.hour === 0 && endDateTime.minute === 0)
              ? [[start, end]]
              : [
                  [start, startDateTime.endOf('day').toISO()],
                  [endDateTime.startOf('day').toISO(), end],
                ]
          })
  })

  const dateBasedTimeRanges = flatted.reduce((accRanges, range) => {
    const [start, end] = range
    const dateKey = getISODateString(start)

    return {
      ...accRanges,
      [dateKey]: [
        ...(accRanges[dateKey] ?? []),
        ...(start === end ? [] : [range]),
      ],
    }
  }, {} as { [key: string]: string[][] })

  return Object.keys(dateBasedTimeRanges)
    .map(date => ({
      date,
      timeRanges: dateBasedTimeRanges[date] as TimeSlots,
    }))
    .sort((a, b) => +new Date(a.date) - +new Date(b.date))
}

export const getExceptionsInMinutes = (exceptions: RoomExceptions[]) =>
  exceptions.map(({ timeRanges, date }) => {
    const timeRange = timeRanges
      ? timeRanges.flatMap(([start, end]: string[]) => {
          const startDateTime = getDate(start)
          const endDateTime = getDate(end)

          return startDateTime.toISODate() === endDateTime.toISODate() ||
            (endDateTime.hour === 0 && endDateTime.minute === 0)
            ? [getMinutes(start), getMinutes(end) || 1440]
            : [
                getMinutes(start),
                getMinutes(startDateTime.endOf('day').toISO()),

                getMinutes(endDateTime.startOf('day').toISO()),
                getMinutes(end),
              ]
        })
      : null

    return {
      availableSlots: timeRange,
      exceptionDate: date,
    }
  })
