export const ActionsBlocks: FC<ActionsBlocksProps> = ({
  actionTypes,
  name: sectionName,
  headers,
}) => {
  const { formatMessage } = useIntl()
  const [showConfirmation, setShowConfirmation] = useState(false)
  const [actionType, setActionType] = useState<string | null>()
  const [tempNotificationId, setTempNotificationId] = useState('')
  const [headerIndex, setHeaderIndex] = useState<number | null>(null)
  const { systemName: eventName } = useEventLocation().location!.attributes
  const { uuid } = useEngagementType().engagementType
  const location = new URLSearchParams(window.location.search).get('location')
  const { change } = useForm()

  const handleConfirm = () => {
    change(tempNotificationId, false)
    setShowConfirmation(false)
  }

  return (
    <>
      {showConfirmation && (
        <ConfirmationDialog
          heading={formatMessage(modalLabels.modalHeader)}
          message={formatMessage(
            warningMessages[actionType as keyof typeof warningMessages],
            {
              b: (chunks: ReactNode) => <strong>{chunks}</strong>,
              userType:
                headerIndex &&
                formatMessage(
                  headers?.[headerIndex] as MessageDescriptor
                ).toLowerCase(),
            }
          )}
          cancelLabel={formatMessage(modalLabels.cancelButtonLabel)}
          successLabel={formatMessage(modalLabels.successButtonLabel)}
          onClose={() => setShowConfirmation(false)}
          onCancel={() => setShowConfirmation(false)}
          onSuccess={handleConfirm}
        />
      )}
      {actionTypes.map((item, pi) => (
        <SectionWrapper key={`${sectionName}_wrapper_${pi}`}>
          {item.actionColumns.map((actionColumn, i) => (
            <ActionSection
              key={`${sectionName}_actionSection_${i}`}
              $highlight={pi % 2 === 0}
            >
              <ActionType>
                {i === 0 && item.type ? formatMessage(item.type) : ''}
              </ActionType>
              <Action>
                {actionColumn.map(
                  ({ name, id: notificationId, templateType }) => {
                    const isCalendar =
                      name.includes('calendar') || name.includes('ics')
                    const keyName = `mail_actions_${notificationId}`

                    return (
                      <ActionWrapper key={name}>
                        <Field<boolean>
                          name={keyName}
                          type="checkbox"
                          render={({ input: { onChange, checked } }) => (
                            <>
                              <ActionContainer>
                                <StatefulTooltip
                                  content={
                                    templateType === 'GroupNomination'
                                      ? formatMessage(
                                          groupNominationTooltips[
                                            name as GroupNominationActionName
                                          ]
                                        )
                                      : formatMessage(
                                          tooltips[name as MailActionName]
                                        )
                                  }
                                  showArrow
                                  popoverMargin={8}
                                >
                                  <ActionTextWrapper $isFirst={i === 0}>
                                    <NavLink
                                      to={`/events/${eventName}/engagement-types/${uuid}/meeting-type-notifications-details?location=${location}&mail_actions=${name}&notificationId=${notificationId}`}
                                    >
                                      {templateType === 'GroupNomination'
                                        ? formatMessage(
                                            groupNominationDisplayNames[
                                              name as GroupNominationActionName
                                            ]
                                          )
                                        : formatMessage(
                                            displayNames[name as MailActionName]
                                          )}
                                    </NavLink>
                                  </ActionTextWrapper>
                                </StatefulTooltip>
                                <Toggle
                                  ariaLabel={
                                    templateType === 'GroupNomination'
                                      ? formatMessage(
                                          groupNominationDisplayNames[
                                            name as GroupNominationActionName
                                          ]
                                        )
                                      : formatMessage(
                                          displayNames[name as MailActionName]
                                        )
                                  }
                                  checked={checked}
                                  label={' '}
                                  onChange={e => {
                                    if (
                                      isCalendar &&
                                      !e.currentTarget.checked
                                    ) {
                                      item.type &&
                                        setActionType(
                                          formatMessage(
                                            item.type as MessageDescriptor
                                          ).toLowerCase()
                                        )
                                      setTempNotificationId(keyName)
                                      setHeaderIndex(i)
                                      setShowConfirmation(true)
                                    } else {
                                      onChange(e)
                                    }
                                  }}
                                />
                              </ActionContainer>
                              <WarningWrapper>
                                <WarningText
                                  $isVisible={!checked && isCalendar}
                                >
                                  <WarningIcon
                                    size={8}
                                    overrides={warningIconOverride}
                                  />
                                  <FormattedMessage
                                    defaultMessage="{userType} will not receive calendar notification"
                                    description="Warning text"
                                    values={{
                                      br: <br />,
                                      userType:
                                        formatMessage(
                                          headers?.[i] as MessageDescriptor
                                        ) ===
                                        formatMessage(warningUserTypes.attendee)
                                          ? formatMessage(
                                              warningUserTypes.attendee
                                            )
                                          : formatMessage(
                                              warningUserTypes.participant
                                            ),
                                    }}
                                  />
                                </WarningText>
                              </WarningWrapper>
                            </>
                          )}
                        />
                      </ActionWrapper>
                    )
                  }
                )}
              </Action>
            </ActionSection>
          ))}
        </SectionWrapper>
      ))}
    </>
  )
}
