import { ChangeEvent, VFC } from 'react'
import { Textarea } from '@jifflenow/brightui/textarea'
import { FormControl } from '@jifflenow/brightui/form-control'
import { Field } from 'ui-components/Field'

type InputProps = {
  label: string
  name: string
  placeholder: string
}

export const AdvancedSettingsTextArea: VFC<InputProps> = ({
  label,
  name,
  placeholder,
}) => (
  <Field<string>
    name={name}
    parse={value => value}
    render={({ input: { value, onChange }, meta: { error, submitError } }) => (
      <FormControl label={label} error={submitError || error}>
        <Textarea
          name={name}
          value={value}
          placeholder={placeholder}
          onChange={(e: ChangeEvent<HTMLTextAreaElement>) =>
            onChange(e.target.value)
          }
        />
      </FormControl>
    )}
  />
)
