import { VFC, useState } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { IconButton } from '@jifflenow/brightui/button'
import { Trash, Pencil } from '@jifflenow/brightui/icon'
import { formatDate, formatDateRange } from 'utils/dates'
import { getFormattedTimeFromDate, getJSDate } from '../../common'
import { AddEditException as EditException } from '../AddEditException'
import { AddExceptionButton } from '../AddExceptionButton'
import { ListExceptionsProps } from '../types'
import { getGroupedExceptionDates } from './groupExceptionDates'
import { iconButtonOverrides } from './overrides'
import {
  Root,
  ListHeader,
  ExceptionList,
  ExceptionListItem,
  ExceptionDate,
  ExceptionTime,
  ExceptionActions,
  Unavailable,
  ListItemRoot,
} from './styled-components'

const messages = defineMessages({
  unavailable: {
    defaultMessage: 'Unavailable',
    description: 'Button text',
  },
})

export const ListExceptions: VFC<ListExceptionsProps> = ({
  exceptions,
  setExceptions,
}) => {
  const { formatMessage } = useIntl()
  const [isOpen, setIsOpen] = useState(false)
  const [exceptionEditIndex, setExceptionEditIndex] = useState(-1)
  const groupedExceptions = getGroupedExceptionDates(exceptions)

  const handleEdit = (indexForEdit: number) => {
    setExceptionEditIndex(indexForEdit)
    setIsOpen(true)
  }

  const handleDelete = (indexToDelete: number) => {
    const datesToDelete = groupedExceptions.find(
      (_, index) => indexToDelete === index
    )?.date!

    setExceptions(
      exceptions.filter(({ date }) => !datesToDelete.includes(date))
    )
  }

  const onClose = () => {
    setExceptionEditIndex(-1)
    setIsOpen(false)
  }

  return (
    <Root>
      <ListHeader>
        <AddExceptionButton
          exceptions={exceptions}
          setExceptions={setExceptions}
        />
      </ListHeader>
      {isOpen && (
        <EditException
          isOpen={isOpen}
          onClose={onClose}
          exceptions={exceptions}
          setExceptions={setExceptions}
          {...(exceptionEditIndex !== -1
            ? {
                listedExceptions: groupedExceptions,
                listedExceptionEditIndex: exceptionEditIndex,
                timeSlots: groupedExceptions[exceptionEditIndex].timeRanges,
                calendarDates: groupedExceptions[exceptionEditIndex].date,
              }
            : null)}
        />
      )}
      <ExceptionList>
        {groupedExceptions.map(({ date, timeRanges }, exceptionIndex) => (
          <ExceptionListItem key={exceptionIndex}>
            <ListItemRoot>
              <ExceptionDate>
                {date.length === 1
                  ? formatDate(getJSDate(date[0]))
                  : formatDateRange(
                      getJSDate(date[0]),
                      getJSDate(date[date.length - 1])
                    )}
              </ExceptionDate>
              <ExceptionTime>
                {timeRanges.length ? (
                  timeRanges.map((range, index) => (
                    <ExceptionTime key={index}>{`${getFormattedTimeFromDate(
                      range[0]
                    )}  ${getFormattedTimeFromDate(range[1])}`}</ExceptionTime>
                  ))
                ) : (
                  <Unavailable>
                    {formatMessage(messages.unavailable)}
                  </Unavailable>
                )}
              </ExceptionTime>
            </ListItemRoot>

            <ExceptionActions>
              <IconButton
                size={18}
                overrides={iconButtonOverrides}
                onClick={() => handleEdit(exceptionIndex)}
              >
                <Pencil />
              </IconButton>
              <IconButton
                size={18}
                overrides={iconButtonOverrides}
                onClick={() => handleDelete(exceptionIndex)}
              >
                <Trash />
              </IconButton>
            </ExceptionActions>
          </ExceptionListItem>
        ))}
      </ExceptionList>
    </Root>
  )
}
