import { VFC, useState, useMemo, useEffect, ReactNode } from 'react'
import { useHistory, Prompt } from 'react-router-dom'
import { defineMessages, useIntl } from 'react-intl'
import {
  mutateRoomAvailabilities,
  mutateRoomExceptions,
  useApolloClient,
  useGetEngagementTypeSettings,
  validateRoomAvailabilitiesQuery,
  validateRoomExceptionsQuery,
  useRoomAvailabilitiesQuery,
} from 'gql'
import {
  Tabs,
  Tab,
  TabTitle,
  getTabOverrides,
} from '@jifflenow/brightui/tabs-motion'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { CopyOutlined } from '@jifflenow/brightui/icon'
import { KIND } from '@jifflenow/brightui/button'
import { useEventLocation } from 'providers/LocationProvider'
import { getEngagementTypeSettings_getEngagementTypeSettings_value_CustomNameValue } from 'gql/types/getEngagementTypeSettings'
import { upsertRoomAvailabilitiesVariables } from 'gql/types/upsertRoomAvailabilities'
import { RoomAvailabilityLocationTypes } from 'gql/types/globalTypes'
import { upsertRoomExceptionsVariables } from 'gql/types/upsertRoomExceptions'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { Body, Header, Container } from '../common'
import { useEngagementType } from '../EngagementTypeProvider'
import { ConfirmationDialog, getDurationInDays } from './common'
import {
  getAvailabilityInMinutes,
  getNonRecurringAvailabilities,
  getRecurringAvailabilities,
} from './Availability'
import { Footer } from './Footer'
import {
  Root,
  SetAvailabilityHeaderContainer,
  CopyButtonLabel,
  StyledButtonLabel,
  StyledButtonWrapper,
  StyledHeading,
  Wrapper,
  NoteMessage,
} from './styled-components'
import { getTabsOverrides, tabOverrides } from './overrides'
import {
  SelectOptions,
  LocationTypes,
  RoomAvailabilities,
  RoomExceptions,
  RoomAvailabilityProps,
} from './types'
import { AvailabilitySelect } from './AvailabilitySelect'
import { CopyRoomAvailability } from './CopyRoomAvailability'
import { Exceptions, getExceptionsInMinutes } from './Exceptions'
import { Availabilities, Availability } from './Availability'

const messages = defineMessages({
  locationTypes: {
    defaultMessage: 'Location Types',
    description: 'Page title',
  },
  success: {
    defaultMessage: 'Availabilities Saved Successfully.',
    description: 'Success message',
  },
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  sectionTitle: {
    defaultMessage: 'Set',
    description: 'Section title',
  },
  setAvailability: {
    defaultMessage: 'Set Availability',
    description: 'Section title',
  },
  roomsLabel: {
    defaultMessage: 'Onsite Locations',
    description: 'Tab Label',
  },
  offsiteLabel: {
    defaultMessage: 'Offsites',
    description: 'Tab Label',
  },
  boothTourLabel: {
    defaultMessage: 'Designated Location',
    description: 'Tab Label',
  },
  copyRoomAvailabilityLabel: {
    defaultMessage: 'Copy {room} availability to other locations',
    description: 'Button label',
  },
  confirmationModalHeader: {
    defaultMessage: 'You have unsaved changes',
    description: 'Modal heading',
  },
  confirmationModalMessage: {
    defaultMessage:
      'Conflicts with planned meeting(s). Updating availability will be applicable only to future meetings and not for existing meeting',
    description: 'Confirmation message',
  },
  unsavedChangesConfirmationMessage: {
    defaultMessage: 'Please save the changes before proceeding',
    description: 'Confirmation message',
  },
  unsavedChangesConfirmationMessageToCopy: {
    defaultMessage: 'Please save the changes before proceeding to copy',
    description: 'Confirmation message',
  },
  confirmationMessageToAvailabilityDates: {
    defaultMessage:
      'Please save the changes before proceeding to Availability Dates',
    description: 'Confirmation message',
  },
  confirmationMessageToExceptions: {
    defaultMessage:
      'Please save the changes before proceeding to Exception Dates',
    description: 'Confirmation message',
  },
  discardButtonLabel: {
    defaultMessage: 'Discard',
    description: 'Button label',
  },
  saveButtonLabel: {
    defaultMessage: 'Save and Proceed',
    description: 'Button label',
  },
  availablityNoteMessage: {
    defaultMessage:
      '<b>Note:</b> Availability by date cannot be less than the minimum duration or time increments.',
    description: 'Help text',
  },
})

const names = ['offsite_enabled', 'booth_tour_enabled']

const RoomAvailability: VFC<RoomAvailabilityProps> = ({
  weekwise,
  setAvailabilities,
  locationType,
  setHandleDiscard,
}) => {
  const { id: engagementTypeId } = useEngagementType().engagementType

  const { getRoomAvailabilities = [] } = useRoomAvailabilitiesQuery({
    variables: {
      engagementTypeId,
      availabilityLocationType: locationType,
    },
    fetchPolicy: 'cache-and-network',
  })

  const availabilities: RoomAvailabilities[] = useMemo(
    () =>
      weekwise
        ? getRecurringAvailabilities(getRoomAvailabilities)
        : getNonRecurringAvailabilities(getRoomAvailabilities),
    [getRoomAvailabilities, weekwise]
  )

  return availabilities.length ? (
    <Availability
      weekwise={weekwise}
      availabilities={availabilities}
      setAvailabilities={setAvailabilities}
      setHandleDiscard={setHandleDiscard}
    />
  ) : null
}

export const SetAvailability: VFC = () => {
  const { formatMessage } = useIntl()
  const client = useApolloClient()
  const history = useHistory()
  const unload = useBeforeUnload()
  const { Room, Offsite, BoothTour } = RoomAvailabilityLocationTypes
  const [showLoader, setShowLoader] = useState(false)
  const [exceptions, setExceptions] = useState<RoomExceptions[]>([])
  const [availabilities, setAvailabilities] = useState<Availabilities[]>([])
  const [isOpen, setIsOpen] = useState(false)
  const [availabilityConfirmation, setAvailabilityConfirmation] = useState(
    false
  )
  const { id: engagementTypeId } = useEngagementType().engagementType
  const [status, setStatus] = useState<SelectOptions>(
    SelectOptions.AVAILABILITY
  )
  const [statusToBeChanged, setStatusToBeChanged] = useState<SelectOptions>(
    SelectOptions.AVAILABILITY
  )
  const [activeTab, setActiveTab] = useState<RoomAvailabilityLocationTypes>(
    Room
  )
  const [copyChange, setCopyChange] = useState<boolean>(false)
  const [
    tabToBeChanged,
    setTabToBeChanged,
  ] = useState<RoomAvailabilityLocationTypes>(Room)
  const [
    unsavedChangesConfirmation,
    setUnsavedChangesConfirmation,
  ] = useState<boolean>(false)
  const [dirty, setDirty] = useState(false)

  const [handleDiscard, setHandleDiscard] = useState<
    (arg?: () => void) => void
  >(() => {})

  useEffect(() => {
    setDirty(!!(availabilities.length || exceptions.length))
  }, [availabilities.length, exceptions.length])

  useEffect(() => {
    unload(dirty)
  }, [dirty, unload])

  const hasError = availabilities
    .flatMap(({ availableSlots }) => availableSlots)
    .some(({ error }) => error)

  const { getEngagementTypeSettings = [] } = useGetEngagementTypeSettings({
    variables: {
      engagementTypeId,
      names,
    },
    fetchPolicy: 'cache-and-network',
    onError: () => {
      showErrorToast(formatMessage(messages.genericError))
    },
  })

  const getModalLabel = () => {
    if (activeTab === Offsite) {
      return formatMessage(messages.offsiteLabel)
    } else if (activeTab === BoothTour) {
      return formatMessage(messages.boothTourLabel)
    }

    return formatMessage(messages.roomsLabel)
  }

  const renderTabButton = () =>
    engagementTypeSettings?.offsite_enabled === 'true' ||
    engagementTypeSettings?.booth_tour_enabled === 'true' ? (
      <StyledButtonWrapper
        onClick={() => {
          if (dirty) {
            setCopyChange(true)
            setUnsavedChangesConfirmation(true)
          } else {
            setIsOpen(true)
          }
        }}
      >
        <StyledButtonLabel type="button" kind={KIND.minimal}>
          <CopyOutlined color="primaryA" size={16} />
          <CopyButtonLabel>
            {formatMessage(messages.copyRoomAvailabilityLabel, {
              room: getModalLabel(),
            })}
          </CopyButtonLabel>
        </StyledButtonLabel>
      </StyledButtonWrapper>
    ) : null

  const { startDate, endDate } = useEventLocation().location?.attributes ?? {}
  const weekwise = getDurationInDays(startDate, endDate) >= 14

  const engagementTypeSettings: {
    offsite_enabled?: 'true' | 'false'
    booth_tour_enabled?: 'true' | 'false'
  } = useMemo(
    () =>
      getEngagementTypeSettings.reduce(
        (acc, { name, value }) => ({
          ...acc,
          [name]: (value as getEngagementTypeSettings_getEngagementTypeSettings_value_CustomNameValue)
            .value,
        }),
        {}
      ),
    [getEngagementTypeSettings]
  )

  const availabilityLocationTypes = [
    {
      label: formatMessage(messages.roomsLabel),
      location: Room,
    },
    engagementTypeSettings.offsite_enabled === 'true' && {
      label: formatMessage(messages.offsiteLabel),
      location: Offsite,
    },
    engagementTypeSettings.booth_tour_enabled === 'true' && {
      label: formatMessage(messages.boothTourLabel),
      location: BoothTour,
    },
  ].filter(Boolean) as LocationTypes[]

  const handleDialogClose = () => {
    setAvailabilityConfirmation(false)
  }

  const handleDiscardChangesDialogClose = () => {
    setUnsavedChangesConfirmation(false)
    if (copyChange && availabilities.length) {
      setAvailabilities([])
    }
    if (copyChange && exceptions.length) {
      setExceptions([])
    }
    if (availabilities.length) {
      setAvailabilities([])
    }
    if (exceptions.length) {
      setExceptions([])
    }
    if (activeTab !== tabToBeChanged) {
      setActiveTab(tabToBeChanged)
    }
    if (copyChange) {
      setIsOpen(true)
      setCopyChange(false)
    }
    if (statusToBeChanged !== status) {
      setStatus(statusToBeChanged)
    }
  }

  const availabilityExceptionsData = ():
    | upsertRoomAvailabilitiesVariables
    | upsertRoomExceptionsVariables => ({
    availabilityLocationType: activeTab,
    ...(status === SelectOptions.AVAILABILITY
      ? { availabilities: getAvailabilityInMinutes(availabilities) }
      : { exceptions: getExceptionsInMinutes(exceptions) }),
    engagementTypeId,
    weekwise,
  })

  const unsavedConfirmationDialogDesc = () =>
    formatMessage(
      copyChange
        ? messages.unsavedChangesConfirmationMessageToCopy
        : statusToBeChanged !== status
        ? statusToBeChanged === SelectOptions.AVAILABILITY
          ? messages.confirmationMessageToAvailabilityDates
          : messages.confirmationMessageToExceptions
        : messages.unsavedChangesConfirmationMessage
    )

  useEffect(() => {
    setStatusToBeChanged(SelectOptions.AVAILABILITY)
    setTabToBeChanged(RoomAvailabilityLocationTypes.Room)
    setActiveTab(RoomAvailabilityLocationTypes.Room)
  }, [engagementTypeId])

  useEffect(() => {
    if (statusToBeChanged !== status && dirty) {
      setUnsavedChangesConfirmation(true)
    } else {
      setStatus(statusToBeChanged)
    }
    if (copyChange || tabToBeChanged !== activeTab) {
      setStatusToBeChanged(status)
    }
  }, [copyChange, status, statusToBeChanged, tabToBeChanged, activeTab, dirty])

  const checkAvailabilityExceptions = async () => {
    if (unsavedChangesConfirmation) {
      setUnsavedChangesConfirmation(false)
    }
    setAvailabilities(
      availabilities.map(availability => {
        const { availableSlots } = availability

        return {
          ...availability,
          availableSlots: availableSlots.sort((a, b) =>
            a.start < b.start ? -1 : a.start > b.start ? 1 : 0
          ),
        }
      })
    )
    try {
      setShowLoader(true)
      if (status === SelectOptions.AVAILABILITY) {
        const {
          data: { validateRoomAvailabilities },
        } = await validateRoomAvailabilitiesQuery(client, {
          variables: availabilityExceptionsData(),
          fetchPolicy: 'network-only',
        })
        setShowLoader(false)
        validateRoomAvailabilities
          ? handleSave()
          : setAvailabilityConfirmation(true)
      } else {
        const {
          data: { validateRoomExceptions },
        } = await validateRoomExceptionsQuery(client, {
          variables: availabilityExceptionsData(),
          fetchPolicy: 'network-only',
        })
        setShowLoader(false)
        validateRoomExceptions
          ? handleSave()
          : setAvailabilityConfirmation(true)
      }
    } catch (error) {
      if (activeTab !== tabToBeChanged) {
        setTabToBeChanged(activeTab)
      }
      if (copyChange) {
        setCopyChange(false)
      }
      if (statusToBeChanged !== status) {
        setStatusToBeChanged(status)
      }
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
    }
  }

  const handleSave = async () => {
    if (availabilityConfirmation) {
      setAvailabilityConfirmation(false)
    }
    try {
      setShowLoader(true)
      if (status === SelectOptions.AVAILABILITY) {
        await mutateRoomAvailabilities(client, {
          variables: availabilityExceptionsData(),
        })
      } else {
        await mutateRoomExceptions(client, {
          variables: availabilityExceptionsData(),
        })
      }
      if (availabilities.length) {
        setAvailabilities([])
      }
      if (exceptions.length) {
        setExceptions([])
      }
      if (activeTab !== tabToBeChanged) {
        setActiveTab(tabToBeChanged)
      }
      if (copyChange) {
        setIsOpen(true)
        setCopyChange(false)
      }
      if (statusToBeChanged !== status) {
        setStatus(statusToBeChanged)
      }
      setShowLoader(false)
      showSuccessToast(formatMessage(messages.success))
    } catch (error) {
      if (activeTab !== tabToBeChanged) {
        setTabToBeChanged(activeTab)
      }
      if (copyChange) {
        setCopyChange(false)
      }
      if (statusToBeChanged !== status) {
        setStatusToBeChanged(status)
      }
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
    }
  }

  return (
    <>
      {showLoader && <ModalSpinner />}
      <Prompt when={dirty} message={formatMessage(messages.promptMessage)} />
      <Root>
        <Header />
        <Body>
          <Container>
            <SetAvailabilityHeaderContainer>
              {weekwise ? (
                <>
                  <StyledHeading>
                    {formatMessage(messages.sectionTitle)}
                  </StyledHeading>
                  <AvailabilitySelect
                    type={status}
                    onChange={setStatusToBeChanged}
                  />
                </>
              ) : (
                <StyledHeading>
                  {formatMessage(messages.setAvailability)}
                </StyledHeading>
              )}
              <Wrapper>
                <NoteMessage>
                  {formatMessage(messages.availablityNoteMessage, {
                    b: (chunks: ReactNode) => <strong>{chunks}</strong>,
                  })}
                </NoteMessage>
              </Wrapper>
            </SetAvailabilityHeaderContainer>

            <Tabs
              overrides={getTabsOverrides(renderTabButton)}
              activeKey={activeTab}
              onChange={({ activeKey }) => {
                setTabToBeChanged(activeKey as RoomAvailabilityLocationTypes)
                if (dirty) {
                  setUnsavedChangesConfirmation(true)
                } else {
                  setActiveTab(activeKey as RoomAvailabilityLocationTypes)
                }
              }}
            >
              {availabilityLocationTypes.map(({ label, location }) => (
                <Tab
                  title={<TabTitle>{label}</TabTitle>}
                  overrides={getTabOverrides(tabOverrides)}
                  key={location}
                >
                  {status === SelectOptions.AVAILABILITY ? (
                    <RoomAvailability
                      weekwise={weekwise}
                      status={status}
                      locationType={location}
                      setAvailabilities={setAvailabilities}
                      setHandleDiscard={setHandleDiscard}
                    />
                  ) : (
                    <Exceptions
                      weekwise={weekwise}
                      status={status}
                      locationType={location}
                      setExceptions={setExceptions}
                      setHandleDiscard={setHandleDiscard}
                    />
                  )}
                </Tab>
              ))}
            </Tabs>
          </Container>
          {dirty ? (
            <Footer
              onDiscardClick={handleDiscard}
              onSaveClick={checkAvailabilityExceptions}
              hasError={hasError}
              onCancelClick={() => history.goBack()}
            />
          ) : null}
        </Body>
      </Root>
      {isOpen && (
        <CopyRoomAvailability
          isOpen
          engagementTypeSettings={engagementTypeSettings}
          activeTab={activeTab}
          getModalLabel={getModalLabel}
          onClose={() => setIsOpen(false)}
        />
      )}
      {availabilityConfirmation ? (
        <ConfirmationDialog
          heading={formatMessage(messages.confirmationModalHeader)}
          message={formatMessage(messages.confirmationModalMessage)}
          cancelLabel={formatMessage(messages.discardButtonLabel)}
          successLabel={formatMessage(messages.saveButtonLabel)}
          onClose={handleDialogClose}
          onCancel={handleDialogClose}
          onSuccess={handleSave}
        />
      ) : null}
      {unsavedChangesConfirmation ? (
        <ConfirmationDialog
          heading={formatMessage(messages.confirmationModalHeader)}
          message={unsavedConfirmationDialogDesc()}
          cancelLabel={formatMessage(messages.discardButtonLabel)}
          successLabel={formatMessage(messages.saveButtonLabel)}
          onClose={handleDiscardChangesDialogClose}
          onCancel={handleDiscardChangesDialogClose}
          onSuccess={checkAvailabilityExceptions}
        />
      ) : null}
    </>
  )
}
