import { FC, useEffect, useState, VFC } from 'react'
import { Radio } from '@jifflenow/brightui/radio'
import { defineMessages, useIntl } from 'react-intl'
import { Field } from 'ui-components/Field'
import { FieldRenderProps, useField } from 'react-final-form'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { useEventLocation } from 'providers/LocationProvider'
import { instructionRadioOverride } from '../overrides'
import { DependentFieldValues, DependentLinks } from '../types'
import { EngagementModal } from '../../common/EngagementModal'
import { RefetchProps } from '../../common/types'
import { Paths } from '../../Paths'

const messages = defineMessages({
  mappedSchedulingLabel: {
    defaultMessage: 'Auto schedule - mapped request page',
    description: 'Field label',
  },
  warningEnableLabel: {
    defaultMessage: 'Enable the following',
    description: 'Dependent settings group header',
  },
  mappingLabel: {
    defaultMessage: 'Mapping',
    description: 'Dependent setting label',
  },
  warningModalTitle: {
    defaultMessage:
      'Please ensure following settings are done before enabling auto schedule mapped request page.',
    description: 'Dependent settings modal title',
  },
})

const AutoProcessField: FC<FieldRenderProps<string> & RefetchProps> = ({
  input: { value, onChange },
  refetch,
  loading,
  dependentFieldValues,
}) => {
  const { formatMessage } = useIntl()
  const [isOpen, setIsOpen] = useState(false)
  const { default_mapping } = dependentFieldValues

  const { version } = useEventLocation().location!.attributes

  const { onChange: onChangeAutoProcessWithoutMapping } = useField(
    'enable_external_widget_auto_process_without_mapping'
  ).input

  useEffect(() => {
    value === 'true' && setIsOpen(default_mapping !== 'true')
  }, [default_mapping, value])

  const dependentLinks = [
    default_mapping !== 'true' && {
      label: formatMessage(messages.mappingLabel),
      path: Paths.CSM_ONLY,
      group: formatMessage(messages.warningEnableLabel),
    },
  ].filter(Boolean) as DependentLinks[]

  return (
    <>
      {isOpen && (
        <>
          {loading && <ModalSpinner />}
          <EngagementModal
            title={formatMessage(messages.warningModalTitle)}
            dependentLinks={dependentLinks}
            onClose={() => {
              setIsOpen(false)
              default_mapping !== 'true' && onChange('false')
            }}
            onConfirm={() => refetch()}
            version={version}
          />
        </>
      )}
      <Radio
        checked={value === 'true'}
        onChange={e => {
          onChange(e.target.value)
          onChangeAutoProcessWithoutMapping('false')
        }}
        value="true"
        overrides={instructionRadioOverride}
        labelPlacement="right"
        name="mapped_scheduling"
      >
        {formatMessage(messages.mappedSchedulingLabel)}
      </Radio>
    </>
  )
}

export const MethodRequestMappedField: VFC<
  RefetchProps & DependentFieldValues
> = ({ refetch, loading, dependentFieldValues }) => (
  <Field<string>
    name="enable_external_widget_auto_process"
    render={props => (
      <AutoProcessField
        {...props}
        refetch={refetch}
        loading={loading}
        dependentFieldValues={dependentFieldValues}
      />
    )}
  />
)
