import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useEventLocation } from 'providers/LocationProvider'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import {
  EngagementTypeToggle,
  EngagementTypeToggleWithDesc,
  MeetingBreakDurationLabel,
} from '../common'
import { useEngagementType } from '../EngagementTypeProvider'
import { OptionalParticipant } from '../Participants/OptionalParticipant'
import {
  BorderStyled,
  Section,
  SectionTitle,
  InputFieldContainer,
} from './styled-components'
import { MeetingBreakDurationSelect } from './MeetingBreakDurationSelect'
import { MeetingSummitHookSelect } from './MeetingSummitHookSelect'
import { BreakDurationInput } from './BreakDurationInput'
import { FormFieldProps } from './types'
import { Mappings } from './Mappings'
import { GuidedTour } from './GuidedTour'
import { ManualScheduling } from './ManualScheduling'

const messages = defineMessages({
  meetingSubmitHookTitle: {
    defaultMessage: 'Redirect after meeting creation',
    description: 'Section title',
  },
  meetingBreakEnabled: {
    defaultMessage: 'Enable break',
    description: 'Field label',
  },
  meetingBreakHelpText: {
    defaultMessage:
      'NOTE: Break settings cannot be modified if there are active meetings',
    description: 'Help text',
  },
  meetingBreakMeetingDuration: {
    defaultMessage: 'Meeting duration greater than or equal to',
    description: 'Field label',
  },
  meetingBreakDurationLabel: {
    defaultMessage: 'Set break duration',
    description: 'Field label',
  },
  meetingSummitHookLabel: {
    defaultMessage: 'Action [Dropdown with values "No Action" and "Redirect"]',
    description: 'Field label',
  },
  showMeetingBreakNotification: {
    defaultMessage: 'Notify user to break inclusion while requesting meetings',
    description: 'Dependent settings modal title',
  },
})

export const FormField: VFC<FormFieldProps> = ({
  hasActiveMeetings,
  ...rest
}) => {
  const { formatMessage } = useIntl()

  const { guided_tour_survey } = useEventLocation().configs!

  const { subType } = useEventLocation().location?.attributes ?? {}
  const {
    masterEngagementType,
  } = useEngagementType().engagementType.templateEngagementType
  const isGeneralMeetingType =
    masterEngagementType === MasterEngagementTypes.General

  return (
    <>
      {isGeneralMeetingType && (
        <>
          <Section>
            <EngagementTypeToggleWithDesc
              label={formatMessage(messages.meetingBreakEnabled)}
              name="meeting_break_enabled"
              desc={formatMessage(messages.meetingBreakHelpText)}
              disabled={hasActiveMeetings}
            >
              <InputFieldContainer>
                <MeetingBreakDurationLabel>
                  {formatMessage(messages.meetingBreakMeetingDuration)}
                </MeetingBreakDurationLabel>
                <BreakDurationInput
                  name="meeting_break_meeting_duration"
                  disabled={hasActiveMeetings}
                />
              </InputFieldContainer>
              <MeetingBreakDurationSelect
                label={formatMessage(messages.meetingBreakDurationLabel)}
                name="meeting_break_duration"
                disabled={hasActiveMeetings}
              />
              <EngagementTypeToggle
                label={formatMessage(messages.showMeetingBreakNotification)}
                name="show_meeting_break_notification"
              />
            </EngagementTypeToggleWithDesc>
            <BorderStyled />
          </Section>
          <Section>
            <SectionTitle>
              {formatMessage(messages.meetingSubmitHookTitle)}
            </SectionTitle>
            <MeetingSummitHookSelect
              name="meeting_submit_hook"
              label={formatMessage(messages.meetingSummitHookLabel)}
            />
          </Section>
        </>
      )}
      <Mappings {...rest} />
      {isGeneralMeetingType && (
        <>
          {guided_tour_survey === 'true' && <GuidedTour />}
          <ManualScheduling {...rest} />
          {subType !== 'Emeet' && <OptionalParticipant />}
        </>
      )}
    </>
  )
}
