import { VFC, useState } from 'react'
import { defineMessages, FormattedMessage, useIntl } from 'react-intl'
import { Modal, ModalBody, ModalHeader } from '@jifflenow/brightui/modal'
import { formatDate } from 'utils/dates'
import {
  CopySlotsModalBodyWrapper,
  ModalDescription,
  getJSDate,
  CopyModalFooter,
} from '../common'
import { CopyDaySlotsTo } from './CopyDaySlotsTo'
import { modalOverrides } from './overrides'
import { Availabilities, CopyAvailabilityProps } from './types'

export const CopyAvailability: VFC<CopyAvailabilityProps> = props => {
  const { formatMessage } = useIntl()
  const {
    sourceWeekDay,
    availabilities,
    setAvailabilities,
    weekwise,
    ...modalProps
  } = props

  const [targetWeekDays, setTargetWeekDays] = useState<string[]>([])

  const addCopyDestinationDays = (availabilityDate: string) => {
    setTargetWeekDays([...targetWeekDays, availabilityDate])
  }

  const messages = defineMessages({
    copyAvailabilityLabel: {
      defaultMessage: 'Select dates to copy the slots of "{day}" to',
      description: 'Copy availability label',
    },
  })

  const handleCopySlots = () => {
    if (sourceWeekDay) {
      const slots = availabilities.find((availability: Availabilities) =>
        weekwise
          ? availability.availabilityDay === sourceWeekDay
          : availability.availabilityDate === sourceWeekDay
      )

      setAvailabilities(
        availabilities.map((availability: Availabilities) =>
          targetWeekDays.includes(
            weekwise
              ? availability.availabilityDay!
              : availability.availabilityDate!
          )
            ? { ...availability, availableSlots: slots!.availableSlots }
            : availability
        )
      )

      modalProps.onClose()
      setTargetWeekDays([])
    }
  }

  return (
    <Modal {...modalProps} overrides={modalOverrides}>
      <ModalHeader>
        <FormattedMessage
          defaultMessage="Copy availability of {day} to"
          description="Header title"
          values={{
            day: weekwise
              ? sourceWeekDay
              : formatDate(getJSDate(sourceWeekDay)),
          }}
        />
      </ModalHeader>
      <ModalDescription>
        {formatMessage(messages.copyAvailabilityLabel, {
          day: weekwise ? sourceWeekDay : formatDate(getJSDate(sourceWeekDay)),
        })}
      </ModalDescription>
      <ModalBody>
        <CopySlotsModalBodyWrapper>
          {availabilities
            ?.filter((availability: Availabilities) =>
              weekwise
                ? availability.availabilityDay !== sourceWeekDay
                : availability.availabilityDate !== sourceWeekDay
            )
            .map((availability: Availabilities, key: number) => (
              <CopyDaySlotsTo
                weekwise={weekwise}
                key={key}
                weekDay={
                  weekwise
                    ? availability.availabilityDay!
                    : availability.availabilityDate!
                }
                addCopyDestinationDays={addCopyDestinationDays}
              />
            ))}
        </CopySlotsModalBodyWrapper>
      </ModalBody>
      <CopyModalFooter
        onClose={modalProps.onClose}
        handleCopySlots={handleCopySlots}
      />
    </Modal>
  )
}
