import { useEffect, FC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Select } from '@jifflenow/brightui/select'
import { Prompt } from 'react-router-dom'
import { FormRenderProps } from 'react-final-form'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { Container, Footer } from '../common'
import { StyledFormSurvey } from '../common'
import { SurveyStatusField } from './SurveyStatusField'
import { FormProps } from './types'
import {
  FieldWrapper,
  Flex,
  SectionTitle,
  SelectOptionsDesc,
  SelectWrapper,
} from './styled-components'
import { RolesAndPermissionFormValues } from './types'
import { SurveyTab } from './SurveyTab'

const messages = defineMessages({
  surveyLabel: {
    defaultMessage: 'Survey for',
    description: 'Field label',
  },
  placeholderSurveyLabel: {
    defaultMessage: 'Select survey',
    description: 'placeholder text',
  },
  SurveyDesc: {
    defaultMessage:
      'Only the surveys enabled for this engagement type are listed here.',
    description: 'Description label',
  },
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
})

export const Form: FC<
  FormProps & FormRenderProps<RolesAndPermissionFormValues>
> = ({
  options,
  surveyValue,
  setSurveyValue,
  handleSubmit,
  dirty,
  surveyMasterstatus,
}) => {
  const { formatMessage } = useIntl()
  const unload = useBeforeUnload()

  useEffect(() => {
    unload(dirty)
  }, [dirty, unload])

  return (
    <StyledFormSurvey onSubmit={handleSubmit} noValidate>
      <Prompt when={dirty} message={formatMessage(messages.promptMessage)} />
      <Container>
        <FieldWrapper>
          <Flex>
            <SectionTitle>{formatMessage(messages.surveyLabel)}</SectionTitle>
            <SelectWrapper>
              <Select
                clearable={false}
                options={options}
                value={surveyValue}
                valueKey={'value'}
                placeholder={formatMessage(messages.placeholderSurveyLabel)}
                onChange={(option: any) => setSurveyValue(option.value)}
                dataPendo="select-survey"
              />
            </SelectWrapper>
            <SurveyStatusField surveyMasterstatus={!!surveyMasterstatus} />
          </Flex>
          <SelectOptionsDesc>
            {formatMessage(messages.SurveyDesc)}
          </SelectOptionsDesc>
          <SurveyTab />
        </FieldWrapper>
      </Container>
      <Footer />
    </StyledFormSurvey>
  )
}
