import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Field } from 'react-final-form'
import { Input } from '@jifflenow/brightui/input'
import { FormControlStyled } from './styled-components'

const messages = defineMessages({
  engagementNameLabel: {
    defaultMessage: 'Selected Engagement Template',
    description: 'Field label',
  },
})

export const EngagementTemplateField: VFC = () => {
  const { formatMessage } = useIntl()

  return (
    <Field<string> name="engagementTemplateName" type="text">
      {({ input }) => (
        <FormControlStyled label={formatMessage(messages.engagementNameLabel)}>
          <Input
            {...input}
            id="engagementTemplateName"
            autoComplete="off"
            disabled
          />
        </FormControlStyled>
      )}
    </Field>
  )
}
