import { FC, useMemo, useState, VFC, useEffect, useRef } from 'react'
import { Prompt } from 'react-router-dom'
import { Config, FORM_ERROR } from 'final-form'
import { Form as FinalForm, FormRenderProps } from 'react-final-form'
import { defineMessages, useIntl } from 'react-intl'
import { QueryFunction, useQuery, useQueryClient } from 'react-query'
import {
  fetchEngagementTypeSettingsQuery,
  mutateBulkUpsertEngagementSettings,
  useApolloClient,
} from 'gql'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import {
  getEngagementTypeSettingsVariables,
  getEngagementTypeSettings_getEngagementTypeSettings,
  getEngagementTypeSettings_getEngagementTypeSettings_value_CustomNameValue,
} from 'gql/types/getEngagementTypeSettings'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { BulkEngagementSettingUpdateInput } from 'gql/types/globalTypes'
import {
  bulkUpsertEngagementSettingsVariables,
  bulkUpsertEngagementSettings_bulkUpsertEngagementSettings_engagementSettings_value_CustomNameValue,
} from 'gql/types/bulkUpsertEngagementSettings'
import { fetchEngagementTypeSettings, upsertEngagementTypeSettings } from 'rest'
import { useEventLocation } from 'providers/LocationProvider'
import {
  Body,
  Footer,
  Header,
  StyledForm,
  Container,
  usePageTitle,
} from '../common'
import { useEngagementType } from '../EngagementTypeProvider'
import { RefetchProps } from '../common/types'
import { Heading } from './styled-components'
import { FormFields } from './FormFields'
import { IrpContentLoader } from './IrpContentLoader'
import {
  DependentFieldValues,
  FormValues,
  EngagementTypeSettingsRestParams,
  EngagementTypeSettingsRestResponse,
  EngagementTypeSettingsUpsertRestPayload,
} from './types'

type QueryKeyRequestType =
  | getEngagementTypeSettingsVariables
  | EngagementTypeSettingsRestParams
type QueryFuncResponseType = EngagementTypeSettingsRestResponse['data']

const messages = defineMessages({
  Irp: {
    defaultMessage: 'Inbound Request Page',
    description: 'Page title',
  },
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
  sectionTitle: {
    defaultMessage: 'Inbound Request Page',
    description: 'Section title',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  successMessage: {
    defaultMessage: 'Your changes have been saved successfully',
    description: 'Success message',
  },
  updateError: {
    defaultMessage:
      '{failureCount, number} out of {totalCount, number} {totalCount, plural, one {option} other {options}} failed to save',
    description: 'Error message',
  },
  filedError: {
    defaultMessage: 'Error saving changes',
    description: 'Field error message',
  },
})

const LoadingShimmer = () => (
  <Body>
    <Container>
      <IrpContentLoader />
    </Container>
  </Body>
)

const Form: FC<
  FormRenderProps<FormValues> & DependentFieldValues & RefetchProps
> = ({ handleSubmit, dirty, refetch, loading, dependentFieldValues }) => {
  const { formatMessage } = useIntl()

  const shimmerShown = useRef(false)

  useEffect(() => {
    if (!loading && !shimmerShown.current) {
      shimmerShown.current = true
    }
  }, [loading])

  return (
    <StyledForm onSubmit={handleSubmit} noValidate>
      <Prompt when={dirty} message={formatMessage(messages.promptMessage)} />
      <Header />
      {loading && !shimmerShown.current ? (
        <LoadingShimmer />
      ) : (
        <Body>
          <Container>
            <Heading>{formatMessage(messages.sectionTitle)}</Heading>
            <FormFields
              refetch={refetch}
              loading={loading}
              dependentFieldValues={dependentFieldValues}
            />
          </Container>
          <Footer />
        </Body>
      )}
    </StyledForm>
  )
}

const fieldNames = [
  'enable_external_widget',
  'enable_external_widget_url',
  'activity_external_widget_url',
  'enable_irp_scheduling_logic_buffer',
  'irp_meeting_buffer',
  'enable_external_widget_auto_process',
  'enable_external_widget_auto_process_without_mapping',
  'irp_request_success_message_text',
  'show_unmapped_users_for_topic_IRP',
  'show_unmapped_rooms_for_topic_IRP',
  'enable_irp_instant_meeting',
  'enable_irp_instant_meeting_url',
  'irp_instant_meeting_url',
  'irp_instant_meeting_label',
  'irp_instant_meeting_buffer',
  'disable_url',
  'warning',
]

const dependentFieldNames = [
  'theme',
  'default_mapping',
  'hide_internal_attendees_in_book_meeting',
  'internal_attendee_presence',
  'relax_internal_attendee_presence_for_mm',
  'offsite_enabled',
  'enable_manual_scheduling',
  'approval_process',
  'enable_external_widget_url',
  'enable_irp_instant_meeting',
]

const getEngagementTypeSettingsQueryFunc = (
  client: any
): QueryFunction<
  QueryFuncResponseType,
  ['getEngagementTypeSettingsResponse', string, QueryKeyRequestType]
> => async ({ queryKey: [, version, params] }) => {
  if (version === '1') {
    const data = await fetchEngagementTypeSettings(
      params as EngagementTypeSettingsRestParams
    )

    return data.data
  } else {
    const {
      data: { getEngagementTypeSettings },
    } = await fetchEngagementTypeSettingsQuery(client, {
      variables: params as getEngagementTypeSettingsVariables,
      fetchPolicy: 'network-only',
    })

    const data = getEngagementTypeSettings.map(
      ({
        name,
        value: { display_name, value },
      }: {
        name: getEngagementTypeSettings_getEngagementTypeSettings['name']
        value: getEngagementTypeSettings_getEngagementTypeSettings_value_CustomNameValue
      }) => ({
        name,
        displayName: display_name,
        value,
      })
    )

    return data
  }
}

const filterEngagementSettings = (
  response: QueryFuncResponseType,
  fields: string[],
  version: string
) =>
  version === '1'
    ? response
        .filter(({ name }) => !!fields.find(key => key === name))
        .map(v => {
          const tval =
            v.name === 'theme'
              ? JSON.parse(v.value)
              : v.value === 'yes'
              ? 'true'
              : v.value === 'no'
              ? 'false'
              : v.value

          return { ...v, value: tval }
        })
    : response.filter(({ name }) => !!fields.find(key => key === name))

export const Irp: VFC = () => {
  const { formatMessage } = useIntl()
  usePageTitle(formatMessage(messages.Irp))
  const client = useApolloClient()
  const queryClient = useQueryClient()
  const [showLoader, setShowLoader] = useState(false)
  const { id: engagementTypeId } = useEngagementType().engagementType
  const {
    attributes: { systemName: eventName, version },
  } = useEventLocation().location!

  const restQueryParam = {
    path: { eventName, engagementTypeUuid: engagementTypeId },
    query: { 'names[]': [...fieldNames, ...dependentFieldNames] },
  } as EngagementTypeSettingsRestParams

  const {
    isLoading: loading,
    data: getEngagementTypeSettings = [],
    refetch: refetchGetEngagementTypeSettings,
  } = useQuery(
    [
      'getEngagementTypeSettingsResponse',
      version,
      version === '1'
        ? restQueryParam
        : {
            engagementTypeId,
            names: [...fieldNames, ...dependentFieldNames],
          },
    ],
    getEngagementTypeSettingsQueryFunc(client),
    {
      onError: () => {
        showErrorToast(formatMessage(messages.genericError))
      },
    }
  )

  const fieldValues = useMemo(
    () =>
      filterEngagementSettings(getEngagementTypeSettings, fieldNames, version),
    [getEngagementTypeSettings, version]
  )

  const dependentValues = useMemo(
    () =>
      filterEngagementSettings(
        getEngagementTypeSettings,
        [...dependentFieldNames],
        version
      ),
    [getEngagementTypeSettings, version]
  )

  const initialValues = useMemo(
    () =>
      fieldValues.reduce(
        (acc, { name, value }) => ({
          ...acc,
          [name]: value,
        }),
        {}
      ),
    [fieldValues]
  )

  const dependentFieldValues = useMemo(
    () =>
      dependentValues.reduce(
        (acc, { name, value }) => ({
          ...acc,
          [name]: value,
        }),
        {}
      ),
    [dependentValues]
  )

  const handleSubmit: Config<FormValues>['onSubmit'] = async (
    values,
    { change }
  ) => {
    try {
      setShowLoader(true)
      if (version === '1') {
        let IrpConfigValues = {}
        Object.keys(values).forEach(name => {
          const value = values[name as keyof FormValues]
          const findFieldValues = fieldValues.find(
            ({ name: fieldName }) => fieldName === name
          )
          if (!findFieldValues || findFieldValues?.value !== value) {
            const val =
              name === 'event_irp_instant_meeting_enabled'
                ? { [name]: `${value}` }
                : value === 'true'
                ? { [name]: 'yes' }
                : value === 'false'
                ? { [name]: 'no' }
                : { [name]: value }

            IrpConfigValues = { ...IrpConfigValues, ...val }
          }
        })
        const configs: {
          uuid: string
          activity_setting: EngagementTypeSettingsUpsertRestPayload
        } = {
          uuid: engagementTypeId,
          activity_setting: { external_widget: IrpConfigValues },
        }
        const { data } = await upsertEngagementTypeSettings({
          path: { eventName },
          payload: configs,
        })
        setShowLoader(false)
        if (data) {
          refetchGetEngagementTypeSettings()
          showSuccessToast(formatMessage(messages.successMessage))
          client.refetchQueries({ include: ['getEngagementTypeDetails'] })
          queryClient.refetchQueries('getEngagementTypeSettingsResponse')
        }
      } else {
        const configsToUpdate = fieldValues.reduce<
          BulkEngagementSettingUpdateInput[]
        >(
          (acc, { name, displayName: display_name, value }) =>
            value !== values[name as keyof FormValues]
              ? [
                  ...acc,
                  {
                    name,
                    customValue: {
                      display_name,
                      value: values[name as keyof FormValues],
                    },
                  },
                ]
              : acc,
          []
        )
        const variables: bulkUpsertEngagementSettingsVariables = {
          engagementTypeId,
          configsToUpdate,
        }
        const { failureCount, totalCount, engagementSettings, successCount } = (
          await mutateBulkUpsertEngagementSettings(client, {
            variables,
            refetchQueries: ['getEngagementTypeDetails'],
          })
        ).data!.bulkUpsertEngagementSettings
        setShowLoader(false)
        if (failureCount) {
          showErrorToast(
            formatMessage(messages.updateError, { totalCount, failureCount })
          )
          engagementSettings.forEach(
            ({ success, name, value }) =>
              !success &&
              change(
                name as keyof FormValues,
                (value as bulkUpsertEngagementSettings_bulkUpsertEngagementSettings_engagementSettings_value_CustomNameValue)
                  .value ?? undefined
              )
          )
          const errors = engagementSettings.reduce(
            (acc, { success, name }) =>
              success
                ? acc
                : {
                    ...acc,
                    [name]: formatMessage(messages.filedError),
                  },
            {}
          )

          return errors
        }
        if (successCount) {
          // refetchGetEngagementTypeSettings()
          queryClient.refetchQueries('getEngagementTypeSettingsResponse')
          showSuccessToast(formatMessage(messages.successMessage))
        }
      }

      return undefined
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
      return { [FORM_ERROR]: { code: 'GenericError' } }
    }
  }

  return (
    <>
      {showLoader && <ModalSpinner />}
      <FinalForm<FormValues>
        onSubmit={handleSubmit}
        render={props => (
          <Form
            {...props}
            refetch={() => refetchGetEngagementTypeSettings()}
            loading={loading}
            dependentFieldValues={dependentFieldValues}
          />
        )}
        initialValues={initialValues}
      />
    </>
  )
}
