import { VFC, useMemo, useState } from 'react'
import { FieldValidator } from 'final-form'
import { defineMessages, useIntl } from 'react-intl'
import { queryValidEngagementTypeName, useApolloClient } from 'gql'
import { useField } from 'react-final-form'
import { Input } from '@jifflenow/brightui/input'
import { Field } from 'ui-components/Field'
import { asyncValidatorReducer } from 'utils/asyncValidatorReducer'
import { getEngagementNameInputOverride } from './overrides'
import { FormControlStyled } from './styled-components'
import { EngagementNameFieldProps } from './types'

const messages = defineMessages({
  engagementNamePlaceholder: {
    defaultMessage: 'Enter engagement name',
    description: 'Placeholder text',
  },
})

const errors = defineMessages({
  engagementNameExistsError: {
    defaultMessage: 'Name already exists',
    description: 'Field error message',
  },
  required: {
    defaultMessage: 'Required',
    description: 'Field validation error message',
  },
})

const simpleMemoize = (fn: FieldValidator<string>): FieldValidator<string> => {
  let lastArg: string
  let lastResult: string

  return async (arg, ...rest) => {
    if (arg !== lastArg) {
      lastArg = arg
      lastResult = await fn(arg, ...rest)
    }

    return lastResult
  }
}

export const EngagementNameField: VFC<EngagementNameFieldProps> = ({
  label,
  isCopy,
}) => {
  const { formatMessage } = useIntl()
  const client = useApolloClient()
  const [loading, setLoading] = useState(false)
  const { initial } = useField('name').meta

  const required: FieldValidator<string> = async value =>
    value?.trim() ? undefined : formatMessage(errors.required)

  const validateUrl: FieldValidator<string> = useMemo(
    () =>
      simpleMemoize(async value => {
        const searchName = value?.trim()
        if (!searchName || (searchName === initial && !isCopy)) {
          return undefined
        }
        try {
          setLoading(true)
          const {
            data: { isValidEngagementTypeName },
          } = await queryValidEngagementTypeName(client, {
            variables: { searchName },
            // TODO: no-cache policy to force the callback to trigger on every change
            // info: https://github.com/apollographql/apollo-client/issues/6636
            fetchPolicy: 'no-cache',
          })
          setLoading(false)
          return isValidEngagementTypeName
            ? undefined
            : formatMessage(errors.engagementNameExistsError)
        } catch (error) {
          setLoading(false)
          return undefined
        }
      }),
    [client, formatMessage, initial, isCopy]
  )

  const validate = asyncValidatorReducer([required, validateUrl])

  return (
    <Field<string>
      name="name"
      type="text"
      formatOnBlur
      format={value => value?.trim() ?? value}
      validate={validate}
    >
      {({ input, meta }) => (
        <FormControlStyled
          label={label}
          error={(meta.dirty || meta.touched) && meta.error}
        >
          <Input
            {...input}
            id="engagement-name"
            autoComplete="off"
            placeholder={formatMessage(messages.engagementNamePlaceholder)}
            overrides={getEngagementNameInputOverride(loading)}
          />
        </FormControlStyled>
      )}
    </Field>
  )
}
