import { useEventLocation } from 'providers/LocationProvider'
import { useMe } from 'providers/MeProvider'
import { useDocumentTitle } from 'ui-components/DocumentTitle'
import { useEngagementType } from '../EngagementTypeProvider'

export const usePageTitle = (title: string) => {
  const { name: engagementTypeName } = useEngagementType().engagementType
  const { displayName: eventName } =
    useEventLocation().location?.attributes ?? {}
  const { companyName } = useMe().meta
  useDocumentTitle(
    `${title} - ${engagementTypeName} - ${eventName} - ${companyName}`
  )
}
