import { VFC } from 'react'
import { FormattedMessage } from 'react-intl'
import { ModalFooter, ModalButton } from '@jifflenow/brightui/modal'
import { KIND } from '@jifflenow/brightui/button'
import { modalButtonOverrides } from '../overrides'
import { CopyModalFooterProps } from './types'

export const CopyModalFooter: VFC<CopyModalFooterProps> = props => (
  <ModalFooter>
    <ModalButton
      kind={KIND.tertiary}
      overrides={modalButtonOverrides}
      onClick={props.onClose}
    >
      <FormattedMessage defaultMessage="Cancel" description="Button label" />
    </ModalButton>
    <ModalButton
      kind={KIND.primary}
      onClick={props.handleCopySlots}
      overrides={modalButtonOverrides}
      disabled={props.copyButtonDisabled}
    >
      <FormattedMessage defaultMessage="Copy" description="Button label" />
    </ModalButton>
  </ModalFooter>
)
