import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useField, useFormState } from 'react-final-form'
import { Field } from 'ui-components/Field'
import { useEventLocation } from 'providers/LocationProvider'
import { Select } from '@jifflenow/brightui/select'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { FormControl } from 'components/CustomFormFields/Fields/styled-components'
import { useGetConferences } from 'rest'
import { FormFieldWrapper } from './styled-components'
import {
  FormValues,
  DefaultLocationPreferenceType,
  DefaultLocationDropdownProps,
  DefaultLocationOption,
} from './types'

const messages = defineMessages({
  defaultLocationLabel: {
    defaultMessage: 'Default Location ',
    description: 'Field label',
  },
  defaultLocationPlaceholder: {
    defaultMessage: 'Select Default Location ',
    description: 'Placeholder label',
  },
  roomsLabel: {
    defaultMessage: 'Onsite Locations',
    description: 'Options label',
  },
  boothTourLabel: {
    defaultMessage: 'Designated Location',
    description: 'Options label',
  },
})

const { ROOM, BOOTH_TOUR, VCON } = DefaultLocationPreferenceType

const DefaultLocationDropdown: VFC<DefaultLocationDropdownProps> = ({
  renderProps: {
    input,
    meta: { submitError, error },
  },
  options,
  conferences,
}) => {
  const { formatMessage } = useIntl()
  const { input: vconPreference } = useField('default_location_vcon_preference')

  const handleOnChange = (option: DefaultLocationOption[]) => {
    input.onChange(option)
    const [{ id, type }] = option
    if (type === VCON) {
      const {
        uuid: integration_uuid = '',
        integration_name = '',
        integration_adapter_uuid = '',
      } = conferences.find(({ uuid }) => uuid === id) ?? {}

      vconPreference.onChange(
        JSON.stringify({
          integration_adapter_uuid,
          integration_name,
          integration_uuid,
        })
      )
    } else {
      vconPreference.onChange(undefined)
    }
  }

  return (
    <FormControl
      label={formatMessage(messages.defaultLocationLabel)}
      error={submitError || error}
    >
      <Select
        {...input}
        id="default_location_preference"
        clearable={false}
        options={options}
        placeholder={formatMessage(messages.defaultLocationPlaceholder)}
        onChange={({ value }: { value: DefaultLocationOption[] }) =>
          handleOnChange(value)
        }
        dataPendo="default-location-preference"
      />
    </FormControl>
  )
}

export const DefaultLocationField: VFC = () => {
  const { formatMessage } = useIntl()

  const { booth_tour_enabled } = useFormState<FormValues>().values

  const { systemName } = useEventLocation().location?.attributes ?? {}

  const { data: conferences, isLoading } = useGetConferences({
    eventName: systemName,
  })

  const options = [
    {
      label: formatMessage(messages.roomsLabel),
      id: ROOM,
      type: ROOM,
    },
    booth_tour_enabled === 'true' && {
      label: formatMessage(messages.boothTourLabel),
      id: BOOTH_TOUR,
      type: BOOTH_TOUR,
    },
    ...(conferences
      ? conferences.data.map(({ integration_name: label, uuid: id }) => ({
          label,
          id,
          type: VCON,
        }))
      : []),
  ].filter(Boolean) as DefaultLocationOption[]

  return (
    <>
      {isLoading && <ModalSpinner />}
      <FormFieldWrapper>
        <Field<string>
          format={(value: string) => (value ? JSON.parse(value) : [])}
          parse={(value: DefaultLocationOption[]) => JSON.stringify(value)}
          name="default_location_preference"
          render={props => (
            <DefaultLocationDropdown
              renderProps={props}
              options={options}
              conferences={conferences?.data ?? []}
            />
          )}
        />
      </FormFieldWrapper>
    </>
  )
}
