import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { EngagementTypeToggle } from '../common'
import { useEngagementType } from '../EngagementTypeProvider'
import {
  BorderStyled,
  ParticipantsBody,
  ParticipantsSectionDesc,
  ParticipantsSectionTitle,
} from './styled-components'

const messages = defineMessages({
  sectionTitle: {
    defaultMessage: 'Internal Attendee Options',
    description: 'Section title',
  },
  sectionTitleDesc: {
    defaultMessage: 'Internal attendee capabilities during requesting meeting',
    description: 'Help text',
  },
  hideInternalAttendeesInBookMeetingLabel: {
    defaultMessage: 'Hide Internal Attendee section',
    description: 'Field label',
  },
  internalAttendeePresenceLabel: {
    defaultMessage: 'At least one internal attendee is required',
    description: 'Field label',
  },
  relaxInternalAttendeePresenceForMmLabel: {
    defaultMessage:
      'Relax this restriction for MMs/JMMs (applicable if above is enabled).',
    description: 'Field label',
  },
  atLeastOneAttendeeRolePresenceLabel: {
    defaultMessage: "At least one user of 'Attendee' role is required",
    description: 'Field label',
  },
  allowRequestorToAddInternalAttendeeLabel: {
    defaultMessage: 'Allow meeting requestors to add new internal attendees',
    description: 'Field label',
  },
  sendVerificationEmailToAttendeeLabel: {
    defaultMessage:
      'Send verification mails to internal attendees added via Request Meeting',
    description: 'Field label',
  },
  allowRequestorToNominateThemselves: {
    defaultMessage: 'Allow requestors to nominate themselves to a meeting',
    description: 'Field label',
  },
})

export const InternalAttendee: VFC = () => {
  const { formatMessage } = useIntl()
  const {
    masterEngagementType,
  } = useEngagementType().engagementType.templateEngagementType
  const isSession =
    masterEngagementType === MasterEngagementTypes.TracksAndSession

  return (
    <ParticipantsBody>
      <ParticipantsSectionTitle>
        {formatMessage(messages.sectionTitle)}
      </ParticipantsSectionTitle>
      <ParticipantsSectionDesc>
        {formatMessage(messages.sectionTitleDesc)}
      </ParticipantsSectionDesc>
      {!isSession && (
        <>
          <BorderStyled />
          <EngagementTypeToggle
            label={formatMessage(
              messages.hideInternalAttendeesInBookMeetingLabel
            )}
            name="hide_internal_attendees_in_book_meeting"
          />
          <BorderStyled />
          <EngagementTypeToggle
            label={formatMessage(messages.internalAttendeePresenceLabel)}
            name="internal_attendee_presence"
          >
            <BorderStyled />
            <EngagementTypeToggle
              label={formatMessage(
                messages.relaxInternalAttendeePresenceForMmLabel
              )}
              name="relax_internal_attendee_presence_for_mm"
            />
          </EngagementTypeToggle>
          <BorderStyled />
          <EngagementTypeToggle
            label={formatMessage(messages.atLeastOneAttendeeRolePresenceLabel)}
            name="atleast_one_attendee_role_presence"
          />
        </>
      )}
      <BorderStyled />
      <EngagementTypeToggle
        label={formatMessage(messages.allowRequestorToAddInternalAttendeeLabel)}
        name="allow_requestor_to_add_internal_attendee"
      />
      <BorderStyled />
      <EngagementTypeToggle
        label={formatMessage(messages.sendVerificationEmailToAttendeeLabel)}
        name="send_verification_email_to_attendee"
      />
      {isSession && (
        <>
          <BorderStyled />
          <EngagementTypeToggle
            label={formatMessage(messages.allowRequestorToNominateThemselves)}
            name="allow_requestor_to_self_nominate_to_meeting"
          />
        </>
      )}
      <BorderStyled />
    </ParticipantsBody>
  )
}
