import { FC, useState } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { stringifyUrl } from 'query-string'
import { useParams } from 'react-router-dom'
import { KIND } from '@jifflenow/brightui/button'
import { Modal, ModalHeader, SIZE } from '@jifflenow/brightui/modal'
import { Paths } from 'components/Routes/Paths'
import { MessageProps } from './types'
import { modalOverrides } from './modalOverrides'
import {
  Button,
  MessageRoot,
  MessageRow,
  ModalHintText,
  StyledCheckCircleOutlined,
  StyledCloseCircleOutlined,
  Name,
  MeetingCounts,
  ModalBody,
  Anchor,
  ActionContainer,
} from './styled-components'

const messages = defineMessages({
  view: {
    defaultMessage: 'View',
    description: 'Button text',
  },
  meetingCount: {
    defaultMessage:
      '{count, number} {count, plural, one {meeting} other {meetings}} ',
    description: 'Modal body',
  },
})
// TODO: Refactor: try to trigger toasts here by receiving other details as props
export const Message: FC<MessageProps> = ({ message, hint, details }) => {
  const { formatMessage } = useIntl()
  const { eventName } = useParams<{ eventName: string }>()
  const [isOpen, setIsOpen] = useState(false)
  const messageList = details?.sort((first, second) =>
    first.success === second.success ? 0 : first.success ? -1 : 1
  )

  return (
    <MessageRoot>
      {message}
      {!!details?.length && (
        <>
          <Button kind={KIND.secondary} onClick={() => setIsOpen(true)}>
            {formatMessage(messages.view)}
          </Button>
          <Modal
            isOpen={isOpen}
            size={SIZE.medium}
            overrides={modalOverrides}
            onClose={() => setIsOpen(false)}
          >
            <ModalHeader>{message}</ModalHeader>
            <ModalBody>
              {hint && <ModalHintText>{formatMessage(hint)}</ModalHintText>}
              {messageList?.map(
                ({ success, name, associatedMeetings }, index) => (
                  <MessageRow key={index}>
                    {success ? (
                      <StyledCheckCircleOutlined size="20px" />
                    ) : (
                      <StyledCloseCircleOutlined size="20px" />
                    )}
                    <Name>{name}</Name>
                    <MeetingCounts>
                      {formatMessage(messages.meetingCount, {
                        count: associatedMeetings,
                      })}
                    </MeetingCounts>
                    {!success && (
                      <ActionContainer>
                        <Anchor
                          href={stringifyUrl({
                            query: {
                              current_location_uuid: new URLSearchParams(
                                window.location.search
                              ).get('location'),
                            },
                            url: Paths.LEGACY_EVENT_MEETINGS.path.replace(
                              ':eventName',
                              eventName
                            ),
                          })}
                        >
                          {formatMessage(messages.view)}
                        </Anchor>
                      </ActionContainer>
                    )}
                  </MessageRow>
                )
              )}
            </ModalBody>
          </Modal>
        </>
      )}
    </MessageRoot>
  )
}
