import { useState, VFC } from 'react'
import { defineMessages, FormattedMessage, useIntl } from 'react-intl'
import { useQuery } from 'react-query'
import { getMailActionsQueryFn, putEngagementMailActionsQueryFn } from 'rest'
import { ErrorPage } from 'ui-components/ErrorPage'
import { useEventLocation } from 'providers/LocationProvider'
import { useMutation } from 'react-query'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import {
  Body,
  NotificationLoaderContainer,
  usePageTitle,
  Header,
} from '../common'
import { useEngagementType } from '../EngagementTypeProvider'
import { Loader } from './Loader'
import { NotificationsForm } from './NotificationsForm'
import { Wrapper, Title, SubTitle } from './styled-components'
import { ActionsActiveMap } from './types'

const LoadingShimmer = () => (
  <NotificationLoaderContainer>
    <Loader />
  </NotificationLoaderContainer>
)

const messages = defineMessages({
  pageTitle: {
    defaultMessage: 'Notifications',
    description: 'Page title',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  successMessage: {
    defaultMessage: 'Your changes have been saved successfully',
    description: 'Success message',
  },
})

const Container: VFC = () => {
  const { formatMessage } = useIntl()
  usePageTitle(formatMessage(messages.pageTitle))
  const {
    uuid: engagementTypeUuid,
    templateEngagementType: { masterEngagementType },
  } = useEngagementType().engagementType
  const { systemName: eventName } = useEventLocation().location!.attributes
  const [showLoader, setShowLoader] = useState(false)

  const isSession =
    masterEngagementType === MasterEngagementTypes.TracksAndSession

  const { isLoading, data, error, refetch } = useQuery(
    ['mailActions', { path: { eventName, engagementTypeUuid } }],
    getMailActionsQueryFn
  )

  const { mutateAsync } = useMutation(putEngagementMailActionsQueryFn)

  const handleSubmit = async (values: ActionsActiveMap) => {
    const bodyParams = data!.data.map(items => ({
      active: values[`mail_actions_${items.id}`],
      id: items.id,
      name: items.name,
      engagementTypeUuid: items.engagementTypeUuid,
      templateType: items.templateType,
    }))

    try {
      setShowLoader(true)
      await mutateAsync({
        eventName,
        engagementTypeUuid,
        payload: { data: bodyParams },
      })
      setShowLoader(false)
      showSuccessToast(formatMessage(messages.successMessage))
      refetch()
    } catch (err) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
    }
  }

  return isLoading ? (
    <Wrapper>
      <LoadingShimmer />
    </Wrapper>
  ) : error || !data ? (
    // TODO: Need to use only the error component to avoid extra padding
    <ErrorPage onButtonClick={() => refetch()} />
  ) : (
    <>
      {showLoader && <ModalSpinner />}
      <NotificationsForm
        mailActions={data.data}
        onSubmit={handleSubmit}
        isSession={isSession}
      />
    </>
  )
}

export const Notifications: VFC = () => (
  <>
    <Header />
    <Body>
      <Wrapper>
        <Title>
          <FormattedMessage
            defaultMessage="Notification Types"
            description="Notification UI List Header"
          />
        </Title>
        <SubTitle>
          <FormattedMessage
            defaultMessage="Notifications are grouped based on the meeting stages for each user."
            description="Notification UI List Sub Header"
          />
        </SubTitle>
      </Wrapper>
      <Container />
    </Body>
  </>
)
