import { FC } from 'react'
import { NavLink } from 'react-router-dom'
import { Field } from 'react-final-form'
import { Toggle } from '@jifflenow/brightui/toggle'
import { useEventLocation } from 'providers/LocationProvider'
import { StatefulTooltip } from '@jifflenow/brightui/tooltip'
import { useIntl, defineMessages } from 'react-intl'
import { useEngagementType } from '../EngagementTypeProvider'
import {
  SectionWrapper,
  ActionSection,
  ActionTextWrapper,
  ActionContainer,
  Action,
} from './styled-components'
import { SurveyName, SurveyProps } from './types'

const tooltips = defineMessages<SurveyName>({
  pre_session_survey: {
    defaultMessage: 'Email to attendees upon requesting survey to be filled',
    description: 'Description of email template',
  },
  post_session_survey: {
    defaultMessage: 'Email to attendees upon requesting survey to be filled',
    description: 'Description of email template',
  },
  request_for_post_event_meeting_surveys: {
    defaultMessage:
      'Email to internal attendees upon requesting survey to be filled',
    description: 'Description of email template',
  },
})

export const Survey: FC<SurveyProps> = ({ surveyTemplate }) => {
  const { formatMessage } = useIntl()
  const { systemName: eventName } = useEventLocation().location!.attributes
  const { uuid } = useEngagementType().engagementType
  const location = new URLSearchParams(window.location.search).get('location')

  return (
    <SectionWrapper>
      <ActionSection>
        <Action>
          {surveyTemplate.map(
            ({ id: notificationId, displayName, name }, i) => (
              <ActionContainer key={notificationId} $isSurveyFirst={i === 0}>
                <StatefulTooltip
                  content={formatMessage(tooltips[name as SurveyName])}
                  showArrow
                  popoverMargin={8}
                >
                  <ActionTextWrapper $isFirst>
                    <NavLink
                      to={`/events/${eventName}/engagement-types/${uuid}/meeting-type-notifications-details?location=${location}&mail_actions=${name}&notificationId=${notificationId}`}
                    >
                      {displayName}
                    </NavLink>
                  </ActionTextWrapper>
                </StatefulTooltip>
                <Field<boolean>
                  name={`mail_actions_${notificationId}`}
                  type="checkbox"
                  render={({ input: { onChange, checked } }) => (
                    <Toggle
                      ariaLabel={displayName}
                      checked={checked}
                      label={' '}
                      onChange={onChange}
                    />
                  )}
                />
              </ActionContainer>
            )
          )}
        </Action>
      </ActionSection>
    </SectionWrapper>
  )
}
