import { useState, VFC, useEffect } from 'react'
import { defineMessages, FormattedMessage, useIntl } from 'react-intl'
import {
  mutateCopyRoomAvailabilities,
  useApolloClient,
  validateRoomAvailabilitiesToCopyQuery,
} from 'gql'
import { Modal, ModalBody, ModalHeader } from '@jifflenow/brightui/modal'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { copyRoomAvailabilitiesVariables } from 'gql/types/copyRoomAvailabilities'
import { validateRoomAvailabilitiesToCopy_validateRoomAvailabilitiesToCopy } from 'gql/types/validateRoomAvailabilitiesToCopy'
import { RoomAvailabilityLocationTypes } from 'gql/types/globalTypes'
import { useEngagementType } from 'components/EngagementTypes/EngagementTypeEdit/EngagementTypeProvider'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { CopyRoomAvailabilityProps } from '../types'
import {
  ConfirmationDialog,
  CopyModalFooter,
  CopySlotsModalBodyWrapper,
  ModalDescription,
} from '../common'
import { CopyDaySlotsToRoom } from './CopyDaySlotsToRoom'

export const CopyRoomAvailability: VFC<CopyRoomAvailabilityProps> = props => {
  const { formatMessage } = useIntl()
  const { activeTab, getModalLabel, ...modalProps } = props
  const [showLoader, setShowLoader] = useState(false)
  const [roomChecked, setRoomChecked] = useState(false)
  const [offsiteChecked, setOffsiteChecked] = useState(false)
  const [boothTourChecked, setBoothTourChecked] = useState(false)
  const [copyConfirmation, setCopyConfirmation] = useState(false)
  const [copyButtonDisabled, setCopyButtonDisabled] = useState(false)
  const [conflictRoomLabels, setConflictRoomLabels] = useState([])

  const { id: engagementTypeId } = useEngagementType().engagementType
  const client = useApolloClient()

  useEffect(() => {
    if (roomChecked || offsiteChecked || boothTourChecked) {
      setCopyButtonDisabled(false)
    } else {
      setCopyButtonDisabled(true)
    }
  }, [roomChecked, offsiteChecked, boothTourChecked])

  const messages = defineMessages({
    roomAvailabilityLabel: {
      defaultMessage: 'Select locations to copy the "{room}" Availability To',
      description: 'Room label',
    },
    success: {
      defaultMessage: 'Availabilities Copied Successfully.',
      description: 'Success message',
    },
    genericError: {
      defaultMessage: 'Something is not right. Please retry.',
      description: 'Error message',
    },
    roomsLabel: {
      defaultMessage: 'Onsite Locations',
      description: 'Tab Label',
    },
    offsiteLabel: {
      defaultMessage: 'Offsites',
      description: 'Tab Label',
    },
    boothTourLabel: {
      defaultMessage: 'Designated Location',
      description: 'Tab Label',
    },
    confirmationModalHeader: {
      defaultMessage: 'You have unsaved changes',
      description: 'Modal heading',
    },
    confirmationModalMessage: {
      defaultMessage:
        'Conflicts with planned meeting(s) in {rooms}. Updating availability will applicable only for future planned meetings and not for existing planned meeting',
      description: 'Confirmation message',
    },
    cancelButtonLabel: {
      defaultMessage: 'Cancel',
      description: 'Button label',
    },
    saveButtonLabel: {
      defaultMessage: 'Proceed',
      description: 'Button label',
    },
  })
  const { Room, Offsite, BoothTour } = RoomAvailabilityLocationTypes

  const handleDialogClose = () => setCopyConfirmation(false)

  const copyRoomData = () => {
    const { activeTab } = props
    const locationTypes = []
    if (offsiteChecked) {
      locationTypes.push(Offsite)
    }
    if (boothTourChecked) {
      locationTypes.push(BoothTour)
    }
    if (roomChecked) {
      locationTypes.push(Room)
    }

    return {
      engagementTypeId,
      fromAvailabilityLocationType: activeTab,
      toAvailabilityLocationType: locationTypes,
    }
  }

  const validateCopy = async () => {
    setShowLoader(true)
    try {
      const variables: copyRoomAvailabilitiesVariables = copyRoomData()
      const {
        data: { validateRoomAvailabilitiesToCopy },
      } = await validateRoomAvailabilitiesToCopyQuery(client, { variables })
      setShowLoader(false)

      const conflictRooms = validateRoomAvailabilitiesToCopy
        .map(
          (
            room: validateRoomAvailabilitiesToCopy_validateRoomAvailabilitiesToCopy
          ) => {
            let conflictRoom: string | boolean = false
            if (room.isValid === false) {
              if (room.availabilityLocationType === Room) {
                conflictRoom = formatMessage(messages.roomsLabel)
              }
              if (room.availabilityLocationType === Offsite) {
                conflictRoom = formatMessage(messages.offsiteLabel)
              }
              if (room.availabilityLocationType === BoothTour) {
                conflictRoom = formatMessage(messages.boothTourLabel)
              }
            }

            return conflictRoom
          }
        )
        .filter(Boolean)

      setConflictRoomLabels(conflictRooms)

      conflictRooms.length ? setCopyConfirmation(true) : handleCopy()
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
    }
  }

  const handleCopy = async () => {
    setShowLoader(true)
    try {
      const variables: copyRoomAvailabilitiesVariables = copyRoomData()
      await mutateCopyRoomAvailabilities(client, { variables })
      showSuccessToast(formatMessage(messages.success))
      props.onClose()
      setShowLoader(false)
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
    }
  }

  return (
    <>
      {showLoader && <ModalSpinner />}
      <Modal {...modalProps}>
        <ModalHeader>
          <FormattedMessage
            defaultMessage="Copy {room} Availability To"
            description="Header title"
            values={{ room: getModalLabel() }}
          />
        </ModalHeader>
        <ModalDescription>
          {formatMessage(messages.roomAvailabilityLabel, {
            room: getModalLabel(),
          })}
        </ModalDescription>
        <ModalBody>
          <CopySlotsModalBodyWrapper>
            <CopyDaySlotsToRoom
              activeTab={activeTab}
              engagementTypeSettings={props.engagementTypeSettings}
              roomChecked={roomChecked}
              setRoomChecked={setRoomChecked}
              offsiteChecked={offsiteChecked}
              setOffsiteChecked={setOffsiteChecked}
              boothTourChecked={boothTourChecked}
              setBoothTourChecked={setBoothTourChecked}
            />
          </CopySlotsModalBodyWrapper>
        </ModalBody>
        <CopyModalFooter
          onClose={modalProps.onClose}
          handleCopySlots={validateCopy}
          copyButtonDisabled={copyButtonDisabled}
        />
      </Modal>
      {copyConfirmation ? (
        <ConfirmationDialog
          heading={formatMessage(messages.confirmationModalHeader)}
          message={formatMessage(messages.confirmationModalMessage, {
            rooms: conflictRoomLabels.join(', '),
          })}
          cancelLabel={formatMessage(messages.cancelButtonLabel)}
          successLabel={formatMessage(messages.saveButtonLabel)}
          onClose={handleDialogClose}
          onCancel={handleDialogClose}
          onSuccess={handleCopy}
        />
      ) : null}
    </>
  )
}
