import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { generatePath, useLocation, useParams } from 'react-router-dom'
import { Drawer, MenuOptions } from 'ui-components/Drawer'
import { Paths as BasePaths } from 'components/Routes'
import { useEventLocation } from 'providers/LocationProvider'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { useMe } from 'providers/MeProvider'
import { useEngagementType } from './EngagementTypeProvider'
import { Paths } from './Paths'
import { Root } from './styled-components'

const menuItemLabels = defineMessages({
  engagementBasics: {
    defaultMessage: 'Engagement Basic Details',
    description: 'Navigation icon label',
  },
  locationTypes: {
    defaultMessage: 'Location Types',
    description: 'Navigation icon label',
  },
  setting: {
    defaultMessage: 'Setting',
    description: 'Navigation icon label',
  },
  setTimeAvailability: {
    defaultMessage: 'Set Availability',
    description: 'Navigation icon label',
  },
  participants: {
    defaultMessage: 'Participants Options',
    description: 'Navigation icon label',
  },
  approvalProcess: {
    defaultMessage: 'Approval Process',
    description: 'Navigation icon label',
  },
  topicOptions: {
    defaultMessage: 'Topic Options',
    description: 'Navigation icon label',
  },
  topicTrackOptions: {
    defaultMessage: 'Topic & Track Options',
    description: 'Navigation icon label',
  },
  requestForm: {
    defaultMessage: 'Request Form',
    description: 'Navigation icon label',
  },
  inboundRequestForm: {
    defaultMessage: 'Inbound Request Page',
    description: 'Navigation icon label',
  },
  survey: {
    defaultMessage: 'Surveys',
    description: 'Navigation icon label',
  },
  advancedSettings: {
    defaultMessage: 'Advanced Settings',
    description: 'Navigation icon label',
  },
  csmOnly: {
    defaultMessage: 'CSM Only',
    description: 'Navigation icon label',
  },
  notifications: {
    defaultMessage: 'Notifications',
    description: 'Navigation icon label',
  },
  linkSettings: {
    defaultMessage: 'Link Settings',
    description: 'Navigation icon label',
  },
  sessionSettings: {
    defaultMessage: 'Settings',
    description: 'Navigation icon label',
  },
  formsLabel: {
    defaultMessage: 'Forms',
    description: 'Navigation icon label',
  },
  outboundMeetingLabel: {
    defaultMessage: 'Outbound Meeting',
    description: 'Navigation icon label',
  },
  inboundMeetingLabel: {
    defaultMessage: 'Inbound Meeting',
    description: 'Navigation icon label',
  },
  surveyLabel: {
    defaultMessage: 'Survey',
    description: 'Navigation icon label',
  },
})

interface MenuItems {
  labelKey: keyof typeof menuItemLabels
  path?: Paths
  children?: MenuItems[]
}

// eslint-disable-next-line complexity
export const Nav: VFC = () => {
  const { formatMessage } = useIntl()
  const { search, pathname } = useLocation()

  const { eventName, engagementId } = useParams<{
    engagementId: string
    eventName: string
  }>()

  const { role } = useMe().data.attributes

  const {
    engagementType: {
      status,
      templateEngagementType: { masterEngagementType },
    },
    ...rest
  } = useEngagementType()
  const { location, configs, privileges } = useEventLocation()

  const {
    attributes: { subType, version },
  } = location!
  const {
    enable_external_widget,
    enable_topic,
    enable_topic_to_activity,
    enable_room_to_topic,
    enable_user_to_topic,
  } = configs!

  // const activeSurveys = engagementTypeSurveys.filter(({ status }) => status)
  const isGeneralMeeting =
    masterEngagementType === MasterEngagementTypes.General
  const showSettings =
    isGeneralMeeting ||
    masterEngagementType === MasterEngagementTypes.TracksAndSession

  const menuItems: MenuItems[] = [
    ...(version === '1'
      ? [
          masterEngagementType !== MasterEngagementTypes.ConsecutiveMeeting &&
            enable_external_widget === 'true' && {
              labelKey: 'inboundRequestForm',
              path: Paths.INBOUND_REQUEST_FORM,
            },
          status &&
            privileges?.canManageNotifications && {
              labelKey: 'notifications',
              path: Paths.NOTIFICATIONS,
            },
          status &&
            privileges?.canManageForms &&
            privileges?.canManageSelfServeSettings && {
              labelKey: 'formsLabel',
              children: [
                {
                  labelKey: 'outboundMeetingLabel',
                  path: Paths.OUTBOUND_MEETING_FORM,
                },
                enable_external_widget === 'true' &&
                  rest.engagementTypeSettings.enable_external_widget ===
                    'true' && {
                    labelKey: 'inboundMeetingLabel',
                    path: Paths.INBOUND_MEETING_FORM,
                  },
                !!rest.engagementTypeSurveys.filter(({ status }) => status)
                  .length && {
                  labelKey: 'surveyLabel',
                  path: Paths.SURVEY_FORM,
                },
              ].filter(Boolean),
            },
        ]
      : [
          { labelKey: 'engagementBasics', path: Paths.BASICS },
          ...(showSettings
            ? [
                { labelKey: 'approvalProcess', path: Paths.APPROVAL_PROCESS },
                {
                  labelKey: 'locationTypes',
                  children: [
                    { labelKey: 'setting', path: Paths.SETTING },
                    {
                      labelKey: 'setTimeAvailability',
                      path: Paths.SET_AVAILABILITY,
                    },
                  ],
                },
                subType === 'Emeet' &&
                  isGeneralMeeting && {
                    labelKey: 'linkSettings',
                    path: Paths.LINK_SETTINGS,
                  },
                status &&
                  privileges?.canManageForms &&
                  privileges.canManageSelfServeSettings && {
                    labelKey: 'formsLabel',
                    children: [
                      {
                        labelKey: 'outboundMeetingLabel',
                        path: Paths.OUTBOUND_MEETING_FORM,
                      },
                      enable_external_widget === 'true' &&
                        rest.engagementTypeSettings.enable_external_widget ===
                          'true' && {
                          labelKey: 'inboundMeetingLabel',
                          path: Paths.INBOUND_MEETING_FORM,
                        },
                      !!rest.engagementTypeSurveys.filter(
                        ({ status }) => status
                      ).length && {
                        labelKey: 'surveyLabel',
                        path: Paths.SURVEY_FORM,
                      },
                    ].filter(Boolean),
                  },
                status &&
                  privileges?.canManageNotifications &&
                  privileges.canManageSelfServeSettings && {
                    labelKey: 'notifications',
                    path: Paths.NOTIFICATIONS,
                  },
                { labelKey: 'participants', path: Paths.PARTICIPANTS },
                isGeneralMeeting
                  ? enable_topic === 'true' &&
                    enable_topic_to_activity === 'true' &&
                    (enable_room_to_topic === 'true' ||
                      enable_user_to_topic === 'true') && {
                      labelKey: 'topicOptions',
                      path: Paths.TOPIC_OPTIONS,
                    }
                  : {
                      labelKey: 'topicTrackOptions',
                      path: Paths.TOPIC_OPTIONS,
                    },
                enable_external_widget === 'true' && {
                  labelKey: 'inboundRequestForm',
                  path: Paths.INBOUND_REQUEST_FORM,
                },
                privileges?.canManageSurvey &&
                  privileges.canManageSelfServeSettings &&
                  !!rest.engagementTypeSurveys.length && {
                    labelKey: 'survey',
                    path: Paths.SURVEY,
                  },
                { labelKey: 'advancedSettings', path: Paths.ADVANCED_SETTINGS },
                ...(role === 'CSM'
                  ? [{ labelKey: 'csmOnly', path: Paths.CSM_ONLY }]
                  : []),
              ]
            : []),
        ]),
  ].filter(Boolean) as MenuItems[]

  const itemMapper = (isChild = false) => (item: MenuItems): MenuOptions => {
    const { path, labelKey, children } = item

    return {
      label: formatMessage(menuItemLabels[labelKey]),
      ...(children
        ? { children: children.map(itemMapper(true)) }
        : {
            href: `${generatePath(BasePaths.ENGAGEMENT_TYPE.path, {
              eventName,
              engagementId,
            })}${path}${search}`,
          }),
      isChild,
      dataPendo: labelKey,
      ...(labelKey === 'notifications' && {
        isActive:
          pathname.includes(Paths.NOTIFICATIONS) ||
          pathname.includes(Paths.NOTIFICATIONS_DETAILS),
      }),
    }
  }

  const menuOptions = menuItems.map(itemMapper())

  return (
    <Root>
      <Drawer.Nav options={menuOptions} />
    </Root>
  )
}
