import { VFC, FC, useState, useEffect, useMemo } from 'react'
import { Prompt } from 'react-router-dom'
import { Form as FinalForm } from 'react-final-form'
import createFocusDecorator from 'final-form-focus'
import { Config, FORM_ERROR } from 'final-form'
import { defineMessages, useIntl } from 'react-intl'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { Option } from '@jifflenow/brightui/select'
import { useDrawer } from 'ui-components/Drawer'
import { useWizard } from 'ui-components/Wizard'
import { ConfirmationDialog } from 'ui-components/ConfirmationDialog'
import { commonMessages, roles as roleLabel } from 'utils/messages'
import {
  mutateCreateEngagementType,
  mutateDuplicateEngagementType,
  useApolloClient,
  useEngagementTypesGrandTotalQuery,
  useFetchRolesForRequestEngagement,
} from 'gql'
import { createEngagementTypeVariables } from 'gql/types/createEngagementType'
import { duplicateEngagementTypeVariables } from 'gql/types/duplicateEngagementType'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { showErrorToast } from 'utils/toast'
import {
  EngagementNameField,
  RolesField,
  EngagementDescriptionField,
  DisplayOrderField,
} from '../../common'
import { Header as PageHeader } from './Header'
import { Footer } from './Footer'
import { EngagementTemplates } from './EngagementTemplates'
import {
  FormValues,
  FormProps,
  EngagementTypeFormProps,
  FormHeaderProps,
} from './types'
import { FormError } from './FormError'
import {
  Body,
  Title,
  Header,
  HelpText,
  FieldWrapper,
} from './styled-components'

const messages = defineMessages({
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  formHeaderCreate: {
    defaultMessage: 'Select Engagement Template',
    description: 'Section title',
  },
  formHeaderCopy: {
    defaultMessage: 'Selected engagement type',
    description: 'Section title',
  },
  formHelpText: {
    defaultMessage:
      'Select one of the following templates to create new engagement type.',
    description: 'Help text',
  },
  copyDrawerHeader: {
    defaultMessage: 'Copy of {name}',
    description: 'Help text',
  },
  engagementNameLabel: {
    defaultMessage: 'Name of Engagement Type',
    description: 'Field label',
  },
  engagementDescriptionLabel: {
    defaultMessage: 'Description',
    description: 'Field label',
  },
  copyEngagementNameLabel: {
    defaultMessage: 'Name of Engagement Type copy',
    description: 'Field label',
  },
})

const modalLabels = defineMessages({
  modalHeader: {
    defaultMessage: 'Your changes will be lost.',
    description: 'Modal heading',
  },
  modalMessage: {
    defaultMessage: 'Would you like to stay?',
    description: 'Confirmation message',
  },
  cancelButtonLabel: {
    defaultMessage: 'Leave',
    description: 'Button label',
  },
  successButtonLabel: {
    defaultMessage: 'Stay',
    description: 'Button label',
  },
})

const FormHeader: FC<FormHeaderProps> = ({ copy }) => {
  const { formatMessage } = useIntl()

  return (
    <Header>
      <Title>
        {formatMessage(messages[copy ? 'formHeaderCopy' : 'formHeaderCreate'])}
      </Title>
      {!copy && <HelpText>{formatMessage(messages.formHelpText)}</HelpText>}
    </Header>
  )
}

const EngagementTypeForm: FC<EngagementTypeFormProps> = ({
  handleSubmit,
  dirty,
  children,
  ...rest
}) => {
  const { formatMessage } = useIntl()
  const { closeDrawer } = useDrawer()
  const unload = useBeforeUnload()
  const [showConfirmation, setShowConfirmation] = useState(false)

  useEffect(() => {
    unload(dirty)
  }, [dirty, unload])

  const handleDrawerClose = () => {
    if (dirty) {
      setShowConfirmation(true)
      return
    }
    closeDrawer()
  }

  const handleDialogClose = () => setShowConfirmation(false)

  const handleDialogCancel = () => {
    setShowConfirmation(false)
    closeDrawer()
  }

  return (
    <form onSubmit={handleSubmit} noValidate>
      <Prompt when={dirty} message={formatMessage(messages.promptMessage)} />
      {showConfirmation && (
        <ConfirmationDialog
          heading={formatMessage(modalLabels.modalHeader)}
          message={formatMessage(modalLabels.modalMessage)}
          cancelLabel={formatMessage(modalLabels.cancelButtonLabel)}
          successLabel={formatMessage(modalLabels.successButtonLabel)}
          onClose={handleDialogClose}
          onCancel={handleDialogCancel}
          onSuccess={handleDialogClose}
        />
      )}
      <PageHeader onClose={handleDrawerClose} {...rest} />
      <Body>
        <FormHeader {...rest} />
        <EngagementTemplates {...rest} />
        <FieldWrapper>
          <EngagementNameField
            label={formatMessage(
              messages[
                rest.copy ? 'copyEngagementNameLabel' : 'engagementNameLabel'
              ]
            )}
            isCopy={!!rest.copy}
          />
          <EngagementDescriptionField
            label={formatMessage(messages.engagementDescriptionLabel)}
          />
          {children}
        </FieldWrapper>
        <FormError />
      </Body>
      <Footer onCancelClick={handleDrawerClose} {...rest} />
    </form>
  )
}

const focusDecorator = createFocusDecorator<FormValues>()

const RoleMapper = ({ id }: Option) => id as string

export const Form: VFC<FormProps> = ({ onCreateSuccess, copy }) => {
  const { formatMessage } = useIntl()
  const { nextStep } = useWizard()
  const client = useApolloClient()

  const [showLoader, setShowLoader] = useState(false)

  const { roles = [] } = useFetchRolesForRequestEngagement({
    onError: () => showErrorToast(formatMessage(messages.genericError)),
  })

  const handleSubmit: Config<FormValues>['onSubmit'] = async values => {
    try {
      const {
        templateId,
        name,
        roles: selectedRoles,
        description,
        displayOrder,
      } = values
      const defaultRoles = selectedRoles.some(({ id }) => id === 'ALL')
        ? ['ALL']
        : selectedRoles.filter(({ id }) => id !== 'MM').map(RoleMapper)

      const commonParams = {
        name,
        defaultRoles,
        description,
        displayOrder,
      }
      setShowLoader(true)
      if (copy) {
        const { templateEngagementTypeId, id: copyEngagementTypeId } = copy
        const variables: duplicateEngagementTypeVariables = {
          ...commonParams,
          templateEngagementTypeId,
          copyEngagementTypeId,
        }
        await mutateDuplicateEngagementType(client, { variables })
      } else {
        const variables: createEngagementTypeVariables = {
          ...commonParams,
          templateEngagementTypeId: templateId,
        }
        await mutateCreateEngagementType(client, { variables })
      }
      setShowLoader(false)
      onCreateSuccess(name)
      nextStep()
      return undefined
    } catch (error) {
      setShowLoader(false)
      return { [FORM_ERROR]: { code: 'GenericError' } }
    }
  }

  const { getEngagementTypes } = useEngagementTypesGrandTotalQuery({
    notifyOnNetworkStatusChange: true,
    fetchPolicy: 'cache-and-network',
    variables: {
      page: {},
      filters: {},
    },
    onError: () => showErrorToast(formatMessage(messages.genericError)),
  })

  const { grandTotal = 0 } = getEngagementTypes ?? {}

  const initialValues: FormValues | {} = useMemo(() => {
    if (copy) {
      const {
        name,
        defaultRoles,
        description,
        templateEngagementTypeId: templateId,
      } = copy!

      return {
        name: formatMessage(messages.copyDrawerHeader, { name }),
        description,
        templateId,
        displayOrder: grandTotal + 1,

        roles: defaultRoles.some(id => id === 'ALL')
          ? [
              {
                label: formatMessage(commonMessages.allMyAttendees),
                id: 'ALL',
              },
            ]
          : [
              {
                label: formatMessage(roleLabel.MM),
                id: 'MM',
                clearableValue: false,
              },
              ...roles
                .filter(({ roleCompanyid }) =>
                  defaultRoles.some(roleId => roleId === roleCompanyid)
                )
                .map(
                  ({ roleCompanyid, roleDisplayName }): Option => ({
                    label: formatMessage(roleLabel[roleDisplayName]),
                    id: roleCompanyid,
                  })
                ),
            ],
      }
    } else {
      return {
        name: '',
        displayOrder: grandTotal + 1,
        roles: [
          {
            label: formatMessage(commonMessages.allMyAttendees),
            id: 'ALL',
          },
          {
            label: formatMessage(roleLabel.MM),
            id: 'MM',
            clearableValue: false,
          },
        ],
      }
    }
  }, [copy, formatMessage, roles, grandTotal])

  return (
    <>
      {showLoader && <ModalSpinner />}
      <FinalForm<FormValues>
        decorators={[focusDecorator]}
        onSubmit={handleSubmit}
        initialValues={initialValues}
        render={props => (
          <EngagementTypeForm {...props} copy={copy}>
            <RolesField roles={roles} />
            <DisplayOrderField grandTotal={grandTotal + 1} />
          </EngagementTypeForm>
        )}
      />
    </>
  )
}
