import { VFC } from 'react'
import { FieldRenderProps } from 'react-final-form'
import { Toggle } from '@jifflenow/brightui/toggle'
import {
  FieldLabel,
  FieldToggleWrapper,
  GenericError,
  ToggleContainer,
} from '../common/styled-components'

export const EngagementTypeToggle: VFC<FieldRenderProps<string>> = ({
  label,
  input: { name, value, onChange },
  meta: { error, submitError },
  children,
}) => (
  <>
    {(submitError || error) && (
      <GenericError>{submitError || error}</GenericError>
    )}
    <FieldToggleWrapper $error={submitError || error}>
      <FieldLabel>{label}</FieldLabel>
      <ToggleContainer>
        <Toggle
          ariaLabel={label}
          name={name}
          data-pendo={name}
          checked={value === 'true'}
          onChange={(e: any) => onChange(e.target.checked ? 'true' : 'false')}
        />
      </ToggleContainer>
    </FieldToggleWrapper>
    {value === 'true' && children}
  </>
)
