import { SelectOverrides } from '@jifflenow/brightui/select'
import { TagOverrides as Overrides, KIND } from '@jifflenow/brightui/tag'

export const TagOverrides: Overrides = {
  Root: {
    style: ({ $theme: { colors, sizing }, $kind }) => {
      const borderColor =
        $kind === KIND.positive ? colors.primary500 : colors.mono700

      return {
        color: $kind === KIND.positive ? colors.primary500 : colors.mono700,
        borderTopColor: borderColor,
        borderRightColor: borderColor,
        borderBottomColor: borderColor,
        borderLeftColor: borderColor,
        height: sizing.scale600,
        borderTopLeftRadius: sizing.scale400,
        borderTopRightRadius: sizing.scale400,
        borderBottomRightRadius: sizing.scale400,
        borderBottomLeftRadius: sizing.scale400,
      }
    },
  },
}

export const selectOverrides: SelectOverrides = {
  ControlContainer: {
    style: {
      borderLeftStyle: 'none',
      borderRightStyle: 'none',
      borderTopStyle: 'none',
      borderBottomStyle: 'none',
      borderTopLeftRadius: 0,
      borderTopRightRadius: 0,
      borderBottomLeftRadius: 0,
      borderBottomRightRadius: 0,
    },
  },
}
