import { VFC, useState } from 'react'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { formatDate } from 'utils/dates'
import { getJSDate, CopySlotsRoot } from '../common'
import { CopyDaySlotsToProps } from './types'

export const CopyDaySlotsTo: VFC<CopyDaySlotsToProps> = props => {
  const [checked, setChecked] = useState(false)
  const { weekDay, weekwise } = props

  return (
    <CopySlotsRoot>
      <Checkbox
        checked={checked}
        onChange={() => {
          setChecked(!checked)
          props.addCopyDestinationDays(weekDay)
        }}
      >
        {weekwise ? weekDay : formatDate(getJSDate(weekDay))}
      </Checkbox>
    </CopySlotsRoot>
  )
}
