import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Field, Validation } from 'ui-components/Field'
import { EngagementTypeToggleWithDesc } from '../../common'
import { TimeDurationSelect } from '../../common/TimeDurationSelect'
import {
  ToggleWrapper,
  DurationWrapper,
  InputFlexWrapper,
} from '../styled-components'
import { EngagementInputField } from '../EngagementInputField'

const messages = defineMessages({
  boothTourLocationLabel: {
    defaultMessage: 'Designated Location Name',
    description: 'Field label',
  },
  bootTourLocationPlaceholder: {
    defaultMessage: 'Designated Location Name',
    description: 'Placeholder text',
  },
  boothTourLocationAddressLabel: {
    defaultMessage: 'Designated Location Address',
    description: 'Field label',
  },
  bootTourLocationAddressPlaceholder: {
    defaultMessage: 'Designated Location Address',
    description: 'Placeholder text',
  },
  minDurationLabel: {
    defaultMessage: 'Minimum Duration',
    description: 'Field label',
  },
  maxDurationLabel: {
    defaultMessage: 'Maximum Duration',
    description: 'Field label',
  },
  required: {
    defaultMessage: 'Required',
    description: 'Field validation error message',
  },
  designatedLocationLabel: {
    defaultMessage: 'Designated Location',
    description: 'Toggle label',
  },
  designatedLocationDesc: {
    defaultMessage:
      'A fixed location with unlimited bookings; example: Booth #123, Exhibit Hall A',
    description: 'Toggle desc',
  },
})

export const BoothTourSettings: VFC = () => {
  const { formatMessage } = useIntl()

  return (
    <EngagementTypeToggleWithDesc
      label={formatMessage(messages.designatedLocationLabel)}
      name="booth_tour_enabled"
      desc={formatMessage(messages.designatedLocationDesc)}
    >
      <ToggleWrapper>
        <InputFlexWrapper>
          <Field
            name="booth_tour_default_name"
            render={EngagementInputField}
            label={formatMessage(messages.boothTourLocationLabel)}
            placeholder={formatMessage(messages.bootTourLocationPlaceholder)}
            validations={[Validation.REQUIRED]}
          />
          <Field
            name="booth_tour_default_location"
            render={EngagementInputField}
            label={formatMessage(messages.boothTourLocationAddressLabel)}
            placeholder={formatMessage(
              messages.bootTourLocationAddressPlaceholder
            )}
            validations={[Validation.REQUIRED]}
          />
        </InputFlexWrapper>
        <DurationWrapper>
          <TimeDurationSelect
            label={formatMessage(messages.minDurationLabel)}
            name={'booth_tour_min_duration'}
          />
          <TimeDurationSelect
            label={formatMessage(messages.maxDurationLabel)}
            name={'booth_tour_max_duration'}
          />
        </DurationWrapper>
      </ToggleWrapper>
    </EngagementTypeToggleWithDesc>
  )
}
