export const getFrameCode = (
  externalWidgetUrl: string
) => `<iframe src="${externalWidgetUrl}&embedded=true" width="500px" height="760px"   frameborder="0" style="border:0px;"  marginheight="0" marginwidth="0">
  Loading
</iframe>`

