import { useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import {
  getTabOverrides,
  Tab,
  Tabs,
  TabTitle,
} from '@jifflenow/brightui/tabs-motion'
import { TabWrapper } from './styled-components'
import { tabOverrides, tabsOverrides } from './overrides'
import { RolesAndPermissionSettings } from './RolesAndPermissionSettings'

const messages = defineMessages({
  surveyLabel: {
    defaultMessage: 'Design',
    description: 'Field label',
  },
  rolesAndPermissionTabLabel: {
    defaultMessage: 'Roles & Permission',
    description: 'Tab label',
  },
})

export const SurveyTab: VFC = () => {
  const { formatMessage } = useIntl()
  const [activeKey, setActiveKey] = useState('0')
  enum FormElementsTabProps {
    RolesAndPermission = 'Roles & Permission',
  }

  return (
    <Tabs
      activeKey={activeKey}
      onChange={({ activeKey }) => {
        setActiveKey(activeKey as FormElementsTabProps)
      }}
      overrides={tabsOverrides}
    >
      <Tab
        overrides={getTabOverrides(tabOverrides)}
        title={
          <TabTitle>
            {formatMessage(messages.rolesAndPermissionTabLabel)}
          </TabTitle>
        }
      >
        <TabWrapper>
          <RolesAndPermissionSettings />
        </TabWrapper>
      </Tab>
    </Tabs>
  )
}
