import ContentLoader from 'react-content-loader'
import { useStyletron } from '@jifflenow/brightui'

export const IrpContentLoader = () => {
  const [, theme] = useStyletron()
  const loaderOptions = {
    speed: 2,
    primaryColor: theme.colors.mono200,
    secondaryColor: theme.colors.mono300,
  }

  return (
    <ContentLoader
      width="1000"
      height="600"
      viewBox="0 0 1000 600"
      {...loaderOptions}
    >
      <rect y="25" x="0" width="200" height="20" />

      <rect y="85" x="0" width="250" height="15" />
      <rect y="110" x="0" width="282" height="10" />
      <rect y="80" x="955" width="42" height="24" rx="12" ry="12" />
      <rect y="160" x="0" width="1000" height="1.5" />
      <rect y="185" x="0" width="320" height="8" />
    </ContentLoader>
  )
}
