import { VFC } from 'react'
import {
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  SIZE,
} from '@jifflenow/brightui/modal'
import { KIND } from '@jifflenow/brightui/button'
import { ConfirmationMessage, FooterButton } from './styled-components'
import { ConfirmationDialogProps } from './types'
import { successButtonOverrides } from './overrides'

export const ConfirmationDialog: VFC<ConfirmationDialogProps> = ({
  heading,
  message,
  successLabel,
  cancelLabel,
  onClose,
  onCancel,
  onSuccess,
}) => (
  <Modal size={SIZE.small} closeable={false} onClose={onClose} isOpen>
    <ModalHeader>{heading}</ModalHeader>
    <ModalBody>
      <ConfirmationMessage>{message}</ConfirmationMessage>
    </ModalBody>
    <ModalFooter>
      <FooterButton type="button" kind={KIND.tertiary} onClick={onCancel}>
        {cancelLabel}
      </FooterButton>
      <FooterButton
        type="button"
        overrides={successButtonOverrides}
        onClick={onSuccess}
      >
        {successLabel}
      </FooterButton>
    </ModalFooter>
  </Modal>
)
