import { DateTime, DurationObject } from 'luxon'

const referenceISODate = '1970-01-01T00:00:00Z'

export const getDateFromMinute = ({
  days,
  minutes,
}: {
  days: number
  minutes: number
}) => {
  const hour = Math.floor(minutes / 60)
  const minute = minutes % 60

  return DateTime.fromISO(referenceISODate)
    .startOf('day')
    .set({ day: ++days, hour, minute })
    .toString()
}

export const getMinutes = (date: string) => {
  const { minute, hour } = DateTime.fromISO(date)

  return hour * 60 + minute
}

export const getTimeFromDate = (date: string) =>
  DateTime.fromISO(date).toISOTime({ includeOffset: false })

export const getEndTimeOfDay = (date: string) =>
  DateTime.fromISO(`${DateTime.fromISO(date).toISODate()}T24:00:00`).toISOTime({
    includeOffset: false,
  })

export const areDatesAdjacent = (prev: string, next: string) =>
  diffInISODates(prev, next).days === 1

export const getFullMinutes = (date: string) => {
  const { minute, hour } = DateTime.fromISO(date)

  return hour * 60 + minute
}

export const getISODateFromTime = (time: string) =>
  DateTime.fromISO(
    `${DateTime.fromISO(referenceISODate).toISODate()}T${time}`
  ).toISO()

export const getPlusISOTime = (date: string, plus: DurationObject) =>
  DateTime.fromISO(date).plus(plus).toISOTime({ includeOffset: false })

export const getISOTimeFromJSDate = (date: Date) =>
  DateTime.fromJSDate(date).toISOTime({ includeOffset: false })

export const getJSDate = (date: string) => DateTime.fromISO(date).toJSDate()

export const getISODateFromDateAndTime = (date: string, time: string) =>
  `${DateTime.fromISO(date).toISODate()}T${time}`

export const getISODateString = (date: string) =>
  DateTime.fromISO(date).toISODate()

export const getDate = (date: string) => DateTime.fromISO(date)

export const getPlusDate = (date: string, plus: DurationObject) =>
  DateTime.fromISO(date).plus(plus).toISO()

export const getDateStringFromJSDate = (date: Date) =>
  DateTime.fromJSDate(date).toISODate()

export const diffInISODates = (prev: string, next: string) =>
  DateTime.fromISO(next).diff(DateTime.fromISO(prev), 'day')

export const getPlusISOStringDate = (date: string, plus: DurationObject) =>
  DateTime.fromISO(date).plus(plus).toISODate()

export const getFormattedTimeFromDate = (date: string) =>
  DateTime.fromISO(date).toFormat('hh:mm a')

export const getDurationInDays = (startDate?: string, endDate?: string) => {
  const start = DateTime.fromISO(startDate!)
  const end = DateTime.fromISO(endDate!)

  return end.diff(start, ['day']).days
}

export const getEndDateOfDay = (date: string) =>
  DateTime.fromISO(`${DateTime.fromISO(date).toISODate()}T24:00:00`).toISO()

export const getISOTimeFromDate = (date: string) =>
  DateTime.fromISO(date).toISOTime({ includeOffset: false })
