import { useCallback, useEffect, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useField, useFormState } from 'react-final-form'
import { useEventLocation } from 'providers/LocationProvider'
import createDecorator from 'final-form-calculate'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { DependentLinks, EngagementTypeToggle } from '../common'
import { EngagementModal } from '../common/EngagementModal'
import { Paths } from '../Paths'
import { useEngagementType } from '../EngagementTypeProvider'
import { BorderStyled, Section, SectionTitle } from './styled-components'
import { Fields, FormValues, MappingsProps } from './types'

const identity = (value: string) => value

const messages = defineMessages({
  mappingsTitle: {
    defaultMessage: 'Mappings',
    description: 'Section title',
  },
  defaultMapping: {
    defaultMessage: 'Enable Mappings',
    description: 'Field label',
  },
  assignTopic: {
    defaultMessage: 'Show Topic Selection section',
    description: 'Field label',
  },
  assignTopicMandatory: {
    defaultMessage: 'Make Topic selection mandatory',
    description: 'Field label',
  },
  assignAttendees: {
    defaultMessage: "Show 'Pick Attendees' section",
    description: 'Field label',
  },
  assignInternalAttendees: {
    defaultMessage: 'At least one internal attendee is required',
    description: 'Field label',
  },
  assignExternalAttendees: {
    defaultMessage: 'At least one external attendee is required',
    description: 'Field label',
  },
  blockAttendeeCalendar: {
    defaultMessage: 'Block Attendee Calender',
    description: 'Field label',
  },
  assignRoom: {
    defaultMessage: "Show 'Select a Location' section",
    description: 'Field label',
  },
  blockRoomCalendar: {
    defaultMessage: 'Block Room Calender',
    description: 'Field label',
  },
  warningModalTitle: {
    defaultMessage:
      'Please ensure following settings are done before enabling mappings',
    description: 'Dependent settings modal title',
  },
  warningDisableLabel: {
    defaultMessage: 'Disable the following',
    description: 'Dependent settings group header',
  },
  boothTourLabel: {
    defaultMessage: 'Designated Location',
    description: 'Dependent setting label',
  },
  enableManualSchedulingLabel: {
    defaultMessage: 'Enable manual scheduling',
    description: 'Dependent setting label',
  },
  autoScheduleMappedRequestLabel: {
    defaultMessage: 'Auto schedule - mapped request',
    description: 'Dependent setting label',
  },
})

export const mappingDecorator = createDecorator(
  {
    field: 'default_mapping',
    updates: {
      assign_attendees: identity,
      block_attendee_calendar: identity,
      assign_room: identity,
      block_room_calendar: identity,
    },
  },
  {
    field: 'assign_topic',
    updates: {
      assign_topic_mandatory: (value, allValues) =>
        value === 'false'
          ? 'false'
          : (allValues as Fields)?.assign_topic_mandatory,
    },
  },
  {
    field: 'assign_attendees',
    updates: {
      assign_internal_attendees: identity,
    },
  }
)

export const Mappings: VFC<MappingsProps> = ({
  dependentFieldValues: {
    booth_tour_enabled,
    enable_external_widget_auto_process,
  },
  refetch,
  loading,
}) => {
  const { formatMessage } = useIntl()
  const { mapping_module, enable_topic } = useEventLocation().configs!
  const {
    masterEngagementType,
  } = useEngagementType().engagementType.templateEngagementType
  const isSession =
    masterEngagementType === MasterEngagementTypes.TracksAndSession
  const [isOpen, setIsOpen] = useState(false)
  const { enable_manual_scheduling } = useFormState<FormValues>().values

  const {
    input: { value, onChange },
  } = useField('default_mapping')

  const canEnableMappings = useCallback(
    () =>
      booth_tour_enabled === 'false' && enable_manual_scheduling === 'false',
    [booth_tour_enabled, enable_manual_scheduling]
  )

  const canDisableMappings = useCallback(
    () => enable_external_widget_auto_process === 'false',
    [enable_external_widget_auto_process]
  )

  useEffect(() => {
    value === 'true' && setIsOpen(!canEnableMappings())
    value === 'false' && setIsOpen(!canDisableMappings())
  }, [canDisableMappings, canEnableMappings, value])

  const dependentLinks = [
    booth_tour_enabled === 'true' && {
      label: formatMessage(messages.boothTourLabel),
      path: Paths.SETTING,
      group: formatMessage(messages.warningDisableLabel),
    },
    enable_manual_scheduling === 'true' && {
      label: formatMessage(messages.enableManualSchedulingLabel),
      path: Paths.CSM_ONLY,
      group: formatMessage(messages.warningDisableLabel),
    },
    enable_external_widget_auto_process === 'true' && {
      label: formatMessage(messages.autoScheduleMappedRequestLabel),
      path: Paths.INBOUND_REQUEST_FORM,
      group: formatMessage(messages.warningDisableLabel),
    },
  ].filter(Boolean) as DependentLinks[]

  return mapping_module === 'true' ? (
    <>
      {isOpen && (
        <>
          {loading && <ModalSpinner />}
          <EngagementModal
            title={formatMessage(messages.warningModalTitle)}
            dependentLinks={dependentLinks}
            onClose={() => {
              setIsOpen(false)
              !canEnableMappings() && onChange('false')
              !canDisableMappings() && onChange('true')
            }}
            onConfirm={() => refetch()}
          />
        </>
      )}
      <Section>
        <SectionTitle>{formatMessage(messages.mappingsTitle)}</SectionTitle>
        <BorderStyled />
        <EngagementTypeToggle
          label={formatMessage(messages.defaultMapping)}
          name="default_mapping"
          disabled={isSession}
        >
          <BorderStyled />
          {enable_topic === 'true' && (
            <>
              <EngagementTypeToggle
                label={formatMessage(messages.assignTopic)}
                name="assign_topic"
              >
                <BorderStyled />
                <EngagementTypeToggle
                  label={formatMessage(messages.assignTopicMandatory)}
                  name="assign_topic_mandatory"
                />
              </EngagementTypeToggle>
              <BorderStyled />
            </>
          )}
          <EngagementTypeToggle
            label={formatMessage(messages.assignAttendees)}
            name="assign_attendees"
            disabled
          >
            <BorderStyled />
            <EngagementTypeToggle
              label={formatMessage(messages.assignInternalAttendees)}
              name="assign_internal_attendees"
              disabled
            />
            <BorderStyled />
            <EngagementTypeToggle
              label={formatMessage(messages.assignExternalAttendees)}
              name="assign_external_attendees"
              disabled
            />
            <BorderStyled />
            <EngagementTypeToggle
              label={formatMessage(messages.blockAttendeeCalendar)}
              name="block_attendee_calendar"
              disabled
            />
            <BorderStyled />
            <EngagementTypeToggle
              label={formatMessage(messages.assignRoom)}
              name="assign_room"
              disabled
            >
              <BorderStyled />
              <EngagementTypeToggle
                label={formatMessage(messages.blockRoomCalendar)}
                name="block_room_calendar"
                disabled
              />
            </EngagementTypeToggle>
          </EngagementTypeToggle>
        </EngagementTypeToggle>
        <BorderStyled />
      </Section>
    </>
  ) : null
}
