import { createContext, FC, useContext, useMemo } from 'react'
import { useParams } from 'react-router-dom'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { ErrorPage } from 'ui-components/ErrorPage'
import { useEngagementTypeDetails, useEngagementTypeQuery } from 'gql'
import {
  getEngagementTypeDetails_getEngagementTypeSettings,
  getEngagementTypeDetails_getEngagementTypeSettings_value_CustomNameValue as CustomNameValue,
  getEngagementTypeDetails_getEngagementTypeSurveys,
} from 'gql/types/getEngagementTypeDetails'
import { getEngagementType_getEngagementType } from 'gql/types/getEngagementType'
import { useEventLocation } from 'providers/LocationProvider'
import { useQuery } from 'react-query'
import {
  fetchEngagementType,
  fetchEngagementTypeSettingsRestQuery,
  fetchSurveys,
} from 'rest'
import { components } from 'types/openapi'
import { MasterEngagementTypes } from 'gql/types/globalTypes'

type EngagementTypeSettings = Record<'enable_external_widget', string>
type SurverysListType = components['schemas']['SurveyListResponse']['data']

type EngagementTypeDetails = {
  engagementType: getEngagementType_getEngagementType
  engagementTypeSurveys: getEngagementTypeDetails_getEngagementTypeSurveys[]
  engagementTypeSettings: EngagementTypeSettings
}

type EngagementTypeDetailsV1 = {
  engagementType: Omit<
    getEngagementType_getEngagementType,
    '__typename' | 'defaultRoles'
  > & {
    templateEngagementType: MasterEngagementTypes
  }
  engagementTypeSettings: EngagementTypeSettings
  engagementTypeSurveys: SurverysListType
}

const EngagementTypeContext = createContext<
  EngagementTypeDetails | EngagementTypeDetailsV1 | undefined
>(undefined)

const useEngagementTypeQueryHelper = (version: string) =>
  useEngagementTypeQuery({
    variables: { uuid: useParams<{ engagementId: string }>().engagementId },
    notifyOnNetworkStatusChange: true,
    skip: version === '1',
  })

const mapEngagementTypeSettings = (
  settings: getEngagementTypeDetails_getEngagementTypeSettings[]
) =>
  settings.reduce<Partial<EngagementTypeSettings>>((acc, { name, value }) => {
    acc[name as keyof EngagementTypeSettings] = (value as CustomNameValue).value

    return acc
  }, {})

const useEngagementTypeDetailsHelper = (
  engagementType: getEngagementType_getEngagementType | undefined
) => {
  const {
    loading,
    error,
    refetch,
    getEngagementTypeSurveys: engagementTypeSurveys,
    getEngagementTypeSettings: engagementTypeSettings,
  } = useEngagementTypeDetails({
    variables: {
      engagementTypeId: engagementType?.id!,
      names: ['enable_external_widget'],
    },
    skip: !engagementType,
    notifyOnNetworkStatusChange: true,
  })

  return {
    loading,
    error,
    refetch,
    engagementTypeDetails:
      engagementType &&
      engagementTypeSurveys &&
      engagementTypeSettings &&
      ({
        engagementType,
        engagementTypeSurveys,
        engagementTypeSettings: mapEngagementTypeSettings(
          engagementTypeSettings
        ),
      } as EngagementTypeDetails),
  }
}

const resetMasterMeetingTypeName = (
  name: components['schemas']['EngagementTypeMaster']
): MasterEngagementTypes => {
  if (name === 'Consecutive Meetings') {
    return MasterEngagementTypes.ConsecutiveMeeting
  } else if (name === 'Tracks and Sessions') {
    return MasterEngagementTypes.TracksAndSession
  } else {
    return MasterEngagementTypes.General
  }
}

// eslint-disable-next-line complexity
export const EngagementTypeProvider: FC = ({ children }) => {
  const { systemName, version } = useEventLocation().location!.attributes

  const engagementRes = useEngagementTypeQueryHelper(version)

  const detailRes = useEngagementTypeDetailsHelper(
    engagementRes.getEngagementType
  )

  const engagementResV1 = useQuery(
    [
      'getEngagementTypeResponse',
      {
        path: {
          eventName: systemName,
          engagementTypeUuid: useParams<{ engagementId: string }>()
            .engagementId,
        },
      },
    ],
    fetchEngagementType,
    {
      enabled: version === '1',
    }
  )

  const detailResV1 = useQuery(
    [
      'getEngagementTypeSettingsResponse',
      {
        path: {
          eventName: systemName,
          engagementTypeUuid: useParams<{ engagementId: string }>()
            .engagementId,
        },
        query: { 'names[]': ['enable_external_widget'] },
      },
    ],
    fetchEngagementTypeSettingsRestQuery,
    {
      enabled: version === '1',
    }
  )

  const surveysResV1 = useQuery(
    [
      'fetchSurveys',
      {
        path: {
          eventName: systemName,
          engagementTypeUuid: useParams<{ engagementId: string }>()
            .engagementId,
        },
      },
    ],
    fetchSurveys,
    {
      enabled: version === '1',
    }
  )

  const engagementTypeSettings = detailResV1.data?.data.reduce(
    (acc, { name, value }) => {
      const tval = value === 'yes' ? 'true' : value === 'no' ? 'false' : value

      return { ...acc, [name]: tval }
    },
    {}
  )

  const getEngagementTypeV1 = useMemo(() => {
    const { data } = engagementResV1
    const { data: surveysData } = surveysResV1

    return (
      data && {
        engagementType: {
          id: data.id,
          uuid: data.id,
          name: data.name,
          description: data.description,
          displayOrder: data.displayOrder,
          status: data.status,
          templateEngagementType: {
            masterEngagementType: resetMasterMeetingTypeName(
              data.meetingTypeMasterName
            ),
          },
        },
        engagementTypeSettings,
        engagementTypeSurveys: surveysData?.data,
      }
    )
  }, [engagementResV1, engagementTypeSettings, surveysResV1])

  if (version === '1') {
    if (detailResV1.error || engagementResV1.error || surveysResV1.error) {
      return (
        <ErrorPage
          onButtonClick={() =>
            (detailResV1.error
              ? detailResV1.refetch
              : engagementResV1.error
              ? engagementResV1.refetch
              : surveysResV1.refetch)()
          }
        />
      )
    }

    return (
      <>
        {!detailResV1.data &&
          (detailResV1.isLoading ||
            engagementResV1.isLoading ||
            surveysResV1.isLoading) && <ModalSpinner />}
        {detailResV1.data && surveysResV1.data && (
          <EngagementTypeContext.Provider
            value={getEngagementTypeV1 as EngagementTypeDetailsV1}
          >
            {children}
          </EngagementTypeContext.Provider>
        )}
      </>
    )
  } else {
    if (detailRes.error || engagementRes.error) {
      return (
        <ErrorPage
          onButtonClick={() =>
            (detailRes.error ? detailRes.refetch : engagementRes.refetch)()
          }
        />
      )
    }

    return (
      <>
        {!detailRes.engagementTypeDetails &&
          (detailRes.loading || engagementRes.loading) && <ModalSpinner />}
        {detailRes.engagementTypeDetails && (
          <EngagementTypeContext.Provider
            value={detailRes.engagementTypeDetails}
          >
            {children}
          </EngagementTypeContext.Provider>
        )}
      </>
    )
  }
}

export const useEngagementType = () => {
  const context = useContext(EngagementTypeContext) as EngagementTypeDetails
  if (context === undefined && process.env.NODE_ENV !== 'production') {
    throw new Error(
      'useEngagementType must be used within a EngagementTypeProvider'
    )
  }

  return context
}
