import ContentLoader from 'react-content-loader'
import { useStyletron } from '@jifflenow/brightui'

export const FieldLoader = () => {
  const [, theme] = useStyletron()
  const loaderOptions = {
    speed: 2,
    primaryColor: theme.colors.mono200,
    secondaryColor: theme.colors.mono300,
  }

  return (
    <ContentLoader
      width="1000"
      height="200"
      viewBox="0 0 1000 200"
      {...loaderOptions}
    >
      <rect y="70" x="0" width="110" height="11" />
      <rect y="90" x="0" width="335" height="40" rx="5" ry="5" />
    </ContentLoader>
  )
}
