import { FC, VFC } from 'react'
import { FieldRenderProps } from 'react-final-form'
import { RadioGroup, Radio, ALIGN } from '@jifflenow/brightui/radio'
import { defineMessages, useIntl } from 'react-intl'
import { Field } from 'ui-components/Field'
import { TimeDurationSelect } from '../common/TimeDurationSelect'
import {
  GenericError,
  FormControlStyledToggleContainer,
  DurationContainer,
  SchedulingTitle,
} from './styled-components'

const messages = defineMessages({
  MeetingBufferLabel: {
    defaultMessage: 'Duration',
    description: 'Field label',
  },
  bufferTimeLabel: {
    defaultMessage: 'Lead time for IRP slots',
    description: 'Field label',
  },
  nextDayRequestLabel: {
    defaultMessage: 'Do not allow same day requests',
    description: 'Field label',
  },
  schedulingLogicLabel: {
    defaultMessage: 'Scheduling logic',
    description: 'Field label',
  },
})

const BufferTimeDuration: FC = () => {
  const { formatMessage } = useIntl()

  return (
    <DurationContainer>
      <TimeDurationSelect
        label={formatMessage(messages.MeetingBufferLabel)}
        name="irp_meeting_buffer"
        multiDay
      />
    </DurationContainer>
  )
}

const SchedulingLogic: FC<FieldRenderProps<string>> = ({
  input: { value, onChange },
  meta: { submitError },
}) => {
  const { formatMessage } = useIntl()

  return (
    <>
      {submitError && <GenericError>{submitError}</GenericError>}
      <RadioGroup
        align={ALIGN.vertical}
        value={value}
        onChange={e => onChange(e.target.value)}
        name="scheduling_logic"
      >
        <Radio value="true">{formatMessage(messages.bufferTimeLabel)}</Radio>
        {value === 'true' ? <BufferTimeDuration /> : null}
        <Radio value="false">
          {formatMessage(messages.nextDayRequestLabel)}
        </Radio>
      </RadioGroup>
    </>
  )
}

export const SchedulingLogicField: VFC = () => {
  const { formatMessage } = useIntl()

  return (
    <FormControlStyledToggleContainer>
      <SchedulingTitle>
        {formatMessage(messages.schedulingLogicLabel)}{' '}
      </SchedulingTitle>
      <Field
        name="enable_irp_scheduling_logic_buffer"
        render={SchedulingLogic}
      />
    </FormControlStyledToggleContainer>
  )
}
