import { useState, VFC } from 'react'
import { FormSpy, useForm } from 'react-final-form'
import { defineMessages, FormattedMessage, useIntl } from 'react-intl'
import { KIND } from '@jifflenow/brightui/button'
import { ConfirmationDialog } from 'ui-components/ConfirmationDialog'
import { successButtonOverrides } from './overrides'
import { FooterContainer, FooterButton } from './styled-components'

const modalLabels = defineMessages({
  modalHeader: {
    defaultMessage: 'Your changes will be lost.',
    description: 'Modal heading',
  },
  modalMessage: {
    defaultMessage: 'Keep changes?',
    description: 'Confirmation message',
  },
  cancelButtonLabel: {
    defaultMessage: 'Discard',
    description: 'Button label',
  },
  successButtonLabel: {
    defaultMessage: 'Keep',
    description: 'Button label',
  },
})

export const Footer: VFC = () => {
  const { formatMessage } = useIntl()
  const { reset } = useForm()

  const [showConfirmation, setShowConfirmation] = useState(false)

  const handleDiscard = () => setShowConfirmation(true)

  const handleDialogClose = () => setShowConfirmation(false)

  const handleFormReset = () => {
    setShowConfirmation(false)
    reset()
  }

  return (
    <>
      {showConfirmation && (
        <ConfirmationDialog
          heading={formatMessage(modalLabels.modalHeader)}
          message={formatMessage(modalLabels.modalMessage)}
          cancelLabel={formatMessage(modalLabels.cancelButtonLabel)}
          successLabel={formatMessage(modalLabels.successButtonLabel)}
          onClose={handleDialogClose}
          onCancel={handleFormReset}
          onSuccess={handleDialogClose}
        />
      )}
      <FormSpy
        subscription={{
          validating: true,
          submitting: true,
          dirty: true,
        }}
      >
        {({ validating, submitting, dirty }) =>
          dirty && (
            <FooterContainer>
              <FooterButton
                type="button"
                kind={KIND.tertiary}
                onClick={() => handleDiscard()}
                data-pendo="engagement-type/discard-changes"
              >
                <FormattedMessage
                  defaultMessage="Discard Changes"
                  description="Button label"
                />
              </FooterButton>
              <FooterButton
                type="submit"
                disabled={submitting || validating}
                overrides={successButtonOverrides}
                data-pendo="engagement-type/save-changes"
              >
                <FormattedMessage
                  defaultMessage="Save Changes"
                  description="Button label"
                />
              </FooterButton>
            </FooterContainer>
          )
        }
      </FormSpy>
    </>
  )
}
