import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { CopyRoomCheckBox, CopyRoomSlotsRoot } from '../common'
import { CopyDaySlotsToRoomProps } from '../types'

export const CopyDaySlotsToRoom: VFC<CopyDaySlotsToRoomProps> = ({
  activeTab,
  engagementTypeSettings,
  roomChecked,
  setRoomChecked,
  offsiteChecked,
  setOffsiteChecked,
  boothTourChecked,
  setBoothTourChecked,
}) => {
  const { formatMessage } = useIntl()

  const messages = defineMessages({
    roomsLabel: {
      defaultMessage: 'Onsite Locations',
      description: 'Tab Label',
    },
    offsiteLabel: {
      defaultMessage: 'Offsites',
      description: 'Tab Label',
    },
    boothTour: {
      defaultMessage: 'Designated Location',
      description: 'Tab Label',
    },
  })

  const { offsite_enabled, booth_tour_enabled } = engagementTypeSettings

  return (
    <CopyRoomSlotsRoot>
      {activeTab !== 'Room' ? (
        <CopyRoomCheckBox>
          <Checkbox
            checked={roomChecked}
            onChange={() => {
              setRoomChecked(!roomChecked)
            }}
          >
            {formatMessage(messages.roomsLabel)}
          </Checkbox>
        </CopyRoomCheckBox>
      ) : null}
      {offsite_enabled === 'true' && activeTab !== 'Offsite' ? (
        <CopyRoomCheckBox>
          <Checkbox
            checked={offsiteChecked}
            onChange={() => {
              setOffsiteChecked(!offsiteChecked)
            }}
          >
            {formatMessage(messages.offsiteLabel)}
          </Checkbox>
        </CopyRoomCheckBox>
      ) : null}

      {booth_tour_enabled === 'true' && activeTab !== 'BoothTour' ? (
        <Checkbox
          checked={boothTourChecked}
          onChange={() => {
            setBoothTourChecked(!boothTourChecked)
          }}
        >
          {formatMessage(messages.boothTour)}
        </Checkbox>
      ) : null}
    </CopyRoomSlotsRoot>
  )
}
