import { useState } from 'react'
import { useQuery } from 'react-query'
import { useParams } from 'react-router-dom'
import { TextFilter } from '@jifflenow/brightui/filter'
import { useEventLocation } from 'providers/LocationProvider'
import { engagementTypesCreatedByUsersQueryFn } from 'rest'
import { useDebounce } from 'utils/debounce'
import { UsersFilterProps } from 'components/ag-grid'

export const UsersFilter = ({
  value,
  label,
  onChange,
  dataPendo,
}: UsersFilterProps) => {
  const locationId = useEventLocation().location!.id
  const { eventName } = useParams<{ eventName: string }>()
  const [searchText, setSearchText] = useState('')
  const search = useDebounce(searchText, 400)

  const { isLoading, data } = useQuery(
    [
      'engagementTypesCreatedByUsers',
      {
        path: {
          eventName,
        },
        query: {
          location_uuid: locationId,
          search_term: search,
        },
      },
    ],
    engagementTypesCreatedByUsersQueryFn,
    { enabled: !!search }
  )

  const options =
    !search || !data
      ? []
      : data.data.map(({ id, attributes: { firstName, lastName } }) => ({
          id,
          label: `${firstName} ${lastName}`,
        }))

  return (
    <TextFilter
      label={label}
      onChange={onChange}
      options={options}
      value={value}
      asyncOptions
      searchText={searchText}
      onSearch={setSearchText}
      isLoading={isLoading}
      dataPendo={dataPendo}
    />
  )
}
