import { VFC, useState, useEffect, useMemo } from 'react'
import { Prompt } from 'react-router-dom'
import { Config, FORM_ERROR } from 'final-form'
import { Form as FinalForm, FormRenderProps } from 'react-final-form'
import createFocusDecorator from 'final-form-focus'
import { defineMessages, useIntl } from 'react-intl'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import {
  mutateBulkUpsertEngagementSettings,
  useApolloClient,
  useGetEngagementTypeSettings,
} from 'gql'
import { getEngagementTypeSettings_getEngagementTypeSettings_value_CustomNameValue } from 'gql/types/getEngagementTypeSettings'
import { BulkEngagementSettingUpdateInput } from 'gql/types/globalTypes'
import {
  bulkUpsertEngagementSettingsVariables,
  bulkUpsertEngagementSettings_bulkUpsertEngagementSettings_engagementSettings_value_CustomNameValue,
} from 'gql/types/bulkUpsertEngagementSettings'
import { useEngagementType } from '../EngagementTypeProvider'
import {
  Body,
  Footer,
  StyledForm,
  Container,
  usePageTitle,
  Header,
} from '../common'
import { useSettingsLoader } from '../common/useSettingsLoader'
import { RefetchProps } from '../common/types'
import { filterEngagementSettings } from '../common/filterEngagementSettings'
import { engagementSettingErrors } from '../common/EngagementSettingErrors'
import { updateQueryResults } from '../common/utils'
import { ParticipantsTitle } from './styled-components'
import { ParticipantsLoader } from './ParticipantsLoader'
import { FormField } from './FormField'
import { DependentFields, DependentFieldValues, FormValues } from './types'

const LoadingShimmer = () => (
  <Body>
    <Container>
      <ParticipantsLoader />
    </Container>
  </Body>
)

const messages = defineMessages({
  pageTitle: {
    defaultMessage: 'Participants',
    description: 'Page title',
  },
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
  updateError: {
    defaultMessage:
      '{failureCount, number} out of {totalCount, number} {totalCount, plural, one {option} other {options}} failed to save',
    description: 'Error message',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  sectionTitle: {
    defaultMessage: 'Participants Options',
    description: 'Section title',
  },
  successMessage: {
    defaultMessage: 'Your changes have been saved successfully',
    description: 'Success message',
  },
  fieldErrorMessage: {
    defaultMessage: 'Error saving changes',
    description: 'field error message',
  },
})

const focusDecorator = createFocusDecorator<FormValues>()

const Form: VFC<
  FormRenderProps<FormValues> & RefetchProps & DependentFields
> = ({ handleSubmit, dirty, refetch, loading, dependentFields }) => {
  const { formatMessage } = useIntl()
  const unload = useBeforeUnload()
  const showLoading = useSettingsLoader(loading)

  useEffect(() => {
    unload(dirty)
  }, [dirty, unload])

  return (
    <StyledForm onSubmit={handleSubmit} noValidate>
      <Prompt when={dirty} message={formatMessage(messages.promptMessage)} />
      <Header />
      {showLoading ? (
        <LoadingShimmer />
      ) : (
        <Body>
          <Container>
            <ParticipantsTitle>
              {formatMessage(messages.sectionTitle)}
            </ParticipantsTitle>
            <FormField
              refetch={refetch}
              loading={loading}
              dependentFields={dependentFields}
            />
          </Container>
          <Footer />
        </Body>
      )}
    </StyledForm>
  )
}

const fieldNames = [
  'hide_internal_attendees_in_book_meeting',
  'internal_attendee_presence',
  'relax_internal_attendee_presence_for_mm',
  'atleast_one_attendee_role_presence',
  'allow_requestor_to_add_internal_attendee',
  'send_verification_email_to_attendee',
  'hide_external_attendees_in_book_meeting',
  'external_attendee_presence',
  'all_external_attendees_visible',
  'relax_external_attendee_presence_for_mm',
  'external_attendees_visible_added_by_mm',
  'allow_user_to_add_new_external_attendee',
  'external_ics_opt_out',
  'mode_of_attendance',
  'enable_mode_of_attendance',
  'enable_meeting_host',
  'meeting_host_label',
  'atleast_one_meeting_host',
  'assign_requestors_as_meeting_host_by_default',
  'assign_mapped_user_as_meeting_host_by_default',
  'assign_only_topic_mapped_user_as_meeting_host',
  'allow_requestors_to_unassign_meeting_host',
  'allow_meeting_host_to_manage_meeting',
  'allow_meeting_host_to_edit_meeting',
  'send_copy_of_mm_update_email_to_host',
  'make_participants_optional_to_the_meeting',
  'allow_requestor_to_self_nominate_to_meeting',
]

const dependentFieldNames = [
  'enable_external_widget_auto_process_without_mapping',
]

export const Participants: VFC = () => {
  const [showLoader, setShowLoader] = useState(false)
  const { formatMessage } = useIntl()
  const client = useApolloClient()
  usePageTitle(formatMessage(messages.pageTitle))
  const { id: engagementTypeId } = useEngagementType().engagementType

  const {
    loading,
    getEngagementTypeSettings = [],
    refetch: refetchGetEngagementTypeSettings,
    updateQuery,
  } = useGetEngagementTypeSettings({
    notifyOnNetworkStatusChange: true,
    variables: {
      engagementTypeId,
      names: [...fieldNames, ...dependentFieldNames],
    },
    fetchPolicy: 'cache-and-network',
    onError: () => {
      showErrorToast(formatMessage(messages.genericError))
    },
  })

  const fieldValues = useMemo(
    () => filterEngagementSettings(getEngagementTypeSettings, fieldNames),
    [getEngagementTypeSettings]
  )

  const dependentValues = useMemo(
    () =>
      filterEngagementSettings(getEngagementTypeSettings, dependentFieldNames),
    [getEngagementTypeSettings]
  )

  const initialValues = useMemo(
    () =>
      fieldValues.reduce(
        (acc, { name, value }) => ({
          ...acc,
          [name]: (value as getEngagementTypeSettings_getEngagementTypeSettings_value_CustomNameValue)
            .value,
        }),
        {}
      ),
    [fieldValues]
  )

  const dependentFieldValues = useMemo(
    () =>
      dependentValues.reduce(
        (acc, { name, value }) => ({
          ...acc,
          [name]: (value as getEngagementTypeSettings_getEngagementTypeSettings_value_CustomNameValue)
            .value,
        }),
        {}
      ),
    [dependentValues]
  ) as DependentFieldValues

  const handleSubmit: Config<FormValues>['onSubmit'] = async (
    values,
    { change }
  ) => {
    try {
      setShowLoader(true)
      const configsToUpdate = fieldValues.reduce<
        BulkEngagementSettingUpdateInput[]
      >((acc, { name, value: previousValue }) => {
        const {
          display_name,
          value,
        } = previousValue as getEngagementTypeSettings_getEngagementTypeSettings_value_CustomNameValue

        return value !== values[name as keyof FormValues]
          ? [
              ...acc,
              {
                name,
                customValue: {
                  display_name,
                  value: values[name as keyof FormValues],
                },
              },
            ]
          : acc
      }, [])

      const variables: bulkUpsertEngagementSettingsVariables = {
        engagementTypeId,
        configsToUpdate,
      }
      const { failureCount, totalCount, engagementSettings, successCount } = (
        await mutateBulkUpsertEngagementSettings(client, {
          variables,
        })
      ).data!.bulkUpsertEngagementSettings
      setShowLoader(false)
      if (failureCount) {
        showErrorToast(
          formatMessage(messages.updateError, { totalCount, failureCount })
        )
        engagementSettings.forEach(
          ({ success, name, value }) =>
            !success &&
            change(
              name as keyof FormValues,
              (value as bulkUpsertEngagementSettings_bulkUpsertEngagementSettings_engagementSettings_value_CustomNameValue)
                .value ?? undefined
            )
        )
        const errors = engagementSettings.reduce(
          (acc, { success, name, errorCode }) =>
            success
              ? acc
              : {
                  ...acc,
                  [name]: formatMessage(
                    (errorCode && engagementSettingErrors[errorCode]) ??
                      messages.fieldErrorMessage
                  ),
                },
          {}
        )
        if (successCount) {
          updateQueryResults(updateQuery, engagementSettings)
        }

        return errors
      }
      showSuccessToast(formatMessage(messages.successMessage))
      updateQueryResults(updateQuery, engagementSettings)

      return undefined
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
      return { [FORM_ERROR]: { code: 'GenericError' } }
    }
  }

  return (
    <>
      {showLoader && <ModalSpinner />}
      <FinalForm<FormValues>
        decorators={[focusDecorator]}
        initialValues={initialValues}
        onSubmit={handleSubmit}
        render={props => (
          <Form
            {...props}
            refetch={() => refetchGetEngagementTypeSettings()}
            loading={loading}
            dependentFields={dependentFieldValues}
          />
        )}
      />
    </>
  )
}
