import { VFC, useState, useEffect } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Modal, ModalHeader, SIZE } from '@jifflenow/brightui/modal'
import {
  getTimeFromDate,
  getPlusISOStringDate,
  getFullMinutes,
} from '../../common'
import { AddEditExceptionProps, TimeSlots, TimeSlot } from './types'
import { ExceptionModalBody } from './ExceptionModalBody'
import { Footer } from './Footer'

const messages = defineMessages({
  addException: {
    defaultMessage: 'Add Exception',
    description: 'Modal header',
  },
  editException: {
    defaultMessage: 'Edit Exception',
    description: 'Modal header',
  },
})

export const AddEditException: VFC<AddEditExceptionProps> = ({
  isOpen,
  onClose,
  exceptions,
  setExceptions,
  listedExceptionEditIndex,
  listedExceptions,
  ...restProps
}) => {
  const { formatMessage } = useIntl()
  const [calendarDates, setCalendarDates] = useState<string[]>([])
  const [timeSlots, setTimeSlots] = useState<TimeSlot[]>([])

  useEffect(() => {
    if (restProps.calendarDates && restProps.timeSlots) {
      setCalendarDates(restProps.calendarDates)
      setTimeSlots(restProps.timeSlots.map(timeSlot => ({ timeSlot })))
    }
  }, [restProps.calendarDates, restProps.timeSlots])

  const isEditingException =
    listedExceptionEditIndex !== undefined && listedExceptionEditIndex >= 0

  const hasError = timeSlots.some(timeSlot => !!timeSlot.error)

  const handleDone = () => {
    setExceptions(
      [
        ...calendarDates.map(date => ({
          date,
          timeRanges: timeSlots
            .sort(({ timeSlot: [a] }, { timeSlot: [b] }) =>
              a < b ? -1 : a > b ? 1 : 0
            )
            .map(({ timeSlot: [start, end] }) => {
              const isSlotEndingAt12AM = getFullMinutes(end) === 0

              return [
                `${date}T${getTimeFromDate(start)}`,
                `${
                  isSlotEndingAt12AM
                    ? getPlusISOStringDate(date, { day: 1 })
                    : date
                }T${getTimeFromDate(end)}`,
              ]
            }) as TimeSlots,
        })),
        ...(listedExceptions && isEditingException
          ? exceptions.filter(
              ({ date }) =>
                !listedExceptions[listedExceptionEditIndex!].date.includes(date)
            )
          : exceptions
        ).filter(({ date }) => !calendarDates.includes(date)),
      ].sort((next, prev) => (prev.date < next.date ? 1 : -1))
    )

    onClose()
  }

  return (
    <Modal isOpen={isOpen} size={SIZE.large} onClose={onClose}>
      <ModalHeader>
        {formatMessage(
          isEditingException ? messages.editException : messages.addException
        )}
      </ModalHeader>
      <ExceptionModalBody
        exceptions={exceptions}
        calendarDates={calendarDates}
        timeSlots={timeSlots}
        setCalendarDates={setCalendarDates}
        setTimeSlots={setTimeSlots}
      />
      <Footer onCancel={onClose} onDone={handleDone} hasError={hasError} />
    </Modal>
  )
}
