import { ChangeEvent, FC, useEffect, useRef, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { FieldRenderProps } from 'react-final-form'
import { Option, Select, Value } from '@jifflenow/brightui/select'
import { Toggle } from '@jifflenow/brightui/toggle'
import { Input } from '@jifflenow/brightui/input'
import { Field } from 'ui-components/Field'
import {
  DurationMinsContainer,
  DurationLabel,
  DurationError,
  SelectProps,
  FieldLabel,
} from '../common'
import {
  Action,
  ActionDropDownOption,
  MeetingSummitHook,
  UrlParameters,
} from './types'
import {
  TabToggleWrapper,
  UrlFormControl,
  SelectContainer,
} from './styled-components'

const messages = defineMessages({
  actionsSelectPlaceholder: {
    defaultMessage: 'Select Action',
    description: 'Placeholder text',
  },
  openNewTab: {
    defaultMessage: 'Open New Tab',
    description: 'Field label',
  },
  urlLabel: {
    defaultMessage: 'URL',
    description: 'Field label',
  },
  urlParameters: {
    defaultMessage: 'URL Parameters',
    description: 'Field label',
  },
})

const actions = defineMessages<Action>({
  no_action: {
    defaultMessage: 'No Action',
    description: 'Dropdown option',
  },
  redirect: {
    defaultMessage: 'Redirect',
    description: 'Dropdown option',
  },
})

const actionOptions: Action[] = ['no_action', 'redirect']
const actionDropdownOptions: ActionDropDownOption[] = actionOptions.map(
  action => ({ id: action, label: action })
)

const ActionsField: FC<FieldRenderProps<string>> = ({
  input: { value: formValue, onChange, name },
  error,
  label,
}) => {
  const { formatMessage } = useIntl()
  const [value, setValue] = useState<ActionDropDownOption[]>([])
  const [urlParameters, setUrlParameters] = useState<UrlParameters[]>()
  const [urlParameterOptions, setUrlParameterOptions] = useState<
    UrlParameters[]
  >()

  const actionValue = (formValue as unknown) as MeetingSummitHook

  const initialized = useRef(false)

  useEffect(() => {
    if (actionValue && !initialized.current) {
      initialized.current = true
      setValue(
        actionDropdownOptions.filter(({ id }) => id === actionValue.action)
      )
      if (actionValue.url_parameters?.length) {
        setUrlParameterOptions(
          actionValue.url_parameters.map(parameter => ({
            label: parameter.text,
            ...parameter,
          }))
        )

        setUrlParameters(
          actionValue.url_parameters.filter(({ selected }) => selected)
        )
      }
    }
  }, [actionValue, formatMessage])

  return (
    <>
      <DurationLabel> {label} </DurationLabel>
      {error && <DurationError>{error}</DurationError>}
      <DurationMinsContainer>
        <Select
          options={actionDropdownOptions}
          filterOptions={(options: ActionDropDownOption[]): Value =>
            options.map(
              ({ label, id }): Option => ({
                label: formatMessage(actions[label]),
                id,
              })
            )
          }
          clearable={false}
          placeholder={formatMessage(messages.actionsSelectPlaceholder)}
          value={value}
          getValueLabel={({ option }: { option: ActionDropDownOption }) =>
            formatMessage(actions[option.label])
          }
          onChange={({ value }: { value: ActionDropDownOption[] }) => {
            setValue(value)
            onChange({ ...actionValue, action: value[0].id })
          }}
          dataPendo={name}
        />
      </DurationMinsContainer>
      {value[0]?.id === 'redirect' ? (
        <>
          <TabToggleWrapper>
            <FieldLabel>{formatMessage(messages.openNewTab)}</FieldLabel>
            <Toggle
              ariaLabel={label}
              checked={actionValue.open_new_tab}
              onChange={(e: ChangeEvent<HTMLInputElement>) =>
                onChange({ ...actionValue, open_new_tab: e.target.checked })
              }
            />
          </TabToggleWrapper>
          <UrlFormControl label={formatMessage(messages.urlLabel)}>
            <Input
              value={actionValue.url}
              placeholder={formatMessage(messages.urlLabel)}
              onChange={(e: ChangeEvent<HTMLInputElement>) =>
                onChange({ ...actionValue, url: e.target.value })
              }
            />
          </UrlFormControl>
          <SelectContainer label={formatMessage(messages.urlParameters)}>
            <Select
              options={urlParameterOptions}
              clearable={false}
              multi
              value={urlParameters}
              placeholder={formatMessage(messages.actionsSelectPlaceholder)}
              onChange={({ value }: { value: UrlParameters[] }) => {
                setUrlParameters(value)
                onChange({
                  ...actionValue,
                  url_parameters: actionValue.url_parameters.map(parameter =>
                    value.find(param => param.id === parameter.id)
                      ? { ...parameter, selected: true }
                      : { ...parameter, selected: false }
                  ),
                })
              }}
            />
          </SelectContainer>
        </>
      ) : null}
    </>
  )
}

export const MeetingSummitHookSelect: VFC<SelectProps> = ({ name, label }) => (
  <Field<string>
    name={name}
    format={value => (value ? JSON.parse(value) : value)}
    parse={value => JSON.stringify(value)}
    render={props => <ActionsField {...props} label={label} />}
  />
)
