import { VFC, ChangeEvent } from 'react'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { FormattedMessage } from 'react-intl'
import { CopyEngagementTypeRoot } from './styled-components'
import { CopyEngagementTypeToProps } from './types'

export const EngagementCheckBoxField: VFC<CopyEngagementTypeToProps> = ({
  label,
  isActive,
  uuid,
  handleChange,
}) => (
  <CopyEngagementTypeRoot data-pendo={uuid}>
    <Checkbox
      checked={isActive}
      name={uuid}
      onChange={({ target }: ChangeEvent<HTMLInputElement>) => {
        handleChange(target.checked, target.name)
      }}
    >
      <FormattedMessage
        defaultMessage="{label}"
        description="Engagement Type title"
        values={{ label }}
      />
    </Checkbox>
  </CopyEngagementTypeRoot>
)
