import { VFC, useState } from 'react'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { useIntl, defineMessages, FormattedMessage } from 'react-intl'
import { useApolloClient, mutateCopyEngagementSettingToEngagements } from 'gql'
import { copyEngagementSettingToEngagementsVariables } from 'gql/types/copyEngagementSettingToEngagements'
import {
  Modal,
  ModalBody,
  ModalButton,
  ModalFooter,
  ModalHeader,
} from '@jifflenow/brightui/modal'
import { KIND } from '@jifflenow/brightui/button'
import { EngagementSettingsGroupNames } from 'gql/types/globalTypes'
import { useEngagementType } from '../EngagementTypeProvider'
import { ModalDesc } from './styled-components'
import { modalButtonOverrides } from './overrides'
import { CopyEngagementType } from './CopyEngagementType'
import { CopyEngagementTypeSettings } from './CopyEngagementTypeSettings'

const messages = defineMessages({
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  successMessage: {
    defaultMessage: 'Your changes have been saved successfully',
    description: 'Success message',
  },
  modalTitle: {
    defaultMessage: 'Copy to following engagement types',
    description: 'Header title',
  },
  modalTitle2: {
    defaultMessage: 'Copy the following settings',
    description: 'Header title',
  },
  modalDesc: {
    defaultMessage:
      'There maybe some conflicts/dependencies in copying the settings.',
    description: 'Description title',
  },
})

export const CopySettings: VFC<{
  onClose: () => void
}> = ({ onClose }) => {
  const client = useApolloClient()
  const [toEngagementUuids, setToEngagementUuids] = useState<string[]>([])
  const [isSelectAll, setIsSelectAll] = useState<boolean>(false)
  const [excludeEngagementUuids, setExcludeEngagementUuids] = useState<
    string[]
  >([])
  const [settingsToCopyStatus, setSettingsToCopyStatus] = useState<boolean>(
    false
  )
  const [settingsToCopy, setSettingsToCopy] = useState<string[]>([])
  const [showLoader, setShowLoader] = useState(false)
  const { formatMessage } = useIntl()
  const { uuid: engagementTypeUuid } = useEngagementType().engagementType

  const handleSubmit = async () => {
    try {
      setShowLoader(true)
      const configsToUpdate = settingsToCopy.reduce<
        EngagementSettingsGroupNames[]
      >(
        (acc, label) =>
          label ===
          EngagementSettingsGroupNames[
            label as keyof typeof EngagementSettingsGroupNames
          ]
            ? [...acc, label]
            : acc,
        []
      )
      const variables: copyEngagementSettingToEngagementsVariables = {
        excludeEngagementUuids,
        fromEngagementUuid: engagementTypeUuid,
        isSelectAll,
        settingsToCopy: configsToUpdate,
        toEngagementUuids,
      }

      await mutateCopyEngagementSettingToEngagements(client, {
        variables,
      })
      setShowLoader(false)
      showSuccessToast(formatMessage(messages.successMessage))
      onClose()
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
    }
  }

  return (
    <>
      {showLoader && <ModalSpinner />}
      <Modal onClose={onClose} isOpen size={'small'}>
        <ModalHeader>
          {formatMessage(
            settingsToCopyStatus ? messages.modalTitle2 : messages.modalTitle
          )}
        </ModalHeader>
        <ModalDesc>{formatMessage(messages.modalDesc)}</ModalDesc>
        <ModalBody>
          {settingsToCopyStatus ? (
            <CopyEngagementTypeSettings
              setSettingsToCopy={(settings: string[]) =>
                setSettingsToCopy(settings)
              }
            />
          ) : (
            <CopyEngagementType
              setToEngagementUuids={(data: string[]) =>
                setToEngagementUuids(data)
              }
              setIsSelectAll={(status: boolean) => setIsSelectAll(status)}
              setExcludeEngagementUuids={(data: string[]) =>
                setExcludeEngagementUuids(data)
              }
              isSelectAll={isSelectAll}
            />
          )}
        </ModalBody>

        <ModalFooter>
          <ModalButton
            kind={KIND.tertiary}
            overrides={modalButtonOverrides}
            onClick={() => onClose()}
          >
            <FormattedMessage
              defaultMessage="Cancel"
              description="Button label"
            />
          </ModalButton>
          {settingsToCopyStatus ? (
            <ModalButton
              kind={KIND.primary}
              overrides={modalButtonOverrides}
              disabled={settingsToCopy.length > 0 ? false : true}
              onClick={() => handleSubmit()}
            >
              <FormattedMessage
                defaultMessage="Copy"
                description="Button label"
              />
            </ModalButton>
          ) : (
            <ModalButton
              kind={KIND.primary}
              overrides={modalButtonOverrides}
              disabled={
                isSelectAll ||
                toEngagementUuids.length > 0 ||
                excludeEngagementUuids.length > 0
                  ? false
                  : true
              }
              onClick={() => setSettingsToCopyStatus(true)}
            >
              <FormattedMessage
                defaultMessage="Next"
                description="Button label"
              />
            </ModalButton>
          )}
        </ModalFooter>
      </Modal>
    </>
  )
}
