import { useEffect, useState } from 'react'
import { useField } from 'react-final-form'
import { defineMessages, useIntl } from 'react-intl'
import { ConfirmationDialog } from 'ui-components/ConfirmationDialog'
import { useEventLocation } from 'providers/LocationProvider'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { EngagementTypeToggle } from '../common'
import { useEngagementType } from '../EngagementTypeProvider'
import { BorderStyled, Section, SectionTitle } from './styled-components'

const messages = defineMessages({
  videoConferencing: {
    defaultMessage: 'Video Conferencing',
    description: 'Section title',
  },
  timeZone: {
    defaultMessage: 'Time Zone',
    description: 'Section title',
  },
  enableVideoConfLink: {
    defaultMessage: 'Enable video conferencing for this engagement type',
    description: 'Field label',
  },
  meetingTypeAvailability: {
    defaultMessage: 'Make the meeting type available for 24 hours',
    description: 'Field label',
  },
  enableTimeZoneSelection: {
    defaultMessage: 'Enable time zone selection for this engagement type',
    description: 'Field label',
  },
  dialogTitle: {
    defaultMessage: 'Are you sure?',
    description: 'Dialog title',
  },
  vconDisableErrorMessage: {
    defaultMessage:
      'Once the meeting made hybrid and the availability is set to 24 hours, it cannot be changed back.',
    description: 'Vcon cannot be disabled Error message',
  },
  fullDayMeetingTypeConfirmationMessage: {
    defaultMessage:
      'Once the meeting is made available for 24 hours, it cannot be changed back.',
    description: 'Dialog Body',
  },
  labelYes: {
    defaultMessage: 'Yes',
    description: 'Button label',
  },
  labelNo: {
    defaultMessage: 'No',
    description: 'Button label',
  },
})

export const VideoConference = () => {
  const { formatMessage } = useIntl()
  const [open, setOpen] = useState(false)
  const { eventType } = useEventLocation().location?.attributes ?? {}
  const {
    masterEngagementType,
  } = useEngagementType().engagementType.templateEngagementType
  const isGeneralMeeting =
    masterEngagementType === MasterEngagementTypes.General

  const {
    input: {
      value: enableFullDayMeetingTypeAvailability,
      onChange: changeFullDayMeetingTypeAvailability,
    },
    meta: { initial: initialEnableFullDayMeetingTypeAvailability },
  } = useField('enable_full_day_meeting_type_availability')

  const {
    input: { value: EnableVcon },
  } = useField('enable_vcon_link', {
    validate: value =>
      value === 'false' && enableFullDayMeetingTypeAvailability === 'true'
        ? formatMessage(messages.vconDisableErrorMessage)
        : undefined,
  })

  useEffect(() => {
    initialEnableFullDayMeetingTypeAvailability === 'false' &&
      enableFullDayMeetingTypeAvailability === 'true' &&
      setOpen(true)
  }, [
    enableFullDayMeetingTypeAvailability,
    initialEnableFullDayMeetingTypeAvailability,
  ])

  return (
    <>
      {open && (
        <ConfirmationDialog
          heading={formatMessage(messages.dialogTitle)}
          message={formatMessage(
            messages.fullDayMeetingTypeConfirmationMessage
          )}
          successLabel={formatMessage(messages.labelYes)}
          cancelLabel={formatMessage(messages.labelNo)}
          onCancel={() => {
            setOpen(false)
            changeFullDayMeetingTypeAvailability('false')
          }}
          onSuccess={() => setOpen(false)}
        />
      )}
      <Section>
        <SectionTitle>
          {isGeneralMeeting
            ? formatMessage(messages.videoConferencing)
            : formatMessage(messages.timeZone)}
        </SectionTitle>
        {isGeneralMeeting && (
          <>
            <EngagementTypeToggle
              name="enable_vcon_link"
              label={formatMessage(messages.enableVideoConfLink)}
            />
            <BorderStyled />
          </>
        )}
        {eventType !== 'Ebc' && (
          <>
            {isGeneralMeeting && EnableVcon === 'true' && (
              <>
                <EngagementTypeToggle
                  name="enable_full_day_meeting_type_availability"
                  label={formatMessage(messages.meetingTypeAvailability)}
                />
                <BorderStyled />
              </>
            )}
            <EngagementTypeToggle
              name="enable_view_calendar_in_other_timezone"
              label={formatMessage(messages.enableTimeZoneSelection)}
            />
            <BorderStyled />
          </>
        )}
      </Section>
    </>
  )
}
