import { InputOverrides } from '@jifflenow/brightui/input'
import { Spinner, SpinnerContainer } from './styled-components'

export const getEngagementNameInputOverride = (
  isLoading: boolean
): InputOverrides => ({
  After: {
    component: () =>
      isLoading ? (
        <SpinnerContainer>
          <Spinner />
        </SpinnerContainer>
      ) : null,
  },
})
