import { FC, useCallback, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useParams } from 'react-router-dom'
import { Field, Validation } from 'ui-components/Field'
import { Input } from '@jifflenow/brightui/input'
import { useAutoSelectExpertData } from 'rest'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { useEventLocation } from 'providers/LocationProvider'
import { useField } from 'react-final-form'
import { useEffect } from 'react'
import { DependentLinks, EngagementTypeToggle } from '../common'
import { useEngagementType } from '../EngagementTypeProvider'
import { EngagementModal } from '../common/EngagementModal'
import { SchedulingLogicField } from './SchedulingLogicField'
import { instructionInputOverride } from './overrides'
import {
  Divider,
  FieldWrapper,
  InputFieldWrapper,
  Description,
  Title,
  GenericError,
} from './styled-components'

const messages = defineMessages({
  defaultDurationLabel: {
    defaultMessage: 'Default duration of shared availability in days',
    description: 'Field label',
  },
  meetingExpiryLabel: {
    defaultMessage: 'Meeting link expiry in days',
    description: 'Field label',
  },
  meetingExpiryDescLabel: {
    defaultMessage:
      'Link expiry duration needs to be less than or equal to the default duration of shared availability',
    description: 'Field label description',
  },
  autoExpertLabel: {
    defaultMessage: 'Auto Expert Setting',
    description: 'Field label',
  },
  warningModalTitle: {
    defaultMessage:
      'Please ensure following settings are done before changing the Auto Expert Setting',
    description: 'Dependent settings modal title',
  },
  warningEnableLabel: {
    defaultMessage: 'Enable the following',
    description: 'Dependent settings group header',
  },
  topicMappedLabel: {
    defaultMessage: 'Please enable topic',
    description: 'Dependent setting label',
  },
  hasMappedTopicsLabel: {
    defaultMessage: 'Please map at least one topic to enable',
    description: 'Dependent setting label',
  },
  hasUserMappedToTopicLabel: {
    defaultMessage:
      'Please map at least one internal user to mapped topics to enable auto expert setting',
    description: 'Dependent setting label',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
})

const DaysInput: FC<{ name: string; placeholder: string }> = ({
  name,
  placeholder,
}) => (
  <Field<string>
    name={name}
    validations={[Validation.REQUIRED]}
    render={({ input: { value, onChange }, meta: { error, submitError } }) => (
      <>
        <GenericError>{submitError || error} </GenericError>
        <Input
          type="number"
          name={name}
          value={value}
          placeholder={placeholder}
          onChange={(e: any) => onChange(e.target.value)}
          overrides={instructionInputOverride}
        />
      </>
    )}
  />
)

export const FormFields: VFC = () => {
  const { formatMessage } = useIntl()
  const { location } = useEventLocation()
  const { uuid: activity_uuid } = useEngagementType().engagementType
  const {
    isLoading,
    data: autoSelectExpert,
    reload: refetchAutoSelectExpertSettings,
  } = useAutoSelectExpertData({
    eventName: location?.attributes.systemName,
    activity_uuid,
    current_location_uuid: location?.id,
  })
  const [isOpen, setIsOpen] = useState(false)
  const {
    input: { value, onChange },
  } = useField('auto_select_expert')

  const validateAutoSelectExpertSettings = useCallback(
    () =>
      autoSelectExpert?.data.topicEnabled &&
      autoSelectExpert?.data.hasMappedTopics &&
      autoSelectExpert?.data.hasUserMappedToTopic,
    [
      autoSelectExpert?.data.hasMappedTopics,
      autoSelectExpert?.data.hasUserMappedToTopic,
      autoSelectExpert?.data.topicEnabled,
    ]
  )

  useEffect(() => {
    value === 'true' && setIsOpen(!validateAutoSelectExpertSettings())
  }, [validateAutoSelectExpertSettings, value])

  const { eventName } = useParams<{
    eventName: string
  }>()

  const dependentLinks = ([
    !autoSelectExpert?.data.topicEnabled && {
      label: formatMessage(messages.topicMappedLabel),
      path: `${window.location.origin}/${eventName}/event/new?current_location_uuid=${location?.id}`,
      group: formatMessage(messages.warningEnableLabel),
    },
    !autoSelectExpert?.data.hasMappedTopics && {
      label: formatMessage(messages.hasMappedTopicsLabel),
      path: `${window.location.origin}/${eventName}/topics/topic_listing?current_location_uuid=${location?.id}`,
      group: formatMessage(messages.warningEnableLabel),
    },
    !autoSelectExpert?.data.hasUserMappedToTopic && {
      label: formatMessage(messages.hasUserMappedToTopicLabel),
      path: `${window.location.origin}/${eventName}/topics/topic_listing?current_location_uuid=${location?.id}`,
      group: formatMessage(messages.warningEnableLabel),
    },
  ].filter(Boolean) as unknown) as DependentLinks[]

  return (
    <>
      {isOpen && (
        <EngagementModal
          title={formatMessage(messages.warningModalTitle)}
          dependentLinks={dependentLinks}
          onClose={() => {
            setIsOpen(false)
            !validateAutoSelectExpertSettings() && onChange('false')
          }}
          onConfirm={() => refetchAutoSelectExpertSettings()}
        />
      )}

      {isLoading && <ModalSpinner />}
      <FieldWrapper>
        <Title>{formatMessage(messages.defaultDurationLabel)}</Title>
        <InputFieldWrapper>
          <DaysInput
            name="default_availability_duration"
            placeholder={formatMessage(messages.defaultDurationLabel)}
          />
        </InputFieldWrapper>
      </FieldWrapper>
      <FieldWrapper>
        <Title>{formatMessage(messages.meetingExpiryLabel)}</Title>
        <Description>
          {formatMessage(messages.meetingExpiryDescLabel)}
        </Description>
        <InputFieldWrapper>
          <DaysInput
            name="inbound_link_expiry"
            placeholder={formatMessage(messages.meetingExpiryLabel)}
          />
        </InputFieldWrapper>
      </FieldWrapper>
      <Divider />
      <FieldWrapper>
        <EngagementTypeToggle
          label={formatMessage(messages.autoExpertLabel)}
          name="auto_select_expert"
          bold
        />
      </FieldWrapper>
      <Divider />
      <SchedulingLogicField />
    </>
  )
}
