import { VFC } from 'react'
import { useFormState } from 'react-final-form'
import { FormattedMessage } from 'react-intl'
import { Paragraph2 } from '@jifflenow/brightui/typography'
import { Select, Option } from '@jifflenow/brightui/select'
import { FormControl } from '@jifflenow/brightui/form-control'
import { Field } from 'ui-components/Field'
import { FormValues } from '../types'
import { SelectContainer } from '../styled-components'

type SelectField = { label: string; field_name: string; values: string[] }

export const DemoSubTopicFields: VFC<{
  fields: SelectField[]
}> = ({ fields }) => {
  const { demo_topic } = useFormState<FormValues>().values

  const { values: options } =
    fields.find(({ field_name }) => field_name === demo_topic) || {}

  const subTopicFields = fields.filter(
    ({ field_name, label }) =>
      field_name !== demo_topic && options?.find(option => option === label)
  )

  return (
    <>
      {subTopicFields.length > 0 && (
        <Paragraph2>
          <FormattedMessage
            defaultMessage="Choose Sub-Topic/Child field"
            description="Field label"
          />
        </Paragraph2>
      )}
      {subTopicFields.map(({ label, values: options }) => (
        <div key={label}>
          <Field<string>
            name={`demo_sub_topics.${label}`}
            render={({
              input: { value, onChange },
              meta: { submitError, error },
            }) => {
              const subTopicOptions = options.reduce((acc, option) => {
                const field = fields.find(field => field.label === option)

                return [
                  ...acc,
                  ...(field ? [{ label: option, id: field.field_name }] : []),
                ]
              }, [] as Option[])

              return (
                <SelectContainer>
                  <FormControl label={label} error={submitError || error}>
                    <Select
                      options={subTopicOptions}
                      clearable={false}
                      value={subTopicOptions.filter(
                        option => option.id === value
                      )}
                      searchInput
                      onChange={({ value }: { value: Option[] }) => {
                        onChange(value[0].id)
                      }}
                    />
                  </FormControl>
                </SelectContainer>
              )
            }}
          />
        </div>
      ))}
    </>
  )
}
