import { FC } from 'react'
import { RightLongArrow } from '@jifflenow/brightui/icon'
import { generatePath, useLocation, useParams } from 'react-router-dom'
import { Paths as BasePaths } from 'components/Routes/Paths'
import { Anchor, StyledRightArrow } from './styled-components'
import { RedirectLinkProps } from './types'

const urlPrefix = process.env.NODE_ENV === 'production' ? '/app' : ''

export const GenerateRedirectLink: FC<RedirectLinkProps> = ({
  label,
  path,
}) => {
  const { search } = useLocation()
  const { eventName, engagementId } = useParams<{
    engagementId: string
    eventName: string
  }>()

  const isAbsoluteUrl =
    path.startsWith('http://') || path.startsWith('https://')

  const url = isAbsoluteUrl
    ? path
    : `${urlPrefix}${generatePath(BasePaths.ENGAGEMENT_TYPE.path, {
        eventName,
        engagementId,
      })}${path}${search}`

  return (
    <Anchor rel="noopener noreferrer" target="_blank" href={url}>
      {label}
      <StyledRightArrow>
        <RightLongArrow />
      </StyledRightArrow>
    </Anchor>
  )
}
