import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Option, Select, Value } from '@jifflenow/brightui/select'
import { Field, Validation } from 'ui-components/Field'
import { commonMessages, roles as roleLabel } from 'utils/messages'
import { RolesFieldProps } from './types'
import { FormControlStyled } from './styled-components'

const messages = defineMessages({
  roleLabel: {
    defaultMessage: 'Default engagement type access for roles',
    description: 'Field label',
  },
  roleSearchPlaceholder: {
    defaultMessage: 'Search by name',
    description: 'Field label',
  },
})

export const RolesField: VFC<RolesFieldProps> = ({ roles }) => {
  const { formatMessage } = useIntl()

  const dropdownOptions = [
    roles.length && {
      label: formatMessage(commonMessages.allMyAttendees),
      id: 'ALL',
    },
    ...roles.map(
      ({ roleCompanyid, roleDisplayName }): Option => ({
        label: formatMessage(roleLabel[roleDisplayName]),
        id: roleCompanyid,
      })
    ),
  ].filter(Boolean)

  return (
    <Field<Option[] | undefined>
      name="roles"
      format={(value: Option[] | undefined) => value ?? []}
      parse={(value: Option[]) => (value.length ? value : undefined)}
      render={({ input, meta: { touched, error } }) => (
        <FormControlStyled
          label={formatMessage(messages.roleLabel)}
          error={touched && error}
        >
          <Select
            {...input}
            multi
            id="roles"
            options={dropdownOptions}
            backspaceClearsInputValue
            searchable
            placeholder={formatMessage(messages.roleSearchPlaceholder)}
            onChange={({ value }: { value: Value[] }) => input.onChange(value)}
            required
            clearable={input.value && input.value.length > 1}
            dataPendo="create-engagement-roles"
          />
        </FormControlStyled>
      )}
      validations={[Validation.REQUIRED]}
    />
  )
}
