import { FieldRenderProps } from 'react-final-form'
import { components } from 'types/openapi'
import { RefetchProps } from '../common'

export type DefaultLocationOption = {
  label: string
  id: DefaultLocationPreferenceType | string
  type: DefaultLocationPreferenceType
}

export type LocationTypeSettings = {
  default_location_preference: string
  default_location_vcon_preference: {
    integration_adapter_uuid: string
    integration_name: string
    integration_uuid: string
  }
  duration_restrictions_for_mm: string
  max_duration: string
  min_duration: string
  offsite_enabled: string
  offsite_duration_restrictions_for_mm: string
  offsite_label: string
  offsite_min_duration: string
  offsite_max_duration: string
  booth_tour_enabled: string
  booth_tour_label: string
  booth_tour_header: string
  booth_tour_default_name: string
  booth_tour_default_location: string
  booth_tour_min_duration: string
  booth_tour_max_duration: string
}

export type DefaultSettingsFormValues = Omit<
  LocationTypeSettings,
  'default_location_preference' | 'default_location_vcon_preference'
>

export type FormValues = Omit<
  LocationTypeSettings,
  'default_location_vcon_preference'
> & { default_location_vcon_preference: string | undefined; sendIcs?: boolean }

export type DependentFields = {
  enable_external_widget: string
  enable_external_widget_auto_process_without_mapping: string
  minimum_timeslot: string
  default_mapping: string
  min_duration: string
  offsite_enabled: string
  offsite_min_duration: string
  booth_tour_enabled: string
  booth_tour_min_duration: string
}

type DependentFieldValues = { dependentFieldValues: DependentFields }

type EngagementMeetingData = {
  hasActiveMeetings: boolean
}

export type TimeIncrementProps = DependentFieldValues & EngagementMeetingData

export type DependentFieldsProps = {
  dependentFieldValues: DependentFields
}

export enum DefaultLocationPreferenceType {
  ROOM = 'room',
  BOOTH_TOUR = 'booth_tour',
  VCON = 'vcon',
}

export type DefaultLocationDropdownProps = {
  renderProps: FieldRenderProps<string>
  options: DefaultLocationOption[]
  conferences: components['schemas']['Conference'][]
}

export type BoothTourProps = DependentFieldsProps & RefetchProps
