import { FC } from 'react'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import { defineMessages, useIntl } from 'react-intl'
import { CopyOutlined } from '@jifflenow/brightui/icon'
import { Paragraph1 } from '@jifflenow/brightui/typography'
import { Button } from '@jifflenow/brightui/button'
import {
  PLACEMENT,
  StatefulTooltip,
  TRIGGER_TYPE,
} from '@jifflenow/brightui/tooltip'
import { CopyEmbedSourceCodeProps } from './types'
import { CopyContentWrapper } from './styled-components'
import { copyButtonOverrides } from './overrides'

const messages = defineMessages({
  copiedLabel: {
    defaultMessage: 'Copied',
    description: 'Field label',
  },
})

export const CopyEmbedSourceCode: FC<CopyEmbedSourceCodeProps> = ({
  buttonLabel,
  link,
  title,
  disabled,
}) => {
  const { formatMessage } = useIntl()

  return (
    <>
      <Paragraph1>{title}</Paragraph1>
      <StatefulTooltip
        content={() => formatMessage(messages.copiedLabel)}
        placement={PLACEMENT.bottom}
        triggerType={TRIGGER_TYPE.click}
        focusLock={false}
        showArrow
      >
        <CopyContentWrapper>
          <CopyToClipboard text={link}>
            <Button
              startEnhancer={CopyOutlined}
              kind="minimal"
              type="button"
              overrides={copyButtonOverrides}
              disabled={disabled}
            >
              {buttonLabel}
            </Button>
          </CopyToClipboard>
        </CopyContentWrapper>
      </StatefulTooltip>
    </>
  )
}
