import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Option, Select } from '@jifflenow/brightui/select'
import { FormControl } from '@jifflenow/brightui/form-control'
import { Field, Validation } from 'ui-components/Field'
import { DisplayOrderFieldProps } from './types'

const messages = defineMessages({
  label: {
    defaultMessage: 'Order of Display',
    description: 'Field label',
  },
  optionLabel: {
    defaultMessage:
      '{count, selectordinal, one {#st} two {#nd} few {#rd} other {#th}}',
    description: 'Option Label',
  },
})

export const DisplayOrderField: VFC<DisplayOrderFieldProps> = ({
  grandTotal,
}) => {
  const { formatMessage } = useIntl()

  const options = [...Array(grandTotal)].map((_, i) => ({
    id: i + 1,
    label: formatMessage(messages.optionLabel, { count: i + 1 }),
  }))

  return (
    <Field
      name="displayOrder"
      parse={(value: Option[]) => value[0]?.id}
      format={value =>
        value
          ? [
              {
                id: value,
                label: formatMessage(messages.optionLabel, { count: value }),
              },
            ]
          : []
      }
      render={({ input, meta: { touched, error } }) => (
        <FormControl
          label={formatMessage(messages.label)}
          error={touched && error}
        >
          <Select
            {...input}
            id="displayOrder"
            required
            options={options}
            onChange={({ value }: { value: Option[] }) => input.onChange(value)}
            dataPendo="create-engagement-display-order"
          />
        </FormControl>
      )}
      validations={[Validation.REQUIRED]}
    />
  )
}
