import {
  Modal,
  ModalBody,
  ModalButton,
  ModalFooter,
  ModalHeader,
} from '@jifflenow/brightui/modal'
import { defineMessages, useIntl } from 'react-intl'
import { FC } from 'react'
import { Paragraph2 } from '@jifflenow/brightui/typography'
import { GenerateRedirectLink } from './GenerateRedirectLink'
import { DependentLinks } from './types'
import {
  IrpReqPageLink,
  List,
  ListItem,
  StyledWarningTitle,
} from './styled-components'

const messages = defineMessages({
  warningLabel: {
    defaultMessage: 'Warning',
    description: 'Field label',
  },
  confirmLabel: {
    defaultMessage: 'Confirm',
    description: 'Button label',
  },
})

const groupBy = (array: DependentLinks[]) =>
  array.reduce<Record<string, DependentLinks[]>>(
    (result, currentValue) => ({
      ...result,
      [currentValue.group]: [
        ...(result[currentValue.group] ?? []),
        currentValue,
      ],
    }),
    {}
  )

export const EngagementModal: FC<{
  onClose: () => void
  onConfirm: () => void
  dependentLinks: DependentLinks[]
  title: string
  version?: string
}> = ({ title, dependentLinks, onClose, onConfirm, version }) => {
  const { formatMessage } = useIntl()

  const group = groupBy(dependentLinks)

  return (
    <Modal onClose={onClose} isOpen size={'medium'}>
      <ModalHeader>{formatMessage(messages.warningLabel)}</ModalHeader>
      <ModalBody>
        <StyledWarningTitle>{title}</StyledWarningTitle>
        {Object.keys(group).map((key, index) => (
          <StyledWarningTitle key={index}>
            {key}
            <IrpReqPageLink>
              <List $version={version}>
                {group[key].map((link, index) => (
                  <ListItem key={index}>
                    {version === '1' ? (
                      <Paragraph2>{link.label}</Paragraph2>
                    ) : (
                      <GenerateRedirectLink {...link} />
                    )}
                  </ListItem>
                ))}
              </List>
            </IrpReqPageLink>
          </StyledWarningTitle>
        ))}
      </ModalBody>
      <ModalFooter>
        <ModalButton onClick={onConfirm}>
          {formatMessage(messages.confirmLabel)}
        </ModalButton>
      </ModalFooter>
    </Modal>
  )
}
