import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Textarea } from '@jifflenow/brightui/textarea'
import { Field } from 'ui-components/Field'
import { FormControlStyled } from './styled-components'
import { EngagementDescriptionFieldProps } from './types'

const messages = defineMessages({
  engagementDescriptionPlaceholder: {
    defaultMessage: 'Enter description',
    description: 'Placeholder text',
  },
})

export const EngagementDescriptionField: VFC<EngagementDescriptionFieldProps> = ({
  label,
}) => {
  const { formatMessage } = useIntl()

  return (
    <Field<string>
      name="description"
      type="text"
      formatOnBlur
      format={value => value?.trim() ?? value}
      parse={value => value ?? ''}
    >
      {({ input, meta }) => (
        <FormControlStyled
          label={label}
          error={(meta.dirty || meta.touched) && meta.error}
        >
          <Textarea
            {...input}
            id="engagement-description"
            autoComplete="off"
            placeholder={formatMessage(
              messages.engagementDescriptionPlaceholder
            )}
          />
        </FormControlStyled>
      )}
    </Field>
  )
}
