import { FC, useEffect, useState, VFC } from 'react'
import { FieldRenderProps, useField } from 'react-final-form'
import { RadioGroup, Radio, ALIGN } from '@jifflenow/brightui/radio'
import { defineMessages, useIntl } from 'react-intl'
import { Field } from 'ui-components/Field'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { DependentLinks, RefetchProps } from '../common/types'
import { EngagementModal } from '../common/EngagementModal'
import { Paths } from '../Paths'
import { useEngagementType } from '../EngagementTypeProvider'
import { NestedRadio, GenericError } from './styled-components'
import { instructionRadioOverride } from './overrides'
import { DependentFieldValues } from './types'

const messages = defineMessages({
  labelInstant: {
    defaultMessage:
      'INSTANT APPROVAL - Meeting approved upon submission. Calendar invitations will be sent to all participants (internal and external) immediately.',
    description: 'Field label',
  },
  labelAutoApprovedAfterAllAccept: {
    defaultMessage:
      'AUTOMATED APPROVAL - Meeting in pending state upon submission. Calendar invitations will be sent to internal participants immediately. The meeting is approved after ALL internal attendees accept.',
    description: 'Field label',
  },
  labelManualApprovedICSOnMeetingApproval: {
    defaultMessage:
      'MANUAL APPROVAL (option 1) - Meeting in pending state upon submission. Calendar invitations will be sent to internal and external participants ONLY AFTER the meeting is approved by Meeting Manager.',
    description: 'Field label',
  },
  labelManualApprovedICSOnMeetingRequest: {
    defaultMessage:
      'MANUAL APPROVAL (option 2) - Meeting in pending state upon submission. Calendar invitations will be sent to internal participants immediately.',
    description: 'Field label',
  },
  labelManualApprovedICSOnSession: {
    defaultMessage:
      'Manual - Send ICS to Internal attendees ONLY AFTER session is approved',
    description: 'Field label',
  },
  labelNoInternalAttendeeMeetingApproved: {
    defaultMessage:
      'If there is no internal attendee, meeting is auto Approved',
    description: 'Field label',
  },
  labelNoInternalAttendeeMeetingPending: {
    defaultMessage:
      'If there is no internal attendee, meeting remains in Pending',
    description: 'Field label',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  warningModalTitle: {
    defaultMessage:
      'Please ensure following settings are done before changing the Approval process',
    description: 'Dependent settings modal title',
  },
  warningDisableLabel: {
    defaultMessage: 'Disable the following',
    description: 'Dependent settings group header',
  },
  meetNowLabel: {
    defaultMessage: 'Meet now',
    description: 'Dependent setting label',
  },
  IRPLabel: {
    defaultMessage: 'Inbound Request Page',
    description: 'Dependent setting label',
  },
  manualForGroupNominations: {
    defaultMessage: 'Manual process applicable only for group nominations',
    description: 'Manual nomination approval help text',
  },
})

const MeetingStatus: FC = () => {
  const { formatMessage } = useIntl()
  const {
    input: { value, onChange },
    meta: { submitError },
  } = useField('default_meeting_status_for_auto_approval')

  return (
    <NestedRadio>
      <RadioGroup
        align={ALIGN.vertical}
        value={value}
        onChange={e => onChange(e.target.value)}
      >
        {submitError && <GenericError>{submitError}</GenericError>}
        <Radio value="approved" overrides={instructionRadioOverride}>
          {formatMessage(messages.labelNoInternalAttendeeMeetingApproved)}
        </Radio>
        <Radio value="pending" overrides={instructionRadioOverride}>
          {formatMessage(messages.labelNoInternalAttendeeMeetingPending)}
        </Radio>
      </RadioGroup>
    </NestedRadio>
  )
}

const ApprovalStatus: FC<FieldRenderProps<string>> = ({
  input: { value, onChange, name },
  meta: { submitError },
  loading,
  refetch,
  dependentFieldValues,
}) => {
  const { formatMessage } = useIntl()
  const {
    enable_irp_instant_meeting,
    enable_external_widget,
  } = dependentFieldValues
  const [isOpen, setIsOpen] = useState(false)
  const {
    templateEngagementType: { masterEngagementType },
  } = useEngagementType().engagementType
  const isSession =
    masterEngagementType === MasterEngagementTypes.TracksAndSession

  useEffect(() => {
    value &&
      !isSession &&
      value !== 'instant' &&
      setIsOpen(enable_irp_instant_meeting === 'true')
    value &&
      isSession &&
      value === 'manual_ics_on_meeting_approve' &&
      setIsOpen(enable_external_widget === 'true')
  }, [enable_irp_instant_meeting, value, isSession, enable_external_widget])

  const dependentLinks = [
    enable_irp_instant_meeting !== 'false' &&
      !isSession && {
        label: formatMessage(messages.meetNowLabel),
        path: Paths.INBOUND_REQUEST_FORM,
        group: formatMessage(messages.warningDisableLabel),
      },
    enable_external_widget !== 'false' &&
      isSession && {
        label: formatMessage(messages.IRPLabel),
        path: Paths.INBOUND_REQUEST_FORM,
        group: formatMessage(messages.warningDisableLabel),
      },
  ].filter(Boolean) as DependentLinks[]

  return (
    <>
      {isOpen && (
        <>
          {loading && <ModalSpinner />}
          <EngagementModal
            title={formatMessage(messages.warningModalTitle)}
            dependentLinks={dependentLinks}
            onClose={() => {
              setIsOpen(false)
              enable_irp_instant_meeting === 'true' &&
                !isSession &&
                onChange('instant')
              enable_external_widget === 'true' &&
                isSession &&
                onChange('instant')
            }}
            onConfirm={() => refetch()}
          />
        </>
      )}
      {submitError && <GenericError>{submitError}</GenericError>}
      <RadioGroup
        align={ALIGN.vertical}
        value={value}
        onChange={e => onChange(e.target.value)}
        name={name}
      >
        <Radio value="instant" overrides={instructionRadioOverride}>
          {formatMessage(messages.labelInstant)}
        </Radio>
        {isSession && (
          <Radio
            value="manual_ics_on_meeting_approve"
            overrides={instructionRadioOverride}
            description={formatMessage(messages.manualForGroupNominations)}
          >
            {formatMessage(messages.labelManualApprovedICSOnSession)}
          </Radio>
        )}
        {!isSession && (
          <Radio
            value="auto_approved_after_all_int_att"
            overrides={instructionRadioOverride}
          >
            {formatMessage(messages.labelAutoApprovedAfterAllAccept)}
          </Radio>
        )}
        {value === 'auto_approved_after_all_int_att' && !isSession ? (
          <MeetingStatus />
        ) : null}
        {!isSession && (
          <Radio
            value="manual_ics_on_meeting_approve"
            overrides={instructionRadioOverride}
          >
            {formatMessage(messages.labelManualApprovedICSOnMeetingApproval)}
          </Radio>
        )}
        {!isSession && (
          <Radio
            value="manual_ics_on_meeting_create"
            overrides={instructionRadioOverride}
          >
            {formatMessage(messages.labelManualApprovedICSOnMeetingRequest)}
          </Radio>
        )}
      </RadioGroup>
    </>
  )
}

export const FormFields: VFC<RefetchProps & DependentFieldValues> = ({
  loading,
  dependentFieldValues,
  refetch,
}) => (
  <Field<string>
    name="approval_process"
    render={props => (
      <ApprovalStatus
        {...props}
        refetch={refetch}
        loading={loading}
        dependentFieldValues={dependentFieldValues}
      />
    )}
  />
)
