import { FC, useState } from 'react'
import { useApolloClient } from '@apollo/client'
import { defineMessages, useIntl } from 'react-intl'
import { useQueryClient } from 'react-query'
import { Select, Value } from '@jifflenow/brightui/select'
import { ICellRendererParams } from '@ag-grid-community/core'
import { showErrorToast } from 'utils/toast'
import { mutateUpdateEngagementType } from 'gql'
import { selectOverrides } from './overrides'

const messages = defineMessages({
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
})

export const DisplayOrderCell: FC<
  ICellRendererParams & {
    grandTotal: number
  }
> = ({ value: displayOrder, data, api, grandTotal }) => {
  const queryClient = useQueryClient()
  const client = useApolloClient()
  const { formatMessage } = useIntl()
  const options = [...Array(grandTotal)].map((_, i) => ({
    label: i + 1,
    id: i + 1,
  }))

  const [value, setValue] = useState([
    { label: displayOrder, id: displayOrder },
  ])

  const onChangeHandler = async ({ value }: { value: Value }) => {
    try {
      setValue([{ label: value[0].id, id: value[0].id }])
      api.showLoadingOverlay()
      await mutateUpdateEngagementType(client, {
        variables: {
          uuid: data.uuid,
          displayOrder: Number(value[0].id),
        },
      })
      queryClient.refetchQueries('getEngagementTypes')
    } catch (error) {
      api.hideOverlay()
      showErrorToast(formatMessage(messages.genericError))
    }
  }

  return (
    <Select
      disabled={!data.sagaCompleted}
      overrides={selectOverrides}
      options={options}
      clearable={false}
      value={value}
      onChange={onChangeHandler}
    />
  )
}
