import { FC, useState } from 'react'
import { defineMessages, useIntl, FormattedMessage } from 'react-intl'
import { PLACEMENT, StatefulPopover } from '@jifflenow/brightui/popover'
import { StatefulMenu, OnItemSelect } from '@jifflenow/brightui/menu'
import { ThreeDots } from '@jifflenow/brightui/icon'
import { IconButton } from '@jifflenow/brightui/button'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { useBulkDeleteEngagementTypeMutation } from 'gql'
import { bulkDeleteEngagementTypes as Data } from 'gql/types/bulkDeleteEngagementTypes'
import {
  PopoverOverrides,
  InlineMenuOverrides,
  ThreeDotButtonOverrides,
} from 'components/ag-grid'
import { DeleteConfirmationDialog } from '../DeleteConfirmationDialog'
import { BulkButtonProps } from './types'
import { Message } from './Message'

const messages = defineMessages({
  allDelete: {
    defaultMessage: 'Delete all',
    description: 'Button label',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  hintText: {
    defaultMessage:
      'Engagement types with any meetings in the past or upcoming cannot be deleted. Please clear associated meetings before deletion.',
    description: 'Modal hint text',
  },
})

const onItemSelect: OnItemSelect = args => args.item.onItemSelect?.(args)

const showToast = ({ bulkDeleteEngagementTypes: data }: Data) => {
  const { totalCount, failureCount, successCount, engagementTypeDetails } = data
  failureCount === 0
    ? showSuccessToast(
        <Message
          message={
            <FormattedMessage
              defaultMessage="{successCount, number} engagement {successCount, plural, one {type has} other {types have}} been deleted successfully"
              description="Toast message"
              values={{ successCount }}
            />
          }
        />
      )
    : failureCount === totalCount
    ? showErrorToast(
        <Message
          hint={messages.hintText}
          details={engagementTypeDetails}
          message={
            <FormattedMessage
              defaultMessage="Engagement types deletion failed"
              description="Toast message"
            />
          }
        />
      )
    : showErrorToast(
        <Message
          hint={messages.hintText}
          details={engagementTypeDetails}
          message={
            <FormattedMessage
              defaultMessage="{failureCount, number} out of {totalCount, number} engagement {totalCount, plural, one {type} other {types}} deletion failed"
              description="Toast message"
              values={{ failureCount, totalCount }}
            />
          }
        />,
        { autoHideDuration: 0 }
      )
}

export const DeleteButton: FC<BulkButtonProps> = props => {
  const { formatMessage } = useIntl()
  const [isOpen, setIsOpen] = useState(false)
  const { gridApi, variables, resetToFirstPage } = props
  const { mutateBulkDelete } = useBulkDeleteEngagementTypeMutation({
    onCompleted: data => {
      resetToFirstPage()
      showToast(data)
    },
    onError: () => {
      gridApi.hideOverlay()
      showErrorToast(<Message message={formatMessage(messages.genericError)} />)
    },
  })

  const onClose = () => setIsOpen(false)

  const onConfirm = () => {
    setIsOpen(false)
    gridApi.showLoadingOverlay()
    mutateBulkDelete({ variables })
  }

  return (
    <>
      <StatefulPopover
        popoverMargin={8}
        content={
          <StatefulMenu
            items={[
              {
                label: formatMessage(messages.allDelete),
                onItemSelect: () => {
                  setIsOpen(true)
                },
              },
            ]}
            onItemSelect={onItemSelect}
            overrides={InlineMenuOverrides}
          />
        }
        placement={PLACEMENT.bottomRight}
        overrides={PopoverOverrides}
      >
        <IconButton size={20} overrides={ThreeDotButtonOverrides}>
          <ThreeDots />
        </IconButton>
      </StatefulPopover>
      {isOpen && (
        <DeleteConfirmationDialog onClose={onClose} onConfirm={onConfirm} />
      )}
    </>
  )
}
