import { VFC } from 'react'
import { FormSpy } from 'react-final-form'
import { FormattedMessage } from 'react-intl'
import { KIND } from '@jifflenow/brightui/button'
import { FooterContainer } from 'ui-components/Drawer'
import { FooterProps } from './types'
import { successButtonOverrides } from './overrides'
import { FooterButton } from './styled-components'

export const Footer: VFC<FooterProps> = ({ onCancelClick, copy }) => (
  <FooterContainer>
    <FormSpy subscription={{ validating: true }}>
      {({ validating }) => (
        <>
          <FooterButton
            type="button"
            kind={KIND.tertiary}
            onClick={onCancelClick}
            data-pendo="engagement-create-cancel"
          >
            <FormattedMessage
              defaultMessage="Cancel"
              description="Button label"
            />
          </FooterButton>

          <FooterButton
            type="submit"
            disabled={validating}
            overrides={successButtonOverrides}
            data-pendo="engagement-create-submit"
          >
            {copy ? (
              <FormattedMessage
                defaultMessage="Copy Engagement Type"
                description="Button label"
              />
            ) : (
              <FormattedMessage
                defaultMessage="Create Engagement Type"
                description="Button label"
              />
            )}
          </FooterButton>
        </>
      )}
    </FormSpy>
  </FooterContainer>
)
