import { FC, useCallback, useEffect, useState, VFC } from 'react'
import { Radio } from '@jifflenow/brightui/radio'
import { defineMessages, useIntl } from 'react-intl'
import { Field } from 'ui-components/Field'
import { FieldRenderProps, useField } from 'react-final-form'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { useEventLocation } from 'providers/LocationProvider'
import { instructionRadioOverride } from '../overrides'
import { EngagementModal } from '../../common/EngagementModal'
import { DependentFieldValues, DependentLinks } from '../types'
import { Paths } from '../../Paths'
import { RefetchProps } from '../../common/types'

const messages = defineMessages({
  unmappedSchedulingLabel: {
    defaultMessage: 'Auto schedule - unmapped request page',
    description: 'Field label',
  },
  hideInternalAttendeeLabel: {
    defaultMessage: 'Hide internal attendee section.',
    description: 'Dependent setting label',
  },
  relaxRestrictionLabel: {
    defaultMessage: 'Relax the restriction',
    description: 'Dependent setting label',
  },
  mappingLabel: {
    defaultMessage: 'Mapping',
    description: 'Dependent setting label',
  },
  offsiteLabel: {
    defaultMessage: 'Offsite',
    description: 'Dependent setting label',
  },
  mstLabel: {
    defaultMessage: 'MST',
    description: 'Dependent setting label',
  },
  atLeastOneInternalAttendeeLabel: {
    defaultMessage:
      'At least one internal attendee is required to enable unmapped inbound request auto processing.',
    description: 'Dependent setting info',
  },
  warningDisableLabel: {
    defaultMessage: 'Disable the following',
    description: 'Dependent settings group header',
  },
  warningEnableLabel: {
    defaultMessage: 'Enable the following',
    description: 'Dependent settings group header',
  },
  warningModalTitle: {
    defaultMessage:
      'Please ensure following settings are done before enabling auto schedule unmapped request page.',
    description: 'Dependent settings modal title',
  },
})

const AutoProcessWithoutMappingField: FC<
  FieldRenderProps<string> & RefetchProps
> = ({
  input: { value, onChange },
  refetch,
  loading,
  dependentFieldValues,
}) => {
  const { formatMessage } = useIntl()
  const [isOpen, setIsOpen] = useState(false)
  const {
    hide_internal_attendees_in_book_meeting,
    offsite_enabled,
    relax_internal_attendee_presence_for_mm,
    default_mapping,
    internal_attendee_presence,
    enable_manual_scheduling,
  } = dependentFieldValues

  const { version } = useEventLocation().location!.attributes

  const getExternalReqWithoutMappingState = useCallback(
    () =>
      hide_internal_attendees_in_book_meeting === 'false' &&
      enable_manual_scheduling === 'false' &&
      offsite_enabled === 'false' &&
      relax_internal_attendee_presence_for_mm === 'false' &&
      default_mapping === 'false' &&
      internal_attendee_presence === 'true',
    [
      default_mapping,
      enable_manual_scheduling,
      hide_internal_attendees_in_book_meeting,
      internal_attendee_presence,
      offsite_enabled,
      relax_internal_attendee_presence_for_mm,
    ]
  )

  useEffect(() => {
    value === 'true' && setIsOpen(!getExternalReqWithoutMappingState())
  }, [value, getExternalReqWithoutMappingState])

  const { onChange: onChangeEnableExternalWidgetAutoProcess } = useField(
    'enable_external_widget_auto_process'
  ).input

  const dependentLinks = [
    hide_internal_attendees_in_book_meeting !== 'false' && {
      label: formatMessage(messages.hideInternalAttendeeLabel),
      path: Paths.PARTICIPANTS,
      group: formatMessage(messages.warningDisableLabel),
    },
    default_mapping !== 'false' && {
      label: formatMessage(messages.mappingLabel),
      path: Paths.CSM_ONLY,
      group: formatMessage(messages.warningDisableLabel),
    },
    enable_manual_scheduling !== 'false' && {
      label: formatMessage(messages.mstLabel),
      path: Paths.CSM_ONLY,
      group: formatMessage(messages.warningDisableLabel),
    },
    offsite_enabled !== 'false' && {
      label: formatMessage(messages.offsiteLabel),
      path: Paths.SETTING,
      group: formatMessage(messages.warningDisableLabel),
    },
    relax_internal_attendee_presence_for_mm !== 'false' && {
      label: formatMessage(messages.relaxRestrictionLabel),
      path: Paths.PARTICIPANTS,
      group: formatMessage(messages.warningDisableLabel),
    },
    internal_attendee_presence !== 'true' && {
      label: formatMessage(messages.atLeastOneInternalAttendeeLabel),
      path: Paths.PARTICIPANTS,
      group: formatMessage(messages.warningEnableLabel),
    },
  ].filter(Boolean) as DependentLinks[]

  return (
    <>
      {isOpen && (
        <>
          {loading && <ModalSpinner />}
          <EngagementModal
            title={formatMessage(messages.warningModalTitle)}
            dependentLinks={dependentLinks}
            onClose={() => {
              setIsOpen(false)
              !getExternalReqWithoutMappingState() && onChange('false')
            }}
            onConfirm={() => refetch()}
            version={version}
          />
        </>
      )}
      <Radio
        checked={value === 'true' ? true : false}
        onChange={e => {
          onChange(e.target.value)
          onChangeEnableExternalWidgetAutoProcess('false')
        }}
        value="true"
        overrides={instructionRadioOverride}
        labelPlacement="right"
        name="unmapped_scheduling"
      >
        {formatMessage(messages.unmappedSchedulingLabel)}
      </Radio>
    </>
  )
}

export const MethodRequestUnMappedField: VFC<
  RefetchProps & DependentFieldValues
> = ({ refetch, loading, dependentFieldValues }) => (
  <Field<string>
    name="enable_external_widget_auto_process_without_mapping"
    render={props => (
      <AutoProcessWithoutMappingField
        {...props}
        refetch={refetch}
        loading={loading}
        dependentFieldValues={dependentFieldValues}
      />
    )}
  />
)
