import { FC, ReactNode } from 'react'
import { FieldRenderProps } from 'react-final-form'
import { FormattedMessage } from 'react-intl'
import { Paragraph1 } from '@jifflenow/brightui/typography'
import {
  Anchor,
  IrpLinkWrapper,
  IrpReqPageLink,
  Flex,
  LinkDesc,
} from './styled-components'

export const EngagementLinkField: FC<FieldRenderProps<string>> = ({
  input: { value },
  label,
  disabled,
  warning,
}) => (
  <Flex>
    <Paragraph1>{label}</Paragraph1>
    <IrpLinkWrapper>
      <IrpReqPageLink>
        <FormattedMessage
          defaultMessage="<a>Click here </a>"
          description="Link label"
          values={{
            a: (chunks: ReactNode) => (
              <Anchor
                href={value}
                rel="noopener noreferrer"
                target="_blank"
                $disabled={disabled}
              >
                {chunks}
              </Anchor>
            ),
          }}
        />
      </IrpReqPageLink>
      <LinkDesc> {warning}</LinkDesc>
    </IrpLinkWrapper>
  </Flex>
)
