import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useEventLocation } from 'providers/LocationProvider'
import { EngagementTypeToggleWithDesc } from '../common'
import {
  EngagementToggleDesc,
  FieldToggleDescWrapper,
  FieldToggleLabel,
} from '../common'
import { RefetchProps } from '../common/types'
import { OnSiteFormField } from './OnSiteFormField'
import { OffSitesFormField } from './OffSitesFormField'
import { BoothTour } from './BoothTour'
import { Divider, LocationTypeBody } from './styled-components'
import { DependentFieldsProps } from './types'
import { DefaultLocationField } from './DefaultLocationField'

const messages = defineMessages({
  onSiteLabel: {
    defaultMessage: 'Onsite Locations',
    description: 'Toggle label',
  },
  onSiteDesc: {
    defaultMessage: 'Do these meetings happen in onsite locations',
    description: 'Toggle desc',
  },
  offSitesLabel: {
    defaultMessage: 'Offsites',
    description: 'Toggle label',
  },
  offSitesDesc: {
    defaultMessage: 'Do these meetings happen in offsite locations',
    description: 'Toggle desc',
  },
})

export const FormFields: VFC<RefetchProps & DependentFieldsProps> = props => {
  const { formatMessage } = useIntl()

  const { subType } = useEventLocation().location?.attributes ?? {}

  return (
    <LocationTypeBody>
      {subType === 'Emeet' && <DefaultLocationField />}
      <FieldToggleDescWrapper>
        <FieldToggleLabel>
          {formatMessage(messages.onSiteLabel)}
        </FieldToggleLabel>
      </FieldToggleDescWrapper>
      <EngagementToggleDesc>
        {formatMessage(messages.onSiteDesc)}
      </EngagementToggleDesc>
      <OnSiteFormField />
      <Divider />
      <EngagementTypeToggleWithDesc
        label={formatMessage(messages.offSitesLabel)}
        name="offsite_enabled"
        desc={formatMessage(messages.offSitesDesc)}
      >
        <OffSitesFormField {...props} />
      </EngagementTypeToggleWithDesc>
      <Divider />
      <BoothTour {...props} />
      <Divider />
    </LocationTypeBody>
  )
}
