import { useCallback } from 'react'
import { QueryFunction, useQuery, UseQueryOptions } from 'react-query'
import { fetchEngagementTypes_fetchEngagementTypes } from 'gql/types/fetchEngagementTypes'
import { fetchEngagementTypesQuery, useApolloClient } from 'gql'
import { fetchEngagementTypes } from 'rest'

type ParamsType = {
  eventName: string
  version: string
  showOnlyNonConsecutiveEngagementTypes?: boolean
  showIrpEnabled?: boolean
}

type QueryFuncResponseType = Omit<
  fetchEngagementTypes_fetchEngagementTypes & {
    irp_enabled?: boolean
    uuid?: string
  },
  '__typename'
>[]

export const useEngagementTypesQuery = (
  params: ParamsType,
  options?: Omit<
    UseQueryOptions<
      QueryFuncResponseType,
      Error,
      QueryFuncResponseType,
      ['getEngagementTypeSettings', ParamsType]
    >,
    'queryKey' | 'queryFn'
  >
) => {
  const client = useApolloClient()

  const callback = useCallback<
    QueryFunction<
      QueryFuncResponseType,
      ['getEngagementTypeSettings', ParamsType]
    >
  >(
    async ({
      queryKey: [
        ,
        {
          version,
          eventName,
          showIrpEnabled,
          showOnlyNonConsecutiveEngagementTypes,
        },
      ],
    }) => {
      if (version === '2') {
        const {
          data: { fetchEngagementTypes },
        } = await fetchEngagementTypesQuery(client, {
          variables: {
            ...(showOnlyNonConsecutiveEngagementTypes && {
              showOnlyNonConsecutiveEngagementTypes: true,
            }),
            showCompleted: true,
          },
          fetchPolicy: 'network-only',
        })

        return fetchEngagementTypes
      } else {
        const data = await fetchEngagementTypes({
          path: { eventName },
          query: {
            ...(showIrpEnabled && { show_irp_enabled: 'true' }),
            ...(showOnlyNonConsecutiveEngagementTypes && {
              show_only_non_consecutive_engagement_types: 'true',
            }),
          },
        })

        return data.data
      }
    },
    [client]
  )

  return useQuery(['getEngagementTypeSettings', params], callback, options)
}
