import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useEventLocation } from 'providers/LocationProvider'
import { MasterEngagementTypes } from 'gql/types/globalTypes'
import { EngagementTypeToggle } from '../common'
import { useEngagementType } from '../EngagementTypeProvider'
import { BorderStyled, TopicOptionsBody } from './styled-components'
import { SectionTitle } from './styled-components'

const togglesLabel = defineMessages({
  labelUnmappedUsers: {
    defaultMessage: 'Show unmapped users for topic',
    description: 'Field Label',
  },
  labelUnmappedRooms: {
    defaultMessage: 'Show unmapped rooms for topic',
    description: 'Field Label',
  },
  labelAllRoomsMapped: {
    defaultMessage:
      "Show ALL rooms mapped to the topic (under 'RECOMMENDED'), irrespective of their Meeting Type mapping",
    description: 'Field Label',
  },
  labelSelfNominate: {
    defaultMessage: 'Allow requestor to nominate themselves for group meetings',
    description: 'Field Label',
  },
  labelAllowInternalAttendeeNomination: {
    defaultMessage: 'Allow Internal Attendee(s) to be added to a group meeting',
    description: 'Field Label',
  },
  sectionTitle: {
    defaultMessage: 'Track Options',
    description: 'Section title',
  },
})

export const FormFields: VFC = () => {
  const { formatMessage } = useIntl()
  const {
    enable_user_to_topic,
    enable_room_to_topic,
  } = useEventLocation().configs!
  const {
    masterEngagementType,
  } = useEngagementType().engagementType.templateEngagementType
  const isGeneralMeeting =
    masterEngagementType === MasterEngagementTypes.General

  return (
    <TopicOptionsBody>
      <BorderStyled />
      {enable_user_to_topic === 'true' && (
        <>
          <EngagementTypeToggle
            label={formatMessage(togglesLabel.labelUnmappedUsers)}
            name="show_unmapped_users_for_topic"
          />
          <BorderStyled />
        </>
      )}
      {enable_room_to_topic === 'true' && isGeneralMeeting && (
        <>
          <EngagementTypeToggle
            label={formatMessage(togglesLabel.labelUnmappedRooms)}
            name="show_unmapped_rooms_for_topic"
          />
          <BorderStyled />
          <EngagementTypeToggle
            label={formatMessage(togglesLabel.labelAllRoomsMapped)}
            name="show_all_mapped_rooms_for_topic"
          />
          <BorderStyled />
        </>
      )}
      {!isGeneralMeeting && (
        <>
          <SectionTitle>
            {formatMessage(togglesLabel.sectionTitle)}
          </SectionTitle>
          <EngagementTypeToggle
            label={formatMessage(togglesLabel.labelSelfNominate)}
            name="allow_requestor_to_self_nominate_to_group_meeting"
          />
          <BorderStyled />
          <EngagementTypeToggle
            label={formatMessage(
              togglesLabel.labelAllowInternalAttendeeNomination
            )}
            name="allow_internal_attendee_to_group_meeting"
          />
          <BorderStyled />
        </>
      )}
    </TopicOptionsBody>
  )
}
