import { defineMessages, useIntl } from 'react-intl'
import { VFC, useState, useEffect } from 'react'
import { useFetchByMasterEngagementTypeAndLocation } from 'gql'
import { showErrorToast } from 'utils/toast'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { useEngagementType } from '../EngagementTypeProvider'
import { EngagementCheckBoxField } from './EngagementCheckBoxField'
import { CopyEngagementTypeProps, EngagementTypeOptions } from './types'

const messages = defineMessages({
  modalTitle: {
    defaultMessage: 'Copy to following engagement types',
    description: 'Header title',
  },
  modalDesc: {
    defaultMessage:
      'There maybe some conflicts/dependencies in copying the settings.',
    description: 'Description title',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  all: {
    defaultMessage: 'All',
    description: 'Options label',
  },
})

export const CopyEngagementType: VFC<CopyEngagementTypeProps> = ({
  setIsSelectAll,
  isSelectAll,
  setExcludeEngagementUuids,
  setToEngagementUuids,
}) => {
  const { formatMessage } = useIntl()
  const { id: engagementTypeId } = useEngagementType().engagementType

  const [engagementType, setEngagementType] = useState<EngagementTypeOptions[]>(
    []
  )

  const {
    loading,
    fetchByMasterEngagementTypeAndLocation = [],
  } = useFetchByMasterEngagementTypeAndLocation({
    variables: {
      engagementId: parseInt(engagementTypeId),
    },
    notifyOnNetworkStatusChange: true,
    fetchPolicy: 'cache-and-network',
    onError: () => {
      showErrorToast(formatMessage(messages.genericError))
    },
  })

  useEffect(() => {
    setEngagementType([
      {
        __typename: 'EngagementType',
        id: 'all',
        isActive: false,
        name: formatMessage(messages.all),
        uuid: 'all',
      },
      ...fetchByMasterEngagementTypeAndLocation
        .filter(engagement => engagement.id !== engagementTypeId)
        .map(engagement => ({
          ...engagement,
          isActive: false,
        })),
    ])
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [fetchByMasterEngagementTypeAndLocation.length])

  useEffect(() => {
    if (isSelectAll) {
      setExcludeEngagementUuids(
        engagementType
          .filter(item => item.uuid !== 'all' && item.isActive === false)
          .map(item => item.uuid)
      )
    } else {
      setToEngagementUuids(
        engagementType
          .filter(item => item.uuid !== 'all' && item.isActive === true)
          .map(item => item.uuid)
      )
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [engagementType])

  const handleChange = (checked: boolean, uuid: string) => {
    if (uuid === 'all') {
      setEngagementType([
        ...engagementType.map(engagement => ({
          ...engagement,
          isActive: checked,
        })),
      ])
      setIsSelectAll(checked)
    } else {
      const engagementOptions = [
        {
          __typename: 'EngagementType',
          id: 'all',
          isActive: false,
          name: formatMessage(messages.all),
          uuid: 'all',
        },
        ...engagementType.filter(item => item.uuid !== 'all'),
      ]
      const index = engagementType.findIndex(element => element.uuid === uuid)
      const activeEngagement = engagementType
        .filter(item => item.uuid === uuid)
        .map(item =>
          item.uuid === uuid ? { ...item, isActive: checked } : item
        )
      engagementOptions.splice(index, 1, ...activeEngagement)
      setEngagementType(engagementOptions)
    }
  }

  return (
    <>
      {loading && <ModalSpinner />}
      {engagementType.map((engagement, index) => (
        <EngagementCheckBoxField
          key={index}
          label={engagement.name}
          isActive={engagement.isActive}
          uuid={engagement.uuid}
          handleChange={handleChange}
        />
      ))}
    </>
  )
}
