import { useEffect, useState, useMemo, VFC } from 'react'
import { Form as FinalForm } from 'react-final-form'
import { defineMessages, useIntl } from 'react-intl'
import { Prompt } from 'react-router-dom'
import { useMutation, useQuery } from 'react-query'
import { Config, FORM_ERROR } from 'final-form'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import {
  mutateBulkUpsertEngagementSettings,
  useApolloClient,
  useGetEngagementTypeSettings,
} from 'gql'
import { BulkEngagementSettingUpdateInput } from 'gql/types/globalTypes'
import { getEngagementTypeSettings_getEngagementTypeSettings_value_CustomNameValue as CustomNameValue } from 'gql/types/getEngagementTypeSettings'
import {
  bulkUpsertEngagementSettingsVariables,
  bulkUpsertEngagementSettings_bulkUpsertEngagementSettings_engagementSettings_value_CustomNameValue as UpsertCustomNameValue,
} from 'gql/types/bulkUpsertEngagementSettings'
import { useAllEventUsers } from 'rest'
import { useEventLocation } from 'providers/LocationProvider'
import { changeMeetingTypeMutationFn, changeMeetingTypeQueryFn } from 'rest'
import {
  Body,
  Footer,
  StyledForm,
  Container,
  usePageTitle,
  Header,
} from '../common'
import { useSettingsLoader } from '../common/useSettingsLoader'
import { updateQueryResults } from '../common/utils'
import { useEngagementType } from '../EngagementTypeProvider'
import { AdvancedSettingsLoader } from './AdvancedSettingsLoader'
import { AdvancedSettingsTitle } from './styled-components'
import { FormField } from './FormField'
import { DependentFields, FormProps, FormValues } from './types'

const LoadingShimmer = () => (
  <Body>
    <Container>
      <AdvancedSettingsLoader />
    </Container>
  </Body>
)

const messages = defineMessages({
  pageTitle: {
    defaultMessage: 'Advanced Settings',
    description: 'Page title',
  },
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
  updateError: {
    defaultMessage:
      '{failureCount, number} out of {totalCount, number} {totalCount, plural, one {option} other {options}} failed to save',
    description: 'Error message',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  sectionTitle: {
    defaultMessage: 'Advanced Settings',
    description: 'Section title',
  },
  successMessage: {
    defaultMessage: 'Your changes have been saved successfully',
    description: 'Success message',
  },
  fieldErrorMessage: {
    defaultMessage: 'Error saving changes',
    description: 'field error message',
  },
})

const settings = [
  'minimum_timeslot',
  'enable_vcon_link',
  'enable_full_day_meeting_type_availability',
  'enable_view_calendar_in_other_timezone',
  'instruction_book_meeting',
  'book_meeting_instructions_header',
  'book_meeting_instructions_body',
  'consecutive_meeting_instructions_body',
  'show_summary',
  'decline_comments_enabled',
  'predefined_decline_comments',
  'cancel_comments_enabled',
  'cc_notification_reply_to',
  'predefined_cancel_comments',
  'display_filled_customer_info_only',
  'send_ics_reply_to_requestor',
  'send_requestor_mail_to_ea',
  'send_attendee_ics_to_ea',
  'send_copy_of_mm_email_to_ea_participant',
  'include_briefing_docs',
  'briefing_docs_in_mobile',
  'briefing_doc_as_attachment',
  'briefing_doc_as_link',
  'briefing_docs_in_notification_templates',
  'doc_access_only_for_logged_in',
  'ea_can_approve_or_cancel_meeting',
  'ea_can_edit_meeting_of_their_executives',
  'can_edit_approved_meeting',
  'default_state_of_are_you_attending',
  'are_you_attending_non_changeable',
  'show_all_requestors',
  'allow_requestors_to_reinvite',
]

const dependentSettings: Array<keyof DependentFields> = [
  'min_duration',
  'offsite_enabled',
  'offsite_min_duration',
  'booth_tour_enabled',
  'booth_tour_min_duration',
]

const Form: VFC<FormProps> = ({
  handleSubmit,
  dirty,
  loading,
  eventUsers,
  meetingType,
}) => {
  const { formatMessage } = useIntl()
  const unload = useBeforeUnload()
  const showLoading = useSettingsLoader(loading)

  useEffect(() => {
    unload(dirty)
  }, [dirty, unload])

  return (
    <StyledForm onSubmit={handleSubmit} noValidate>
      <Prompt when={dirty} message={formatMessage(messages.promptMessage)} />
      <Header />
      {showLoading ? (
        <LoadingShimmer />
      ) : (
        <Body>
          <Container>
            <AdvancedSettingsTitle>
              {formatMessage(messages.sectionTitle)}
            </AdvancedSettingsTitle>
            <FormField meetingType={meetingType} eventUsers={eventUsers} />
          </Container>
          <Footer />
        </Body>
      )}
    </StyledForm>
  )
}

export const AdvancedSettings: VFC = () => {
  const [showLoader, setShowLoader] = useState(false)
  const { formatMessage } = useIntl()
  usePageTitle(formatMessage(messages.pageTitle))
  const client = useApolloClient()
  const {
    id: engagementTypeId,
    uuid: activity_uuid,
  } = useEngagementType().engagementType
  const {
    getEngagementTypeSettings = [],
    loading,
    updateQuery,
  } = useGetEngagementTypeSettings({
    variables: {
      engagementTypeId,
      names: [...settings, ...dependentSettings],
    },
    fetchPolicy: 'cache-and-network',
    onError: () => showErrorToast(formatMessage(messages.genericError)),
  })

  const {
    id: current_location_uuid,
    attributes: { systemName: subSystem },
  } = useEventLocation().location!

  const eventUsers =
    useAllEventUsers({
      subSystem,
      locationId: current_location_uuid,
      page: {
        number: 1,
        size: 1000,
      },
      eventRoles: ['MM', 'JMM'],
    }).data?.data ?? []

  const {
    isRefetching,
    data: { data: changeMeetingType } = {},
    refetch: refetchChangeMeetingTypes,
  } = useQuery(
    [
      'changeMeetingType',
      {
        path: { eventName: subSystem },
        query: {
          current_location_uuid,
          activity_uuid,
        },
      },
    ],
    changeMeetingTypeQueryFn,
    {
      onError: () => showErrorToast(formatMessage(messages.genericError)),
    }
  )

  const { mutateAsync } = useMutation(changeMeetingTypeMutationFn)

  const initialValues = useMemo(
    () => ({
      ...getEngagementTypeSettings.reduce(
        (acc, { name, value }) => ({
          ...acc,
          [name]: (value as CustomNameValue).value,
        }),
        {}
      ),
      change_meeting_type_links: JSON.stringify(
        changeMeetingType?.select_all_engagement_types
          ? ['ALL']
          : changeMeetingType?.activities
              .filter(({ selected }) => selected)
              .map(({ id }) => id)
      ),
    }),
    [
      getEngagementTypeSettings,
      changeMeetingType?.select_all_engagement_types,
      changeMeetingType?.activities,
    ]
  )

  const handleSubmit: Config<FormValues>['onSubmit'] = async (
    values,
    { change }
  ) => {
    try {
      setShowLoader(true)
      const configsToUpdate = getEngagementTypeSettings.reduce<
        BulkEngagementSettingUpdateInput[]
      >((acc, { name, value: previousValue }) => {
        const { display_name, value } = previousValue as CustomNameValue

        return value !== values[name as keyof FormValues]
          ? [
              ...acc,
              {
                name,
                customValue: {
                  display_name,
                  value: values[name as keyof FormValues],
                },
              },
            ]
          : acc
      }, [])

      if (
        initialValues.change_meeting_type_links !==
        values.change_meeting_type_links
      ) {
        const value = JSON.parse(
          values.change_meeting_type_links.includes('ALL')
            ? JSON.stringify(['ALL'])
            : values.change_meeting_type_links
        )
        await mutateAsync({
          eventName: subSystem,
          payload: {
            current_location_uuid,
            activity_uuid,
            transfer_activities: value,
          },
        })
        value.includes('ALL') &&
          change('change_meeting_type_links', JSON.stringify(value))
        refetchChangeMeetingTypes()
        setShowLoader(false)
        showSuccessToast(formatMessage(messages.successMessage))
      }
      const variables: bulkUpsertEngagementSettingsVariables = {
        engagementTypeId,
        configsToUpdate,
      }
      if (configsToUpdate.length) {
        const { failureCount, totalCount, engagementSettings, successCount } = (
          await mutateBulkUpsertEngagementSettings(client, {
            variables,
          })
        ).data!.bulkUpsertEngagementSettings
        setShowLoader(false)
        if (failureCount) {
          showErrorToast(
            formatMessage(messages.updateError, { totalCount, failureCount })
          )
          engagementSettings.forEach(
            ({ success, name, value }) =>
              !success &&
              change(
                name as keyof FormValues,
                (value as UpsertCustomNameValue).value ?? undefined
              )
          )
          const errors = engagementSettings.reduce(
            (acc, { success, name }) =>
              success
                ? acc
                : {
                    ...acc,
                    [name]: formatMessage(messages.fieldErrorMessage),
                  },
            {}
          )
          if (successCount) {
            updateQueryResults(updateQuery, engagementSettings)
          }

          return errors
        }
        showSuccessToast(formatMessage(messages.successMessage))
        updateQueryResults(updateQuery, engagementSettings)
      }

      return undefined
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
      return { [FORM_ERROR]: { code: 'GenericError' } }
    }
  }

  return (
    <>
      {(showLoader || isRefetching) && <ModalSpinner />}
      <FinalForm<FormValues>
        initialValues={initialValues}
        onSubmit={handleSubmit}
        render={props => (
          <Form
            {...props}
            meetingType={changeMeetingType?.activities ?? []}
            loading={loading}
            eventUsers={eventUsers}
          />
        )}
      />
    </>
  )
}
