import { useState, VFC } from 'react'
import { FormattedMessage } from 'react-intl'
import { Button } from '@jifflenow/brightui/button'
import { AddExceptionButtonProps } from './types'
import { AddEditException as AddException } from './AddEditException'

export const AddExceptionButton: VFC<AddExceptionButtonProps> = ({
  exceptions,
  setExceptions,
}) => {
  const [isOpen, setIsOpen] = useState(false)

  return (
    <>
      {isOpen && (
        <AddException
          isOpen={isOpen}
          onClose={() => setIsOpen(false)}
          exceptions={exceptions}
          setExceptions={setExceptions}
        />
      )}
      <Button onClick={() => setIsOpen(true)}>
        <FormattedMessage
          defaultMessage="Add Exception Date"
          description="Button label"
        />
      </Button>
    </>
  )
}
